<script setup lang="ts">
/**
 * DateCell - Date Cell Renderer
 *
 * Purpose: Renders date/datetime values with proper formatting
 * Design: Supports multiple formats, locale-aware, handles invalid dates
 */

export interface DateCellProps {
  // Core
  value: Date | string | number | null | undefined

  // Formatting
  format?: 'date' | 'datetime' | 'time' | 'relative' | 'custom'
  customFormat?: Intl.DateTimeFormatOptions
  locale?: string

  // Display
  align?: 'left' | 'center' | 'right'
}

const props = withDefaults(defineProps<DateCellProps>(), {
  format: 'date',
  locale: 'en-US',
  align: 'left',
})

// Parse date
const parseDate = (value: Date | string | number | null | undefined): Date | null => {
  if (!value) return null

  if (value instanceof Date) {
    return isNaN(value.getTime()) ? null : value
  }

  const parsed = new Date(value)
  return isNaN(parsed.getTime()) ? null : parsed
}

// Format presets
const formatPresets: Record<string, Intl.DateTimeFormatOptions> = {
  date: { year: 'numeric', month: 'short', day: 'numeric' },
  datetime: {
    year: 'numeric',
    month: 'short',
    day: 'numeric',
    hour: '2-digit',
    minute: '2-digit',
  },
  time: { hour: '2-digit', minute: '2-digit', second: '2-digit' },
}

// Relative time formatting (e.g., "2 hours ago")
const formatRelative = (date: Date): string => {
  const now = new Date()
  const diffMs = now.getTime() - date.getTime()
  const diffSecs = Math.floor(diffMs / 1000)
  const diffMins = Math.floor(diffSecs / 60)
  const diffHours = Math.floor(diffMins / 60)
  const diffDays = Math.floor(diffHours / 24)

  if (diffSecs < 60) return `${diffSecs}s ago`
  if (diffMins < 60) return `${diffMins}m ago`
  if (diffHours < 24) return `${diffHours}h ago`
  if (diffDays < 30) return `${diffDays}d ago`

  return date.toLocaleDateString(props.locale)
}

// Format date value
const formattedValue = (() => {
  const date = parseDate(props.value)

  if (!date) {
    return ''
  }

  if (props.format === 'relative') {
    return formatRelative(date)
  }

  const formatOptions = props.format === 'custom'
    ? props.customFormat
    : formatPresets[props.format]

  return new Intl.DateTimeFormat(props.locale, formatOptions).format(date)
})()

// Alignment classes
const alignClasses: Record<string, string> = {
  left: 'text-left',
  center: 'text-center',
  right: 'text-right',
}
</script>

<template>
  <span
    :class="[
      'inline-block w-full',
      alignClasses[align],
    ]"
    :title="value ? new Date(value).toISOString() : undefined"
  >
    {{ formattedValue }}
  </span>
</template>
