<!--
  FilterIndicator.vue - Excel-style Unified Filter UI

  Design Philosophy (First Principles):
  1. Single continuous interface (no tab switching)
  2. Type-aware adaptive layout (date vs text vs enum)
  3. Progressive disclosure (collapsible sections)
  4. Zero cognitive overhead (all options visible)
  5. Visual error feedback (UX)
-->

<script setup lang="ts">
import { computed, ref, watch, nextTick } from 'vue'
import type { ColumnDef, GridAPI, FilterConfig, CellValue } from '../../../types'
import FilterPopover from '../../shared/FilterPopover.vue'
import ValueFilterEditor from '../../shared/filters/ValueFilterEditor.vue'
import CustomFilterEditor from '../../shared/filters/CustomFilterEditor.vue'

const props = defineProps<{
  column: ColumnDef
  gridAPI: GridAPI
}>()

const emit = defineEmits<{
  'filter-open': [field: string]
  'filter-apply': [configs: FilterConfig[]]
  'filter-clear': [field: string]
}>()

// UI state
const isOpen = ref(false)
const triggerRef = ref<HTMLElement | null>(null)

// Refs to child components
const valueFilterRef = ref<InstanceType<typeof ValueFilterEditor> | null>(null)
const customFilterRef = ref<InstanceType<typeof CustomFilterEditor> | null>(null)

const remeasureValueFilter = () => {
  valueFilterRef.value?.remeasure?.()
}

// Layout strategy: Always render Custom (top) then Values (bottom)
const isCustomExpanded = ref(false)
const defaultCustomExpanded = computed(() => {
  const t = props.column.dataType || 'text'
  return (
    t === 'date' ||
    t === 'datetime' ||
    t === 'time' ||
    t === 'interval' ||
    t === 'number' ||
    t === 'currency' ||
    t === 'percentage' ||
    t === 'json' ||
    t === 'multiline'
  )
})

const showValueList = computed(() => {
  const type = props.column.dataType
  return !(type === 'date' || type === 'datetime' || type === 'json' || type === 'multiline')
})

const columnFilters = computed<FilterConfig[]>(() => {
  try {
    const filters = (props.gridAPI.getState().config?.filters || []) as FilterConfig[]
    return filters.filter(f => f.field === props.column.field)
  } catch {
    return []
  }
})

const valueFilterConfig = computed<FilterConfig | null>(() =>
  columnFilters.value.find(f => f.operator === 'in' || f.operator === 'notIn') || null
)

const customFilterConfig = computed<FilterConfig | null>(() =>
  columnFilters.value.find(f => !['in', 'oneOf', 'notIn'].includes(f.operator)) || null
)

const valueFilterState = computed<{ operator: 'in' | 'notIn'; value: unknown[] } | null>(() => {
  const filter = valueFilterConfig.value
  if (!filter) return null
  const values = Array.isArray(filter.value) ? filter.value : []
  return { operator: filter.operator as 'in' | 'notIn', value: values }
})

const isActive = computed(() => columnFilters.value.length > 0)

watch(
  columnFilters,
  () => {
    if (customFilterConfig.value) {
      isCustomExpanded.value = true
    } else if (!isOpen.value) {
      isCustomExpanded.value = false
    }
  },
  { immediate: true }
)

watch(
  () => props.column,
  () => {
    if (!customFilterConfig.value && !isOpen.value) {
      isCustomExpanded.value = false
    }
  }
)

// Lazy data loading (only when popover is open)
const allData = computed(() => {
  if (!isOpen.value) return []
  return props.gridAPI.getData() || []
})

// Domain size heuristic for text: if unique non-null values > 50, prefer custom
const valueDomainSize = computed(() => {
  if (!isOpen.value) return 0
  try {
    const data = allData.value
    const field = props.column.field
    const set = new Set<unknown>()
    for (let i = 0; i < data.length; i++) {
      const v = (data[i] as Record<string, unknown>)[field]
      if (v !== null && v !== undefined) {
        set.add(v)
        if (set.size > 50) break
      }
    }
    return set.size
  } catch {
    return 0
  }
})

// Enum options
const enumOptions = computed<{ label: string; value: unknown }[]>(() => {
  if (props.column.dataType !== 'enum') return []
  const opts = props.column.options
  if (Array.isArray(opts) && opts.length > 0) {
    return opts.map(o =>
      typeof o === 'object' && o !== null && 'value' in o
        ? {
            label: String((o as Record<string, unknown>).label ?? (o as Record<string, unknown>).value),
            value: (o as Record<string, unknown>).value,
          }
        : { label: String(o), value: o }
    )
  }
  // Derive from data
  try {
    const data = props.gridAPI.getData() || []
    const set = new Set<unknown>()
    for (let i = 0; i < data.length && set.size < 100; i++) {
      const v = (data[i] as Record<string, unknown>)[props.column.field]
      if (v !== undefined && v !== null) set.add(v)
    }
    return Array.from(set)
      .sort((a, b) => String(a).localeCompare(String(b)))
      .map(v => ({ label: String(v), value: v }))
  } catch {
    return []
  }
})

function open() {
  emit('filter-open', props.column.field)
  isOpen.value = true
  if (customFilterConfig.value) {
    isCustomExpanded.value = true
  } else {
    isCustomExpanded.value = defaultCustomExpanded.value
  }
  // After open, apply domain heuristic for text
  nextTick(() => {
    const t = props.column.dataType || 'text'
    if (t === 'text' && valueDomainSize.value > 50) {
      isCustomExpanded.value = true
    }
  })
}

function close() {
  isOpen.value = false
}

// Unified apply function - reads state from child components
function applyFilter() {
  const cfg = props.gridAPI.getState().config
  const prev = (cfg?.filters || []) as FilterConfig[]
  const rest = prev.filter(f => f.field !== props.column.field)

  const nextFilters: FilterConfig[] = []

  if (showValueList.value) {
    const vf = valueFilterRef.value?.getValueFilter?.()
    if (vf && Array.isArray(vf.value)) {
      const normalized = vf.value.filter(v => v !== undefined) as CellValue[]
      if (normalized.length > 0) {
        nextFilters.push({
          field: props.column.field,
          operator: vf.operator as FilterConfig['operator'],
          value: normalized,
        })
      }
    }
  }

  const customConfig = customFilterRef.value?.getFilterConfig?.()
  if (customConfig) {
    nextFilters.push({
      field: props.column.field,
      operator: customConfig.operator,
      value: customConfig.value as CellValue | CellValue[] | [CellValue, CellValue],
    })
  }

  const updatedFilters = nextFilters.length > 0 ? [...rest, ...nextFilters] : rest

  props.gridAPI.updateConfig({ filters: updatedFilters })

  if (nextFilters.length > 0) {
    emit('filter-apply', nextFilters)
  } else {
    emit('filter-clear', props.column.field)
  }
  close()
}

function clearFilter() {
  const cfg = props.gridAPI.getState().config
  const prev = (cfg?.filters || []) as FilterConfig[]
  const rest = prev.filter(f => f.field !== props.column.field)
  props.gridAPI.updateConfig({ filters: rest })
  emit('filter-clear', props.column.field)
  close()
}

// Listen to programmatic open requests from FilterBar
if (props.gridAPI.on) {
  props.gridAPI.on('column-open-filter', payload => {
    if (payload && payload.field === props.column.field) {
      open()
    }
  })
}
</script>

<template>
  <div ref="triggerRef" class="relative inline-flex items-center ml-1 select-none" @click.stop>
    <!-- Trigger Button -->
    <button
      :class="[
        'inline-flex h-5 w-5 items-center justify-center rounded cursor-pointer transition-all duration-200 hover:scale-105 focus:outline-none focus:ring-1 focus:ring-[var(--ht-primary)]',
        isActive ? 'text-[var(--ht-primary)] bg-[var(--ht-primary-subtle)]/70 shadow-sm' : 'text-[var(--ht-text-muted)] hover:text-[var(--ht-text)] hover:bg-[var(--ht-primary-subtle)]/60',
      ]"
      :aria-label="`Filter ${column.title}`"
      :aria-pressed="isActive ? 'true' : 'false'"
      :title="isActive ? 'Clear filter' : 'Set filter'"
      @click.stop="isOpen ? close() : open()"
    >
      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="h-3.5 w-3.5 transition-transform duration-200">
        <path d="M4 4h16l-6 8v5l-4 2v-7L4 4Z" />
      </svg>
    </button>

    <!-- Popover Content -->
    <FilterPopover
      :open="isOpen"
      :anchor="triggerRef"
      :title="`Filter: ${column.title}`"
      @update:open="isOpen = $event"
      @opened="remeasureValueFilter"
    >
      <!-- Simplified unified interface: Custom (top) + Values (bottom) -->
      <div class="space-y-3">
        <!-- Custom Filter Section (Collapsible) -->
        <div class="space-y-2">
          <button
            class="flex w-full items-center justify-between text-xs font-semibold text-[var(--ht-text)] hover:text-[var(--ht-primary)] transition-colors"
            @click="isCustomExpanded = !isCustomExpanded"
          >
            <span>Custom Conditions</span>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              stroke-width="2"
              stroke-linecap="round"
              stroke-linejoin="round"
              class="h-4 w-4 transition-transform duration-200"
              :class="{ 'rotate-180': isCustomExpanded }"
            >
              <path d="m6 9 6 6 6-6" />
            </svg>
          </button>

          <div v-show="isCustomExpanded" class="overflow-hidden transition-all duration-200">
            <CustomFilterEditor
              ref="customFilterRef"
              :field="column.field"
              :data-type="column.dataType || 'text'"
              :current-filter="customFilterConfig || null"
              :enum-options="enumOptions"
            />
          </div>
        </div>

        <!-- Separator and Value Filter Section -->
        <div v-if="showValueList" class="border-t border-[var(--ht-border)]" />
        <div v-if="showValueList" class="space-y-2">
          <div class="text-xs font-semibold text-[var(--ht-text)]">Filter by Values</div>
          <ValueFilterEditor
            ref="valueFilterRef"
            :field="column.field"
            :all-data="allData"
            :current-filter="valueFilterState"
          />
        </div>

        <!-- Unified Action Buttons (always visible at bottom) -->
        <div class="flex items-center justify-end gap-2 pt-3 border-t border-[var(--ht-border)]">
          <button
            class="h-8 rounded border border-[var(--ht-border)] px-2 text-xs text-[var(--ht-text)] hover:bg-[var(--ht-bg-subtle)] transition-colors"
            @click="close"
          >
            Cancel
          </button>
          <button
            class="h-8 rounded bg-[var(--ht-primary)] px-3 text-xs text-white hover:opacity-90 transition-colors"
            @click="applyFilter"
          >
            Apply
          </button>
        </div>

        <!-- Clear Filter Button (separate, always visible when active) -->
        <div v-if="isActive" class="border-t border-[var(--ht-border)] pt-2">
          <button
            class="w-full h-8 rounded border border-[var(--ht-border)] text-xs text-[var(--ht-text)] hover:bg-[var(--ht-bg-subtle)] transition-colors"
            @click="clearFilter"
          >
            Clear Filter
          </button>
        </div>
      </div>
    </FilterPopover>
  </div>
</template>
