<!--
  CellBooleanEditor.vue - Boolean Toggle Editor (using HtCheckbox)

  Responsibilities:
  1. Toggle switch or checkbox for boolean values
  2. Keyboard navigation (Space, Enter)
  3. Visual feedback for state changes
  4. Accessibility compliance
-->

<script setup lang="ts">
import { ref, nextTick, onMounted, computed, watch } from 'vue'
import type { GridAPI, ColumnDef, RowData, CellValue } from '../../../types'
import HtCheckbox from '../../primitives/HtCheckbox.vue'
import HtButton from '../../primitives/HtButton.vue'

// Props
const props = defineProps<{
  value: CellValue
  row: RowData
  column: ColumnDef
  cellRect: DOMRect
  gridAPI: GridAPI
}>()

// Emits
const emit = defineEmits<{
  'update:value': [value: CellValue]
  commit: []
  cancel: []
}>()

// Local state
const checkboxRef = ref<InstanceType<typeof HtCheckbox> | HTMLButtonElement>()
const localValue = ref<boolean>(Boolean(props.value))

// Computed
const containerStyle = computed(() => ({
  position: 'absolute' as const,
  left: `${props.cellRect.left}px`,
  top: `${props.cellRect.top}px`,
  width: `${props.cellRect.width}px`,
  height: `${props.cellRect.height}px`,
  zIndex: 1000,
}))

const containerClasses = computed(() => [
  'ht-cell-editor',
  'ht-boolean-editor',
  'flex items-center justify-center',
  'border-2 border-[var(--ht-primary)]',
  'bg-[var(--ht-bg)]',
  'rounded',
  'shadow-md',
])

const displayMode = computed(() => {
  return props.column.booleanDisplay || 'checkbox' // 'checkbox', 'toggle', 'buttons'
})

const labels = computed(() => ({
  true: props.column.trueLabel || 'Yes',
  false: props.column.falseLabel || 'No',
}))

// Methods
const toggleValue = () => {
  const newValue = !localValue.value
  localValue.value = newValue

  emit('update:value', newValue)

  // Update plugin state
  if (props.gridAPI?.updateCellEditValue) {
    props.gridAPI.updateCellEditValue(newValue)
  }

  // Auto-commit boolean changes unless configured otherwise
  if (props.column.autoCommit !== false) {
    nextTick(() => {
      emit('commit')
    })
  }
}

const setValue = (value: boolean) => {
  localValue.value = value

  emit('update:value', value)

  // Update plugin state
  if (props.gridAPI?.updateCellEditValue) {
    props.gridAPI.updateCellEditValue(value)
  }

  // Auto-commit boolean changes
  if (props.column.autoCommit !== false) {
    nextTick(() => {
      emit('commit')
    })
  }
}

const handleCheckboxChange = (checked: boolean | 'indeterminate') => {
  // Cell boolean editor doesn't use indeterminate state, convert to boolean
  const booleanValue = checked === true
  localValue.value = booleanValue

  emit('update:value', booleanValue)

  // Update plugin state
  if (props.gridAPI?.updateCellEditValue) {
    props.gridAPI.updateCellEditValue(booleanValue)
  }

  // Auto-commit
  if (props.column.autoCommit !== false) {
    nextTick(() => {
      emit('commit')
    })
  }
}

const handleKeydown = (event: KeyboardEvent) => {
  switch (event.key) {
    case 'Enter':
    case ' ':
      event.preventDefault()
      event.stopPropagation()
      if (displayMode.value === 'buttons') {
        // For buttons mode, Enter/Space commits current value
        emit('commit')
      } else {
        // For checkbox/toggle, toggle the value
        toggleValue()
      }
      break
    case 'Escape':
      event.preventDefault()
      event.stopPropagation()
      emit('cancel')
      break
    case 'Tab':
      event.preventDefault()
      event.stopPropagation()
      emit('commit')
      break
    case 'ArrowLeft':
    case 'ArrowDown':
      if (displayMode.value === 'buttons') {
        event.preventDefault()
        setValue(false)
      }
      break
    case 'ArrowRight':
    case 'ArrowUp':
      if (displayMode.value === 'buttons') {
        event.preventDefault()
        setValue(true)
      }
      break
  }
}

const focus = () => {
  // Focus the appropriate element based on display mode
  if (displayMode.value === 'buttons') {
    const button = checkboxRef.value as HTMLButtonElement
    if (button) {
      button.focus()
    }
  } else {
    // For checkbox/toggle, focus the native input inside HtCheckbox
    const checkbox = checkboxRef.value as InstanceType<typeof HtCheckbox>
    const inputElement = checkbox?.$el?.querySelector('input')
    if (inputElement) {
      inputElement.focus()
    }
  }
}

// Watch for external value changes
watch(
  () => props.value,
  newValue => {
    localValue.value = Boolean(newValue)
  }
)

// Lifecycle
onMounted(async () => {
  await nextTick()
  focus()
})

// Expose methods for parent
defineExpose({
  focus,
})
</script>

<template>
  <div :class="containerClasses" :style="containerStyle" @keydown="handleKeydown">
    <!-- Checkbox Mode -->
    <div v-if="displayMode === 'checkbox'" class="flex items-center gap-2">
      <HtCheckbox
        ref="checkboxRef"
        :model-value="localValue"
        variant="default"
        size="md"
        :label="localValue ? labels.true : labels.false"
        @update:model-value="handleCheckboxChange"
      />
    </div>

    <!-- Toggle Mode (using HtCheckbox styled as toggle) -->
    <div v-else-if="displayMode === 'toggle'" class="flex items-center gap-2">
      <div class="ht-toggle-container flex items-center gap-2">
        <!-- Custom toggle switch -->
        <div
          class="ht-toggle relative inline-block w-10 h-6 cursor-pointer"
          tabindex="0"
          role="switch"
          :aria-checked="localValue"
          @click="toggleValue"
          @keydown="handleKeydown"
        >
          <div
            class="ht-toggle-bg w-full h-full rounded-full border-2 transition-colors duration-200"
            :class="localValue ? 'bg-[var(--ht-primary)] border-[var(--ht-primary)]' : 'bg-[var(--ht-bg-subtle)] border-[var(--ht-border)]'"
          />
          <div
            class="ht-toggle-thumb absolute top-0.5 left-0.5 w-4 h-4 bg-[var(--color-ht-white)] rounded-full shadow-sm transform transition-transform duration-200"
            :class="{ 'translate-x-4': localValue }"
          />
        </div>
        <span class="text-sm text-[var(--ht-text)] select-none">
          {{ localValue ? labels.true : labels.false }}
        </span>
      </div>
    </div>

    <!-- Button Mode -->
    <div v-else-if="displayMode === 'buttons'" class="flex gap-1">
      <HtButton
        ref="checkboxRef"
        type="button"
        size="xs"
        :variant="localValue ? 'primary' : 'secondary'"
        tabindex="0"
        @click="setValue(true)"
      >
        {{ labels.true }}
      </HtButton>
      <HtButton
        type="button"
        size="xs"
        :variant="!localValue ? 'primary' : 'secondary'"
        @click="setValue(false)"
      >
        {{ labels.false }}
      </HtButton>
    </div>
  </div>
</template>

<style scoped>
.ht-cell-editor {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

.ht-boolean-editor:focus-within {
  box-shadow: 0 0 0 1px var(--ht-primary);
}

/* Toggle styling */
.ht-toggle:focus {
  outline: none;
}

.ht-toggle:focus .ht-toggle-bg {
  box-shadow: 0 0 0 1px var(--ht-primary);
}

.ht-toggle-thumb {
  transition: transform 0.2s ease-in-out;
}

/* Ensure proper positioning */
.ht-cell-editor {
  transform: translateZ(0);
  backface-visibility: hidden;
}

/* Hover effects */
.ht-toggle:hover .ht-toggle-bg {
  opacity: 0.8;
}
</style>
