<template>
  <span
    class="ht-seq-cell"
    data-ht-functional="seq"
  >
    {{ displayNumber }}
  </span>
</template>

<script setup lang="ts">
import { computed } from 'vue'
import type { SeqColumnOptions } from '../../types/functional-columns'

interface Props {
  /** Zero-based row index in the current view */
  rowIndex: number
  /** Column configuration options */
  options?: SeqColumnOptions
  /** Pagination offset (if applicable) */
  paginationOffset?: number
}

const props = withDefaults(defineProps<Props>(), {
  options: () => ({}),
  paginationOffset: 0,
})

const baseNumber = computed(() => props.options?.baseNumber ?? 1)

const displayNumber = computed(() => {
  const actualIndex = props.rowIndex + props.paginationOffset
  const number = actualIndex + baseNumber.value

  // Use custom formatter if provided
  if (props.options?.formatter) {
    return props.options.formatter(actualIndex, baseNumber.value)
  }

  return number
})
</script>

<style scoped>
.ht-seq-cell {
  font-variant-numeric: tabular-nums;
  color: var(--ht-fg-muted, rgb(107 114 128));
  font-size: 0.875rem;
  user-select: none;
}
</style>
