import { reactive } from 'vue'
import { createLogger } from '../utils/logger'
import type { DataGridProps, GridPlugin } from '../types'

export interface UseHappyTableOptions {
  /** Base props applied to the DataGrid component */
  props?: Partial<DataGridProps>
  /** Optional override for grid data */
  data?: DataGridProps['data']
  /** Optional override for grid columns */
  columns?: DataGridProps['columns']
  /** Optional override for grid plugins */
  plugins?: GridPlugin[]
  /** Convenience shortcut to set grid height */
  height?: DataGridProps['height']
  /** Toggle the inline keyboard shortcut hint */
  keyboardHelpHint?: DataGridProps['keyboardHelpHint']
  /** Logger name */
  loggerModule?: string
}

export interface UseHappyTableResult {
  gridProps: DataGridProps
  updateGridProps: (props: Partial<DataGridProps>) => void
  logger: ReturnType<typeof createLogger>
}

const DEFAULT_GRID_PROPS: Pick<Required<DataGridProps>, 'data' | 'columns' | 'plugins' | 'height' | 'showKeyboardShortcuts' | 'keyboardHelpHint'> = {
  data: [],
  columns: [],
  plugins: [],
  height: 400,
  showKeyboardShortcuts: false,
  keyboardHelpHint: false,
}

export function useHappyTable(options: UseHappyTableOptions = {}): UseHappyTableResult {
  const {
    props,
    data,
    columns,
    plugins,
    height,
    keyboardHelpHint,
    loggerModule = 'HappyTable',
  } = options

  const logger = createLogger(loggerModule)

  const gridProps = reactive<DataGridProps>({
    ...DEFAULT_GRID_PROPS,
    ...(props ?? {}),
  })

  if (data !== undefined) gridProps.data = data
  if (columns !== undefined) gridProps.columns = columns
  if (plugins !== undefined) gridProps.plugins = plugins
  if (height !== undefined) gridProps.height = height
  if (keyboardHelpHint !== undefined) gridProps.keyboardHelpHint = keyboardHelpHint

  const updateGridProps = (nextProps: Partial<DataGridProps>) => {
    Object.assign(gridProps, nextProps)
  }

  return {
    gridProps,
    updateGridProps,
    logger,
  }
}
