<!--
  GridFooter.vue - Grid Footer Component

  Responsibilities:
  1. Display row count and selection info
  2. Performance metrics (development mode)
  3. Status messages and loading indicators
  4. Action buttons area

  Vue 3 Pattern: Simple status display component
-->

<script setup lang="ts">
import { computed } from 'vue'
import type { RenderMetrics } from '../../../types'

// Props
const props = defineProps<{
  totalRows: number
  selectedCount: number
  renderMetrics?: RenderMetrics
}>()

// Computed
const statusText = computed(() => {
  if (props.selectedCount > 0) {
    return `${props.selectedCount} of ${props.totalRows} rows selected`
  }
  return `${props.totalRows.toLocaleString()} rows`
})

const performanceText = computed(() => {
  // Show performance metrics in development
  const isDev = import.meta.env?.DEV ?? false
  if (isDev && props.renderMetrics) {
    const renderTime = props.renderMetrics.lastRenderTime.toFixed(1)
    const fps = Math.round(props.renderMetrics.averageFPS)
    return `Render: ${renderTime}ms | FPS: ${fps}`
  }
  // Placeholder performance text for development
  return isDev ? 'Render: 2.5ms | FPS: 60' : ''
})

const footerClasses = computed(() => [
  'ht-grid-footer',
  'flex items-center justify-between',
  'px-3 py-2',
  'bg-[var(--ht-footer-bg)]',
  'border-t border-[var(--ht-border)]',
  'text-sm text-[var(--ht-text-muted)]',
  'h-10',
])
</script>

<template>
  <div :class="footerClasses">
    <!-- Left Section: Row Information -->
    <div class="flex items-center space-x-4">
      <span>{{ statusText }}</span>

      <!-- Performance Metrics (Development Only) -->
      <span v-if="performanceText" class="text-xs opacity-75">
        {{ performanceText }}
      </span>
    </div>

    <!-- Right Section: Actions and Status -->
    <div class="flex items-center space-x-2">
      <!-- Loading Indicator -->
      <div v-if="false" class="flex items-center space-x-2">
        <div class="animate-spin w-3 h-3 border border-[var(--ht-primary)] border-t-transparent rounded-full" />
        <span class="text-xs">Loading...</span>
      </div>

      <!-- Action Buttons Slot -->
      <slot name="actions" />

      <!-- Status Indicator -->
      <div class="flex items-center space-x-1">
        <div class="ht-status-ready w-2 h-2 bg-[var(--ht-success)] rounded-full" />
        <span class="text-xs">Ready</span>
      </div>
    </div>
  </div>
</template>

<style scoped>
.ht-grid-footer {
  contain: layout style;
  min-height: 40px;
}

/* Status indicator animations */
@keyframes pulse-green {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

.ht-grid-footer .ht-status-ready {
  animation: pulse-green 2s ease-in-out infinite;
}
</style>
