/**
 * scroll-optimization.ts - RAF-based scroll optimization with velocity tracking
 *
 * Features:
 * 1. requestAnimationFrame for smooth scroll handling
 * 2. Debouncing for scroll end detection
 * 3. Velocity tracking for adaptive performance optimization
 * 4. Memory-efficient event handling
 */

/**
 * Scroll velocity information
 */
export interface ScrollVelocity {
  /** Horizontal velocity in pixels per millisecond */
  horizontal: number
  /** Vertical velocity in pixels per millisecond */
  vertical: number
  /** Overall scroll speed */
  magnitude: number
}

/**
 * RAF-based scroll handler with debouncing
 * Combines requestAnimationFrame for smooth updates with debouncing for scroll end detection
 */
export function createRAFScrollHandler(options: {
  /** Callback for scroll events (throttled by RAF) */
  onScroll: (event: Event, velocity: ScrollVelocity) => void
  /** Callback when scrolling ends (debounced) */
  onScrollEnd?: (event: Event) => void
  /** Debounce delay in ms for scroll end detection (default: 150ms) */
  debounceDelay?: number
  /** Enable velocity tracking (default: true) */
  trackVelocity?: boolean
}) {
  const { onScroll, onScrollEnd, debounceDelay = 150, trackVelocity = true } = options

  let rafId: number | null = null
  let debounceTimer: number | null = null
  let latestEvent: Event | null = null
  let isScheduled = false

  // Velocity tracking
  let lastScrollLeft = 0
  let lastScrollTop = 0
  let lastScrollTime = 0
  let currentVelocity: ScrollVelocity = { horizontal: 0, vertical: 0, magnitude: 0 }

  const calculateVelocity = (event: Event): ScrollVelocity => {
    if (!trackVelocity) {
      return { horizontal: 0, vertical: 0, magnitude: 0 }
    }

    const target = event.target as HTMLElement
    const currentTime = performance.now()
    const currentScrollLeft = target.scrollLeft || 0
    const currentScrollTop = target.scrollTop || 0

    const timeDelta = currentTime - lastScrollTime
    if (timeDelta > 0) {
      const scrollLeftDelta = Math.abs(currentScrollLeft - lastScrollLeft)
      const scrollTopDelta = Math.abs(currentScrollTop - lastScrollTop)

      const horizontalVelocity = scrollLeftDelta / timeDelta
      const verticalVelocity = scrollTopDelta / timeDelta
      const magnitude = Math.sqrt(horizontalVelocity ** 2 + verticalVelocity ** 2)

      currentVelocity = {
        horizontal: horizontalVelocity,
        vertical: verticalVelocity,
        magnitude,
      }
    }

    lastScrollLeft = currentScrollLeft
    lastScrollTop = currentScrollTop
    lastScrollTime = currentTime

    return currentVelocity
  }

  const scheduleRAF = () => {
    if (isScheduled || !latestEvent) return

    isScheduled = true
    rafId = requestAnimationFrame(() => {
      if (latestEvent) {
        onScroll(latestEvent, currentVelocity)
        latestEvent = null
      }
      isScheduled = false
    })
  }

  const handleScroll = (event: Event) => {
    // Calculate velocity
    calculateVelocity(event)

    // Store latest event
    latestEvent = event

    // Schedule RAF if not already scheduled
    scheduleRAF()

    // Handle scroll end debouncing
    if (onScrollEnd) {
      if (debounceTimer !== null) {
        clearTimeout(debounceTimer)
      }
      debounceTimer = window.setTimeout(() => {
        onScrollEnd(event)
        debounceTimer = null
        // Reset velocity when scrolling stops
        currentVelocity = { horizontal: 0, vertical: 0, magnitude: 0 }
      }, debounceDelay)
    }
  }

  const cleanup = () => {
    if (rafId !== null) {
      cancelAnimationFrame(rafId)
      rafId = null
    }
    if (debounceTimer !== null) {
      clearTimeout(debounceTimer)
      debounceTimer = null
    }
    latestEvent = null
    isScheduled = false
  }

  const getVelocity = () => currentVelocity

  return {
    handleScroll,
    cleanup,
    getVelocity,
  }
}

/**
 * Composable for RAF-based scroll handling in Vue components
 */
export function useRAFScroll(options: {
  onScroll: (event: Event, velocity: ScrollVelocity) => void
  onScrollEnd?: (event: Event) => void
  debounceDelay?: number
  trackVelocity?: boolean
}) {
  const handler = createRAFScrollHandler(options)

  // Cleanup on component unmount
  if (typeof window !== 'undefined' && 'onUnmounted' in globalThis) {
    const { onUnmounted } = globalThis as any
    onUnmounted(() => handler.cleanup())
  }

  return handler
}