/**
 * Three-Pane Virtualization Composable
 *
 * Specialized virtualization system for Happy Table's three-pane layout.
 * Only virtualizes the CENTER pane (scrollable columns) while keeping
 * LEFT and RIGHT panes (frozen columns) fully rendered for performance.
 *
 * Features:
 * - Horizontal virtualization for center pane only
 * - Perfect scroll synchronization between header and body
 * - Integration with existing TableRenderer architecture
 * - Memory-efficient column and cell tracking
 */

import { computed, onUnmounted, ref, watch, type ComputedRef, type Ref } from 'vue'
import type { ColumnDef, RowData } from '../types'
import type { PaneConfig } from '../types/layout'
import { useVirtualizer, type VirtualItem } from './virtualization'
import { DEFAULT_COLUMN_WIDTH } from './column-constants'

// ===================================================================
// TYPE DEFINITIONS
// ===================================================================

export type VirtualColumn = import('../types').VirtualColumn & {
  column: ColumnDef
}

export interface ThreePaneVirtualCell {
  rowIndex: number
  columnIndex: number
  column: ColumnDef
  value: unknown
  rowData: RowData
}

export type VirtualCell = ThreePaneVirtualCell

export interface ThreePaneVirtualizerOptions {
  /** Existing three-pane configuration from TableRenderer */
  paneConfig: ComputedRef<PaneConfig>

  /** Row configuration (reuse existing) */
  rowCount: ComputedRef<number>
  getRowSize: (index: number) => number

  /** Container and performance */
  containerSize: Ref<{ width: number; height: number }>
  overscan?: {
    rows?: number
    columns?: number
  }

  /** Row data for cell virtualization */
  rowData: ComputedRef<RowData[]>
}

export interface PaneVirtualData {
  columns: ColumnDef[]
  rows: VirtualItem[]
  width: number
}

export interface CenterPaneVirtualData {
  virtualColumns: VirtualColumn[]
  virtualRows: VirtualItem[]
  virtualCells: VirtualCell[][]
  width: number
  totalWidth: number
}

export interface ThreePaneVirtualizerReturn {
  /** Per-pane virtual data */
  leftPane: ComputedRef<PaneVirtualData>
  centerPane: ComputedRef<CenterPaneVirtualData>
  rightPane: ComputedRef<PaneVirtualData>

  /** Navigation and scroll control */
  scrollToCell: (row: number, column: number) => void
  scrollToColumn: (column: number, align?: 'start' | 'center' | 'end') => void
  updateScrollPosition: (scroll: { top: number; left: number }) => void

  /** Integration with TableRenderer */
  headerScrollSync: ComputedRef<number>
  totalDimensions: ComputedRef<{ width: number; height: number }>

  /** Internal scroll state for coordination */
  scrollLeft: Ref<number>
  scrollTop: Ref<number>
}

// ===================================================================
// THREE-PANE VIRTUALIZER COMPOSABLE
// ===================================================================

export function useThreePaneVirtualizer(
  options: ThreePaneVirtualizerOptions
): ThreePaneVirtualizerReturn {
  const {
    paneConfig,
    rowCount,
    getRowSize,
    containerSize,
    rowData,
    overscan = { rows: 5, columns: 3 }
  } = options

  // ===================================================================
  // REACTIVE STATE
  // ===================================================================

  const scrollLeft = ref(0)
  const scrollTop = ref(0)

  // ===================================================================
  // ROW VIRTUALIZATION (Reuse existing system)
  // ===================================================================

  // Use existing vertical virtualizer for rows with enhanced overscan for fast scrolling
  const rowVirtualizer = useVirtualizer({
    count: rowCount,
    getItemSize: getRowSize,
    overscan: overscan.rows || 8, // Increased default overscan for better fast-scroll performance
    enabled: computed(() => rowCount.value > 50) // Auto-enable for large datasets
  })

  // ===================================================================
  // COLUMN VIRTUALIZATION (Center pane only)
  // ===================================================================

  // Column size cache for performance
  const columnSizeCache = new Map<string, number>()

  const getColumnSize = (column: ColumnDef): number => {
    const cacheKey = column.id
    if (columnSizeCache.has(cacheKey)) {
      return columnSizeCache.get(cacheKey)!
    }

    const size = column.width ?? DEFAULT_COLUMN_WIDTH
    columnSizeCache.set(cacheKey, size)
    return size
  }

  // Clear cache when columns change
  watch(
    () => paneConfig.value.center.columns,
    () => {
      columnSizeCache.clear()
    },
    { deep: true }
  )

  // Calculate total width of center pane
  const centerPaneTotalWidth = computed(() => {
    return paneConfig.value.center.columns.reduce(
      (sum, col) => sum + getColumnSize(col),
      0
    )
  })

  // Calculate visible column range for center pane
  const centerColumnRange = computed(() => {
    const columns = paneConfig.value.center.columns
    const containerWidth = containerSize.value.width
    const leftPaneWidth = paneConfig.value.left.width
    const rightPaneWidth = paneConfig.value.right.width
    const availableWidth = containerWidth - leftPaneWidth - rightPaneWidth

    if (columns.length === 0 || availableWidth <= 0) {
      return { start: 0, end: -1 }
    }

    let start = 0
    let accumulatedWidth = 0

    // Find start index
    for (let i = 0; i < columns.length; i++) {
      const column = columns[i]
      if (!column) continue

      const columnSize = getColumnSize(column)

      if (accumulatedWidth + columnSize > scrollLeft.value) {
        start = i
        break
      }

      accumulatedWidth += columnSize
    }

    // Find end index
    let end = start
    let visibleWidth = 0

    for (let i = start; i < columns.length; i++) {
      const column = columns[i]
      if (!column) continue

      const columnSize = getColumnSize(column)
      visibleWidth += columnSize
      end = i

      if (visibleWidth >= availableWidth) {
        break
      }
    }

    // Apply overscan
    const overscanStart = Math.max(0, start - (overscan.columns || 3))
    const overscanEnd = Math.min(columns.length - 1, end + (overscan.columns || 3))

    return {
      start: overscanStart,
      end: overscanEnd,
    }
  })

  // Generate virtual columns for center pane
  const centerVirtualColumns = computed((): VirtualColumn[] => {
    const columns = paneConfig.value.center.columns
    const { start, end } = centerColumnRange.value

    if (start > end) return []

    const virtualColumns: VirtualColumn[] = []
    let currentStart = 0

    // Calculate start position
    for (let i = 0; i < start; i++) {
      const column = columns[i]
      if (column) {
        currentStart += getColumnSize(column)
      }
    }

    // Generate virtual columns for visible range
    for (let i = start; i <= end; i++) {
      const column = columns[i]
      if (!column) continue

      const size = getColumnSize(column)

      virtualColumns.push({
        index: i,
        start: currentStart,
        size,
        end: currentStart + size,
        column,
      })

      currentStart += size
    }

    return virtualColumns
  })

  // ===================================================================
  // PANE DATA COMPUTATION
  // ===================================================================

  // Left pane (no virtualization - always fully rendered)
  const leftPane = computed((): PaneVirtualData => ({
    columns: paneConfig.value.left.columns,
    rows: rowVirtualizer.virtualItems.value,
    width: paneConfig.value.left.width,
  }))

  // Right pane (no virtualization - always fully rendered)
  const rightPane = computed((): PaneVirtualData => ({
    columns: paneConfig.value.right.columns,
    rows: rowVirtualizer.virtualItems.value,
    width: paneConfig.value.right.width,
  }))

  // Center pane (with column virtualization)
  const centerPane = computed((): CenterPaneVirtualData => {
    const virtualColumns = centerVirtualColumns.value
    const virtualRows = rowVirtualizer.virtualItems.value
    const data = rowData.value

    // Generate virtual cells for the intersection of visible rows and columns
    const virtualCells: VirtualCell[][] = []

    for (const rowItem of virtualRows) {
      const rowIndex = rowItem.index
      const row = data[rowIndex]
      const rowCells: VirtualCell[] = []

      for (const colItem of virtualColumns) {
        const columnIndex = colItem.index
        const column = colItem.column

        rowCells.push({
          rowIndex,
          columnIndex,
          column,
          value: row?.[column.field],
          rowData: row || {},
        })
      }

      virtualCells.push(rowCells)
    }

    return {
      virtualColumns,
      virtualRows,
      virtualCells,
      width: paneConfig.value.center.width,
      totalWidth: centerPaneTotalWidth.value,
    }
  })

  // ===================================================================
  // SCROLL COORDINATION
  // ===================================================================

  // Header scroll sync (for horizontal scrolling)
  const headerScrollSync = computed(() => scrollLeft.value)

  // Total dimensions for scrollbar calculations
  const totalDimensions = computed(() => ({
    width: centerPaneTotalWidth.value,
    height: rowVirtualizer.totalSize.value,
  }))

  // ===================================================================
  // NAVIGATION METHODS
  // ===================================================================

  const scrollToCell = (row: number, column: number) => {
    // Scroll to row using existing row virtualizer
    rowVirtualizer.scrollToIndex(row, 'start')

    // Scroll to column in center pane
    const centerColumns = paneConfig.value.center.columns
    if (column >= 0 && column < centerColumns.length) {
      scrollToColumn(column, 'start')
    }
  }

  const scrollToColumn = (column: number, align: 'start' | 'center' | 'end' = 'start') => {
    const columns = paneConfig.value.center.columns
    if (column < 0 || column >= columns.length) return

    const containerWidth = containerSize.value.width
    const leftPaneWidth = paneConfig.value.left.width
    const rightPaneWidth = paneConfig.value.right.width
    const availableWidth = containerWidth - leftPaneWidth - rightPaneWidth

    let targetScrollLeft = 0

    // Calculate position of target column
    for (let i = 0; i < column; i++) {
      const col = columns[i]
      if (col) {
        targetScrollLeft += getColumnSize(col)
      }
    }

    // Adjust based on alignment
    const targetColumn = columns[column]
    if (!targetColumn) return

    const columnSize = getColumnSize(targetColumn)

    switch (align) {
      case 'center':
        targetScrollLeft -= (availableWidth - columnSize) / 2
        break
      case 'end':
        targetScrollLeft -= availableWidth - columnSize
        break
      case 'start':
      default:
        // No adjustment needed
        break
    }

    // Clamp to valid range
    const maxScrollLeft = Math.max(0, centerPaneTotalWidth.value - availableWidth)
    targetScrollLeft = Math.max(0, Math.min(targetScrollLeft, maxScrollLeft))

    scrollLeft.value = targetScrollLeft
  }

  const updateScrollPosition = (scroll: { top: number; left: number }) => {
    scrollTop.value = scroll.top
    scrollLeft.value = scroll.left

    // Update row virtualizer
    rowVirtualizer.updateScrollPosition(scroll.top)
  }

  // ===================================================================
  // CONTAINER SIZE COORDINATION
  // ===================================================================

  // Keep row virtualizer in sync with container size
  watch(
    containerSize,
    (size) => {
      rowVirtualizer.setContainerSize(size)
    },
    { immediate: true }
  )

  // ===================================================================
  // CLEANUP
  // ===================================================================

  onUnmounted(() => {
    columnSizeCache.clear()
  })

  // ===================================================================
  // PUBLIC API
  // ===================================================================

  return {
    leftPane,
    centerPane,
    rightPane,
    scrollToCell,
    scrollToColumn,
    updateScrollPosition,
    headerScrollSync,
    totalDimensions,
    scrollLeft,
    scrollTop,
  }
}

// ===================================================================
// UTILITY FUNCTIONS
// ===================================================================

/**
 * Calculate the optimal column overscan based on container size and column sizes
 */
export function calculateOptimalColumnOverscan(
  containerWidth: number,
  averageColumnWidth = 150
): number {
  const visibleColumns = Math.ceil(containerWidth / averageColumnWidth)

  // Use 50% of visible columns as overscan, with min 2 and max 5
  return Math.min(5, Math.max(2, Math.ceil(visibleColumns * 0.5)))
}

/**
 * Estimate the total width of a column array
 */
export function estimateColumnsWidth(columns: ColumnDef[]): number {
  return columns.reduce((sum, col) => sum + (col.width ?? DEFAULT_COLUMN_WIDTH), 0)
}

/**
 * Find the column index by horizontal position
 */
export function findColumnAtPosition(
  columns: ColumnDef[],
  position: number
): { index: number; offset: number } {
  let accumulatedWidth = 0

  for (let i = 0; i < columns.length; i++) {
    const column = columns[i]
    if (!column) continue

    const columnWidth = column.width ?? DEFAULT_COLUMN_WIDTH

    if (accumulatedWidth + columnWidth > position) {
      return {
        index: i,
        offset: position - accumulatedWidth,
      }
    }

    accumulatedWidth += columnWidth
  }

  // Return last column if position exceeds total width
  const lastColumn = columns[columns.length - 1]
  return {
    index: columns.length - 1,
    offset: (lastColumn?.width || 150) - 1,
  }
}
