<script setup lang="ts">
/**
 * BooleanCell - Boolean Cell Renderer
 *
 * Purpose: Renders boolean values with visual checkmark/cross icons
 * Design: Text + icon combination, semantic colors (success/danger)
 */

export interface BooleanCellProps {
  // Core
  value: boolean | null | undefined

  // Display
  showText?: boolean // Show "Yes"/"No" text
  showIcon?: boolean // Show checkmark/cross icon
  align?: 'left' | 'center' | 'right'

  // Customization
  trueText?: string
  falseText?: string
  nullText?: string
}

const props = withDefaults(defineProps<BooleanCellProps>(), {
  showText: true,
  showIcon: true,
  align: 'center',
  trueText: 'Yes',
  falseText: 'No',
  nullText: '',
})

// Display text
const displayText = (() => {
  if (props.value === null || props.value === undefined) {
    return props.nullText
  }
  return props.value ? props.trueText : props.falseText
})()

// Alignment classes
const alignClasses: Record<string, string> = {
  left: 'justify-start',
  center: 'justify-center',
  right: 'justify-end',
}
</script>

<template>
  <div
    :class="[
      'inline-flex items-center gap-2 w-full',
      alignClasses[align],
    ]"
  >
    <!-- Text content -->
    <span v-if="showText" class="truncate">
      {{ displayText }}
    </span>

    <!-- Icon indicator -->
    <div v-if="showIcon && value !== null && value !== undefined" class="flex-shrink-0">
      <!-- Checkmark icon (true) -->
      <svg v-if="value" class="w-4 h-4 text-[var(--ht-success)]" fill="currentColor" viewBox="0 0 20 20">
        <path
          fill-rule="evenodd"
          d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
          clip-rule="evenodd"
        />
      </svg>

      <!-- Cross icon (false) -->
      <svg v-else class="w-4 h-4 text-[var(--ht-danger)]" fill="currentColor" viewBox="0 0 20 20">
        <path
          fill-rule="evenodd"
          d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
          clip-rule="evenodd"
        />
      </svg>
    </div>
  </div>
</template>
