<!--
  Keyboard Shortcuts Helper Component

  Displays available keyboard shortcuts for the grid and provides accessibility
  information for screen reader users.
-->

<template>
  <div v-if="showShortcuts" class="keyboard-shortcuts-panel" role="complementary" aria-labelledby="shortcuts-title">
    <div class="shortcuts-header">
      <h3 id="shortcuts-title" class="shortcuts-title">Keyboard Shortcuts</h3>
      <button class="shortcuts-close" aria-label="Close keyboard shortcuts" @click="closeShortcuts">×</button>
    </div>

    <div class="shortcuts-content">
      <!-- Navigation shortcuts -->
      <div class="shortcut-group">
        <h4 class="shortcut-group-title">Navigation</h4>
        <div class="shortcut-list">
          <div class="shortcut-item">
            <div class="shortcut-keys">
              <kbd>↑</kbd>
              <kbd>↓</kbd>
              <kbd>←</kbd>
              <kbd>→</kbd>
            </div>
            <div class="shortcut-description">Move between cells</div>
          </div>
          <div class="shortcut-item">
