              <kbd>Home</kbd>
              /
              <kbd>End</kbd>
            </div>
            <div class="shortcut-description">First/last cell in row</div>
          </div>
          <div class="shortcut-item">
            <div class="shortcut-keys">
              <kbd>Ctrl</kbd>
              +
              <kbd>Home</kbd>
              /
              <kbd>End</kbd>
            </div>
            <div class="shortcut-description">First/last cell in grid</div>
          </div>
