            <div class="shortcut-keys">
              <kbd>Page Up</kbd>
              /
              <kbd>Page Down</kbd>
            </div>
            <div class="shortcut-description">Move page up/down</div>
          </div>
          <div class="shortcut-item">
            <div class="shortcut-keys">
              <kbd>Tab</kbd>
              /
              <kbd>Shift</kbd>
              +
              <kbd>Tab</kbd>
            </div>
            <div class="shortcut-description">Next/previous cell</div>
          </div>
        </div>
      </div>

      <!-- Editing shortcuts -->
      <div v-if="enableEditing" class="shortcut-group">
        <h4 class="shortcut-group-title">Editing</h4>
        <div class="shortcut-list">
          <div class="shortcut-item">
            <div class="shortcut-keys">
