  <div class="sr-only" aria-live="polite" aria-atomic="true">
    {{ announcement }}
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted, onUnmounted } from 'vue'

interface CustomShortcut {
  key: string
  keys: string[]
  description: string
}

interface Props {
  showShortcuts?: boolean
  showHelpHint?: boolean
  enableEditing?: boolean
  enableSelection?: boolean
  customShortcuts?: CustomShortcut[]
}

interface Emits {
  (e: 'close'): void
  (e: 'show'): void
}

const props = withDefaults(defineProps<Props>(), {
  showShortcuts: false,
  showHelpHint: true,
  enableEditing: true,
  enableSelection: true,
  customShortcuts: () => [],
})

const emit = defineEmits<Emits>()

const announcement = ref('')

const closeShortcuts = () => {
  emit('close')
}

const handleGlobalKeydown = (event: KeyboardEvent) => {
  // Toggle shortcuts panel with '?' key
  if (event.key === '?' && !event.ctrlKey && !event.altKey && !event.metaKey) {
    // Don't trigger if user is typing in an input
    const target = event.target as HTMLElement
    if (target.tagName === 'INPUT' || target.tagName === 'TEXTAREA') {
      return
    }

    event.preventDefault()
    if (props.showShortcuts) {
      closeShortcuts()
    } else {
      emit('show')
    }
  }

  // Escape to close shortcuts
  if (event.key === 'Escape' && props.showShortcuts) {
    event.preventDefault()
    closeShortcuts()
  }
}

const announceShortcut = (shortcut: string, action: string) => {
  announcement.value = `${shortcut} ${action}`

  // Clear announcement after a delay
  setTimeout(() => {
    announcement.value = ''
  }, 1000)
}

onMounted(() => {
  document.addEventListener('keydown', handleGlobalKeydown)
})

onUnmounted(() => {
  document.removeEventListener('keydown', handleGlobalKeydown)
})

// Expose method for external components
defineExpose({
  announceShortcut,
})
</script>

