/**
 * ExcelRenderer - Placeholder for Excel-like grid renderer
 *
 * This is a stub implementation that will be provided by the Excel plugin
 * in Phase 3 of the development roadmap. The interface is defined here
 * to ensure forward compatibility.
 *
 * Features (Future Implementation):
 * - Excel-style grid lines and headers (A/B/C..., 1/2/3...)
 * - Cell-based interactions and selection
 * - Active cell highlighting
 * - Multi-range selection (Ctrl+Click, Shift+Click)
 * - Fill handle for cell dragging
 * - In-cell editing with formula support
 * - Excel-style keyboard navigation
 */

import type {
  GridRenderer,
  GridInteractionContract,
  VirtualRow,
  RendererOptions,
} from '../base/renderer-interface'
import type { GridAPI, GridState, ColumnDef } from '../../types'
import { createInteractionContract } from '../base/interaction-contract'

/**
 * ExcelRenderer stub implementation
 *
 * This class provides the interface contract but throws errors when used,
 * indicating that the Excel plugin needs to be loaded.
 */
export class ExcelRenderer implements GridRenderer {
  readonly name = 'excel' as const
  readonly interactionContract: GridInteractionContract
  private readonly options: RendererOptions

  constructor(options: RendererOptions = {}) {
    this.options = options
    const contract = createInteractionContract('excel-renderer-stub')

    // Create a wrapper to match the GridInteractionContract interface
    this.interactionContract = {
      updateSelection: (_selectedIds: Set<string | number>, _reason: 'programmatic' | 'user') => {
        // Stub implementation
      },
      setActive: (_activeId: string | number | null) => {
        // Stub implementation
      },
      ensureVisible: (_id: string | number, _behavior: 'smooth' | 'auto') => {
        // Stub implementation
      },
      toggleEditMode: async (_id: string | number, _mode: 'enter' | 'exit') => {
        return false // Stub implementation
      },
      registerEventEmitter: (emit: (eventName: any, payload: any) => void) => {
        contract.registerEventEmitter(emit)
      },
      updateScrollPosition: (_position: { top: number; left: number }) => {
        // Stub implementation
      },
      refresh: () => {
        // Stub implementation
      },
      emit: (_eventName: any, _payload: any) => {
        // Stub implementation
      },
      registerCoreCallbacks: (_callbacks: any) => {
        // Stub implementation
      },
      destroy: () => {
        // Stub implementation
      },
    }
  }

  /**
   * Mount renderer - throws error for stub
   */
  async mount(_host: HTMLElement, _api: GridAPI): Promise<void> {
    throw new Error(
      'ExcelRenderer is not available. Please install the Excel plugin to use Excel-style rendering.\n' +
        'Installation: npm install @happy-table/excel-plugin\n' +
        'Usage: import { excelPlugin } from "@happy-table/excel-plugin"\n' +
        '       useGridState(config, [excelPlugin])'
    )
  }

  /**
   * Unmount renderer - stub implementation
   */
  async unmount(): Promise<void> {
    // No-op for stub
  }

  /**
   * Render state - throws error for stub
   */
  render(_state: GridState, _columns: ColumnDef[], _rows: VirtualRow[]): void {
    throw new Error('ExcelRenderer stub cannot render. Please install the Excel plugin.')
  }

  /**
   * Handle resize - stub implementation
   */
  resize(_width: number, _height: number): void {
    // No-op for stub
  }

  /**
   * Get configuration schema
   */
  getConfigSchema(): Record<string, any> {
    return {
      grid: {
        type: 'object',
        description: 'Excel-style grid configuration',
        properties: {
          showGridLines: { type: 'boolean', default: true },
          showRowHeaders: { type: 'boolean', default: true },
          showColumnHeaders: { type: 'boolean', default: true },
          headerStyle: {
            type: 'string',
            enum: ['excel', 'google-sheets'],
            default: 'excel',
          },
        },
      },
      selection: {
        type: 'object',
        description: 'Selection behavior configuration',
        properties: {
          multiRange: { type: 'boolean', default: true },
          showFillHandle: { type: 'boolean', default: true },
          enableRangeSelection: { type: 'boolean', default: true },
        },
      },
      editing: {
        type: 'object',
        description: 'Cell editing configuration',
        properties: {
          enableInCellEditing: { type: 'boolean', default: true },
          enableFormulaBar: { type: 'boolean', default: true },
          autoCommitOnEnter: { type: 'boolean', default: true },
        },
      },
    }
  }

  /**
   * Update configuration - stub implementation
   */
  updateConfig(config: Record<string, any>): void {
    Object.assign(this.options, config)
  }
}

/**
 * Factory function to create ExcelRenderer stub
 */
export function createExcelRenderer(options: RendererOptions = {}): ExcelRenderer {
  return new ExcelRenderer(options)
}

/**
 * Excel-specific interaction contract interface
 *
 * Extends the base GridInteractionContract with Excel-specific methods
 * that will be implemented by the Excel plugin.
 */
export interface ExcelInteractionContract extends GridInteractionContract {
  /**
   * Select a range of cells (Excel-style)
   */
  selectRange(start: { row: number; col: number }, end: { row: number; col: number }): void

  /**
   * Add a range to multi-selection
   */
  addToSelection(range: {
    start: { row: number; col: number }
    end: { row: number; col: number }
  }): void

  /**
   * Clear all selections
   */
  clearSelections(): void

  /**
   * Start fill operation (drag handle)
   */
  startFill(sourceRange: {
    start: { row: number; col: number }
    end: { row: number; col: number }
  }): void

  /**
   * Complete fill operation
   */
  completeFill(targetRange: {
    start: { row: number; col: number }
    end: { row: number; col: number }
  }): void

  /**
   * Show formula bar
   */
  showFormulaBar(cellId: string, formula: string): void

  /**
   * Hide formula bar
   */
  hideFormulaBar(): void

  /**
   * Update cell coordinate display (A1, B2, etc.)
   */
  updateCoordinateDisplay(coordinate: string): void
}

/**
 * Excel renderer configuration interface
 */
export interface ExcelRendererConfig extends RendererOptions {
  grid?: {
    showGridLines?: boolean
    showRowHeaders?: boolean
    showColumnHeaders?: boolean
    headerStyle?: 'excel' | 'google-sheets'
    gridLineColor?: string
    headerBackgroundColor?: string
  }

  selection?: {
    multiRange?: boolean
    showFillHandle?: boolean
    enableRangeSelection?: boolean
    selectionColor?: string
    activeColor?: string
  }

  editing?: {
    enableInCellEditing?: boolean
    enableFormulaBar?: boolean
    autoCommitOnEnter?: boolean
    formulaBarHeight?: number
  }

  coordinates?: {
    showCoordinates?: boolean
    coordinateFormat?: 'A1' | 'R1C1'
    startRow?: number
    startColumn?: string
  }
}

