<script setup lang="ts">
/**
 * HtSelectOptionRender - 自定义选项渲染组件
 *
 * 职责:
 * - 渲染 optionRender 返回的自定义选项内容
 * - 支持 title、description、meta、badge 等丰富布局
 * - 统一管理选项渲染样式
 *
 * 设计目的:
 * - 消除 DRY 违反: 替代 HtSelect.vue 中重复的 62 行模板代码
 * - 提高可维护性: 自定义渲染逻辑集中在一个组件中
 * - 增强复用性: 可在分组和非分组模式中复用
 */
import type { OptionRenderData } from '../../../types/primitives/select'

defineProps<{
  /**
   * optionRender 函数返回的渲染数据
   */
  renderData: OptionRenderData

  /**
   * 获取徽章样式类名的函数
   */
  getBadgeClass: (badgeType?: string) => string
}>()
</script>

<template>
  <div class="ht-option-render">
    <div class="ht-option-render__content">
      <div v-if="renderData.title" class="ht-option-render__title">
        {{ renderData.title }}
      </div>
      <div v-if="renderData.description" class="ht-option-render__description">
        {{ renderData.description }}
      </div>
      <div v-if="renderData.meta?.length" class="ht-option-render__meta">
        <span
          v-for="(metaEntry, metaIndex) in renderData.meta"
          :key="metaIndex"
          class="ht-option-render__meta-item"
        >
          <span class="ht-option-render__meta-label">{{ metaEntry.label }}:</span>
          <span class="ht-option-render__meta-value">{{ metaEntry.value }}</span>
        </span>
      </div>
    </div>
    <div v-if="renderData.badge" class="ht-option-render__badge">
      <span :class="['ht-option-badge', getBadgeClass(renderData.badgeType)]">
        {{ renderData.badge }}
      </span>
    </div>
  </div>
</template>

