<script setup lang="ts">
import { computed } from 'vue'
import type { ButtonStyleProps, ButtonVariant, ButtonSize } from '../../types/primitives'

/**
 * HtButton - Atomic Button Component
 *
 * Design: shadcn-vue pattern (80%) + Headless UI interactions (10%)
 * Features: Variant/Size system, loading state, block mode, full accessibility
 */

export interface HtButtonProps extends ButtonStyleProps {
  // Content
  label?: string

  // State
  disabled?: boolean
  loading?: boolean

  // Native
  type?: 'button' | 'submit' | 'reset'
}

const props = withDefaults(defineProps<HtButtonProps>(), {
  label: '',
  variant: 'default',
  size: 'md',
  block: false,
  disabled: false,
  loading: false,
  type: 'button',
})

// Base styles - Use ring-offset for better focus visibility on all backgrounds
const baseClasses = [
  'inline-flex items-center justify-center',
  'rounded-md font-medium select-none',
  'transition-colors',
  'focus:outline-none focus-visible:ring-1 focus-visible:ring-offset-1',
  'disabled:opacity-50 disabled:pointer-events-none',
  'gap-2',
].join(' ')

// Static class maps to keep Tailwind v4 scanner happy
const variantClasses: Record<ButtonVariant, string> = {
  // All variants use primary ring with offset for visibility on any background
  default:
    'bg-[var(--ht-primary)] text-[var(--ht-text-inverse)] hover:bg-[var(--ht-primary-hover)] focus-visible:ring-[var(--ht-primary)]',
  filled:
    'bg-[var(--ht-bg-subtle)] text-[var(--ht-text)] hover:bg-[var(--ht-cell-hover)] focus-visible:ring-[var(--ht-primary)]',
  primary:
    'bg-[var(--ht-primary)] text-[var(--ht-text-inverse)] hover:bg-[var(--ht-primary-hover)] focus-visible:ring-[var(--ht-primary)]',
  secondary:
    'bg-[var(--ht-bg-subtle)] text-[var(--ht-text)] border border-[var(--ht-border-strong)] hover:bg-[var(--ht-cell-hover)] focus-visible:ring-[var(--ht-primary)]',
  outline:
    'border border-[var(--ht-border-strong)] text-[var(--ht-text)] bg-transparent hover:bg-[var(--ht-cell-hover)] focus-visible:ring-[var(--ht-primary)]',
  ghost:
    'bg-transparent text-[var(--ht-text)] hover:bg-[var(--ht-cell-hover)] focus-visible:ring-[var(--ht-primary)]',
  destructive:
    'bg-[var(--ht-danger)] text-[var(--ht-text-inverse)] hover:bg-[var(--ht-danger)]/90 focus-visible:ring-[var(--ht-danger)]',
  link:
    'bg-transparent text-[var(--ht-primary)] underline-offset-4 hover:underline focus-visible:ring-[var(--ht-primary)]',
}

const sizeClasses: Record<ButtonSize, string> = {
  micro: 'h-6 px-1.5 text-xs gap-1',  // 24px height - smallest functional button
  xs: 'h-7 px-2 text-xs',
  sm: 'h-8 px-2.5 text-sm',
  md: 'h-9 px-3 text-sm',
  lg: 'h-11 px-4 text-base',
  'icon-micro': 'size-6 p-0',  // 24px - smallest icon button
  'icon-xs': 'size-7 p-0',
  'icon-sm': 'size-8 p-0',
  icon: 'size-9 p-0',
  'icon-lg': 'size-11 p-0',
}

const classes = computed(() =>
  [baseClasses, variantClasses[props.variant], sizeClasses[props.size], props.block ? 'w-full' : ''].join(' ')
)
</script>

<template>
  <button :type="props.type" :disabled="props.disabled || props.loading" :class="classes">
    <span v-if="props.loading" class="inline-flex items-center justify-center">
      <svg class="animate-spin w-4 h-4" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
        <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle>
        <path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
      </svg>
    </span>
    <slot>{{ props.label || 'Button' }}</slot>
  </button>
</template>
