<script setup lang="ts">
/**
 * HtSelectTrigger - 下拉触发器
 *
 * 职责:
 * - 根据 searchable 自动切换模式:
 *   - searchable=false: Button 模式 (HtButton)
 *   - searchable=true: Combobox 模式 (HtInput)
 * - 将 ref 注册到 context (供 BasePopover 定位)
 * - 处理点击/输入事件
 * - ARIA 属性
 */
import { ref, onMounted, computed, watch } from 'vue'
import HtButton from '../HtButton.vue'
import { injectSelectContext, injectSelectProps } from './composables/useSelectContext'

const context = injectSelectContext()
const selectProps = injectSelectProps()
const buttonComponentRef = ref<InstanceType<typeof HtButton> | null>(null)
const comboboxTriggerRef = ref<HTMLElement | null>(null) // Combobox 外层容器 ref

// Combobox: 选中项标签
const selectedLabel = computed(() => {
  const selectedVal = context.selectedValue.value
  if (selectedVal === undefined || selectedVal === null) return ''
  return context.optionsMap.value.get(selectedVal) || ''
})

// Combobox: 输入框是否有选中项
const hasSelection = computed(() => {
  return context.selectedValue.value !== undefined && context.selectedValue.value !== null
})

const pendingClear = ref(false)

// Compute active descendant id for combobox
const activeDescendantId = computed(() => {
  const idx = context.highlightedIndex.value
  if (!selectProps.searchable || !context.open.value || idx < 0) return undefined
  return `${context.ids.contentId}-opt-${idx}`
})

// Combobox: 输入事件处理
const handleInput = (value: string | number) => {
  // 只在用户输入时打开下拉（更精准的意图识别）
  if (!context.open.value) {
    context.openDropdown()
  }
  context.inputValue.value = String(value)
  pendingClear.value = false
}

// Combobox: 聚焦时不自动打开下拉
// 只有用户主动输入或点击箭头时才打开
// 这样避免 Tab 导航时误触，交互更精准
const handleFocus = () => {
  // Do nothing - dropdown opens on input or chevron click only
}

// Combobox: 点击箭头图标切换下拉框
const handleChevronClick = (e: MouseEvent) => {
  e.preventDefault()
  e.stopPropagation()
  context.toggle()
}

// Combobox: 清除选中项
const handleClearSelection = (e: MouseEvent) => {
  e.preventDefault()
  e.stopPropagation()
  context.select(null) // 清空选中值
  context.inputValue.value = '' // 清空输入
  pendingClear.value = false

  // 聚焦到输入框（如果有自定义输入框）
  const inputEl = document.getElementById(context.ids.triggerId) as HTMLInputElement
  if (inputEl) {
    inputEl.focus()
  }
}

const handleInputKeydown = (event: KeyboardEvent) => {
  if (selectProps.disabled) return

  if (event.key === 'Backspace') {
    if (!context.inputValue.value && hasSelection.value) {
      event.preventDefault()
      if (!pendingClear.value) {
        pendingClear.value = true
      } else {
        context.select(null)
        context.inputValue.value = ''
        pendingClear.value = false
      }
      return
    }
  } else {
    pendingClear.value = false
  }

  switch (event.key) {
    case 'ArrowDown':
      event.preventDefault()
      if (!context.open.value) {
        context.openDropdown()
        context.highlightFirst()
      } else {
        context.moveHighlight(1)
      }
      break
    case 'ArrowUp':
      event.preventDefault()
      if (!context.open.value) {
        context.openDropdown()
        context.highlightLast()
      } else {
        context.moveHighlight(-1)
      }
      break
    case 'Home':
      if (context.open.value) {
        event.preventDefault()
        context.highlightFirst()
      }
      break
    case 'End':
      if (context.open.value) {
        event.preventDefault()
        context.highlightLast()
      }
      break
    case 'Enter':
      if (context.open.value) {
        event.preventDefault()
        context.selectHighlighted()
      }
      break
    case 'Escape':
      if (context.open.value) {
        event.preventDefault()
        context.close()
      }
      break
    case 'Tab':
      if (context.open.value) {
        context.close()
      }
      break
    default:
      pendingClear.value = false
      break
  }
}

const handleButtonKeydown = (event: KeyboardEvent) => {
  if (selectProps.disabled) return

  switch (event.key) {
    case 'ArrowDown':
      event.preventDefault()
      if (!context.open.value) {
        context.openDropdown()
        context.highlightFirst()
      } else {
        context.moveHighlight(1)
      }
      break
    case 'ArrowUp':
      event.preventDefault()
      if (!context.open.value) {
        context.openDropdown()
        context.highlightLast()
      } else {
        context.moveHighlight(-1)
      }
      break
    case 'Home':
      if (context.open.value) {
        event.preventDefault()
        context.highlightFirst()
      }
      break
    case 'End':
      if (context.open.value) {
        event.preventDefault()
        context.highlightLast()
      }
      break
    case 'Enter':
    case ' ': // Space key
    case 'Space':
    case 'Spacebar':
      event.preventDefault()
      if (!context.open.value) {
        context.openDropdown()
        context.highlightFirst()
      } else {
        context.selectHighlighted()
      }
      break
    case 'Escape':
      if (context.open.value) {
        event.preventDefault()
        context.close()
      }
      break
    case 'Tab':
      if (context.open.value) {
        context.close()
      }
      break
    default:
      pendingClear.value = false
      break
  }
}

watch(
  () => context.selectedValue.value,
  () => {
    pendingClear.value = false
  }
)

// Register trigger DOM element to context when mounted
onMounted(() => {
  if (selectProps.searchable) {
    // Combobox 模式: 注册外层容器元素（而非 input 元素）
    // 这样 BasePopover 可以正确计算包含标签的完整高度
    if (comboboxTriggerRef.value) {
      context.triggerRef.value = comboboxTriggerRef.value
    }
  } else {
    // Button 模式: 注册按钮元素
    if (buttonComponentRef.value && buttonComponentRef.value.$el) {
      context.triggerRef.value = buttonComponentRef.value.$el as HTMLElement
    }
  }
})
</script>

<template>
  <!-- Combobox 模式: 自定义输入框布局（标签在输入框内） -->
  <div
    v-if="selectProps.searchable"
    ref="comboboxTriggerRef"
    class="ht-combobox-trigger"
    :class="{
      'ht-combobox-trigger--disabled': selectProps.disabled,
      'ht-combobox-trigger--open': context.open.value,
      [`ht-combobox-trigger--${selectProps.size}`]: selectProps.size,
      [`ht-combobox-trigger--${selectProps.variant}`]: selectProps.variant,
      [`ht-combobox-trigger--${selectProps.state}`]: selectProps.state,
    }"
  >
    <!-- 选中项标签 -->
    <div
      v-if="hasSelection"
      :class="['ht-combobox-tag', { 'ht-combobox-tag--pending-clear': pendingClear }]"
    >
      <span class="ht-combobox-tag__text">{{ selectedLabel }}</span>
      <button
        v-if="!selectProps.disabled"
        type="button"
        class="ht-combobox-tag__close"
        aria-label="Clear selection"
        tabindex="-1"
        @click="handleClearSelection"
      >
        <svg class="w-3 h-3" fill="currentColor" viewBox="0 0 20 20">
          <path
            d="M6.28 5.22a.75.75 0 00-1.06 1.06L8.94 10l-3.72 3.72a.75.75 0 101.06 1.06L10 11.06l3.72 3.72a.75.75 0 101.06-1.06L11.06 10l3.72-3.72a.75.75 0 00-1.06-1.06L10 8.94 6.28 5.22z"
          />
        </svg>
      </button>
    </div>

    <!-- 输入框 -->
    <input
      :id="context.ids.triggerId"
      type="text"
      :value="context.inputValue.value"
      :disabled="selectProps.disabled"
      :placeholder="hasSelection ? '' : selectProps.placeholder"
      class="ht-combobox-input"
      role="combobox"
      :aria-expanded="context.open.value ? 'true' : 'false'"
      :aria-haspopup="'listbox'"
      :aria-controls="context.ids.contentId"
      aria-autocomplete="list"
      :aria-activedescendant="activeDescendantId"
      @input="handleInput(($event.target as HTMLInputElement).value)"
      @focus="handleFocus"
      @keydown="handleInputKeydown"
    />

    <!-- 下拉箭头图标 -->
    <div class="ht-combobox-suffix">
      <svg
        class="h-4 w-4 transition-transform cursor-pointer"
        :class="{ 'rotate-180': context.open.value }"
        fill="none"
        stroke="currentColor"
        viewBox="0 0 24 24"
        xmlns="http://www.w3.org/2000/svg"
        @click="handleChevronClick"
      >
        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M19 9l-7 7-7-7" />
      </svg>
    </div>
  </div>

  <!-- Button 模式: 按钮 Trigger (默认) -->
  <HtButton
    v-else
    ref="buttonComponentRef"
    type="button"
    :id="context.ids.triggerId"
    role="button"
    :aria-expanded="context.open.value ? 'true' : 'false'"
    :aria-haspopup="'listbox'"
    :aria-controls="context.ids.contentId"
    :size="selectProps.size"
    :variant="selectProps.variant"
    :disabled="selectProps.disabled"
    class="w-full justify-between"
    @click="context.toggle()"
    @keydown="handleButtonKeydown"
  >
    <slot />

    <!-- Dropdown chevron icon -->
    <svg
      class="w-4 h-4 ml-2 shrink-0 transition-transform"
      :class="{ 'rotate-180': context.open.value }"
      fill="none"
      stroke="currentColor"
      viewBox="0 0 24 24"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M19 9l-7 7-7-7" />
    </svg>
  </HtButton>
</template>

<style scoped>
/* Combobox Trigger Container */
.ht-combobox-trigger {
  position: relative;
  display: flex;
  align-items: center;
  gap: 0.375rem;
  width: 100%;
  min-height: var(--size-ht-9); /* md size default */
  padding: 0 0.75rem;
  border: 1px solid var(--ht-border);
  border-radius: var(--radius-ht-md);
  background-color: var(--ht-bg);
  transition: var(--ht-transition-fast);
  cursor: text;
}

/* Size variants */
.ht-combobox-trigger--sm {
  min-height: var(--size-ht-8);
  padding: 0 0.625rem;
  font-size: 0.875rem;
}

.ht-combobox-trigger--md {
  min-height: var(--size-ht-9);
  padding: 0 0.75rem;
  font-size: 0.875rem;
}

.ht-combobox-trigger--lg {
  min-height: var(--size-ht-10);
  padding: 0 0.875rem;
  font-size: 1rem;
}

/* Variant styles */
.ht-combobox-trigger--default {
  border-color: var(--ht-border);
  background-color: var(--ht-bg);
}

.ht-combobox-trigger--outline {
  border-color: var(--ht-border);
  background-color: transparent;
}

.ht-combobox-trigger--ghost {
  border-color: transparent;
  background-color: transparent;
}

/* State styles */
.ht-combobox-trigger--error {
  border-color: var(--ht-error);
}

.ht-combobox-trigger--warning {
  border-color: var(--ht-warning);
}

.ht-combobox-trigger--success {
  border-color: var(--ht-success);
}

/* Focus state */
.ht-combobox-trigger:focus-within:not(.ht-combobox-trigger--disabled) {
  outline: 2px solid var(--ht-primary);
  outline-offset: 1px;
  border-color: var(--ht-primary);
}

/* Open state */
.ht-combobox-trigger--open {
  border-color: var(--ht-primary);
}

/* Disabled state */
.ht-combobox-trigger--disabled {
  opacity: 0.5;
  pointer-events: none;
  cursor: not-allowed;
}

/* Selected Item Tag */
.ht-combobox-tag {
  display: inline-flex;
  align-items: center;
  gap: 0.25rem;
  padding: 0.125rem 0.375rem;
  background-color: var(--ht-primary-subtle);
  color: var(--ht-primary);
  border-radius: var(--radius-ht-sm);
  border: 1px solid transparent;
  font-size: 0.8125rem;
  font-weight: 500;
  line-height: 1.25rem;
  white-space: nowrap;
  flex-shrink: 1; /* 允许标签收缩，为箭头留出空间 */
  min-width: 0; /* 允许标签缩小到内容宽度以下 */
  max-width: calc(100% - 80px); /* 确保留出输入框和箭头的最小空间 */
}

.ht-combobox-tag--pending-clear {
  border-color: var(--ht-error-border);
  background-color: var(--ht-error-subtle);
  color: var(--ht-error);
}

.ht-combobox-tag__text {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  /* 移除固定的 max-width，让父容器控制 */
}

.ht-combobox-tag__close {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.125rem;
  border-radius: var(--radius-ht-sm);
  color: var(--ht-primary);
  transition: var(--ht-transition-fast);
  cursor: pointer;
}

.ht-combobox-tag__close:hover {
  background-color: var(--ht-primary);
  color: var(--ht-bg);
}

/* Input Field */
.ht-combobox-input {
  flex: 1;
  min-width: 40px; /* 减小最小宽度，但确保输入框始终可见 */
  border: none;
  outline: none;
  background: transparent;
  font-size: inherit;
  color: var(--ht-text);
  padding: 0;
}

.ht-combobox-input::placeholder {
  color: var(--ht-text-muted);
}

.ht-combobox-input:disabled {
  cursor: not-allowed;
}

/* Suffix Icon Container */
.ht-combobox-suffix {
  display: flex;
  align-items: center;
  flex-shrink: 0; /* 箭头图标永不收缩 */
  color: var(--ht-text-muted);
  pointer-events: auto;
  margin-left: auto; /* 确保箭头始终靠右 */
}
</style>
