<!--
  CustomFilterEditor.vue - Operator-based filter editor

  Responsibilities:
  - Display operator selector based on data type
  - Render appropriate input (text/number/date/enum)
  - Handle date presets for quick selection
  - Validate input before applying
-->

<script setup lang="ts">
import { computed, ref, watch } from 'vue'
import type { FilterConfig } from '../../../types'

const props = defineProps<{
  field: string
  dataType: 'text' | 'number' | 'date' | 'datetime' | 'time' | 'interval' | 'boolean' | 'enum' | string
  currentFilter?: FilterConfig | null
  enumOptions?: Array<{ label: string; value: unknown }>
}>()

// Editor state
const operator = ref<FilterConfig['operator'] | ''>('')
const value = ref<unknown>('')
const rangeMin = ref<unknown>('')
const rangeMax = ref<unknown>('')
const enumSelections = ref<Set<unknown>>(new Set())
const validationError = ref('')

// Expose state to parent component
defineExpose({
  getFilterConfig: () => {
    validationError.value = ''

    const selectedOperator = operator.value
    if (!selectedOperator) {
      return null
    }

    let v: unknown = value.value

    // Type coercion and validation
    if (inputType.value === 'number') {
      const nv = Number(v)
      if (!Number.isFinite(nv)) {
        validationError.value = 'Invalid number input'
        return null
      }
      v = nv
    }

    if (inputType.value === 'boolean') {
      v = v === true || v === 'true'
    }

    if (inputType.value === 'datetime-local' && selectedOperator === 'equals' && typeof v === 'string' && v) {
      const d = new Date(v)
      if (!isNaN(d.getTime())) v = d.toISOString()
    }

    // Between operator
    if (selectedOperator === 'between') {
      let minV: unknown = rangeMin.value
      let maxV: unknown = rangeMax.value

      if (inputType.value === 'number') {
        const nmin = Number(minV)
        const nmax = Number(maxV)
        if (!Number.isFinite(nmin) || !Number.isFinite(nmax)) {
          validationError.value = 'Invalid range input'
          return null
        }
        minV = nmin
        maxV = nmax
      }

      if (inputType.value === 'datetime-local' || inputType.value === 'date') {
        const dmin = new Date(minV as string)
        const dmax = new Date(maxV as string)
        if (isNaN(dmin.getTime()) || isNaN(dmax.getTime())) {
          validationError.value = 'Invalid range input'
          return null
        }
        if (inputType.value === 'datetime-local') {
          minV = dmin.toISOString()
          maxV = dmax.toISOString()
        }
      }

      v = [minV, maxV]
    }

    // Enum handling
    if (inputType.value === 'enum') {
      if (selectedOperator === 'in') {
        const selected = Array.from(enumSelections.value)
        if (selected.length === 0) {
          validationError.value = 'Select at least one option'
          return null
        }
        v = selected
      } else {
        enumSelections.value = new Set()
      }
    }

    return { operator: selectedOperator, value: v }
  },
  hasValidationError: () => validationError.value
})

// Operator options by type
const operatorOptions = computed<{ label: string; value: FilterConfig['operator'] }[]>(() => {
  const type = props.dataType
  if (type === 'date' || type === 'datetime' || type === 'time' || type === 'interval') {
    return [
      { label: 'Equals', value: 'equals' },
      { label: 'On or after', value: 'gte' },
      { label: 'On or before', value: 'lte' },
      { label: 'Between', value: 'between' },
    ]
  }
  if (type === 'enum') {
    return [
      { label: 'Equals', value: 'equals' },
      { label: 'Not equals', value: 'notEquals' },
      { label: 'Includes any', value: 'in' },
    ]
  }
  if (type === 'number' || type === 'currency' || type === 'percentage') {
    return [
      { label: 'Equals', value: 'equals' },
      { label: 'Not equals', value: 'notEquals' },
      { label: 'Greater than', value: 'gt' },
      { label: 'Less than', value: 'lt' },
      { label: 'Greater or equal', value: 'gte' },
      { label: 'Less or equal', value: 'lte' },
      { label: 'Between', value: 'between' },
    ]
  }
  if (type === 'boolean') {
    return [
      { label: 'Equals', value: 'equals' },
      { label: 'Not equals', value: 'notEquals' },
    ]
  }
  // Text fallback
  return [
    { label: 'Contains', value: 'contains' },
    { label: 'Equals', value: 'equals' },
    { label: 'Not equals', value: 'notEquals' },
    { label: 'Starts with', value: 'startsWith' },
    { label: 'Ends with', value: 'endsWith' },
    { label: 'Is empty', value: 'isEmpty' },
    { label: 'Is not empty', value: 'notEmpty' },
  ]
})

const inputType = computed(() => {
  if (props.dataType === 'number' || props.dataType === 'currency' || props.dataType === 'percentage') return 'number'
  if (props.dataType === 'boolean') return 'boolean'
  if (props.dataType === 'date') return 'date'
  if (props.dataType === 'datetime') return 'datetime-local'
  if (props.dataType === 'time') return 'time'
  if (props.dataType === 'interval') return 'text'
  if (props.dataType === 'enum') return 'enum'
  return 'text'
})

// Sync from current filter
watch(
  () => props.currentFilter,
  (filter) => {
    if (!filter) {
      operator.value = ''
      value.value = inputType.value === 'boolean' ? true : ''
      rangeMin.value = ''
      rangeMax.value = ''
      enumSelections.value = new Set()
      return
    }

    operator.value = filter.operator

    if ((filter.operator === 'in' || filter.operator === 'oneOf') && Array.isArray(filter.value)) {
      enumSelections.value = new Set(filter.value)
      value.value = ''
      rangeMin.value = ''
      rangeMax.value = ''
    } else if (filter.operator === 'between' && Array.isArray(filter.value)) {
      const [min, max] = filter.value
      rangeMin.value = formatForInput(min)
      rangeMax.value = formatForInput(max)
      value.value = ''
      enumSelections.value = new Set()
    } else {
      rangeMin.value = ''
      rangeMax.value = ''
      enumSelections.value = new Set()
      value.value = formatForInput(filter.value)
    }
  },
  { immediate: true }
)

watch(operator, newOperator => {
  validationError.value = ''
  if (!newOperator) {
    if (inputType.value === 'enum') {
      enumSelections.value = new Set()
    }
    return
  }
  if (inputType.value === 'enum') {
    if (newOperator === 'in') {
      value.value = ''
    } else {
      enumSelections.value = new Set()
      if ((newOperator === 'equals' || newOperator === 'notEquals') && (value.value === '' || value.value === undefined)) {
        const firstOption = props.enumOptions?.[0]
        if (firstOption) value.value = firstOption.value
      }
    }
  }
})

function formatForInput(raw: unknown): unknown {
  if (raw === null || raw === undefined) return ''
  if (inputType.value === 'datetime-local') {
    const d = raw instanceof Date ? raw : new Date(raw as string)
    if (!isNaN(d.getTime())) return d.toISOString().slice(0, 16)
  }
  if (inputType.value === 'date') {
    const d = raw instanceof Date ? raw : new Date(raw as string)
    if (!isNaN(d.getTime())) return d.toISOString().slice(0, 10)
    if (typeof raw === 'string') return raw.slice(0, 10)
  }
  return raw
}

// Date presets
function applyDatePreset(kind: 'today' | 'yesterday' | 'last7' | 'last30' | 'thisMonth') {
  const now = new Date()
  let a: Date, b: Date

  switch (kind) {
    case 'today':
      a = new Date(now.setHours(0, 0, 0, 0))
      b = new Date(now.setHours(23, 59, 59, 999))
      break
    case 'yesterday': {
      const y = new Date(now)
      y.setDate(y.getDate() - 1)
      a = new Date(y.setHours(0, 0, 0, 0))
      b = new Date(y.setHours(23, 59, 59, 999))
      break
    }
    case 'last7': {
      const start = new Date(now)
      start.setDate(start.getDate() - 6)
      a = new Date(start.setHours(0, 0, 0, 0))
      b = new Date(now.setHours(23, 59, 59, 999))
      break
    }
    case 'last30': {
      const start = new Date(now)
      start.setDate(start.getDate() - 29)
      a = new Date(start.setHours(0, 0, 0, 0))
      b = new Date(now.setHours(23, 59, 59, 999))
      break
    }
    case 'thisMonth': {
      a = new Date(now.getFullYear(), now.getMonth(), 1, 0, 0, 0, 0)
      b = new Date(now.setHours(23, 59, 59, 999))
      break
    }
    default:
      return
  }

  operator.value = 'between'
  if (inputType.value === 'datetime-local') {
    rangeMin.value = a.toISOString().slice(0, 19)
    rangeMax.value = b.toISOString().slice(0, 19)
  } else {
    rangeMin.value = a.toISOString().slice(0, 10)
    rangeMax.value = b.toISOString().slice(0, 10)
  }
}

function handleEnumToggle(optionValue: unknown, checked: boolean) {
  const next = new Set(enumSelections.value)
  if (checked) {
    next.add(optionValue)
  } else {
    next.delete(optionValue)
  }
  enumSelections.value = next
  if (next.size > 0) {
    validationError.value = ''
  }
}
</script>

<template>
  <div class="space-y-3">
    <!-- Date presets -->
    <div v-if="inputType === 'date' || inputType === 'datetime-local'" class="flex flex-wrap gap-2">
      <button
        class="px-3 py-1.5 text-xs font-medium text-[var(--ht-text)] rounded-md border border-[var(--ht-border)] bg-[var(--ht-bg)] hover:bg-[var(--ht-bg-subtle)] hover:border-[var(--ht-border-strong)] transition-colors"
        @click="applyDatePreset('today')"
      >
        Today
      </button>
      <button
        class="px-3 py-1.5 text-xs font-medium text-[var(--ht-text)] rounded-md border border-[var(--ht-border)] bg-[var(--ht-bg)] hover:bg-[var(--ht-bg-subtle)] hover:border-[var(--ht-border-strong)] transition-colors"
        @click="applyDatePreset('yesterday')"
      >
        Yesterday
      </button>
      <button
        class="px-3 py-1.5 text-xs font-medium text-[var(--ht-text)] rounded-md border border-[var(--ht-border)] bg-[var(--ht-bg)] hover:bg-[var(--ht-bg-subtle)] hover:border-[var(--ht-border-strong)] transition-colors"
        @click="applyDatePreset('last7')"
      >
        Last 7 days
      </button>
      <button
        class="px-3 py-1.5 text-xs font-medium text-[var(--ht-text)] rounded-md border border-[var(--ht-border)] bg-[var(--ht-bg)] hover:bg-[var(--ht-bg-subtle)] hover:border-[var(--ht-border-strong)] transition-colors"
        @click="applyDatePreset('last30')"
      >
        Last 30 days
      </button>
      <button
        class="px-3 py-1.5 text-xs font-medium text-[var(--ht-text)] rounded-md border border-[var(--ht-border)] bg-[var(--ht-bg)] hover:bg-[var(--ht-bg-subtle)] hover:border-[var(--ht-border-strong)] transition-colors"
        @click="applyDatePreset('thisMonth')"
      >
        This month
      </button>
    </div>

    <!-- Operator and value input -->
    <div v-if="inputType !== 'boolean' && inputType !== 'enum'" class="flex gap-2 items-center min-w-0">
      <select
        v-model="operator"
        class="h-9 min-w-[140px] shrink-0 rounded-md border border-[var(--ht-border)] bg-[var(--ht-bg)] px-3 text-sm text-[var(--ht-text)] transition-colors focus:border-[var(--ht-primary)] focus:outline-none focus:ring-2 focus:ring-[var(--ht-primary)]/20"
      >
        <option disabled value="">Select condition…</option>
        <option v-for="opt in operatorOptions" :key="opt.value" :value="opt.value">{{ opt.label }}</option>
      </select>

      <!-- Single input -->
      <input
        v-if="operator !== 'between'"
        v-model="value"
        :type="inputType"
        :style="(inputType === 'date' || inputType === 'datetime-local' || inputType === 'time') ? 'color-scheme: light dark;' : undefined"
        class="h-9 flex-1 min-w-0 rounded-md border border-[var(--ht-border)] bg-[var(--ht-bg)] px-3 text-sm text-[var(--ht-text)] placeholder:text-[var(--ht-text-subtle)] transition-colors focus:border-[var(--ht-primary)] focus:outline-none focus:ring-2 focus:ring-[var(--ht-primary)]/20"
        :placeholder="inputType === 'text' ? 'Enter value' : ''"
      />

      <!-- Range inputs -->
      <template v-else>
        <input
          v-model="rangeMin"
          :type="inputType"
          placeholder="Min"
          :style="(inputType === 'date' || inputType === 'datetime-local' || inputType === 'time') ? 'color-scheme: light dark;' : undefined"
          :class="[
            'h-9 shrink-0 rounded-md border border-[var(--ht-border)] bg-[var(--ht-bg)] px-3 text-sm text-[var(--ht-text)] placeholder:text-[var(--ht-text-subtle)] transition-colors focus:border-[var(--ht-primary)] focus:outline-none focus:ring-2 focus:ring-[var(--ht-primary)]/20',
            inputType === 'datetime-local' ? 'w-[160px]' : inputType === 'time' ? 'w-[90px]' : 'w-[110px]'
          ]"
        />
        <span class="text-[var(--ht-text-muted)] text-sm shrink-0">to</span>
        <input
          v-model="rangeMax"
          :type="inputType"
          placeholder="Max"
          :style="(inputType === 'date' || inputType === 'datetime-local' || inputType === 'time') ? 'color-scheme: light dark;' : undefined"
          :class="[
            'h-9 shrink-0 rounded-md border border-[var(--ht-border)] bg-[var(--ht-bg)] px-3 text-sm text-[var(--ht-text)] placeholder:text-[var(--ht-text-subtle)] transition-colors focus:border-[var(--ht-primary)] focus:outline-none focus:ring-2 focus:ring-[var(--ht-primary)]/20',
            inputType === 'datetime-local' ? 'w-[160px]' : inputType === 'time' ? 'w-[90px]' : 'w-[110px]'
          ]"
        />
      </template>
    </div>

    <!-- Boolean select -->
    <select
      v-else-if="inputType === 'boolean'"
      v-model="value"
      class="h-9 flex-1 rounded-md border border-[var(--ht-border)] bg-[var(--ht-bg)] px-3 text-sm text-[var(--ht-text)] transition-colors focus:border-[var(--ht-primary)] focus:outline-none focus:ring-2 focus:ring-[var(--ht-primary)]/20"
    >
      <option :value="true">True</option>
      <option :value="false">False</option>
    </select>

    <!-- Enum select -->
    <template v-else-if="inputType === 'enum'">
      <select
        v-if="operator === 'equals'"
        v-model="value"
        class="h-9 flex-1 rounded-md border border-[var(--ht-border)] bg-[var(--ht-bg)] px-3 text-sm text-[var(--ht-text)] transition-colors focus:border-[var(--ht-primary)] focus:outline-none focus:ring-2 focus:ring-[var(--ht-primary)]/20"
      >
        <option v-for="opt in enumOptions" :key="String(opt.value)" :value="opt.value">{{ opt.label }}</option>
      </select>
      <div v-else class="max-h-48 overflow-auto rounded-md border border-[var(--ht-border)] bg-[var(--ht-bg-subtle)] p-2 space-y-1">
        <label
          v-for="opt in enumOptions"
          :key="String(opt.value)"
          class="flex items-center gap-3 px-2 py-2 text-sm rounded hover:bg-[var(--ht-cell-hover)] cursor-pointer transition-colors group"
        >
          <input
            type="checkbox"
            class="w-4 h-4 rounded border-[var(--ht-border-strong)] text-[var(--ht-primary)] focus:ring-2 focus:ring-[var(--ht-primary)]/30 cursor-pointer"
            :checked="enumSelections.has(opt.value)"
            @change="(e: Event) => handleEnumToggle(opt.value, (e.target as HTMLInputElement).checked)"
          />
          <span class="truncate text-[var(--ht-text)] group-hover:text-[var(--ht-text)]">{{ opt.label }}</span>
        </label>
      </div>
    </template>

    <!-- Validation error -->
    <div v-if="validationError" class="flex items-start gap-2 rounded-md bg-[var(--ht-danger-bg)] border border-[var(--ht-danger-border)] px-3 py-2 text-xs text-[var(--ht-danger)]">
      <svg class="w-4 h-4 shrink-0 mt-0.5" fill="currentColor" viewBox="0 0 20 20">
        <path fill-rule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clip-rule="evenodd" />
      </svg>
      <span>{{ validationError }}</span>
    </div>
  </div>
</template>
