import { ref, computed, watch } from 'vue'
import { useDebounceFn } from '../../../../utils/composables'
import type { Ref } from 'vue'
import type { SelectOption, AsyncSourceConfig, AsyncLoadingState } from '../../../../types/primitives/select'

/**
 * useAsyncSource - 异步数据源管理
 *
 * 职责边界:
 * - HtSelect 负责: 调用函数、管理状态、取消请求、防抖缓存
 * - 用户代码负责: HTTP请求、认证、重试、数据转换
 *
 * @param config - 异步数据源配置
 * @param inputValue - 搜索输入值
 * @param emit - 事件触发函数
 * @returns 异步数据源管理对象
 */
export function useAsyncSource(
  config: AsyncSourceConfig,
  inputValue: Ref<string>,
  emit: (event: string, ...args: any[]) => void
) {
  // ==========================================
  // 状态管理
  // ==========================================

  const loadingState = ref<AsyncLoadingState>({
    loading: false,
    error: null,
    abortController: null,
  })

  const cachedOptions = ref<Map<string, SelectOption[]>>(new Map())
  const currentOptions = ref<SelectOption[]>([])

  // ==========================================
  // 缓存管理
  // ==========================================

  const getCacheKey = (query: string): string => {
    return `async-source:${query.toLowerCase().trim()}`
  }

  const getFromCache = async (query: string): Promise<SelectOption[] | null> => {
    const key = getCacheKey(query)

    // 内存缓存
    if (config.cacheStrategy === 'memory' || !config.cacheStrategy) {
      return cachedOptions.value.get(key) || null
    }

    // 会话缓存
    if (config.cacheStrategy === 'session') {
      const cached = sessionStorage.getItem(key)
      return cached ? JSON.parse(cached) : null
    }

    // 自定义缓存
    if (config.cacheStrategy === 'custom' && config.cache) {
      return await config.cache.get(key)
    }

    return null
  }

  const setToCache = async (query: string, options: SelectOption[]): Promise<void> => {
    const key = getCacheKey(query)

    // 内存缓存
    if (config.cacheStrategy === 'memory' || !config.cacheStrategy) {
      cachedOptions.value.set(key, options)
      return
    }

    // 会话缓存
    if (config.cacheStrategy === 'session') {
      sessionStorage.setItem(key, JSON.stringify(options))
      return
    }

    // 自定义缓存
    if (config.cacheStrategy === 'custom' && config.cache) {
      await config.cache.set(key, options)
    }
  }

  // ==========================================
  // 数据加载
  // ==========================================

  const loadOptions = async (query: string): Promise<void> => {
    // 取消上一个请求
    if (loadingState.value.abortController) {
      loadingState.value.abortController.abort()
    }

    // 检查缓存
    const cached = await getFromCache(query)
    if (cached) {
      currentOptions.value = cached
      return
    }

    // 创建新的 AbortController
    const abortController = new AbortController()
    loadingState.value = {
      loading: true,
      error: null,
      abortController,
    }

    emit('loading-start', query)

    try {
      const options = await config.source(query, abortController.signal)

      // 检查请求是否已被取消
      if (abortController.signal.aborted) {
        return
      }

      currentOptions.value = options
      await setToCache(query, options)

      loadingState.value = {
        loading: false,
        error: null,
        abortController: null,
      }

      emit('loading-end', options, query)
    } catch (error) {
      // 忽略取消错误
      if (error instanceof Error && error.name === 'AbortError') {
        return
      }

      const err = error instanceof Error ? error : new Error(String(error))
      loadingState.value = {
        loading: false,
        error: err,
        abortController: null,
      }

      emit('loading-error', err, query)

      // 调用错误处理回调
      if (config.onError) {
        config.onError(err)
      }
    }
  }

  // ==========================================
  // 防抖搜索
  // ==========================================

  const debouncedLoad = useDebounceFn(
    loadOptions,
    config.debounce ?? 250
  )

  // ==========================================
  // 搜索阈值检查
  // ==========================================

  const shouldSearch = computed(() => {
    const threshold = config.searchThreshold ?? 0
    return inputValue.value.length >= threshold
  })

  // ==========================================
  // 监听输入变化
  // ==========================================

  watch(
    () => inputValue.value,
    (query) => {
      if (!shouldSearch.value) {
        currentOptions.value = []
        return
      }

      debouncedLoad(query)
    },
    { immediate: false }
  )

  // ==========================================
  // 清理
  // ==========================================

  const cleanup = (): void => {
    if (loadingState.value.abortController) {
      loadingState.value.abortController.abort()
    }
    cachedOptions.value.clear()
  }

  // ==========================================
  // 导出
  // ==========================================

  return {
    // 状态
    loading: computed(() => loadingState.value.loading),
    error: computed(() => loadingState.value.error),
    options: computed(() => currentOptions.value),

    // 方法
    loadOptions,
    cleanup,
    clearCache: () => cachedOptions.value.clear(),
  }
}
