import { provide, inject, ref, watch, nextTick, computed } from 'vue'
import type { SelectContext, NavigableOption } from '../../../../types/primitives/select'
import type { HtSelectBaseProps } from '../../../../types/primitives/select'
import { generateIdGroup } from '../../../../utils/id-generator'

const SELECT_CONTEXT_KEY = Symbol('HtSelect')
const SELECT_PROPS_KEY = Symbol('HtSelectProps')
export const SELECT_GROUP_KEY = Symbol('HtSelectGroup') // 导出供 HtSelectGroup 和 HtSelectItem 使用

/**
 * Provide Select Context
 * 在 HtSelect 根组件中调用,提供状态管理
 */
export function provideSelectContext(props: any, emit: any): SelectContext {
  const open = ref(false)
  const selectedValue = ref(props.modelValue)
  const highlightedIndex = ref(-1)
  const triggerRef = ref<HTMLElement | null>(null)
  const inputValue = ref('') // 统一的输入框值/搜索查询
  const navigableOptions = ref<NavigableOption[]>([])

  // Generate stable, unique IDs for accessibility
  const ids = generateIdGroup('ht-select', ['triggerId', 'contentId'] as const)

  // Use ref to make Map reactive - whenever set/delete is called, trigger updates
  const optionsMap = ref(new Map<any, string>())

  const enabledOptionIndexes = computed<number[]>(() =>
    navigableOptions.value.filter(option => !option.disabled).map(option => option.index)
  )

  function highlightIndex(index: number | null) {
    highlightedIndex.value = typeof index === 'number' && index >= 0 ? index : -1
  }

  const clearHighlight = () => {
    highlightIndex(null)
  }

  const getEnabledOptionByIndex = (index: number) =>
    navigableOptions.value.find(option => option.index === index && !option.disabled)

  const highlightFirst = () => {
    const [first] = enabledOptionIndexes.value
    highlightIndex(first ?? null)
  }

  const highlightLast = () => {
    const enabled = enabledOptionIndexes.value
    const last = enabled[enabled.length - 1]
    highlightIndex(typeof last === 'number' ? last : null)
  }

  const moveHighlight = (direction: 1 | -1) => {
    const enabled = enabledOptionIndexes.value
    if (!enabled.length) {
      highlightIndex(null)
      return
    }

    const current = highlightedIndex.value
    const currentPosition = enabled.indexOf(current)

    if (currentPosition === -1) {
      highlightIndex(direction === 1 ? enabled[0]! : enabled[enabled.length - 1]!)
      return
    }

    const nextPosition = (currentPosition + direction + enabled.length) % enabled.length
    highlightIndex(enabled[nextPosition]!)
  }

  const selectHighlighted = () => {
    const active = getEnabledOptionByIndex(highlightedIndex.value)
    if (!active) return
    select(active.value)
  }

  const syncHighlightWithSelection = (preferCurrentHighlight = false) => {
    const enabled = enabledOptionIndexes.value
    if (!enabled.length) {
      clearHighlight()
      return
    }

    if (preferCurrentHighlight && getEnabledOptionByIndex(highlightedIndex.value)) {
      return
    }

    const selected = navigableOptions.value.find(option => !option.disabled && isSelected(option.value))
    if (selected) {
      highlightIndex(selected.index)
      return
    }

    highlightFirst()
  }

  // Sync external modelValue changes
  watch(
    () => props.modelValue,
    (newValue) => {
      selectedValue.value = newValue
    }
  )

  const toggle = () => {
    if (props.disabled) return
    open.value = !open.value
  }

  const close = () => {
    open.value = false
    clearHighlight()
    inputValue.value = '' // 清空输入/搜索

    // Return focus to trigger when closing (Button mode only)
    // In Combobox mode (searchable), input keeps focus naturally
    // Use nextTick to ensure DOM has updated before focusing
    if (!props.searchable) {
      nextTick(() => {
        if (triggerRef.value) {
          triggerRef.value.focus()
        }
      })
    }
  }

  const openDropdown = () => {
    if (props.disabled) return
    open.value = true
  }

  const select = (value: any) => {
    selectedValue.value = value
    emit('update:modelValue', value)
    emit('change', value)
    close()
  }

  const isSelected = (value: any): boolean => {
    const current = selectedValue.value

    // No selection
    if (current === undefined || current === null) return false

    // Custom comparison function
    if (props.by && typeof props.by === 'function') {
      return props.by(current, value)
    }

    // Field-based comparison
    if (props.by && typeof props.by === 'string') {
      return current?.[props.by] === value?.[props.by]
    }

    // Reference equality (default)
    return current === value
  }

  const registerOption = (value: any, textValue: string) => {
    optionsMap.value.set(value, textValue)
  }

  const unregisterOption = (value: any) => {
    optionsMap.value.delete(value)
  }

  const context: SelectContext = {
    open,
    selectedValue,
    highlightedIndex,
    triggerRef,
    optionsMap,
    inputValue,
    ids,
    toggle,
    close,
    openDropdown,
    select,
    isSelected,
    registerOption,
    unregisterOption,
    setNavigableOptions: (options: NavigableOption[]) => {
      navigableOptions.value = options
      if (open.value) {
        syncHighlightWithSelection(true)
      } else {
        clearHighlight()
      }
    },
    moveHighlight,
    highlightFirst,
    highlightLast,
    selectHighlighted,
    clearHighlight,
  }

  watch(selectedValue, () => {
    if (open.value) {
      syncHighlightWithSelection()
    }
  })

  watch(() => open.value, (isOpen) => {
    if (isOpen) {
      nextTick(() => {
        syncHighlightWithSelection()
      })
    } else {
      clearHighlight()
    }
  })

  provide(SELECT_CONTEXT_KEY, context)
  return context
}

/**
 * Inject Select Context
 * 在子组件中调用,获取共享状态
 */
export function injectSelectContext(): SelectContext {
  const context = inject<SelectContext>(SELECT_CONTEXT_KEY)

  if (!context) {
    throw new Error('useSelectContext must be used within HtSelect')
  }

  return context
}

/**
 * Provide Select Props
 * 在 HtSelect 根组件中调用,提供 props 给子组件
 */
export function provideSelectProps(props: HtSelectBaseProps): void {
  provide(SELECT_PROPS_KEY, props)
}

/**
 * Inject Select Props
 * 在子组件(如 HtSelectTrigger)中调用,获取父组件 props
 */
export function injectSelectProps(): HtSelectBaseProps {
  const props = inject<HtSelectBaseProps>(SELECT_PROPS_KEY, {
    size: 'md',
    variant: 'outline',
    state: 'default',
    disabled: false,
  })

  return props
}
