/**
 * DOM Performance Optimization Utilities
 *
 * Features:
 * - Element pooling for virtual scrolling
 * - Hardware acceleration utilities
 * - DOM batching for performance
 * - Intersection Observer for visibility
 * - Memory-efficient event delegation
 */

import { ref, onUnmounted } from 'vue'

// Element Pool for efficient DOM reuse
export class ElementPool<T extends HTMLElement = HTMLElement> {
  private pool: T[] = []
  private activeElements = new Set<T>()
  private createElement: () => T
  private resetElement: (element: T) => void
  private maxPoolSize: number

  constructor(
    createElement: () => T,
    resetElement: (element: T) => void = () => {},
    maxPoolSize: number = 100
  ) {
    this.createElement = createElement
    this.resetElement = resetElement
    this.maxPoolSize = maxPoolSize
  }

  acquire(): T {
    let element = this.pool.pop()

    if (!element) {
      element = this.createElement()
    }

    this.activeElements.add(element)
    return element
  }

  release(element: T): void {
    if (!this.activeElements.has(element)) return

    this.activeElements.delete(element)
    this.resetElement(element)

    if (this.pool.length < this.maxPoolSize) {
      this.pool.push(element)
    }
  }

  clear(): void {
    this.pool = []
    this.activeElements.clear()
  }

  get stats() {
    return {
      poolSize: this.pool.length,
      activeElements: this.activeElements.size,
      totalElements: this.pool.length + this.activeElements.size,
    }
  }
}

// Hardware acceleration utilities
export function enableHardwareAcceleration(element: HTMLElement): void {
  element.style.transform = 'translateZ(0)'
  element.style.willChange = 'transform'
}

export function disableHardwareAcceleration(element: HTMLElement): void {
  element.style.transform = ''
  element.style.willChange = 'auto'
}

// CSS Containment helper
export function applyCSSContainment(
  element: HTMLElement,
  types: string[] = ['layout', 'style', 'paint']
): void {
  element.style.contain = types.join(' ')
}

// DOM Batching utility for performance
export class DOMBatcher {
  private batches: Array<() => void> = []
  private rafId: number | null = null

  add(operation: () => void): void {
    this.batches.push(operation)
    this.schedule()
  }

  private schedule(): void {
    if (this.rafId) return

    this.rafId = requestAnimationFrame(() => {
      const operations = this.batches.splice(0)
      operations.forEach(op => op())
      this.rafId = null
    })
  }

  flush(): void {
    if (this.rafId) {
      cancelAnimationFrame(this.rafId)
      this.rafId = null
    }

    const operations = this.batches.splice(0)
    operations.forEach(op => op())
  }

  clear(): void {
    if (this.rafId) {
      cancelAnimationFrame(this.rafId)
      this.rafId = null
    }
    this.batches = []
  }
}

// Efficient event delegation
export class EventDelegator {
  private listeners = new Map<string, Set<(event: Event, target: Element) => void>>()
  private boundHandler: (event: Event) => void
  private container: HTMLElement

  constructor(container: HTMLElement) {
    this.container = container
    this.boundHandler = this.handleEvent.bind(this)
    this.container.addEventListener('click', this.boundHandler, { passive: true })
    this.container.addEventListener('dblclick', this.boundHandler, { passive: true })
    this.container.addEventListener('mousedown', this.boundHandler, { passive: true })
    this.container.addEventListener('mouseup', this.boundHandler, { passive: true })
  }

  on(eventType: string, selector: string, handler: (event: Event, target: Element) => void): void {
    const key = `${eventType}:${selector}`
    if (!this.listeners.has(key)) {
      this.listeners.set(key, new Set())
    }
    this.listeners.get(key)!.add(handler)
  }

  off(eventType: string, selector: string, handler: (event: Event, target: Element) => void): void {
    const key = `${eventType}:${selector}`
    const handlers = this.listeners.get(key)
    if (handlers) {
      handlers.delete(handler)
      if (handlers.size === 0) {
        this.listeners.delete(key)
      }
    }
  }

  private handleEvent(event: Event): void {
    const target = event.target as Element
    if (!target) return

    for (const [key, handlers] of this.listeners.entries()) {
      const [eventType, selector] = key.split(':')
      if (event.type === eventType && selector) {
        const matchedElement = target.closest(selector)
        if (matchedElement && this.container.contains(matchedElement)) {
          handlers.forEach(handler => handler(event, matchedElement))
        }
      }
    }
  }

  destroy(): void {
    this.container.removeEventListener('click', this.boundHandler)
    this.container.removeEventListener('dblclick', this.boundHandler)
    this.container.removeEventListener('mousedown', this.boundHandler)
    this.container.removeEventListener('mouseup', this.boundHandler)
    this.listeners.clear()
  }
}

// Intersection Observer for visibility optimization
export function useVisibilityObserver(threshold: number = 0.1, rootMargin: string = '50px') {
  const visibleElements = ref(new Set<Element>())

  const observer = new IntersectionObserver(
    entries => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          visibleElements.value.add(entry.target)
        } else {
          visibleElements.value.delete(entry.target)
        }
      })
    },
    {
      threshold,
      rootMargin,
    }
  )

  const observe = (element: Element) => {
    observer.observe(element)
  }

  const unobserve = (element: Element) => {
    observer.unobserve(element)
    visibleElements.value.delete(element)
  }

  const isVisible = (element: Element) => {
    return visibleElements.value.has(element)
  }

  onUnmounted(() => {
    observer.disconnect()
    visibleElements.value.clear()
  })

  return {
    visibleElements,
    observe,
    unobserve,
    isVisible,
  }
}

// Performance-optimized style updates
export class StyleBatcher {
  private updates = new Map<HTMLElement, Record<string, string>>()
  private rafId: number | null = null

  setStyle(element: HTMLElement, property: string, value: string): void {
    if (!this.updates.has(element)) {
      this.updates.set(element, {})
    }

    this.updates.get(element)![property] = value
    this.schedule()
  }

  setStyles(element: HTMLElement, styles: Record<string, string>): void {
    if (!this.updates.has(element)) {
      this.updates.set(element, {})
    }

    Object.assign(this.updates.get(element)!, styles)
    this.schedule()
  }

  private schedule(): void {
    if (this.rafId) return

    this.rafId = requestAnimationFrame(() => {
      for (const [element, styles] of this.updates.entries()) {
        Object.assign(element.style, styles)
      }
      this.updates.clear()
      this.rafId = null
    })
  }

  flush(): void {
    if (this.rafId) {
      cancelAnimationFrame(this.rafId)
      this.rafId = null
    }

    for (const [element, styles] of this.updates.entries()) {
      Object.assign(element.style, styles)
    }
    this.updates.clear()
  }
}

// Optimized virtual row component factory
export function createVirtualRowFactory() {
  const rowPool = new ElementPool(
    () => {
      const row = document.createElement('div')
      row.className = 'ht-virtual-row'
      applyCSSContainment(row)
      enableHardwareAcceleration(row)
      return row
    },
    row => {
      // Reset row state
      row.style.transform = ''
      row.style.height = ''
      row.innerHTML = ''
      row.className = 'ht-virtual-row'
    }
  )

  const cellPool = new ElementPool(
    () => {
      const cell = document.createElement('div')
      cell.className = 'ht-virtual-cell'
      return cell
    },
    cell => {
      // Reset cell state
      cell.innerHTML = ''
      cell.className = 'ht-virtual-cell'
      cell.style.width = ''
    }
  )

  const styleBatcher = new StyleBatcher()

  return {
    acquireRow: () => rowPool.acquire(),
    releaseRow: (row: HTMLDivElement) => rowPool.release(row as HTMLDivElement),
    acquireCell: () => cellPool.acquire(),
    releaseCell: (cell: HTMLDivElement) => cellPool.release(cell as HTMLDivElement),
    batchStyle: (element: HTMLElement, styles: Record<string, string>) => {
      styleBatcher.setStyles(element, styles)
    },
    flushStyles: () => styleBatcher.flush(),
    getStats: () => ({
      rows: rowPool.stats,
      cells: cellPool.stats,
    }),
    cleanup: () => {
      rowPool.clear()
      cellPool.clear()
      styleBatcher.flush()
    },
  }
}

// Memory leak detection utilities
export class MemoryLeakDetector {
  private elements = new WeakSet<HTMLElement>()
  private callbacks = new Set<() => void>()
  private checkInterval: number

  constructor(intervalMs: number = 5000) {
    this.checkInterval = setInterval(() => {
      this.checkForLeaks()
    }, intervalMs)
  }

  track(element: HTMLElement): void {
    this.elements.add(element)
  }

  onLeak(callback: () => void): void {
    this.callbacks.add(callback)
  }

  private checkForLeaks(): void {
    // Simple heuristic: check for unusually high element counts
    const allElements = document.querySelectorAll('*').length
    const threshold = 10000 // Adjust based on your app

    if (allElements > threshold) {
      this.callbacks.forEach(callback => callback())
    }
  }

  destroy(): void {
    clearInterval(this.checkInterval)
    this.callbacks.clear()
  }
}

// Optimized scroll handler
export function createOptimizedScrollHandler(
  callback: (scrollTop: number, scrollLeft: number) => void
) {
  let rafId: number | null = null
  let lastScrollTop = 0
  let lastScrollLeft = 0

  const handler = (event: Event) => {
    const target = event.target as HTMLElement
    const scrollTop = target.scrollTop
    const scrollLeft = target.scrollLeft

    // Skip if position hasn't changed
    if (scrollTop === lastScrollTop && scrollLeft === lastScrollLeft) {
      return
    }

    if (rafId) {
      cancelAnimationFrame(rafId)
    }

    rafId = requestAnimationFrame(() => {
      callback(scrollTop, scrollLeft)
      lastScrollTop = scrollTop
      lastScrollLeft = scrollLeft
      rafId = null
    })
  }

  const cleanup = () => {
    if (rafId) {
      cancelAnimationFrame(rafId)
    }
  }

  return { handler, cleanup }
}
