import type { GridAPI } from '../../types'
import type { GridPlugin } from '../types'
import { ScopedLogger, createLogger } from '../../utils/logger'

/**
 * Abstract base class for plugins to extend.
 * Provides common functionality like API access and logging.
 */
export abstract class BasePlugin implements GridPlugin {
  abstract name: string
  protected api!: GridAPI
  protected logger: ScopedLogger

  constructor() {
    // The `name` property must be defined by the subclass,
    // but we can provide a default logger instance here.
    this.logger = createLogger({ module: 'Plugin', operation: 'base-plugin' })
  }

  /**
   * Called when the plugin is installed with the grid.
   * @param api The GridAPI instance.
   */
  install(api: GridAPI): void {
    this.api = api
    // Now that we have the definitive name from the subclass, create the specific logger.
    this.logger = createLogger({ module: 'Plugin', operation: this.name })
    this.logger.info(`Plugin installed: ${this.name}`)
  }

  /**
   * Called when the plugin is cleaned up from the grid.
   */
  cleanup?(): void
}
