<script setup lang="ts">
/**
 * HtSelect - 下拉选择根组件
 *
 * 职责:
 * - Context Provider (状态管理)
 * - Props 接收和验证
 * - 事件触发
 * - Props 模式: 自动渲染选项 (Phase 2)
 * - 搜索过滤 (Phase 2.2)
 * - 自定义选项渲染 (Phase 2.2.21)
 * - 异步数据加载 (Phase 2 Async)
 *
 * Phase 1 MVP: 单选模式 + 键盘导航 + ARIA
 * Phase 2: Props 模式 + 自动分组 + 搜索 + 异步数据源
 * Phase 2.2.21: optionRender 自定义渲染
 */
import { computed, watch } from 'vue'
import type { HtSelectBaseProps, SelectOption, OptionRenderData, NavigableOption } from '../../../types/primitives/select'
import { provideSelectContext, provideSelectProps } from './composables/useSelectContext'
import { useAsyncSource } from './composables/useAsyncSource'
import HtSelectTrigger from './HtSelectTrigger.vue'
import HtSelectValue from './HtSelectValue.vue'
import HtSelectContent from './HtSelectContent.vue'
import HtSelectItem from './HtSelectItem.vue'
import HtSelectGroup from './HtSelectGroup.vue'
import HtSelectLabel from './HtSelectLabel.vue'
import HtSelectSeparator from './HtSelectSeparator.vue'
import HtSelectOptionRender from './HtSelectOptionRender.vue'

const props = withDefaults(defineProps<HtSelectBaseProps>(), {
  disabled: false,
  required: false,
  size: 'md',
  variant: 'outline',
  state: 'default',
  clearable: false,
  searchable: false,
  searchFields: 'auto',
  groupBy: 'group',
  groupLabelSticky: false,
  placeholder: 'Select...',
  emptyText: 'No options available',
  emptySearchText: 'No results found',
  defaultGroupName: 'Other',
  valueField: 'value',
  labelField: 'label',
  showMultiLines: 1,
})

const emit = defineEmits<{
  'update:modelValue': [value: any]
  'change': [value: any]
  'loading-start': [query: string]
  'loading-end': [options: SelectOption[], query: string]
  'loading-error': [error: Error, query: string]
}>()

// Provide context and props for child components
const context = provideSelectContext(props, emit)
provideSelectProps(props)

// ==========================================
// 异步数据源模式检测 (Phase 2 Async)
// ==========================================

const isAsyncMode = computed(() => !!props.source)

// 创建类型兼容的 emit 包装用于 useAsyncSource
const asyncEmit = (event: string, ...args: any[]) => {
  if (event === 'loading-start') {
    emit('loading-start', args[0])
  } else if (event === 'loading-end') {
    emit('loading-end', args[0], args[1])
  } else if (event === 'loading-error') {
    emit('loading-error', args[0], args[1])
  }
}

// 异步数据源 composable (仅在异步模式下启用)
const asyncSource = isAsyncMode.value
  ? useAsyncSource(
      {
        source: props.source!,
        debounce: props.debounce,
        searchThreshold: props.searchThreshold,
        cacheStrategy: props.cacheStrategy,
        cache: props.cache,
      },
      context.inputValue,
      asyncEmit
    )
  : null

// Compute dynamic styles
const rootStyle = computed(() => ({
  width: props.width,
}))

// Props 模式检测
const isPropsMode = computed(() => {
  // 异步模式优先级高于 Props 模式
  if (isAsyncMode.value) return true
  return Array.isArray(props.options) && props.options.length > 0
})

interface RenderedOption {
  option: SelectOption
  index: number
  group?: string
  renderData: OptionRenderData | null
}

// 标准化选项格式
const normalizedOptions = computed<SelectOption[]>(() => {
  // 异步模式: 使用异步数据源返回的选项
  if (isAsyncMode.value && asyncSource) {
    // 初始化时使用 initialOptions
    if (!context.inputValue.value && props.initialOptions) {
      return props.initialOptions
    }
    // 搜索时使用异步返回的选项
    return asyncSource.options.value
  }

  // Props 模式: 使用 props.options
  if (!props.options) return []

  return props.options.map(opt => {
    // 简单类型 (string | number)
    if (typeof opt === 'string' || typeof opt === 'number') {
      return {
        value: opt,
        label: String(opt),
        disabled: false,
      }
    }

    // 对象类型 - 使用字段映射
    const valueField = props.valueField || 'value'
    const labelField = props.labelField || 'label'

    // 禁用状态：优先使用 optionDisabled 函数，否则检查 disabled 字段
    const disabled = props.optionDisabled
      ? props.optionDisabled(opt)
      : (opt.disabled || false)

    return {
      value: opt[valueField],
      label: opt[labelField] || String(opt[valueField]),
      disabled,
      ...opt,  // 保留所有原始属性 (包括 group/自定义分组字段)
    }
  })
})

/**
 * 搜索匹配辅助函数
 * 检查选项是否匹配搜索查询
 * @param option - 选项对象
 * @param query - 搜索查询(小写)
 * @param searchFields - 要搜索的字段列表
 * @param renderDataGetter - 获取渲染数据的函数
 * @returns 是否匹配
 */
function matchesSearch(
  option: SelectOption,
  query: string,
  searchFields: Array<string>,
  renderDataGetter?: (option: SelectOption) => OptionRenderData | null
): boolean {
  const shouldSearchField = (field: string) => searchFields.includes(field)

  // 1. 搜索 label (基础字段)
  if (shouldSearchField('label')) {
    const label = option.label?.toLowerCase() || ''
    if (label.includes(query)) return true
  }

  // 2-5. 搜索 optionRender 自定义字段
  if (renderDataGetter) {
    const renderData = renderDataGetter(option)
    if (!renderData) return false

    // 2. 搜索 title
    if (shouldSearchField('title') && renderData.title?.toLowerCase().includes(query)) {
      return true
    }

    // 3. 搜索 description
    if (shouldSearchField('description') && renderData.description?.toLowerCase().includes(query)) {
      return true
    }

    // 4. 搜索 meta (所有 label 和 value)
    if (shouldSearchField('meta') && renderData.meta && Array.isArray(renderData.meta)) {
      const metaMatches = renderData.meta.some(item => {
        const metaLabel = String(item.label || '').toLowerCase()
        const metaValue = String(item.value || '').toLowerCase()
        return metaLabel.includes(query) || metaValue.includes(query)
      })
      if (metaMatches) return true
    }

    // 5. 搜索 badge
    if (shouldSearchField('badge') && renderData.badge?.toLowerCase().includes(query)) {
      return true
    }
  }

  return false
}

// 搜索过滤 (Phase 2.2.22 - 全文检索支持)
// 支持智能搜索和自定义字段搜索
const filteredOptions = computed<SelectOption[]>(() => {
  // 异步模式: 不进行本地过滤(异步源已处理)
  if (isAsyncMode.value) {
    return normalizedOptions.value
  }

  const query = context.inputValue.value.trim().toLowerCase()

  if (!query) {
    return normalizedOptions.value
  }

  // 确定搜索字段 (支持预设配置)
  let searchFields: string[]

  if (props.searchFields === 'auto') {
    // 智能模式: 根据是否使用 optionRender 自动选择
    searchFields = props.optionRender
      ? ['label', 'title', 'description', 'meta', 'badge']
      : ['label']
  } else if (props.searchFields === 'basic') {
    // 基础模式: 仅搜索 label (性能最佳)
    searchFields = ['label']
  } else if (props.searchFields === 'full') {
    // 完整模式: 搜索所有字段
    searchFields = ['label', 'title', 'description', 'meta', 'badge']
  } else {
    // 自定义模式: 使用用户指定的字段
    searchFields = props.searchFields
  }

  // 获取渲染数据的函数(仅在需要时创建)
  const renderDataGetter = props.optionRender
    ? (option: SelectOption) => props.optionRender!(option, { index: 0 })
    : undefined

  return normalizedOptions.value.filter(option =>
    matchesSearch(option, query, searchFields, renderDataGetter)
  )
})

const renderedOptions = computed<RenderedOption[]>(() => {
  if (!isPropsMode.value) return []

  let runningIndex = 0

  return filteredOptions.value.map((option) => {
    const rawGroup = props.groupBy ? option[props.groupBy] : undefined
    const hasGroupValue = rawGroup !== undefined && rawGroup !== null && rawGroup !== ''
    const currentIndex = runningIndex++

    return {
      option,
      index: currentIndex,
      group: hasGroupValue ? String(rawGroup) : undefined,
      renderData: props.optionRender ? props.optionRender(option, { index: currentIndex }) : null,
    }
  })
})

const groupedOptionBlocks = computed(() => {
  if (!props.groupBy || !renderedOptions.value.length) {
    return null
  }

  const hasGroupField = renderedOptions.value.some(item => item.group !== undefined)
  if (!hasGroupField) {
    return null
  }

  const groups = new Map<string, RenderedOption[]>()

  renderedOptions.value.forEach(item => {
    const groupName = item.group ?? props.defaultGroupName
    if (!groups.has(groupName)) {
      groups.set(groupName, [])
    }
    groups.get(groupName)!.push(item)
  })

  return Array.from(groups.entries()).map(([name, items]) => ({
    name,
    options: items,
  }))
})

const navigableOptions = computed<NavigableOption[]>(() => {
  if (!isPropsMode.value) return []

  return renderedOptions.value.map(item => ({
    index: item.index,
    value: item.option.value,
    disabled: !!item.option.disabled,
  }))
})

watch(
  () => (isPropsMode.value ? navigableOptions.value : []),
  (options) => {
    context.setNavigableOptions(options)
  },
  { immediate: true }
)

// 自动分组逻辑
const groupedOptions = computed(() => groupedOptionBlocks.value)

// 徽章样式映射
const getBadgeClass = (badgeType?: string): string => {
  const typeMap: Record<string, string> = {
    primary: 'ht-option-badge--primary',
    success: 'ht-option-badge--success',
    warning: 'ht-option-badge--warning',
    info: 'ht-option-badge--info',
    default: 'ht-option-badge--default',
  }
  const key = badgeType || 'primary'
  return (typeMap[key] !== undefined ? typeMap[key] : typeMap.primary) as string
}

// ==========================================
// 搜索阈值检测 (Phase 2 Async)
// ==========================================

/**
 * 检查是否低于搜索阈值
 * 仅在异步模式且设置了 searchThreshold 时生效
 */
const isBelowSearchThreshold = computed(() => {
  if (!isAsyncMode.value || !props.searchThreshold) return false
  const inputLength = context.inputValue.value.trim().length
  return inputLength > 0 && inputLength < props.searchThreshold
})

/**
 * 获取阈值提示文本
 * 支持 {n} 占位符替换为实际阈值数字
 */
const getThresholdHintText = computed(() => {
  const threshold = props.searchThreshold || 0
  const defaultHint = `Type ${threshold} or more characters to search...`
  const hint = props.searchThresholdHint || defaultHint
  return hint.replace('{n}', String(threshold))
})
</script>

<template>
  <div class="ht-select-root" :style="rootStyle">
    <!-- Props 模式: 自动渲染 -->
    <template v-if="isPropsMode">
      <HtSelectTrigger>
        <HtSelectValue :placeholder="props.placeholder" />
      </HtSelectTrigger>

      <HtSelectContent>
        <!-- 无分组 -->
        <template v-if="!groupedOptions">
          <HtSelectItem
            v-for="item in renderedOptions"
            :key="String(item.option.value)"
            :value="item.option.value"
            :disabled="item.option.disabled"
            :text-value="item.option.label"
            :item-index="item.index"
            :class="{ 'ht-option-custom': !!props.optionRender }"
          >
            <HtSelectOptionRender
              v-if="item.renderData"
              :render-data="item.renderData"
              :get-badge-class="getBadgeClass"
            />
            <template v-else>
              {{ item.option.label }}
            </template>
          </HtSelectItem>

          <!-- 空状态 -->
          <div v-if="renderedOptions.length === 0" class="ht-select-empty">
            <!-- 搜索阈值未达到 -->
            <span v-if="isBelowSearchThreshold" class="ht-select-threshold-hint">
              {{ getThresholdHintText }}
            </span>
            <!-- 异步加载中 -->
            <span v-else-if="isAsyncMode && asyncSource?.loading.value">
              Loading...
            </span>
            <!-- 异步错误 -->
            <span v-else-if="isAsyncMode && asyncSource?.error.value" class="ht-select-error">
              {{ asyncSource.error.value.message }}
            </span>
            <!-- 正常空状态 -->
            <span v-else>
              {{ context.inputValue.value ? props.emptySearchText : props.emptyText }}
            </span>
          </div>
        </template>

        <!-- 分组模式 -->
        <template v-else>
          <template v-for="(group, groupIndex) in groupedOptions" :key="group.name">
            <HtSelectGroup :id="`${context.ids.contentId}-group-${groupIndex}-label`">
              <HtSelectLabel
                :id="`${context.ids.contentId}-group-${groupIndex}-label`"
                :sticky="props.groupLabelSticky"
              >
                {{ group.name }}
              </HtSelectLabel>
              <HtSelectItem
                v-for="item in group.options"
                :key="String(item.option.value)"
                :value="item.option.value"
                :disabled="item.option.disabled"
                :text-value="item.option.label"
                :item-index="item.index"
                :class="{ 'ht-option-custom': !!props.optionRender }"
              >
                <HtSelectOptionRender
                  v-if="item.renderData"
                  :render-data="item.renderData"
                  :get-badge-class="getBadgeClass"
                />
                <template v-else>
                  {{ item.option.label }}
                </template>
              </HtSelectItem>
            </HtSelectGroup>
            <HtSelectSeparator v-if="groupIndex < groupedOptions.length - 1" />
          </template>

          <!-- 空状态 (分组模式) -->
          <div v-if="!groupedOptions.length" class="ht-select-empty">
            <!-- 搜索阈值未达到 -->
            <span v-if="isBelowSearchThreshold" class="ht-select-threshold-hint">
              {{ getThresholdHintText }}
            </span>
            <!-- 异步加载中 -->
            <span v-else-if="isAsyncMode && asyncSource?.loading.value">
              Loading...
            </span>
            <!-- 异步错误 -->
            <span v-else-if="isAsyncMode && asyncSource?.error.value" class="ht-select-error">
              {{ asyncSource.error.value.message }}
            </span>
            <!-- 正常空状态 -->
            <span v-else>
              {{ context.inputValue.value ? props.emptySearchText : props.emptyText }}
            </span>
          </div>
        </template>
      </HtSelectContent>
    </template>

    <!-- Slots 模式: 完全自定义 -->
    <template v-else>
      <slot />
    </template>
  </div>
</template>

<style scoped>
.ht-select-root {
  position: relative;
  display: inline-block;
}

.ht-select-empty {
  padding: 0.75rem 0.5rem;
  text-align: center;
  color: var(--ht-text-muted);
  font-size: 0.875rem;
}

.ht-select-threshold-hint {
  color: var(--ht-info);
  font-style: italic;
}

.ht-select-error {
  color: var(--ht-error);
}

/* Custom Option Rendering Styles */
.ht-option-custom {
  padding: 0.75rem 0.5rem !important;
  min-height: auto !important;
  align-items: stretch !important;
}
</style>
