<template>
  <HtCheckbox
    :model-value="isAllSelected"
    :indeterminate="isIndeterminate"
    :title="headerTitle"
    class="ht-checkbox-header"
    size="sm"
    variant="default"
    data-ht-functional="checkbox-header"
    @update:model-value="handleSelectAll"
    @click.stop
  />
</template>

<script setup lang="ts">
import { computed } from 'vue'
import type { CheckboxColumnOptions } from '../../types'
import { HtCheckbox } from '../primitives'

interface Props {
  /** Total number of selectable rows */
  totalSelectableRows: number
  /** Number of currently selected rows */
  selectedCount: number
  /** Column configuration options */
  options?: CheckboxColumnOptions
}

const props = withDefaults(defineProps<Props>(), {
  options: () => ({}),
})

const emit = defineEmits<{
  'select-all': []
  'clear-selection': []
}>()

const isAllSelected = computed(() => {
  return props.totalSelectableRows > 0 && props.selectedCount === props.totalSelectableRows
})

const isIndeterminate = computed(() => {
  const showIndeterminate = props.options?.showIndeterminate !== false
  return showIndeterminate && props.selectedCount > 0 && props.selectedCount < props.totalSelectableRows
})

const headerTitle = computed(() => {
  if (isAllSelected.value) {
    return `Deselect all (${props.selectedCount} selected)`
  } else if (isIndeterminate.value) {
    return `Select all (${props.selectedCount}/${props.totalSelectableRows} selected)`
  } else {
    return `Select all (${props.totalSelectableRows} rows)`
  }
})

function handleSelectAll(_checked: boolean | 'indeterminate') {
  if (isAllSelected.value) {
    emit('clear-selection')
  } else {
    emit('select-all')
  }
}
</script>

<script lang="ts">
export default {
  name: 'HtCheckboxHeader',
}
</script>
