<!--
  ToastNotification.vue - Simple Toast Notification Component

  Responsibilities:
  1. Display temporary notification messages to users
  2. Auto-dismiss after configurable duration
  3. Support different notification types (info, warn, error, success)
  4. Accessible with proper ARIA attributes

  Vue 3 Pattern: Composition API with reactive state
-->

<script setup lang="ts">
import { ref, watch, onMounted } from 'vue'

// Props
const props = withDefaults(
  defineProps<{
    message: string
    type?: 'info' | 'warn' | 'error' | 'success'
    duration?: number // Duration in milliseconds
    visible?: boolean
  }>(),
  {
    type: 'info',
    duration: 4000,
    visible: false,
  }
)

// Emits
const emit = defineEmits<{
  'close': []
}>()

// Local state
const show = ref(props.visible)
let timer: ReturnType<typeof setTimeout> | null = null

// Watch for visibility changes
watch(
  () => props.visible,
  (newValue) => {
    show.value = newValue
    if (newValue) {
      startTimer()
    } else {
      clearTimer()
    }
  }
)

// Auto-dismiss timer
function startTimer() {
  clearTimer()
  if (props.duration > 0) {
    timer = setTimeout(() => {
      show.value = false
      emit('close')
    }, props.duration)
  }
}

function clearTimer() {
  if (timer) {
    clearTimeout(timer)
    timer = null
  }
}

function handleClose() {
  clearTimer()
  show.value = false
  emit('close')
}

onMounted(() => {
  if (props.visible) {
    startTimer()
  }
})

// Type-specific styling
const typeClasses = {
  info: 'bg-[var(--ht-primary)] text-[var(--ht-text-inverse)]',
  warn: 'bg-orange-500 text-white',
  error: 'bg-red-500 text-white',
  success: 'bg-green-500 text-white',
}
</script>

<template>
  <Transition name="toast-slide">
    <div
      v-if="show"
      class="ht-toast fixed bottom-4 right-4 z-50 px-4 py-3 rounded-lg shadow-lg max-w-md flex items-center gap-3"
      :class="typeClasses[type]"
      role="alert"
      :aria-live="type === 'error' ? 'assertive' : 'polite'"
    >
      <!-- Icon -->
      <div class="flex-shrink-0">
        <svg
          v-if="type === 'info'"
          class="w-5 h-5"
          fill="none"
          stroke="currentColor"
          stroke-width="2"
          viewBox="0 0 24 24"
        >
          <circle cx="12" cy="12" r="10"></circle>
          <path d="M12 16v-4"></path>
          <path d="M12 8h.01"></path>
        </svg>
        <svg
          v-else-if="type === 'warn'"
          class="w-5 h-5"
          fill="none"
          stroke="currentColor"
          stroke-width="2"
          viewBox="0 0 24 24"
        >
          <path d="m21.73 18-8-14a2 2 0 0 0-3.48 0l-8 14A2 2 0 0 0 4 21h16a2 2 0 0 0 1.73-3"></path>
          <path d="M12 9v4"></path>
          <path d="M12 17h.01"></path>
        </svg>
        <svg
          v-else-if="type === 'error'"
          class="w-5 h-5"
          fill="none"
          stroke="currentColor"
          stroke-width="2"
          viewBox="0 0 24 24"
        >
          <circle cx="12" cy="12" r="10"></circle>
          <path d="m15 9-6 6"></path>
          <path d="m9 9 6 6"></path>
        </svg>
        <svg
          v-else-if="type === 'success'"
          class="w-5 h-5"
          fill="none"
          stroke="currentColor"
          stroke-width="2"
          viewBox="0 0 24 24"
        >
          <path d="M22 11.08V12a10 10 0 1 1-5.93-9.14"></path>
          <path d="m9 11 3 3L22 4"></path>
        </svg>
      </div>

      <!-- Message -->
      <div class="flex-1 text-sm font-medium">
        {{ message }}
      </div>

      <!-- Close button -->
      <button
        class="flex-shrink-0 ml-2 p-1 rounded hover:bg-white/20 transition-colors focus:outline-none focus:ring-2 focus:ring-white/50"
        aria-label="Close notification"
        @click="handleClose"
      >
        <svg
          class="w-4 h-4"
          fill="none"
          stroke="currentColor"
          stroke-width="2"
          viewBox="0 0 24 24"
        >
          <path d="M18 6 6 18"></path>
          <path d="m6 6 12 12"></path>
        </svg>
      </button>
    </div>
  </Transition>
</template>

<style scoped>
.toast-slide-enter-active,
.toast-slide-leave-active {
  transition: all 0.3s ease;
}

.toast-slide-enter-from {
  opacity: 0;
  transform: translateX(100%);
}

.toast-slide-leave-to {
  opacity: 0;
  transform: translateY(20px);
}

/* Accessibility - reduce motion */
@media (prefers-reduced-motion: reduce) {
  .toast-slide-enter-active,
  .toast-slide-leave-active {
    transition: opacity 0.1s ease;
  }

  .toast-slide-enter-from,
  .toast-slide-leave-to {
    transform: none;
  }
}
</style>
