/**
 * Core Plugin Presets
 *
 * Pre-configured plugin combinations for common use cases.
 * Uses factory functions to create fresh plugin instances for each grid.
 *
 * IMPORTANT: Plugin instances MUST NOT be shared between multiple grid instances
 * as they contain internal state (e.g., currentColumns, sort config).
 * Always use the factory functions to create new instances.
 */

import { createSortingPlugin } from './sorting-plugin'
import { filteringPlugin } from './filtering-plugin'
import { selectionPlugin } from './selection-plugin'
import { columnManagementPlugin } from './column-management-plugin'
import { cellEditingPlugin } from './cell-editing-plugin'
import { keyboardNavigationPlugin } from './keyboard-navigation-plugin'

/**
 * Plugin preset factory functions
 * These create fresh plugin instances for each grid to avoid state sharing
 */
export const pluginPresets = {
  // Basic functionality with single-field sorting and keyboard navigation
  get basic() {
    return [
      createSortingPlugin({ enableMultiSort: false }),
      columnManagementPlugin,
      filteringPlugin,
      selectionPlugin,
      keyboardNavigationPlugin
    ]
  },

  // Enhanced functionality with multi-field sorting and full navigation
  get enhanced() {
    return [
      createSortingPlugin({ enableMultiSort: true }),
      filteringPlugin,
      selectionPlugin,
      columnManagementPlugin,
      keyboardNavigationPlugin,
    ]
  },

  // Full-featured preset for advanced use cases
  get full() {
    return [
      createSortingPlugin({ enableMultiSort: true }),
      filteringPlugin,
      selectionPlugin,
      columnManagementPlugin,
      cellEditingPlugin,
      keyboardNavigationPlugin,
    ]
  },

  // Editing-focused preset for data entry scenarios
  get editing() {
    return [
      createSortingPlugin({ enableMultiSort: false }),
      filteringPlugin,
      selectionPlugin,
      cellEditingPlugin,
      keyboardNavigationPlugin,
    ]
  },

  // Accessibility-focused preset with comprehensive keyboard support
  get accessible() {
    return [
      createSortingPlugin({ enableMultiSort: false }),
      filteringPlugin,
      selectionPlugin,
      keyboardNavigationPlugin
    ]
  },
}