/**
 * Sort Configuration Presets
 *
 * Pre-configured sort options for common use cases.
 * Simplifies configuration while maintaining full flexibility.
 */

import type { SortOptions } from '../types'

/**
 * Common sort configuration presets
 */
export const SORT_PRESETS = {
  /**
   * Default sorting - respects case and accents
   * Use for: General text sorting
   */
  default: {
    sensitivity: 'variant',
  } as SortOptions,

  /**
   * Case-insensitive sorting - ignores case and accents
   * Use for: User-friendly text sorting, search results
   */
  caseInsensitive: {
    sensitivity: 'base',
  } as SortOptions,

  /**
   * Natural sorting - smart number handling, case-insensitive
   * Use for: File names, IDs, version numbers (e.g., v1, v2, v10)
   */
  natural: {
    naturalSort: true,
    sensitivity: 'base',
  } as SortOptions,

  /**
   * Natural sorting (case-sensitive) - smart numbers with case distinction
   * Use for: File systems that distinguish case
   */
  naturalCaseSensitive: {
    naturalSort: true,
    sensitivity: 'variant',
  } as SortOptions,

  /**
   * Chinese pinyin sorting - case-insensitive
   * Use for: Chinese text, names
   */
  chinese: {
    locale: 'zh-CN',
    sensitivity: 'base',
  } as SortOptions,

  /**
   * Strict sorting - distinguishes all differences
   * Use for: Code, technical data, strict ordering requirements
   */
  strict: {
    sensitivity: 'variant',
  } as SortOptions,

  /**
   * Numeric priority - numbers before text
   * Use for: Mixed alphanumeric data
   */
  numericFirst: {
    naturalSort: true,
    sensitivity: 'base',
  } as SortOptions,
} as const

/**
 * Locale-specific presets
 */
export const LOCALE_PRESETS = {
  'en-US': { locale: 'en-US', sensitivity: 'base' } as SortOptions,
  'zh-CN': { locale: 'zh-CN', sensitivity: 'base' } as SortOptions,
  'ja-JP': { locale: 'ja-JP', sensitivity: 'base' } as SortOptions,
  'ko-KR': { locale: 'ko-KR', sensitivity: 'base' } as SortOptions,
  'de-DE': { locale: 'de-DE', sensitivity: 'base' } as SortOptions,
  'fr-FR': { locale: 'fr-FR', sensitivity: 'base' } as SortOptions,
  'es-ES': { locale: 'es-ES', sensitivity: 'base' } as SortOptions,
} as const

/**
 * Create custom sort options with preset as base
 */
export function createSortOptions(
  preset: keyof typeof SORT_PRESETS,
  overrides?: Partial<SortOptions>
): SortOptions {
  return {
    ...SORT_PRESETS[preset],
    ...overrides,
  }
}
