<!--
  CellSelectEditor.vue - Select/Dropdown Editor

  Responsibilities:
  1. Dropdown selection with options
  2. Keyboard navigation (arrow keys, Enter, Escape)
  3. Search/filter functionality (optional)
  4. Custom option rendering
-->

<script setup lang="ts">
import { ref, nextTick, onMounted, computed, watch } from 'vue'
import type { GridAPI, ColumnDef, RowData, CellValue } from '../../../types'

interface SelectOption {
  value: CellValue
  label: string
  disabled?: boolean
}

// Props
const props = defineProps<{
  value: CellValue
  row: RowData
  column: ColumnDef
  cellRect: DOMRect
  gridAPI: GridAPI
}>()

// Emits
const emit = defineEmits<{
  'update:value': [value: CellValue]
  commit: []
  cancel: []
}>()

// Local state
const selectRef = ref<HTMLSelectElement>()
const dropdownRef = ref<HTMLDivElement>()
const localValue = ref(props.value)
const isOpen = ref(false)
const searchQuery = ref('')
const focusedIndex = ref(-1)

// Computed
const selectStyle = computed(() => ({
  position: 'absolute' as const,
  left: `${props.cellRect.left}px`,
  top: `${props.cellRect.top}px`,
  width: `${props.cellRect.width}px`,
  minWidth: `${Math.max(props.cellRect.width, 150)}px`,
  zIndex: 1000,
}))

const dropdownStyle = computed(() => ({
  position: 'absolute' as const,
  left: `${props.cellRect.left}px`,
  top: `${props.cellRect.bottom}px`,
  width: `${Math.max(props.cellRect.width, 150)}px`,
  maxHeight: '200px',
  zIndex: 1001,
}))

const options = computed((): SelectOption[] => {
  if (!props.column.options) {
    return []
  }

  // Support both simple arrays and option objects
  return props.column.options.map((opt: unknown) => {
    if (typeof opt === 'string' || typeof opt === 'number') {
      return { value: opt, label: String(opt) }
    }
    const optObj = opt as { value: CellValue; label?: string; disabled?: boolean }
    return {
      value: optObj.value,
      label: optObj.label || String(optObj.value),
      disabled: optObj.disabled || false,
    }
  })
})

const filteredOptions = computed(() => {
  if (!searchQuery.value) {
    return options.value
  }

  const query = searchQuery.value.toLowerCase()
  return options.value.filter(
    opt => opt.label.toLowerCase().includes(query) || String(opt.value).toLowerCase().includes(query)
  )
})

const selectedOption = computed(() => {
  return options.value.find(opt => opt.value === localValue.value)
})

const displayValue = computed(() => {
  return selectedOption.value?.label || String(localValue.value || '')
})

const selectClasses = computed(() => [
  'ht-cell-editor',
  'ht-select-editor',
  'px-3 py-1',
  'border-2 border-[var(--ht-primary)]',
  'bg-[var(--ht-bg)]',
  'text-[var(--ht-text)]',
  'text-sm',
  'outline-none',
  'rounded',
  'shadow-md',
  'cursor-pointer',
  'flex items-center justify-between',
])

const dropdownClasses = computed(() => [
  'ht-select-dropdown',
  'border border-[var(--ht-border)]',
  'bg-[var(--ht-bg)]',
  'rounded',
  'shadow-lg',
  'overflow-hidden',
])

// Methods
const openDropdown = () => {
  isOpen.value = true
  searchQuery.value = ''
  focusedIndex.value = filteredOptions.value.findIndex(opt => opt.value === localValue.value)
  if (focusedIndex.value === -1 && filteredOptions.value.length > 0) {
    focusedIndex.value = 0
  }
}

const closeDropdown = () => {
  isOpen.value = false
  focusedIndex.value = -1
  searchQuery.value = ''
}

const selectOption = (option: SelectOption) => {
  if (option.disabled) return

  localValue.value = option.value
  emit('update:value', option.value)

  // Update plugin state
  if (props.gridAPI?.updateCellEditValue) {
    props.gridAPI.updateCellEditValue(option.value)
  }

  closeDropdown()
  emit('commit')
}

const handleKeydown = (event: KeyboardEvent) => {
  switch (event.key) {
    case 'Enter':
      event.preventDefault()
      event.stopPropagation()
      if (isOpen.value && focusedIndex.value >= 0) {
        const option = filteredOptions.value[focusedIndex.value]
        if (option && !option.disabled) {
          selectOption(option)
        }
      } else if (!isOpen.value) {
        openDropdown()
      }
      break

    case 'Escape':
      event.preventDefault()
      event.stopPropagation()
      if (isOpen.value) {
        closeDropdown()
      } else {
        emit('cancel')
      }
      break

    case 'Tab':
      event.preventDefault()
      event.stopPropagation()
      closeDropdown()
      emit('commit')
      break

    case 'ArrowDown':
      event.preventDefault()
      if (!isOpen.value) {
        openDropdown()
      } else {
        focusedIndex.value = Math.min(focusedIndex.value + 1, filteredOptions.value.length - 1)
        scrollToOption(focusedIndex.value)
      }
      break

    case 'ArrowUp':
      event.preventDefault()
      if (isOpen.value) {
        focusedIndex.value = Math.max(focusedIndex.value - 1, 0)
        scrollToOption(focusedIndex.value)
      }
      break

    case 'Space':
      if (!isOpen.value) {
        event.preventDefault()
        openDropdown()
      }
      break

    default:
      // Type to search
      if (event.key.length === 1 && isOpen.value) {
        searchQuery.value += event.key
        // Find first matching option
        const matchIndex = filteredOptions.value.findIndex(opt =>
          opt.label.toLowerCase().startsWith(searchQuery.value.toLowerCase())
        )
        if (matchIndex >= 0) {
          focusedIndex.value = matchIndex
          scrollToOption(focusedIndex.value)
        }
      }
      break
  }
}

const scrollToOption = (index: number) => {
  if (dropdownRef.value) {
    const optionElements = dropdownRef.value.querySelectorAll('.ht-select-option')
    const optionElement = optionElements[index] as HTMLElement
    if (optionElement) {
      optionElement.scrollIntoView({ block: 'nearest' })
    }
  }
}

const handleClick = () => {
  if (isOpen.value) {
    closeDropdown()
  } else {
    openDropdown()
  }
}

const handleClickOutside = (event: MouseEvent) => {
  const target = event.target as HTMLElement
  if (
    selectRef.value &&
    !selectRef.value.contains(target) &&
    dropdownRef.value &&
    !dropdownRef.value.contains(target)
  ) {
    closeDropdown()
    emit('commit')
  }
}

const focus = () => {
  if (selectRef.value) {
    selectRef.value.focus()
  }
}

// Watch for external value changes
watch(
  () => props.value,
  newValue => {
    localValue.value = newValue
  }
)

// Lifecycle
onMounted(async () => {
  await nextTick()
  focus()
  document.addEventListener('mousedown', handleClickOutside)
})

// Cleanup
const cleanup = () => {
  document.removeEventListener('mousedown', handleClickOutside)
}

// Expose methods for parent
defineExpose({
  focus,
  cleanup,
})
</script>

<template>
  <div class="ht-select-container">
    <!-- Select trigger -->
    <div
      ref="selectRef"
      :class="selectClasses"
      :style="selectStyle"
      tabindex="0"
      role="combobox"
      :aria-expanded="isOpen"
      :aria-haspopup="true"
      @click="handleClick"
      @keydown="handleKeydown"
    >
      <span class="truncate flex-1">{{ displayValue }}</span>
      <svg
        class="w-4 h-4 ml-2 flex-shrink-0 transform transition-transform"
        :class="{ 'rotate-180': isOpen }"
        fill="none"
        stroke="currentColor"
        viewBox="0 0 24 24"
      >
        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M19 9l-7 7-7-7" />
      </svg>
    </div>

    <!-- Dropdown -->
    <div v-if="isOpen" ref="dropdownRef" :class="dropdownClasses" :style="dropdownStyle" role="listbox">
      <!-- Search input (if searchable) -->
      <div v-if="column.searchable" class="p-2 border-b border-[var(--ht-border)]">
        <input
          v-model="searchQuery"
          type="text"
          class="w-full px-2 py-1 text-sm border border-[var(--ht-border)] rounded outline-none"
          placeholder="Search options..."
          @keydown.stop
        />
      </div>

      <!-- Options list -->
      <div class="max-h-40 overflow-y-auto">
        <div
          v-for="(option, index) in filteredOptions"
          :key="`${option.value}-${index}`"
          class="ht-select-option px-3 py-2 text-sm cursor-pointer hover:bg-[var(--ht-cell-hover)] flex items-center justify-between"
          :class="{
            'bg-[var(--ht-primary)] text-[var(--ht-text-inverse)]': index === focusedIndex,
            'text-[var(--ht-text-muted)] cursor-not-allowed': option.disabled,
            'bg-[var(--ht-cell-selected)]': option.value === localValue && index !== focusedIndex,
          }"
          role="option"
          :aria-selected="option.value === localValue"
          @click="selectOption(option)"
        >
          <span class="truncate">{{ option.label }}</span>
          <svg
            v-if="option.value === localValue"
            class="w-4 h-4 ml-2 flex-shrink-0"
            fill="currentColor"
            viewBox="0 0 20 20"
          >
            <path
              fill-rule="evenodd"
              d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
              clip-rule="evenodd"
            />
          </svg>
        </div>

        <!-- No options message -->
        <div v-if="filteredOptions.length === 0" class="px-3 py-2 text-sm text-[var(--ht-text-muted)] text-center">
          No options available
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped>
.ht-select-editor:focus {
  box-shadow: 0 0 0 1px var(--ht-primary);
}

.ht-select-dropdown {
  animation: slideDown 0.1s ease-out;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-5px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* positioning handled by ht-cell-editor utility */

/* Option hover effects */
.ht-select-option:not(.text-muted):hover {
  background-color: var(--ht-cell-hover);
}

/* Scrollbar styling for options */
.ht-select-dropdown .max-h-40::-webkit-scrollbar {
  width: 6px;
}

.ht-select-dropdown .max-h-40::-webkit-scrollbar-track {
  background: var(--ht-bg);
}

.ht-select-dropdown .max-h-40::-webkit-scrollbar-thumb {
  background: var(--ht-border);
  border-radius: 3px;
}

.ht-select-dropdown .max-h-40::-webkit-scrollbar-thumb:hover {
  background: var(--ht-text-muted);
}
</style>
