import { computed, ref, onBeforeUnmount, type Ref } from 'vue'
import type { ColumnDef, GridAPI } from '../../../../types'

interface UseColumnResizeOptions {
  columnRef: Ref<ColumnDef>
  gridAPI: GridAPI
  getCurrentWidth: () => number
}

export function useColumnResize({ columnRef, gridAPI, getCurrentWidth }: UseColumnResizeOptions) {
  const isColumnResizable = computed(() => columnRef.value.resizable !== false)
  const isResizeActive = ref(false)

  const handleResizeEndVisuals = () => {
    if (!isResizeActive.value) return

    isResizeActive.value = false

    if (typeof document !== 'undefined') {
      document.body.classList.remove('ht-grid-resizing')
    }

    if (typeof window !== 'undefined') {
      window.removeEventListener('mouseup', handleResizeEndVisuals)
      window.removeEventListener('blur', handleResizeEndVisuals)
    }
  }

  const handleResize = (event: MouseEvent) => {
    if (!isColumnResizable.value || event.button !== 0) return

    const isDoubleClick = event.detail > 1

    event.preventDefault()
    event.stopPropagation()

    if (isDoubleClick) {
      return
    }

    if (!isResizeActive.value) {
      isResizeActive.value = true
      if (typeof document !== 'undefined') {
        document.body.classList.add('ht-grid-resizing')
      }
      if (typeof window !== 'undefined') {
        window.addEventListener('mouseup', handleResizeEndVisuals)
        window.addEventListener('blur', handleResizeEndVisuals)
      }
    }

    gridAPI?.emit('column-resize-start', {
      columnId: columnRef.value.id,
      startX: event.clientX,
      currentWidth: getCurrentWidth(),
    })
  }

  const handleResizeDoubleClick = (event: MouseEvent) => {
    if (!isColumnResizable.value) return

    event.preventDefault()
    event.stopPropagation()

    const extendedApi = gridAPI as GridAPI & { autoSizeColumn?: (columnId: string) => boolean }
    if (typeof extendedApi.autoSizeColumn === 'function') {
      extendedApi.autoSizeColumn(columnRef.value.id)
    }
  }

  onBeforeUnmount(() => {
    handleResizeEndVisuals()
  })

  return {
    isColumnResizable,
    isResizeActive,
    handleResize,
    handleResizeDoubleClick,
    handleResizeEndVisuals,
  }
}
