<!--
  BasePopover.vue - Reusable Popover Component

  Responsibilities:
  - Smart positioning with viewport detection
  - Focus trap and keyboard navigation
  - Click-outside-to-close behavior
  - ARIA-compliant accessibility

  Features:
  - Auto-repositioning on scroll/resize
  - Configurable anchor side and alignment
  - Teleport to body to avoid clipping
  - Animation support
-->

<script setup lang="ts">
import { onUnmounted, ref, watch, nextTick } from 'vue'

export interface PopoverPosition {
  placement?: 'bottom' | 'top' | 'left' | 'right'
  align?: 'start' | 'center' | 'end'
  offset?: number
  autoFlip?: boolean
}

const props = withDefaults(defineProps<{
  open: boolean
  anchor?: HTMLElement | null
  position?: PopoverPosition
  role?: 'dialog' | 'menu' | 'listbox'
  ariaLabel?: string
  closeOnClickOutside?: boolean
  closeOnEscape?: boolean
  trapFocus?: boolean
  keepAlive?: boolean
}>(), {
  anchor: null,
  position: () => ({ placement: 'bottom', align: 'center', offset: 4, autoFlip: true }),
  role: 'dialog',
  ariaLabel: '',
  closeOnClickOutside: true,
  closeOnEscape: true,
  trapFocus: true,
  keepAlive: false,
})

const emit = defineEmits<{
  'update:open': [value: boolean]
  'close': []
  'opened': []
  'closed': []
}>()

// Refs
const popoverRef = ref<HTMLElement | null>(null)
const popoverStyle = ref<Record<string, string>>({
  position: 'fixed',
  top: '-10000px',
  left: '-10000px',
  transform: 'none',
  zIndex: '1000',
})
const visible = ref(false)

// Positioning logic
function updatePosition() {
  const anchor = props.anchor
  const popover = popoverRef.value
  if (!anchor || !popover) return

  const anchorRect = anchor.getBoundingClientRect()
  const popoverRect = popover.getBoundingClientRect()
  const vw = window.innerWidth
  const vh = window.innerHeight

  // Check if anchor is out of viewport
  // Strategy: Close on vertical scroll out, follow on horizontal scroll
  // Reasoning:
  // - Vertical scroll: Usually means context switch, dropdown loses meaning
  // - Horizontal scroll: Usually browsing content, user expects dropdown to follow
  const isOutOfViewport =
    anchorRect.bottom < 0 ||           // 完全在上方：关闭
    anchorRect.top > vh                 // 完全在下方：关闭
    // 水平移出：继续跟随，不关闭（响应式设计考虑）

  if (isOutOfViewport) {
    close()
    return
  }

  const { placement = 'bottom', align = 'center', offset = 4, autoFlip = true } = props.position || {}

  let top = 0
  let bottom: number | undefined
  let left = 0

  // Calculate base position
  switch (placement) {
    case 'bottom':
      top = anchorRect.bottom + offset
      left = anchorRect.left + (align === 'center' ? anchorRect.width / 2 - popoverRect.width / 2 : align === 'end' ? anchorRect.width - popoverRect.width : 0)
      break
    case 'top':
      // Use bottom positioning to keep bottom edge sticky when content height changes
      bottom = vh - anchorRect.top + offset
      left = anchorRect.left + (align === 'center' ? anchorRect.width / 2 - popoverRect.width / 2 : align === 'end' ? anchorRect.width - popoverRect.width : 0)
      break
    case 'left':
      top = anchorRect.top + (align === 'center' ? anchorRect.height / 2 - popoverRect.height / 2 : align === 'end' ? anchorRect.height - popoverRect.height : 0)
      left = anchorRect.left - popoverRect.width - offset
      break
    case 'right':
      top = anchorRect.top + (align === 'center' ? anchorRect.height / 2 - popoverRect.height / 2 : align === 'end' ? anchorRect.height - popoverRect.height : 0)
      left = anchorRect.right + offset
      break
  }

  // Auto-flip if overflow
  if (autoFlip) {
    if (placement === 'bottom' && top + popoverRect.height > vh) {
      // Flip to top: use bottom positioning
      bottom = vh - anchorRect.top + offset
      top = 0  // Will be ignored when bottom is set
    } else if (placement === 'top' && bottom !== undefined && vh - bottom + popoverRect.height > vh) {
      // Flip to bottom: use top positioning
      top = anchorRect.bottom + offset
      bottom = undefined
    }
  }

  // Clamp to viewport
  const padding = 8
  if (left < padding) left = padding
  if (left + popoverRect.width > vw - padding) left = vw - padding - popoverRect.width

  // Clamp vertical position based on positioning mode
  if (bottom !== undefined) {
    // Using bottom positioning (placement='top')
    if (bottom < padding) bottom = padding
    if (vh - bottom - popoverRect.height < padding) {
      bottom = Math.max(padding, vh - padding - popoverRect.height)
    }
  } else {
    // Using top positioning (placement='bottom')
    if (top < padding) top = padding
    if (top + popoverRect.height > vh - padding) {
      top = Math.max(padding, vh - padding - popoverRect.height)
    }
  }

  popoverStyle.value = {
    position: 'fixed',
    ...(bottom !== undefined ? { bottom: `${bottom}px` } : { top: `${top}px` }),
    left: `${left}px`,
    transform: 'none',
    zIndex: '1000',
  }

  visible.value = true
}

// Focus management
function getFocusableElements(): HTMLElement[] {
  const selectors = [
    'button:not([disabled])',
    'input:not([disabled])',
    'select:not([disabled])',
    'textarea:not([disabled])',
    '[tabindex]:not([tabindex="-1"])',
  ].join(',')
  if (!popoverRef.value) return []
  return Array.from(popoverRef.value.querySelectorAll<HTMLElement>(selectors)).filter(el => el.offsetParent !== null)
}

function focusFirst() {
  if (!props.trapFocus) return
  const focusables = getFocusableElements()
  const first = focusables[0]
  if (first) {
    try {
      first.focus()
    } catch {
      // Ignore focus errors
    }
  }
}

function trapFocusOnTab(e: KeyboardEvent) {
  if (!props.trapFocus) return
  const focusables = getFocusableElements()
  if (focusables.length === 0) return

  const first = focusables[0]!
  const last = focusables[focusables.length - 1]!
  const active = document.activeElement as HTMLElement | null

  if (e.shiftKey) {
    if (!active || active === first || !popoverRef.value?.contains(active)) {
      e.preventDefault()
      if (last) last.focus()
    }
  } else {
    if (!active || active === last || !popoverRef.value?.contains(active)) {
      e.preventDefault()
      if (first) first.focus()
    }
  }
}

// Event handlers
function handleKeydown(e: KeyboardEvent) {
  if (e.key === 'Escape' && props.closeOnEscape) {
    e.preventDefault()
    e.stopPropagation()
    close()
  } else if (e.key === 'Tab') {
    trapFocusOnTab(e)
  }
}

function close() {
  emit('update:open', false)
  emit('close')
}

// Lifecycle
let cleanupFns: Array<() => void> = []
let scrollRAF: number | null = null // Track RAF for scroll updates

// Watch anchor changes and update position
watch(() => props.anchor, (newAnchor) => {
  if (newAnchor && props.open) {
    nextTick(() => {
      updatePosition()
    })
  }
}, { immediate: false })

watch(() => props.open, (isOpen) => {
  if (isOpen) {
    visible.value = false
    nextTick(() => {
      updatePosition()
      requestAnimationFrame(() => {
        focusFirst()
        emit('opened')
      })
    })

    // Setup event listeners
    const onDocClick = (e: MouseEvent) => {
      if (!props.closeOnClickOutside) return
      const target = e.target as Node
      if (props.anchor?.contains(target) || popoverRef.value?.contains(target)) return
      close()
    }

    // Optimized scroll handler with RAF for smooth following
    const onScroll = () => {
      // Cancel previous RAF if still pending
      if (scrollRAF !== null) {
        cancelAnimationFrame(scrollRAF)
      }

      // Schedule position update in next animation frame
      scrollRAF = requestAnimationFrame(() => {
        updatePosition()
        scrollRAF = null
      })
    }

    const onResize = () => updatePosition()

    document.addEventListener('mousedown', onDocClick)
    document.addEventListener('scroll', onScroll, { capture: true, passive: true })
    window.addEventListener('resize', onResize)

    cleanupFns = [
      () => document.removeEventListener('mousedown', onDocClick),
      () => document.removeEventListener('scroll', onScroll, { capture: true }),
      () => window.removeEventListener('resize', onResize),
      () => {
        if (scrollRAF !== null) {
          cancelAnimationFrame(scrollRAF)
          scrollRAF = null
        }
      },
    ]
  } else {
    visible.value = false
    emit('closed')
    cleanupFns.forEach(fn => {
      try {
        fn()
      } catch {
        // Ignore cleanup errors
      }
    })
    cleanupFns = []
  }
})

onUnmounted(() => {
  cleanupFns.forEach(fn => {
    try {
      fn()
    } catch {
      // Ignore cleanup errors
    }
  })
  cleanupFns = []
})
</script>

<template>
  <teleport to="body">
    <div
      v-if="keepAlive || open"
      v-show="!keepAlive || open"
      ref="popoverRef"
      :role="role"
      :aria-modal="role === 'dialog' ? 'true' : undefined"
      :aria-label="ariaLabel"
      :style="{ ...popoverStyle, opacity: visible ? '1' : '0' }"
      class="ht-popover"
      @click.stop
      @mousedown.stop
      @keydown.stop="handleKeydown"
    >
      <slot />
    </div>
  </teleport>
</template>

<style scoped>
.ht-popover {
  /* Opacity transition for show/hide */
  transition:
    opacity 150ms cubic-bezier(0.4, 0, 0.2, 1),
    transform 100ms cubic-bezier(0.4, 0, 0.2, 1);
  contain: layout style paint;

  /* Enable GPU acceleration for smoother scrolling */
  will-change: transform;
}
</style>
