/**
 * Performance Monitoring and Testing Framework
 *
 * Features:
 * - Real-time FPS monitoring
 * - Render time tracking
 * - Memory usage analysis
 * - Performance regression detection
 * - Automated performance testing
 * - Core Web Vitals measurement
 */

import { ref, onUnmounted, nextTick } from 'vue'
import { createLogger } from './logger'

const logger = createLogger('PerformanceMonitoring')

// Performance metrics interface
export interface PerformanceMetrics {
  fps: number
  avgRenderTime: number
  memoryUsage: number
  scrollLatency: number
  firstContentfulPaint: number
  largestContentfulPaint: number
  cumulativeLayoutShift: number
  interactionLatency: number
  timestamp: number
}

// FPS Counter
export class FPSCounter {
  private frames = 0
  private lastTime = performance.now()
  private fps = 60
  private callbacks = new Set<(fps: number) => void>()
  private rafId: number | null = null
  private isRunning = false

  start(): void {
    if (this.isRunning) return

    this.isRunning = true
    this.lastTime = performance.now()
    this.frames = 0
    this.tick()
  }

  stop(): void {
    this.isRunning = false
    if (this.rafId) {
      cancelAnimationFrame(this.rafId)
      this.rafId = null
    }
  }

  onUpdate(callback: (fps: number) => void): () => void {
    this.callbacks.add(callback)
    return () => this.callbacks.delete(callback)
  }

  get currentFPS(): number {
    return this.fps
  }

  private tick(): void {
    if (!this.isRunning) return

    this.frames++
    const now = performance.now()

    if (now - this.lastTime >= 1000) {
      this.fps = Math.round((this.frames * 1000) / (now - this.lastTime))
      this.frames = 0
      this.lastTime = now

      this.callbacks.forEach(callback => callback(this.fps))
    }

    this.rafId = requestAnimationFrame(() => this.tick())
  }
}

// Render time profiler
export class RenderProfiler {
  private measurements: Array<{ name: string; duration: number; timestamp: number }> = []
  private activeTimers = new Map<string, number>()
  private maxMeasurements = 1000

  start(name: string): void {
    this.activeTimers.set(name, performance.now())
  }

  end(name: string): number {
    const startTime = this.activeTimers.get(name)
    if (!startTime) {
      logger.warn(`No start time found for measurement: ${name}`)
      return 0
    }

    const duration = performance.now() - startTime
    this.activeTimers.delete(name)

    this.measurements.push({
      name,
      duration,
      timestamp: Date.now(),
    })

    // Keep only recent measurements
    if (this.measurements.length > this.maxMeasurements) {
      this.measurements.shift()
    }

    return duration
  }

  getAverage(name: string, samples: number = 10): number {
    const recent = this.measurements.filter(m => m.name === name).slice(-samples)

    if (recent.length === 0) return 0

    return recent.reduce((sum, m) => sum + m.duration, 0) / recent.length
  }

  getAll(): Array<{ name: string; duration: number; timestamp: number }> {
    return [...this.measurements]
  }

  clear(): void {
    this.measurements = []
    this.activeTimers.clear()
  }
}

// Scroll performance tracker
export class ScrollPerformanceTracker {
  private scrollEvents: Array<{ timestamp: number; position: number }> = []
  private frameCount = 0
  private lastFrame = performance.now()
  private isTracking = false

  start(): void {
    this.isTracking = true
    this.scrollEvents = []
    this.frameCount = 0
    this.lastFrame = performance.now()
  }

  recordScroll(position: number): void {
    if (!this.isTracking) return

    this.scrollEvents.push({
      timestamp: performance.now(),
      position,
    })

    this.frameCount++
  }

  stop(): { avgFPS: number; scrollLatency: number; jankCount: number } {
    if (!this.isTracking) return { avgFPS: 0, scrollLatency: 0, jankCount: 0 }

    this.isTracking = false

    const now = performance.now()
    const duration = now - this.lastFrame
    const avgFPS = (this.frameCount * 1000) / duration

    // Calculate scroll latency (time between scroll events)
    let totalLatency = 0
    let jankCount = 0

    for (let i = 1; i < this.scrollEvents.length; i++) {
      const latency = this.scrollEvents[i]!.timestamp - this.scrollEvents[i - 1]!.timestamp
      totalLatency += latency

      // Count jank (frames taking longer than 16.67ms for 60fps)
      if (latency > 16.67) {
        jankCount++
      }
    }

    const scrollLatency = totalLatency / (this.scrollEvents.length - 1) || 0

    return {
      avgFPS: Math.round(avgFPS),
      scrollLatency: Math.round(scrollLatency * 100) / 100,
      jankCount,
    }
  }
}

// Core Web Vitals measurement
export class WebVitalsMonitor {
  private metrics = ref<{
    lcp: number | null
    fid: number | null
    cls: number | null
    fcp: number | null
    ttfb: number | null
  }>({
    lcp: null,
    fid: null,
    cls: null,
    fcp: null,
    ttfb: null,
  })

  constructor() {
    this.measureCoreWebVitals()
  }

  private measureCoreWebVitals(): void {
    // Largest Contentful Paint
    if ('PerformanceObserver' in window) {
      try {
        const lcpObserver = new PerformanceObserver(list => {
          const entries = list.getEntries()
          const lastEntry = entries[entries.length - 1] as any
          this.metrics.value.lcp = lastEntry.startTime
        })
        lcpObserver.observe({ entryTypes: ['largest-contentful-paint'] })

        // First Contentful Paint
        const fcpObserver = new PerformanceObserver(list => {
          const entries = list.getEntries()
          const fcpEntry = entries.find(entry => entry.name === 'first-contentful-paint') as any
          if (fcpEntry) {
            this.metrics.value.fcp = fcpEntry.startTime
          }
        })
        fcpObserver.observe({ entryTypes: ['paint'] })

        // Cumulative Layout Shift
        const clsObserver = new PerformanceObserver(list => {
          let clsValue = 0
          for (const entry of list.getEntries() as any[]) {
            if (!entry.hadRecentInput) {
              clsValue += entry.value
            }
          }
          this.metrics.value.cls = clsValue
        })
        clsObserver.observe({ entryTypes: ['layout-shift'] })

        // First Input Delay
        const fidObserver = new PerformanceObserver(list => {
          const entries = list.getEntries() as any[]
          const firstInput = entries[0]
          if (firstInput) {
            this.metrics.value.fid = firstInput.processingStart - firstInput.startTime
          }
        })
        fidObserver.observe({ entryTypes: ['first-input'] })
      } catch (error) {
        logger.warn('Performance observer not fully supported:', { module: 'PerformanceMonitoring' }, error)
      }
    }

    // Time to First Byte
    if ('performance' in window && 'timing' in performance) {
      const timing = performance.timing
      this.metrics.value.ttfb = timing.responseStart - timing.fetchStart
    }
  }

  getMetrics() {
    return this.metrics
  }
}

// Performance test suite
export class PerformanceTestSuite {
  private tests = new Map<string, () => Promise<PerformanceMetrics>>()
  private results: Array<{ testName: string; metrics: PerformanceMetrics; passed: boolean }> = []

  addTest(name: string, test: () => Promise<PerformanceMetrics>): void {
    this.tests.set(name, test)
  }

  async runAll(): Promise<
    Array<{ testName: string; metrics: PerformanceMetrics; passed: boolean }>
  > {
    this.results = []

    for (const [testName, test] of this.tests.entries()) {
      try {
        const metrics = await test()
        const passed = this.evaluatePerformance(metrics)

        this.results.push({
          testName,
          metrics,
          passed,
        })
      } catch (error) {
        logger.error(`Performance test failed: ${testName}`, { module: 'PerformanceMonitoring' }, error)
        this.results.push({
          testName,
          metrics: this.getDefaultMetrics(),
          passed: false,
        })
      }
    }

    return this.results
  }

  private evaluatePerformance(metrics: PerformanceMetrics): boolean {
    // Performance thresholds
    const thresholds = {
      fps: 55, // Minimum acceptable FPS
      avgRenderTime: 16.67, // Maximum render time for 60fps
      memoryUsage: 200, // Maximum memory usage in MB
      scrollLatency: 20, // Maximum scroll latency in ms
      interactionLatency: 100, // Maximum interaction latency in ms
    }

    return (
      metrics.fps >= thresholds.fps &&
      metrics.avgRenderTime <= thresholds.avgRenderTime &&
      metrics.memoryUsage <= thresholds.memoryUsage &&
      metrics.scrollLatency <= thresholds.scrollLatency &&
      metrics.interactionLatency <= thresholds.interactionLatency
    )
  }

  private getDefaultMetrics(): PerformanceMetrics {
    return {
      fps: 0,
      avgRenderTime: 0,
      memoryUsage: 0,
      scrollLatency: 0,
      firstContentfulPaint: 0,
      largestContentfulPaint: 0,
      cumulativeLayoutShift: 0,
      interactionLatency: 0,
      timestamp: Date.now(),
    }
  }

  getResults() {
    return this.results
  }

  generateReport(): string {
    const passed = this.results.filter(r => r.passed).length
    const total = this.results.length

    let report = `Performance Test Report\n`
    report += `========================\n`
    report += `Passed: ${passed}/${total}\n\n`

    this.results.forEach(result => {
      const status = result.passed ? '✅' : '❌'
      report += `${status} ${result.testName}\n`
      report += `   FPS: ${result.metrics.fps}\n`
      report += `   Render Time: ${result.metrics.avgRenderTime.toFixed(2)}ms\n`
      report += `   Memory: ${result.metrics.memoryUsage.toFixed(2)}MB\n`
      report += `   Scroll Latency: ${result.metrics.scrollLatency.toFixed(2)}ms\n\n`
    })

    return report
  }
}

// Vue composable for performance monitoring
export function usePerformanceMonitoring() {

  const fpsCounter = new FPSCounter()
  const renderProfiler = new RenderProfiler()
  const scrollTracker = new ScrollPerformanceTracker()
  const webVitals = new WebVitalsMonitor()

  const currentFPS = ref(60)
  const isMonitoring = ref(false)

  let unsubscribeFPS: (() => void) | null = null

  // Start monitoring
  const startMonitoring = () => {
    if (isMonitoring.value) return

    isMonitoring.value = true
    fpsCounter.start()

    // Update FPS display
    unsubscribeFPS = fpsCounter.onUpdate(fps => {
      currentFPS.value = fps
    })
  }

  const stopMonitoring = () => {
    isMonitoring.value = false
    fpsCounter.stop()
    if (unsubscribeFPS) {
      unsubscribeFPS()
      unsubscribeFPS = null
    }
  }

  // Profile render operations
  const profileRender = async (name: string, renderFn: () => Promise<void> | void) => {
    renderProfiler.start(name)
    await renderFn()
    const duration = renderProfiler.end(name)
    return duration
  }

  // Track scroll performance
  const trackScrollStart = () => scrollTracker.start()
  const trackScrollEvent = (position: number) => scrollTracker.recordScroll(position)
  const trackScrollEnd = () => scrollTracker.stop()

  // Get performance metrics
  const getMetrics = (): PerformanceMetrics => {
    const memory = 'memory' in performance ? (performance as any).memory : null

    return {
      fps: currentFPS.value,
      avgRenderTime: renderProfiler.getAverage('render'),
      memoryUsage: memory ? memory.usedJSHeapSize / (1024 * 1024) : 0,
      scrollLatency: 0, // Will be updated by scroll tracking
      firstContentfulPaint: webVitals.getMetrics().value.fcp || 0,
      largestContentfulPaint: webVitals.getMetrics().value.lcp || 0,
      cumulativeLayoutShift: webVitals.getMetrics().value.cls || 0,
      interactionLatency: webVitals.getMetrics().value.fid || 0,
      timestamp: Date.now(),
    }
  }

  // Create performance test
  const createPerformanceTest = (_name: string, testFn: () => Promise<void>) => {
    return async (): Promise<PerformanceMetrics> => {
      const startTime = performance.now()

      await testFn()
      await nextTick() // Wait for Vue to update

      const endTime = performance.now()
      const endMetrics = getMetrics()

      return {
        ...endMetrics,
        avgRenderTime: endTime - startTime,
        timestamp: Date.now(),
      }
    }
  }

  // Cleanup
  onUnmounted(() => {
    stopMonitoring()
    renderProfiler.clear()
  })

  return {
    // State
    currentFPS,
    isMonitoring,
    webVitalsMetrics: webVitals.getMetrics(),

    // Controls
    startMonitoring,
    stopMonitoring,

    // Profiling
    profileRender,
    trackScrollStart,
    trackScrollEvent,
    trackScrollEnd,

    // Metrics
    getMetrics,
    getRenderStats: () => renderProfiler.getAll(),

    // Testing
    createPerformanceTest,

    // Utilities
    createTestSuite: () => new PerformanceTestSuite(),
  }
}

// Performance benchmarking utilities
export async function benchmarkOperation(
  name: string,
  operation: () => Promise<void> | void,
  iterations: number = 100
): Promise<{ name: string; avgTime: number; minTime: number; maxTime: number; totalTime: number }> {
  const times: number[] = []

  for (let i = 0; i < iterations; i++) {
    const start = performance.now()
    await operation()
    const end = performance.now()
    times.push(end - start)
  }

  const totalTime = times.reduce((sum, time) => sum + time, 0)
  const avgTime = totalTime / iterations
  const minTime = Math.min(...times)
  const maxTime = Math.max(...times)

  return {
    name,
    avgTime: Math.round(avgTime * 100) / 100,
    minTime: Math.round(minTime * 100) / 100,
    maxTime: Math.round(maxTime * 100) / 100,
    totalTime: Math.round(totalTime * 100) / 100,
  }
}

