/**
 * Platform Detection Utilities
 *
 * Provides cross-platform compatibility helpers for Happy Table components.
 * Handles keyboard shortcuts, OS-specific behaviors, and accessibility features.
 */

export interface PlatformInfo {
  /** Whether the current platform is macOS/iOS */
  isMac: boolean
  /** Whether the current platform is Windows */
  isWindows: boolean
  /** Whether the current platform is Linux */
  isLinux: boolean
  /** Whether the current platform is mobile */
  isMobile: boolean
  /** The appropriate modifier key name for shortcuts */
  modifierKey: 'Cmd' | 'Ctrl'
  /** The appropriate modifier key symbol for UI display */
  modifierSymbol: '⌘' | 'Ctrl'
}

/**
 * Detect current platform and return platform information
 */
export function detectPlatform(): PlatformInfo {
  // Server-side rendering fallback
  if (typeof navigator === 'undefined') {
    return {
      isMac: false,
      isWindows: false,
      isLinux: false,
      isMobile: false,
      modifierKey: 'Ctrl',
      modifierSymbol: 'Ctrl',
    }
  }

  const userAgent = navigator.userAgent
  const platform = navigator.platform

  // macOS/iOS detection
  const isMac = /Mac|iPhone|iPad|iPod/.test(platform) ||
                ((navigator as any).userAgentData?.platform === 'macOS') ||
                /Macintosh|MacIntel|MacPPC|Mac68K/.test(platform)

  // Windows detection
  const isWindows = /Win/.test(platform) ||
                    ((navigator as any).userAgentData?.platform === 'Windows') ||
                    /Windows/.test(userAgent)

  // Linux detection
  const isLinux = /Linux/.test(platform) ||
                  ((navigator as any).userAgentData?.platform === 'Linux') ||
                  /X11/.test(platform)

  // Mobile detection
  const isMobile = /Mobi|Android/i.test(userAgent) ||
                   /iPhone|iPad|iPod/.test(platform) ||
                   ((navigator as any).userAgentData?.mobile === true)

  return {
    isMac,
    isWindows,
    isLinux,
    isMobile,
    modifierKey: isMac ? 'Cmd' : 'Ctrl',
    modifierSymbol: isMac ? '⌘' : 'Ctrl',
  }
}

/**
 * Check if a keyboard event has the platform-appropriate modifier key pressed
 */
export function hasModifierKey(event: KeyboardEvent | MouseEvent): boolean {
  return event.ctrlKey || event.metaKey
}

/**
 * Get platform-appropriate modifier key text for tooltips and help text
 */
export function getModifierKeyText(): string {
  return detectPlatform().modifierKey
}

/**
 * Get platform-appropriate modifier key symbol for compact UI display
 */
export function getModifierKeySymbol(): string {
  return detectPlatform().modifierSymbol
}

/**
 * Generate platform-aware keyboard shortcut text
 */
export function formatShortcut(key: string, withModifier: boolean = true): string {
  const platform = detectPlatform()

  if (!withModifier) {
    return key
  }

  return `${platform.modifierKey}+${key}`
}

/**
 * Generate platform-aware keyboard shortcut text with symbols
 */
export function formatShortcutSymbol(key: string, withModifier: boolean = true): string {
  const platform = detectPlatform()

  if (!withModifier) {
    return key
  }

  return `${platform.modifierSymbol}+${key}`
}