<!--
  CellEditorOverlay.vue - Cell Editor Overlay Container

  Responsibilities:
  1. Position and manage editor components over cells
  2. Handle editor lifecycle (mount/unmount)
  3. Coordinate with cell editing plugin
  4. Event delegation and cleanup
-->

<script setup lang="ts">
import { ref, computed, nextTick, onUnmounted, shallowRef } from 'vue'
import type { GridAPI, ColumnDef, RowData, CellValue } from '../../../types'
import { createLogger } from '../../../utils/logger'

const logger = createLogger('cell-editor-overlay')

// Import editor components
import CellTextEditor from './CellTextEditor.vue'
import CellNumberEditor from './CellNumberEditor.vue'
import CellSelectEditor from './CellSelectEditor.vue'
import CellDateEditor from './CellDateEditor.vue'
import CellBooleanEditor from './CellBooleanEditor.vue'

// Props
const props = defineProps<{
  containerElement?: HTMLElement
  gridAPI: GridAPI
}>()

// Local state
const activeEditor = shallowRef<typeof CellTextEditor | null>(null)
const editorInstance = ref<InstanceType<typeof CellTextEditor> | null>(null)
const editorProps = ref<Record<string, unknown>>({})
const isVisible = ref(false)

// Editor component registry
const editorComponents = {
  text: CellTextEditor,
  number: CellNumberEditor,
  select: CellSelectEditor,
  date: CellDateEditor,
  boolean: CellBooleanEditor,
} as const

// Computed
const currentEditorComponent = computed(() => {
  if (!activeEditor.value) return null

  const editorType = activeEditor.value.type || 'text'
  return editorComponents[editorType as keyof typeof editorComponents] || CellTextEditor
})

const containerElement = computed(() => {
  return props.containerElement || document.body
})

// Methods
const getCellRect = (rowId: string | number, columnId: string): DOMRect | null => {
  // Find the cell element in the DOM
  const cellElement = document.querySelector(`[data-row-id="${rowId}"][data-column-id="${columnId}"]`) as HTMLElement

  if (!cellElement) {
    logger.warn(`Cell not found: row=${rowId}, column=${columnId}`)
    return null
  }

  return cellElement.getBoundingClientRect()
}

const showEditor = async (editState: {
  rowId: string | number
  columnId: string
  value: any
  row: RowData
  column: ColumnDef
}) => {
  // Hide any existing editor first
  hideEditor()

  const cellRect = getCellRect(editState.rowId, editState.columnId)
  if (!cellRect) {
    return false
  }

  // Determine editor type
  const editorType = getEditorType(editState.column)

  // Prepare editor props
  editorProps.value = {
    value: editState.value,
    row: editState.row,
    column: editState.column,
    cellRect,
    gridAPI: props.gridAPI,
    'onUpdate:value': handleValueUpdate,
    onCommit: handleCommit,
    onCancel: handleCancel,
  }

  // Set active editor
  activeEditor.value = {
    type: editorType,
    rowId: editState.rowId,
    columnId: editState.columnId,
  } as any

  isVisible.value = true

  await nextTick()

  // Focus the editor
  if ((editorInstance.value as any)?.focus) {
    ;(editorInstance.value as any).focus()
  }

  return true
}

const hideEditor = () => {
  if ((editorInstance.value as any)?.cleanup) {
    ;(editorInstance.value as any).cleanup()
  }

  activeEditor.value = null
  editorInstance.value = null
  editorProps.value = {}
  isVisible.value = false
}

const handleValueUpdate = (value: any) => {
  if (props.gridAPI.updateCellEditValue) {
    props.gridAPI.updateCellEditValue(value)
  }
}

const handleCommit = async () => {
  if (props.gridAPI.commitCellEdit) {
    const success = await props.gridAPI.commitCellEdit()
    if (success) {
      hideEditor()
    }
  }
}

const handleCancel = () => {
  if (props.gridAPI.cancelCellEdit) {
    props.gridAPI.cancelCellEdit()
  }
  hideEditor()
}

const getEditorType = (column: ColumnDef): string => {
  if (column.editor) {
    return column.editor
  }

  // Check if custom editor is registered
  if (column.editor && props.gridAPI.getCellEditor) {
    const customEditor = props.gridAPI.getCellEditor(column.editor)
    if (customEditor) {
      return column.editor
    }
  }

  // Default based on column dataType
  return column.dataType || 'text'
}

// Handle keyboard events at overlay level
const handleOverlayKeydown = (event: KeyboardEvent) => {
  // Let the editor handle its own keyboard events first
  if ((editorInstance.value as any)?.handleKeydown) {
    const handled = (editorInstance.value as any).handleKeydown(event)
    if (handled) {
      return
    }
  }

  // Handle global keyboard shortcuts
  if (props.gridAPI.handleCellEditKeydown) {
    props.gridAPI.handleCellEditKeydown(event)
  }
}

// Listen for cell editing events from plugin
const setupEventListeners = () => {
  if (!props.gridAPI.on) return

  // Listen for edit start events
  props.gridAPI.on('cell-edit-start', (payload: { rowId: string | number; columnId: string; value: CellValue }) => {
    // Convert the payload to the expected format
    const rowData = props.gridAPI.getData().find(row => (row.id || row._index) === payload.rowId)
    const columnDef = (props as any).columns?.find((col: any) => col.id === payload.columnId)

    if (rowData && columnDef) {
      showEditor({
        row: rowData,
        column: columnDef,
        value: payload.value,
        cellRect: new DOMRect(0, 0, 0, 0) // Fallback rect, will be calculated in showEditor
      } as any)
    }
  })

  // Listen for edit cancel events
  props.gridAPI.on('cell-edit-cancel', () => {
    hideEditor()
  })

  // Listen for edit complete events
  props.gridAPI.on('cell-edit-complete', () => {
    hideEditor()
  })
}

// Update editor position when cell position changes
const updateEditorPosition = () => {
  if (!activeEditor.value || !editorProps.value.cellRect) {
    return
  }

  const newRect = getCellRect(activeEditor.value.rowId, activeEditor.value.columnId)
  if (newRect) {
    editorProps.value = {
      ...editorProps.value,
      cellRect: newRect,
    }
  }
}

// Handle scroll events to update position
const handleScroll = () => {
  if (isVisible.value) {
    updateEditorPosition()
  }
}

// Handle resize events
const handleResize = () => {
  if (isVisible.value) {
    updateEditorPosition()
  }
}

// Lifecycle
setupEventListeners()

// Add global event listeners
if (typeof window !== 'undefined') {
  window.addEventListener('scroll', handleScroll, { passive: true })
  window.addEventListener('resize', handleResize, { passive: true })
  window.addEventListener('keydown', handleOverlayKeydown, { capture: true })
}

onUnmounted(() => {
  hideEditor()

  if (typeof window !== 'undefined') {
    window.removeEventListener('scroll', handleScroll)
    window.removeEventListener('resize', handleResize)
    window.removeEventListener('keydown', handleOverlayKeydown, { capture: true })
  }
})

// Expose methods for external control
defineExpose({
  showEditor,
  hideEditor,
  updatePosition: updateEditorPosition,
  isVisible: computed(() => isVisible.value),
})
</script>

<template>
  <!-- Editor overlay portal -->
  <Teleport :to="containerElement" :disabled="!isVisible">
    <div
      v-if="isVisible && currentEditorComponent && activeEditor"
      class="ht-cell-editor-overlay"
      style="position: fixed; top: 0; left: 0; pointer-events: none; z-index: 1000"
    >
      <component
        :is="currentEditorComponent"
        ref="editorInstance"
        v-bind="editorProps"
        :value="editorProps.value as any"
        :row="editorProps.row as any"
        :column="editorProps.column as any"
        :cell-rect="editorProps.cellRect as any"
        :grid-a-p-i="editorProps.gridAPI as any"
        style="pointer-events: auto"
        @update:value="(editorProps['onUpdate:value'] as any)"
        @cancel="(editorProps.onCancel as any)"
        @commit="(editorProps.onCommit as any)"
      />
    </div>
  </Teleport>
</template>

<style scoped>
.ht-cell-editor-overlay {
  contain: layout style paint;
}

/* Ensure editor overlays are above all grid content */
.ht-cell-editor-overlay > * {
  z-index: 1000;
}
</style>
