import type { GridEvents } from '../types'

type OnFn = <K extends keyof GridEvents>(event: K, handler: (payload: GridEvents[K]) => void) => void
type OffFn = <K extends keyof GridEvents>(event: K) => void

export interface GridEventsBinding {
  on: OnFn
  off: OffFn
}

export function useGridEvents(bind: GridEventsBinding) {
  const { on, off } = bind

  const onEvent = <K extends keyof GridEvents>(event: K, handler: (p: GridEvents[K]) => void) => on(event, handler)

  const onceEvent = <K extends keyof GridEvents>(event: K, handler: (p: GridEvents[K]) => void) => {
    const wrapper = (p: GridEvents[K]) => {
      handler(p)
      off(event)
    }
    on(event, wrapper)
  }

  // Cell events
  const onCellClick = (h: (p: GridEvents['cell-click']) => void) => on('cell-click', h)
  const onceCellClick = (h: (p: GridEvents['cell-click']) => void) => onceEvent('cell-click', h)
  const offCellClick = () => off('cell-click')

  const onCellDoubleClick = (h: (p: GridEvents['cell-double-click']) => void) => on('cell-double-click', h)
  const offCellDoubleClick = () => off('cell-double-click')

  const onCellRightClick = (h: (p: GridEvents['cell-right-click']) => void) => on('cell-right-click', h)
  const offCellRightClick = () => off('cell-right-click')

  // Row events
  const onRowClick = (h: (p: GridEvents['row-click']) => void) => on('row-click', h)
  const onceRowClick = (h: (p: GridEvents['row-click']) => void) => onceEvent('row-click', h)
  const offRowClick = () => off('row-click')

  const onRowSelect = (h: (p: GridEvents['row-select']) => void) => on('row-select', h)
  const offRowSelect = () => off('row-select')

  // Sorting / filtering
  const onMultiSortChange = (h: (p: GridEvents['multi-sort-change']) => void) => on('multi-sort-change', h)
  const offMultiSortChange = () => off('multi-sort-change')

  const onFilterChange = (h: (p: GridEvents['filter-change']) => void) => on('filter-change', h)
  const offFilterChange = () => off('filter-change')

  // Scroll / column
  const onScroll = (h: (p: GridEvents['scroll']) => void) => on('scroll', h)
  const offScroll = () => off('scroll')

  const onColumnResizeStart = (h: (p: GridEvents['column-resize-start']) => void) => on('column-resize-start', h)
  const onColumnResize = (h: (p: GridEvents['column-resize']) => void) => on('column-resize', h)

  return {
    // generic
    onEvent,
    onceEvent,
    // cell
    onCellClick,
    onceCellClick,
    offCellClick,
    onCellDoubleClick,
    offCellDoubleClick,
    onCellRightClick,
    offCellRightClick,
    // row
    onRowClick,
    onceRowClick,
    offRowClick,
    onRowSelect,
    offRowSelect,
    // sort/filter
    onMultiSortChange,
    offMultiSortChange,
    onFilterChange,
    offFilterChange,
    // misc
    onScroll,
    offScroll,
    onColumnResizeStart,
    onColumnResize,
  }
}
