<script setup lang="ts">
/**
 * HtSelectValue - 选中值显示
 *
 * 职责:
 * - 显示当前选中值的文本（从 optionsMap 查找）
 * - 未选中时显示 placeholder
 * - 支持自定义格式化 (Phase 3)
 */
import { computed } from 'vue'
import { injectSelectContext } from './composables/useSelectContext'

const props = withDefaults(defineProps<{
  placeholder?: string
}>(), {
  placeholder: 'Select...',
})

const context = injectSelectContext()

const displayValue = computed(() => {
  const value = context.selectedValue.value

  // No selection
  if (value === undefined || value === null) {
    return props.placeholder
  }

  // Look up text value from optionsMap
  const textValue = context.optionsMap.value.get(value)
  if (textValue) {
    return textValue
  }

  // Fallback: Object with label property
  if (typeof value === 'object' && value !== null && 'label' in value) {
    return value.label
  }

  // Fallback: Primitive value
  return String(value)
})

const hasValue = computed(() => {
  const value = context.selectedValue.value
  return value !== undefined && value !== null
})
</script>

<template>
  <span
    class="flex-1 text-left truncate"
    :class="{ 'text-[var(--ht-text-muted)]': !hasValue }"
  >
    {{ displayValue }}
  </span>
</template>
