import type { ColumnDef, ColumnDragContext, PinnedLocation } from '../types'

const validPinnedLocations: ReadonlyArray<PinnedLocation> = ['left', 'right', 'center']

function normalizePinnedLocation(value: ColumnDef['pinned']): PinnedLocation {
  if (value === 'left' || value === 'right') {
    return value
  }
  return 'center'
}

/**
 * 判断拖拽中的列是否允许在目标列附近释放
 */
export function isColumnDropBlocked(
  dragContext: ColumnDragContext | null | undefined,
  targetColumn: ColumnDef | null | undefined
): boolean {
  if (!dragContext || !targetColumn) {
    return false
  }

  if (dragContext.columnId === targetColumn.id) {
    return false
  }

  const targetPinned = normalizePinnedLocation(targetColumn.pinned)
  return dragContext.pinned !== targetPinned
}

/**
 * 读取列的固定区域
 */
export function getColumnPinnedLocation(column: ColumnDef | null | undefined): PinnedLocation {
  if (!column) {
    return 'center'
  }

  const normalized = normalizePinnedLocation(column.pinned)
  return validPinnedLocations.includes(normalized) ? normalized : 'center'
}

