<!--
  CellTextEditor.vue - Text Input Editor (using HtInput)

  Responsibilities:
  1. Text input with focus management
  2. Auto-sizing to cell dimensions
  3. Escape/Enter key handling
  4. Value synchronization with plugin
-->

<script setup lang="ts">
import { ref, nextTick, onMounted, computed } from 'vue'
import type { GridAPI, ColumnDef, RowData, CellValue } from '../../../types'
import HtInput from '../../primitives/HtInput.vue'

// Props
const props = defineProps<{
  value: CellValue
  row: RowData
  column: ColumnDef
  cellRect: DOMRect
  gridAPI: GridAPI
}>()

// Emits
const emit = defineEmits<{
  'update:value': [value: CellValue]
  commit: []
  cancel: []
}>()

// Local state
const inputRef = ref<InstanceType<typeof HtInput>>()
const localValue = ref<string>(String(props.value || ''))

// Computed
const inputStyle = computed(() => ({
  position: 'absolute' as const,
  left: `${props.cellRect.left}px`,
  top: `${props.cellRect.top}px`,
  width: `${props.cellRect.width}px`,
  height: `${props.cellRect.height}px`,
  zIndex: 1000,
}))

// Methods
const handleInput = (value: string | number) => {
  const stringValue = String(value)
  localValue.value = stringValue
  emit('update:value', stringValue)

  // Update plugin state
  if (props.gridAPI?.updateCellEditValue) {
    props.gridAPI.updateCellEditValue(stringValue)
  }
}

const handleKeydown = (event: KeyboardEvent) => {
  switch (event.key) {
    case 'Enter':
      if (!event.shiftKey) {
        event.preventDefault()
        event.stopPropagation()
        emit('commit')
      }
      break
    case 'Escape':
      event.preventDefault()
      event.stopPropagation()
      emit('cancel')
      break
    case 'Tab':
      event.preventDefault()
      event.stopPropagation()
      emit('commit')
      break
  }
}

const handleBlur = () => {
  // Auto-commit on blur unless explicitly cancelled
  emit('commit')
}

const focus = () => {
  // Focus the native input inside HtInput
  const inputElement = inputRef.value?.$el?.querySelector('input')
  if (inputElement) {
    inputElement.focus()
    inputElement.select()
  }
}

// Lifecycle
onMounted(async () => {
  await nextTick()
  focus()
})

// Expose methods for parent
defineExpose({
  focus,
})
</script>

<template>
  <div :style="inputStyle" class="ht-cell-text-editor">
    <HtInput
      ref="inputRef"
      :model-value="localValue"
      type="text"
      variant="default"
      size="md"
      :placeholder="column.placeholder || `Enter ${column.title}`"
      :maxlength="column.maxLength"
      autocomplete="off"
      class="ht-cell-editor-input"
      @update:model-value="handleInput"
      @keydown="handleKeydown"
      @blur="handleBlur"
    />
  </div>
</template>

<style scoped>
.ht-cell-text-editor {
  /* Ensure proper absolute positioning */
  display: block;
}

.ht-cell-editor-input {
  /* Override default styles for cell editing */
  width: 100%;
  height: 100%;
}

/* Enhance focus ring for cell editing context */
.ht-cell-editor-input :deep(input) {
  border-width: 2px;
  border-color: var(--ht-primary);
  box-shadow: 0 0 0 1px var(--ht-primary);
  border-radius: 4px;
  height: 100%;
}

.ht-cell-editor-input :deep(input:focus) {
  outline: none;
  box-shadow: 0 0 0 2px var(--ht-primary);
}
</style>
