// Utility constants
export const UTILS_VERSION = '1.1.0'

// Feature detection
export const UtilityFeatures = {
  ADVANCED_VIRTUALIZATION: true,
  BIDIRECTIONAL_VIRTUALIZATION: true, // New feature flag
  MEMORY_MANAGEMENT: true,
  PERFORMANCE_MONITORING: true,
  DOM_OPTIMIZATION: true,
  BROWSER_OPTIMIZATION: true,
  DATA_PIPELINE: true,
  HARDWARE_ACCELERATION: typeof CSS !== 'undefined' && CSS.supports('transform', 'translateZ(0)'),
  INTERSECTION_OBSERVER: typeof IntersectionObserver !== 'undefined',
  RESIZE_OBSERVER: typeof ResizeObserver !== 'undefined',
  PERFORMANCE_OBSERVER: typeof PerformanceObserver !== 'undefined',
  WEB_WORKERS: typeof Worker !== 'undefined',
  OFFSCREEN_CANVAS: typeof OffscreenCanvas !== 'undefined',
} as const

/**
 * Initialize all utility systems
 * Call this once during application startup for optimal performance
 */
export async function initializeUtilities(): Promise<void> {
  // Initialize browser optimizations
  if (typeof window !== 'undefined') {
    const { initializeBrowserOptimizations } = await import('./browser-optimization')
    initializeBrowserOptimizations()
  }
}

/**
 * Get information about available utility features
 */
export function getUtilityInfo() {
  return {
    version: UTILS_VERSION,
    features: UtilityFeatures,
    modules: {
      virtualization: 'Core virtual scrolling with dynamic sizing',
      advancedVirtualization: 'Advanced virtual scrolling with velocity-based overscan',
      bidirectionalVirtualization: 'Combined row and column virtualization for large grids',
      threePaneVirtualization: 'Three-pane virtualization for frozen columns',
      memoryManagement: 'Memory leak prevention and cleanup utilities',
      performanceMonitoring: 'Real-time performance metrics and benchmarking',
      domOptimization: 'DOM manipulation optimization and pooling',
      browserOptimization: 'Browser-specific performance optimizations',
      dataPipeline: 'High-performance data processing pipeline',
    },
  }
}

/**
 * Quick start utilities for common scenarios
 */
export const quickStartUtils = {
  /**
   * Setup basic virtualization
   */
  basicVirtualization: (count: number, itemHeight: number = 50) => ({
    count,
    getItemSize: () => itemHeight,
    overscan: 5,
    enabled: true,
  }),

  /**
   * Setup high-performance virtualization for large datasets
   */
  highPerformanceVirtualization: (count: number, itemHeight: number = 50) => ({
    count,
    getItemSize: () => itemHeight,
    overscanCount: 10,
    enableDynamicOverscan: true,
    scrollingDelay: 100,
  }),

  /**
   * Setup bidirectional virtualization for large grids
   */
  bidirectionalVirtualization: (rowCount: number, rowHeight: number = 32, columnWidth: number = 150) => ({
    rowCount,
    getRowSize: () => rowHeight,
    columns: [], // Will be provided from grid state
    getColumnWidth: () => columnWidth,
    overscan: { rows: 5, columns: 2 },
    enabled: { vertical: true, horizontal: true },
  }),

  /**
   * Setup memory management for large datasets
   */
  memoryOptimization: () => ({
    enableGC: true,
    poolSize: 100,
    cacheSize: 1000,
    cleanupInterval: 30000,
  }),

  /**
   * Setup performance monitoring
   */
  performanceTracking: () => ({
    enableFPS: true,
    enableRenderProfiling: true,
    enableMemoryTracking: true,
    enableWebVitals: true,
  }),
}
