import type { GridEvents } from './index'

// Public event names exposed to consumers (template + helpers)
export const GRID_EVENT_NAMES = [
  'row-click',
  'row-select',
  'cell-click',
  'cell-double-click',
  'cell-right-click',
  'multi-sort-change',
  'filter-change',
  'scroll',
  'column-resize-start',
  'column-resize',
  'toggle-selection',
  'select-all',
  'clear-selection',
] as const

export type PublicEventName = typeof GRID_EVENT_NAMES[number]
export type GridEventName = keyof GridEvents
export type PublicEventMap = Pick<GridEvents, PublicEventName>
