<script setup lang="ts">
import { computed, useId } from 'vue'
import type { ComponentSize, CoreVariant, ComponentState } from '../../types/primitives'

/**
 * HtRadio - Atomic Radio Component
 *
 * Design: shadcn-vue pattern (80%) + Headless UI interactions (10%)
 * Features: Variant/Size/State system, full accessibility, radio group support
 */

export interface HtRadioProps {
  // Core
  modelValue?: any
  value: any // Required for radio button - the value this radio represents

  // Style system (matches CheckboxStyleProps pattern)
  variant?: CoreVariant
  size?: ComponentSize
  state?: ComponentState

  // Functionality
  disabled?: boolean
  required?: boolean

  // Label
  label?: string
  description?: string

  // Accessibility
  name?: string
}

export interface HtRadioEmits {
  'update:modelValue': [value: any]
  'change': [value: any, event: Event]
}

const props = withDefaults(defineProps<HtRadioProps>(), {
  size: 'md',
  variant: 'default',
  state: 'default',
})

const emit = defineEmits<HtRadioEmits>()

// Generate stable ID for accessibility (SSR compatible)
const radioId = useId()

// Computed
const isChecked = computed(() => {
  return props.modelValue === props.value
})

// Base styles aligned with HtButton/HtInput/HtCheckbox pattern + 2025 UI standards
const baseClasses = [
  'inline-flex items-center justify-center',
  'rounded-full border-[1px]', // Use 1.5px border for better visibility
  'cursor-pointer',
  'transition-all duration-200', // Smooth 0.2s transitions per 2025 standards
  'will-change-auto',
].join(' ')

// Variant system (matches CoreVariant from shared types)
// Uses text color for maximum visibility in dark mode, matching main text brightness
const variantClasses: Record<string, string> = {
  default: 'border-[var(--ht-text)] bg-[var(--ht-bg)]',
  filled: 'border-[var(--ht-text)] bg-[var(--ht-bg-subtle)]',
  outline: 'border-[var(--ht-text)] bg-transparent',
  ghost: 'border-[var(--ht-text)] bg-transparent',
}

// Checked state styles - uses high-opacity primary bg for clear visibility (2025 UI standards)
const checkedVariantClasses: Record<string, string> = {
  default: 'border-[var(--ht-primary)] bg-[var(--ht-primary)]/90',
  filled: 'border-[var(--ht-primary)] bg-[var(--ht-primary)]/90',
  outline: 'border-[var(--ht-primary)] bg-[var(--ht-primary)]/90',
  ghost: 'border-[var(--ht-primary)] bg-[var(--ht-primary)]/90',
}

// Disabled unchecked state - gray border, no background (for label element)
const disabledVariantClasses: Record<string, string> = {
  default: 'border-[var(--ht-text-subtle)] bg-[var(--ht-bg)]',
  filled: 'border-[var(--ht-text-subtle)] bg-[var(--ht-bg-subtle)]',
  outline: 'border-[var(--ht-text-subtle)] bg-transparent',
  ghost: 'border-[var(--ht-text-subtle)] bg-transparent',
}

// Disabled checked state - gray border and background (for label element)
const disabledCheckedVariantClasses: Record<string, string> = {
  default: 'border-[var(--ht-text-subtle)] bg-[var(--ht-text-subtle)]/10',
  filled: 'border-[var(--ht-text-subtle)] bg-[var(--ht-text-subtle)]/10',
  outline: 'border-[var(--ht-text-subtle)] bg-[var(--ht-text-subtle)]/10',
  ghost: 'border-[var(--ht-text-subtle)] bg-[var(--ht-text-subtle)]/10',
}

// Common disabled behavior - 40% opacity per 2025 UI standards
const disabledClasses = [
  'opacity-40',
  'cursor-not-allowed',
  'pointer-events-none',
].join(' ')

// Size system (uses @theme tokens from primitives.css)
const sizeClasses: Record<string, string> = {
  sm: 'w-4 h-4',   // 16px
  md: 'w-5 h-5',   // 20px
  lg: 'w-6 h-6',   // 24px
}

// Inner dot size (smaller than outer circle)
const dotSizeClasses: Record<string, string> = {
  sm: 'w-2 h-2',   // 8px (50% of 16px)
  md: 'w-2.5 h-2.5', // 10px (50% of 20px)
  lg: 'w-3 h-3',   // 12px (50% of 24px)
}

// State system (visual feedback for validation)
const stateClasses: Record<string, string> = {
  default: 'focus-visible:ring-[var(--ht-primary)]',
  error: 'border-[var(--ht-danger)] focus-visible:ring-[var(--ht-danger)]',
  success: 'border-[var(--ht-success)] focus-visible:ring-[var(--ht-success)]',
  warning: 'border-[var(--ht-warning)] focus-visible:ring-[var(--ht-warning)]',
}

const radioClasses = computed(() => {
  // Choose classes based on disabled state and checked state
  let stateClass: string
  if (props.disabled) {
    // Disabled state - use gray colors
    stateClass = isChecked.value
      ? (disabledCheckedVariantClasses[props.variant] ?? '')
      : (disabledVariantClasses[props.variant] ?? '')
  } else {
    // Enabled state - use primary colors when checked
    stateClass = isChecked.value
      ? (checkedVariantClasses[props.variant] ?? '')
      : (variantClasses[props.variant] ?? '')
  }

  return [
    baseClasses,
    props.disabled ? disabledClasses : '',
    stateClass,
    sizeClasses[props.size],
    stateClasses[props.state],
  ].join(' ')
})

// Top margin offset based on radio size for better text alignment
const topOffset = computed(() => {
  const offsets = {
    sm: 'mt-[-5px]',  // 16px radio + 12px text: negative offset
    md: 'mt-[-2px]',     // 20px radio + 14px text: 2px positive offset
    lg: 'mt-[0px]',   // 24px radio + 16px text: 3px positive offset
  }
  return offsets[props.size]
})

// Text size based on radio size for proportional appearance
const textSize = computed(() => {
  const sizes = {
    sm: 'text-xs',    // 12px font for 16px radio
    md: 'text-sm',    // 14px font for 20px radio
    lg: 'text-base',  // 16px font for 24px radio
  }
  return sizes[props.size]
})

// Description text size (always one step smaller than label)
const descriptionSize = computed(() => {
  const sizes = {
    sm: 'text-[11px]',  // Extra small for sm radio
    md: 'text-xs',      // 12px for md radio
    lg: 'text-sm',      // 14px for lg radio
  }
  return sizes[props.size]
})

// Inner dot color - uses inverse text when enabled, subtle gray when disabled (2025 UI standards)
const dotClasses = computed(() => {
  return [
    dotSizeClasses[props.size],
    'rounded-full transition-transform',
    props.disabled ? 'bg-[var(--ht-text-subtle)]' : 'bg-[var(--ht-text-inverse)]',
  ].join(' ')
})

// Methods
const handleChange = (event: Event) => {
  if (props.disabled) return

  // Radio buttons always become checked when clicked
  // They cannot be unchecked by clicking again (use different radio or reset)
  emit('update:modelValue', props.value)
  emit('change', props.value, event)
}

const handleLabelClick = (event: MouseEvent) => {
  // Prevent default label behavior that might cause unwanted scrolling
  event.preventDefault()

  if (props.disabled) return

  // Manually select the radio
  emit('update:modelValue', props.value)
  emit('change', props.value, event)
}

const handleKeydown = (event: KeyboardEvent) => {
  // Space key selects radio
  if (event.key === ' ') {
    event.preventDefault()
    const radio = event.target as HTMLInputElement
    radio.click()
  }

  // Arrow keys navigate within radio group
  // This is handled automatically by native radio inputs with the same name
}
</script>

<template>
  <div class="ht-radio-wrapper inline-flex items-start gap-2">
    <div class="ht-radio-container relative flex-shrink-0" :class="topOffset">
      <!-- Hidden native radio for form integration -->
      <input
        :id="radioId"
        type="radio"
        :name="name"
        :value="value"
        :checked="isChecked"
        :disabled="disabled"
        :required="required"
        :aria-checked="isChecked"
        :aria-invalid="state === 'error'"
        :aria-required="required"
        class="sr-only peer"
        @change="handleChange"
        @keydown="handleKeydown"
      />

      <!-- Visual radio button -->
      <label
        :for="radioId"
        :class="radioClasses"
        @click="handleLabelClick"
      >
        <!-- Inner dot (only visible when checked) -->
        <span
          v-show="isChecked"
          :class="dotClasses"
          aria-hidden="true"
        ></span>

        <!-- Empty placeholder for unchecked state -->
        <span
          v-show="!isChecked"
          :class="dotSizeClasses[size]"
          aria-hidden="true"
        ></span>
      </label>
    </div>

    <!-- Label and description -->
    <div v-if="label || description || $slots.default" class="ht-radio-label-container flex-1">
      <label
        v-if="label || $slots.default"
        :for="radioId"
        :class="[
          textSize,
          'font-medium text-[var(--ht-text)] cursor-pointer select-none block leading-snug',
          { 'opacity-50': disabled }
        ]"
      >
        <slot>{{ label }}</slot>
      </label>
      <p
        v-if="description || $slots.description"
        :class="[
          descriptionSize,
          'text-[var(--ht-text-muted)] mt-1 leading-snug',
          { 'opacity-50': disabled }
        ]"
      >
        <slot name="description">{{ description }}</slot>
      </p>
    </div>
  </div>
</template>

<style scoped>
/* Screen reader only - hidden but accessible */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border-width: 0;
}

/* Prevent layout shift on state change */
.ht-radio-container {
  /* Use contain to isolate layout changes */
  contain: layout;
}

.ht-radio-container label {
  /* Fixed dimensions prevent layout shift */
  display: inline-flex;
  /* Prevent any size changes during transitions */
  flex-shrink: 0;
}

/* Focus state from peer (native radio) - use outline with offset for universal visibility */
.peer:focus-visible + label {
  outline: 1px solid var(--ht-primary);
  outline-offset: 1px;
}

/* Disabled state */
.ht-radio-container input:disabled + label {
  cursor: not-allowed;
}

/* Ensure inner dot doesn't trigger layout */
.ht-radio-container label > * {
  display: block;
  flex-shrink: 0;
}
</style>
