/**
 * Data Pipeline Processing Utilities
 *
 * Features:
 * - High-performance data transformation pipeline
 * - Memory-efficient data processing with streaming support
 * - Pluggable interceptor system integration
 * - Async and sync processing modes
 * - Performance monitoring and error handling
 * - Integration with Vue 3 reactivity system
 */

import { ref, computed, onUnmounted, type Ref, type ComputedRef } from 'vue'
import type { RowData, GridAPI } from '../types'
import { createLogger } from './logger'

export interface DataPipelineInterceptor {
  type: 'filter' | 'sort' | 'transform' | 'grouping' | 'aggregation'
  priority: number
  name: string
  process: (data: RowData[], context?: unknown) => RowData[] | Promise<RowData[]>
  enabled: boolean
}

export interface DataPipelineStage {
  name: string
  interceptors: DataPipelineInterceptor[]
  metrics: {
    processedCount: number
    averageTime: number
    lastProcessTime: number
    errorCount: number
  }
}

export interface DataPipelineMetrics {
  totalProcessTime: number
  stagesCompleted: number
  itemsProcessed: number
  errorCount: number
  lastRunTime: number
  throughputPerSecond: number
}

export interface DataPipelineOptions {
  enableMetrics?: boolean
  maxBatchSize?: number
  asyncProcessing?: boolean
  errorHandling?: 'throw' | 'log' | 'ignore'
  debounceMs?: number
}

export interface DataPipelineReturn {
  // State
  isProcessing: ComputedRef<boolean>
  metrics: ComputedRef<DataPipelineMetrics>
  stages: ComputedRef<DataPipelineStage[]>
  lastError: Ref<Error | null>

  // Data processing
  processData: (data: RowData[], context?: unknown) => Promise<RowData[]>

  // Interceptor management
  addInterceptor: (interceptor: DataPipelineInterceptor) => void
  removeInterceptor: (name: string) => void
  enableInterceptor: (name: string) => void
  disableInterceptor: (name: string) => void
  getInterceptors: (type?: DataPipelineInterceptor['type']) => DataPipelineInterceptor[]

  // Pipeline control
  clearPipeline: (type?: DataPipelineInterceptor['type']) => void
  resetMetrics: () => void

  // Performance
  benchmark: (data: RowData[], iterations?: number) => Promise<DataPipelineBenchmarkResult>
}

export interface DataPipelineBenchmarkResult {
  averageTime: number
  minTime: number
  maxTime: number
  totalTime: number
  throughput: number
  iterations: number
}

// Memory-efficient data stream processor
class DataStreamProcessor {
  private batchSize: number
  private abortController?: AbortController

  constructor(batchSize: number = 1000) {
    this.batchSize = batchSize
  }

  async processBatches<T>(
    data: T[],
    processor: (batch: T[], batchIndex: number) => T[] | Promise<T[]>,
    signal?: AbortSignal
  ): Promise<T[]> {
    if (data.length <= this.batchSize) {
      return await processor(data, 0)
    }

    const result: T[] = []
    const batches = Math.ceil(data.length / this.batchSize)

    for (let i = 0; i < batches; i++) {
      if (signal?.aborted) {
        throw new Error('Processing aborted')
      }

      const start = i * this.batchSize
      const end = Math.min((i + 1) * this.batchSize, data.length)
      const batch = data.slice(start, end)

      const processed = await processor(batch, i)
      result.push(...processed)

      // Allow other tasks to run
      if (i < batches - 1) {
        await new Promise(resolve => setTimeout(resolve, 0))
      }
    }

    return result
  }

  abort(): void {
    if (this.abortController) {
      this.abortController.abort()
    }
  }
}

// Performance monitoring for pipeline stages
class PipelineProfiler {
  private measurements = new Map<string, number[]>()
  private readonly maxMeasurements = 100

  start(stageName: string): () => number {
    const startTime = performance.now()

    return (): number => {
      const duration = performance.now() - startTime
      this.recordMeasurement(stageName, duration)
      return duration
    }
  }

  private recordMeasurement(stageName: string, duration: number): void {
    if (!this.measurements.has(stageName)) {
      this.measurements.set(stageName, [])
    }

    const measurements = this.measurements.get(stageName)!
    measurements.push(duration)

    // Keep only recent measurements
    if (measurements.length > this.maxMeasurements) {
      measurements.shift()
    }
  }

  getAverageTime(stageName: string): number {
    const measurements = this.measurements.get(stageName)
    if (!measurements || measurements.length === 0) return 0

    return measurements.reduce((sum, time) => sum + time, 0) / measurements.length
  }

  getMetrics(stageName: string): { avg: number; min: number; max: number; count: number } {
    const measurements = this.measurements.get(stageName) || []

    if (measurements.length === 0) {
      return { avg: 0, min: 0, max: 0, count: 0 }
    }

    return {
      avg: measurements.reduce((sum, time) => sum + time, 0) / measurements.length,
      min: Math.min(...measurements),
      max: Math.max(...measurements),
      count: measurements.length,
    }
  }

  clear(): void {
    this.measurements.clear()
  }
}

// Main data pipeline composable
export function useDataPipeline(
  api?: GridAPI,
  options: DataPipelineOptions = {}
): DataPipelineReturn {
  // Logger for data pipeline
  const logger = createLogger('DataPipeline')

  const {
    enableMetrics = true,
    maxBatchSize = 1000,
    asyncProcessing = true,
    errorHandling = 'log',
    debounceMs = 16,
  } = options

  // State
  const interceptors = ref<DataPipelineInterceptor[]>([])
  const isProcessing = ref(false)
  const lastError = ref<Error | null>(null)
  const metrics = ref<DataPipelineMetrics>({
    totalProcessTime: 0,
    stagesCompleted: 0,
    itemsProcessed: 0,
    errorCount: 0,
    lastRunTime: 0,
    throughputPerSecond: 0,
  })

  // Internal utilities
  const streamProcessor = new DataStreamProcessor(maxBatchSize)
  const profiler = enableMetrics ? new PipelineProfiler() : null
  let debounceTimeout: number | null = null

  // Computed stages grouped by type
  const stages = computed((): DataPipelineStage[] => {
    const stageTypes: DataPipelineInterceptor['type'][] = [
      'filter',
      'sort',
      'transform',
      'grouping',
      'aggregation',
    ]

    return stageTypes.map(type => {
      const stageInterceptors = interceptors.value
        .filter(i => i.type === type && i.enabled)
        .sort((a, b) => a.priority - b.priority)

      const stageMetrics = profiler?.getMetrics(type) || { avg: 0, min: 0, max: 0, count: 0 }

      return {
        name: type,
        interceptors: stageInterceptors,
        metrics: {
          processedCount: stageMetrics.count,
          averageTime: stageMetrics.avg,
          lastProcessTime: stageMetrics.max,
          errorCount: 0, // Track separately if needed
        },
      }
    })
  })

  // Core data processing function
  const processData = async (data: RowData[], context?: unknown): Promise<RowData[]> => {
    if (isProcessing.value) {
      logger.warn('Processing already in progress, skipping')
      return data
    }

    isProcessing.value = true
    lastError.value = null

    const startTime = performance.now()
    let currentData = [...data] // Defensive copy
    let stagesCompleted = 0

    try {
      // Process through each stage in order
      const stageTypes: DataPipelineInterceptor['type'][] = [
        'filter',
        'sort',
        'transform',
        'grouping',
        'aggregation',
      ]

      for (const stageType of stageTypes) {
        const stageInterceptors = interceptors.value
          .filter(i => i.type === stageType && i.enabled)
          .sort((a, b) => a.priority - b.priority)

        if (stageInterceptors.length === 0) continue

        const stageProfiler = profiler?.start(stageType)

        try {
          // Process through all interceptors in this stage
          for (const interceptor of stageInterceptors) {
            const interceptorProfiler = profiler?.start(`${stageType}:${interceptor.name}`)

            if (asyncProcessing && currentData.length > maxBatchSize) {
              // Use streaming for large datasets
              currentData = await streamProcessor.processBatches(currentData, async batch => {
                const result = await interceptor.process(batch, context)
                return Array.isArray(result) ? result : ([result] as RowData[])
              })
            } else {
              // Direct processing for smaller datasets
              const result = await interceptor.process(currentData, context)
              currentData = Array.isArray(result) ? result : ([result] as RowData[])
            }

            interceptorProfiler?.()
          }

          stagesCompleted++
        } catch (error) {
          logger.error(`Error in ${stageType} stage:`, { module: 'DataPipeline', stage: stageType }, error)

          if (errorHandling === 'throw') {
            throw error
          }

          if (errorHandling === 'log') {
            lastError.value = error as Error
            metrics.value.errorCount++
          }
        }

        stageProfiler?.()
      }

      // Update metrics
      const totalTime = performance.now() - startTime
      if (enableMetrics) {
        metrics.value = {
          totalProcessTime: totalTime,
          stagesCompleted,
          itemsProcessed: currentData.length,
          errorCount: metrics.value.errorCount,
          lastRunTime: Date.now(),
          throughputPerSecond: currentData.length / (totalTime / 1000),
        }
      }

      // Emit completion event if API is available
      if (api) {
        api.emit?.('data-pipeline-complete', {
          originalCount: data.length,
          processedCount: currentData.length,
          processingTime: totalTime,
          stagesCompleted,
        })
      }

      return currentData
    } catch (error) {
      lastError.value = error as Error
      metrics.value.errorCount++

      if (api) {
        api.emit?.('data-pipeline-error', { error, stage: stagesCompleted })
      }

      if (errorHandling === 'throw') {
        throw error
      }

      // Return original data on error for graceful degradation
      return data
    } finally {
      isProcessing.value = false
    }
  }

  // Debounced data processing
  const debouncedProcessData = (data: RowData[], context?: unknown): Promise<RowData[]> => {
    return new Promise((resolve, reject) => {
      if (debounceTimeout) {
        clearTimeout(debounceTimeout)
      }

      debounceTimeout = window.setTimeout(async () => {
        try {
          const result = await processData(data, context)
          resolve(result)
        } catch (error) {
          reject(error)
        }
      }, debounceMs)
    })
  }

  // Interceptor management
  const addInterceptor = (interceptor: DataPipelineInterceptor): void => {
    // Validate interceptor
    if (!interceptor.name || !interceptor.process) {
      throw new Error('Invalid interceptor: name and process function are required')
    }

    // Remove existing interceptor with same name
    removeInterceptor(interceptor.name)

    // Add new interceptor
    interceptors.value.push({ ...interceptor })

    if (api) {
      // Backward compatible event name
      api.emit?.('data-interceptor-added', { name: interceptor.name, type: interceptor.type })
      // Standardized event name used by capability-based pipeline
      api.emit?.('data-interceptor-registered', { type: interceptor.type, interceptor })
    }
  }

  const removeInterceptor = (name: string): void => {
    const index = interceptors.value.findIndex(i => i.name === name)
    if (index !== -1) {
      const removed = interceptors.value.splice(index, 1)[0]

      if (api && removed) {
        api.emit?.('data-interceptor-removed', { name, type: removed.type })
      }
    }
  }

  const enableInterceptor = (name: string): void => {
    const interceptor = interceptors.value.find(i => i.name === name)
    if (interceptor) {
      interceptor.enabled = true
    }
  }

  const disableInterceptor = (name: string): void => {
    const interceptor = interceptors.value.find(i => i.name === name)
    if (interceptor) {
      interceptor.enabled = false
    }
  }

  const getInterceptors = (type?: DataPipelineInterceptor['type']): DataPipelineInterceptor[] => {
    if (type) {
      return interceptors.value.filter(i => i.type === type)
    }
    return [...interceptors.value]
  }

  // Pipeline control
  const clearPipeline = (type?: DataPipelineInterceptor['type']): void => {
    if (type) {
      interceptors.value = interceptors.value.filter(i => i.type !== type)
    } else {
      interceptors.value = []
    }

    if (api) {
      api.emit?.('data-pipeline-cleared', { type })
    }
  }

  const resetMetrics = (): void => {
    metrics.value = {
      totalProcessTime: 0,
      stagesCompleted: 0,
      itemsProcessed: 0,
      errorCount: 0,
      lastRunTime: 0,
      throughputPerSecond: 0,
    }

    profiler?.clear()
  }

  // Performance benchmarking
  const benchmark = async (
    data: RowData[],
    iterations: number = 10
  ): Promise<DataPipelineBenchmarkResult> => {
    const times: number[] = []

    for (let i = 0; i < iterations; i++) {
      const start = performance.now()
      await processData(data)
      const end = performance.now()
      times.push(end - start)
    }

    const totalTime = times.reduce((sum, time) => sum + time, 0)
    const averageTime = totalTime / iterations
    const minTime = Math.min(...times)
    const maxTime = Math.max(...times)
    const throughput = data.length / (averageTime / 1000)

    return {
      averageTime,
      minTime,
      maxTime,
      totalTime,
      throughput,
      iterations,
    }
  }

  // Cleanup
  onUnmounted(() => {
    if (debounceTimeout) {
      clearTimeout(debounceTimeout)
    }
    streamProcessor.abort()
    interceptors.value = []
    profiler?.clear()
  })

  return {
    // State
    isProcessing: computed(() => isProcessing.value),
    metrics: computed(() => metrics.value),
    stages,
    lastError,

    // Data processing
    processData: debounceMs > 0 ? debouncedProcessData : processData,

    // Interceptor management
    addInterceptor,
    removeInterceptor,
    enableInterceptor,
    disableInterceptor,
    getInterceptors,

    // Pipeline control
    clearPipeline,
    resetMetrics,

    // Performance
    benchmark,
  }
}

// Utility functions for common data operations
export const DataPipelineUtils = {
  // Create common filter interceptors
  createFilterInterceptor: (
    name: string,
    filterFn: (row: RowData) => boolean,
    priority: number = 0
  ): DataPipelineInterceptor => ({
    type: 'filter',
    name,
    priority,
    enabled: true,
    process: (data: RowData[]) => data.filter(filterFn),
  }),

  // Create common sort interceptors
  createSortInterceptor: (
    name: string,
    sortFn: (a: RowData, b: RowData) => number,
    priority: number = 0
  ): DataPipelineInterceptor => ({
    type: 'sort',
    name,
    priority,
    enabled: true,
    process: (data: RowData[]) => [...data].sort(sortFn),
  }),

  // Create common transform interceptors
  createTransformInterceptor: (
    name: string,
    transformFn: (row: RowData) => RowData,
    priority: number = 0
  ): DataPipelineInterceptor => ({
    type: 'transform',
    name,
    priority,
    enabled: true,
    process: (data: RowData[]) => data.map(transformFn),
  }),

  // Create async interceptor with progress tracking
  createAsyncInterceptor: (
    name: string,
    type: DataPipelineInterceptor['type'],
    asyncProcess: (data: RowData[], onProgress?: (progress: number) => void) => Promise<RowData[]>,
    priority: number = 0
  ): DataPipelineInterceptor => ({
    type,
    name,
    priority,
    enabled: true,
    process: async (data: RowData[]) => {
      return await asyncProcess(data, (_progress) => {
        // Progress callback - consider emitting progress events instead
        // Note: Logger not imported here to avoid circular dependencies
      })
    },
  }),
}
