<!--
  Accessibility Announcer Component

  Provides live region announcements for screen reader users during
  keyboard navigation and other grid interactions.
-->

<template>
  <!-- Primary live region for important announcements -->
  <div ref="primaryRegion" class="sr-only" aria-live="assertive" aria-atomic="true" role="status">
    {{ primaryMessage }}
  </div>

  <!-- Secondary live region for less critical announcements -->
  <div ref="secondaryRegion" class="sr-only" aria-live="polite" aria-atomic="false" role="log">
    {{ secondaryMessage }}
  </div>

  <!-- Navigation announcements -->
  <div ref="navigationRegion" class="sr-only" aria-live="polite" aria-atomic="true" role="status">
    {{ navigationMessage }}
  </div>
</template>

<script setup lang="ts">
import { ref, onUnmounted } from 'vue'
import type { CellValue } from '../../types'

interface AnnouncementOptions {
  priority?: 'high' | 'medium' | 'low'
  category?: 'navigation' | 'editing' | 'selection' | 'error' | 'success'
  delay?: number
  clearAfter?: number
}

const primaryMessage = ref('')
const secondaryMessage = ref('')
const navigationMessage = ref('')

// Message queues for different priority levels
const messageQueue = {
  high: [] as Array<{ message: string; options: AnnouncementOptions }>,
  medium: [] as Array<{ message: string; options: AnnouncementOptions }>,
  low: [] as Array<{ message: string; options: AnnouncementOptions }>,
}

let isProcessingQueue = false
let clearTimeouts: number[] = []

/**
 * Announce a message to screen readers
 */
const announce = (message: string, options: AnnouncementOptions = {}) => {
  const { priority = 'medium', category: _category = 'navigation', delay = 0, clearAfter: _clearAfter = 3000 } = options

  // Don't announce empty messages
  if (!message.trim()) return

  // Add to appropriate queue
  messageQueue[priority].push({ message, options })

  // Process queue if not already processing
  if (!isProcessingQueue) {
    setTimeout(() => processQueue(), delay)
  }
}

/**
 * Process the message queue in priority order
 */
const processQueue = () => {
  isProcessingQueue = true

  // Process high priority first, then medium, then low
  const priorities = ['high', 'medium', 'low'] as const

  for (const priority of priorities) {
    const queue = messageQueue[priority]
    if (queue.length > 0) {
      const { message, options } = queue.shift()!
      displayMessage(message, options)
      break
    }
  }

  // Continue processing if there are more messages
  const hasMoreMessages = priorities.some(p => messageQueue[p].length > 0)
  if (hasMoreMessages) {
    setTimeout(() => processQueue(), 100)
  } else {
    isProcessingQueue = false
  }
}

/**
 * Display message in appropriate live region
 */
const displayMessage = (message: string, options: AnnouncementOptions) => {
  const { priority = 'medium', category = 'navigation', clearAfter = 3000 } = options

  // Clear any existing timeouts
  clearTimeouts.forEach(timeout => clearTimeout(timeout))
  clearTimeouts = []

  // Choose appropriate live region based on priority and category
  if (priority === 'high' || category === 'error') {
    primaryMessage.value = message
    if (clearAfter > 0) {
      const timeout = window.setTimeout(() => {
        primaryMessage.value = ''
      }, clearAfter)
      clearTimeouts.push(timeout)
    }
  } else if (category === 'navigation') {
    navigationMessage.value = message
    if (clearAfter > 0) {
      const timeout = window.setTimeout(() => {
        navigationMessage.value = ''
      }, clearAfter)
      clearTimeouts.push(timeout)
    }
  } else {
    secondaryMessage.value = message
    if (clearAfter > 0) {
      const timeout = window.setTimeout(() => {
        secondaryMessage.value = ''
      }, clearAfter)
      clearTimeouts.push(timeout)
    }
  }
}

/**
 * Announce cell focus change
 */
const announceCellFocus = (rowIndex: number, columnIndex: number, cellValue?: CellValue, columnTitle?: string) => {
  const position = `Row ${rowIndex + 1}, Column ${columnIndex + 1}`
  const column = columnTitle ? `, ${columnTitle}` : ''
  const value = cellValue ? `, ${cellValue}` : ''

  announce(`${position}${column}${value}`, {
    category: 'navigation',
    priority: 'low',
    clearAfter: 2000,
  })
}

/**
 * Announce selection change
 */
const announceSelection = (selectedCount: number, totalCount: number) => {
  const message =
    selectedCount === 0
      ? 'Selection cleared'
      : selectedCount === 1
        ? '1 row selected'
        : selectedCount === totalCount
          ? 'All rows selected'
          : `${selectedCount} of ${totalCount} rows selected`

  announce(message, {
    category: 'selection',
    priority: 'medium',
    clearAfter: 2000,
  })
}

/**
 * Announce edit mode changes
 */
const announceEditMode = (entering: boolean, cellInfo?: { row: number; column: number; title?: string }) => {
  if (entering && cellInfo) {
    const position = `Row ${cellInfo.row + 1}, Column ${cellInfo.column + 1}`
    const column = cellInfo.title ? `, ${cellInfo.title}` : ''
    announce(`Editing ${position}${column}`, {
      category: 'editing',
      priority: 'medium',
      clearAfter: 1000,
    })
  } else if (!entering) {
    announce('Edit mode exited', {
      category: 'editing',
      priority: 'low',
      clearAfter: 1000,
    })
  }
}

/**
 * Announce sorting changes
 */
const announceSorting = (columnTitle: string, direction: 'asc' | 'desc' | 'none') => {
  const directionText = direction === 'asc' ? 'ascending' : direction === 'desc' ? 'descending' : 'none'
  const message = direction === 'none' ? `${columnTitle} sorting cleared` : `${columnTitle} sorted ${directionText}`

  announce(message, {
    category: 'navigation',
    priority: 'medium',
    clearAfter: 2000,
  })
}

/**
 * Announce filtering changes
 */
const announceFiltering = (activeFilters: number, totalRows: number, visibleRows: number) => {
  if (activeFilters === 0) {
    announce(`All filters cleared. Showing all ${totalRows} rows`, {
      category: 'navigation',
      priority: 'medium',
      clearAfter: 2000,
    })
  } else {
    announce(
      `${activeFilters} filter${activeFilters > 1 ? 's' : ''} applied. Showing ${visibleRows} of ${totalRows} rows`,
      {
        category: 'navigation',
        priority: 'medium',
        clearAfter: 2000,
      }
    )
  }
}

/**
 * Announce error messages
 */
const announceError = (message: string) => {
  announce(`Error: ${message}`, {
    category: 'error',
    priority: 'high',
    clearAfter: 5000,
  })
}

/**
 * Announce success messages
 */
const announceSuccess = (message: string) => {
  announce(message, {
    category: 'success',
    priority: 'medium',
    clearAfter: 2000,
  })
}

/**
 * Clear all messages
 */
const clearAll = () => {
  primaryMessage.value = ''
  secondaryMessage.value = ''
  navigationMessage.value = ''

  clearTimeouts.forEach(timeout => clearTimeout(timeout))
  clearTimeouts = []

  // Clear queues
  Object.keys(messageQueue).forEach(key => {
    messageQueue[key as keyof typeof messageQueue] = []
  })

  isProcessingQueue = false
}

onUnmounted(() => {
  clearAll()
})

// Expose methods for external use
defineExpose({
  announce,
  announceCellFocus,
  announceSelection,
  announceEditMode,
  announceSorting,
  announceFiltering,
  announceError,
  announceSuccess,
  clearAll,
})
</script>

<style scoped>
/* Using Tailwind's built-in `sr-only` utility */
</style>
