<!--
  DataGrid.vue - Root "Dumb" Component

  Responsibilities:
  1. Props validation and defaults
  2. Event emission to parent
  3. Coordinate with useGridState composable
  4. Render selected renderer (Table/Excel)

  Architecture: Smart Composable, Dumb Component pattern
-->

<script setup lang="ts">
import { ref, computed, provide, onMounted, onUnmounted, watch, nextTick } from 'vue'
import { useGridState } from '../core/useGridState'
import { createTableRenderer } from '../renderers/table/TableRenderer'
import KeyboardShortcuts from './shared/KeyboardShortcuts.vue'
import AccessibilityAnnouncer from './shared/AccessibilityAnnouncer.vue'
import ToastNotification from './shared/ToastNotification.vue'
import FilterBar from './shared/FilterBar.vue'
import { createLogger } from '../utils/logger'
import type { DataGridProps, GridEvents, RowData, GridInteractionContract, CellValue, CoreLogicCallbacks, RendererOptions, FilterConfig } from '../types'
import { isFunctionalColumn } from '../utils/functional-columns'
import { GRID_EVENT_NAMES } from '../types/events'
import HtButton from './primitives/HtButton.vue'
import { GRID_API_KEY } from '../utils/injection-keys'

// 1. ENHANCED PROPS DEFINITION with renderer support
const props = withDefaults(
  defineProps<DataGridProps>(),
  {
    data: () => [],
    columns: () => [],
    config: () => ({}),
    loading: false,
    height: 400,
    class: '',
    plugins: () => [],
    renderer: 'table',
    showKeyboardShortcuts: false,
    keyboardHelpHint: true,
  }
)

// 2. EVENTS DEFINITION (enhanced)
const emit = defineEmits<{
  'row-click': [payload: GridEvents['row-click']]
  'row-select': [payload: GridEvents['row-select']]
  'cell-click': [payload: GridEvents['cell-click']]
  'cell-double-click': [payload: GridEvents['cell-double-click']]
  'cell-right-click': [payload: GridEvents['cell-right-click']]
  'multi-sort-change': [payload: GridEvents['multi-sort-change']]
  'filter-change': [payload: GridEvents['filter-change']]
  scroll: [payload: GridEvents['scroll']]
  'column-resize-start': [payload: GridEvents['column-resize-start']]
  'column-resize': [payload: GridEvents['column-resize']]
  // Renderer lifecycle events
  'renderer-changed': [payload: { name: string; previousRenderer: string }]
  'renderer-error': [payload: { error: Error; context: string; renderer: string }]
  'toggle-selection': [payload: GridEvents['toggle-selection']]
  'clear-selection': [payload: GridEvents['clear-selection']]
}>()

// 3. ENHANCED COMPOSABLE INTEGRATION with renderer support
const mergedConfig = computed(() => ({
  ...props.config,
  renderer: props.renderer, // Ensure renderer prop overrides config
}))

// Logger for component
const logger = createLogger('DataGrid')

const gridState = useGridState(mergedConfig.value, props.plugins)
const { api } = gridState

// 4. COMPONENT REFS
const gridContainer = ref<HTMLElement>()
const rendererContainer = ref<HTMLElement>()
const rendererHost = ref<HTMLElement>()
const keyboardShortcutsRef = ref<InstanceType<typeof KeyboardShortcuts>>()
const accessibilityAnnouncerRef = ref<InstanceType<typeof AccessibilityAnnouncer>>()
const teardownFns: Array<() => void> = []

// Component state
const rendererInitialized = ref(false)
const rendererError = ref<string | null>(null)
const showShortcuts = ref(props.showKeyboardShortcuts)

// Toast notification state
const toastVisible = ref(false)
const toastMessage = ref('')
const toastType = ref<'info' | 'warn' | 'error' | 'success'>('info')
// Track currently installed plugin names to support dynamic add/remove
const installedPluginNames = ref<Set<string>>(new Set((props.plugins || []).map(p => p.name)))

// Selection mode tracking to clear selection when switching between checkbox/radio
type SelectionKind = 'checkbox' | 'radio' | null
const currentSelectionKind = ref<SelectionKind>(null)

const deriveSelectionKind = (cols: Array<any>): SelectionKind => {
  let kind: SelectionKind = null
  for (const c of cols || []) {
    if (isFunctionalColumn(c)) {
      const t = (c as any).type
      if (t === 'radio') return 'radio'
      if (t === 'checkbox') kind = kind ?? 'checkbox'
    }
  }
  return kind
}

// 5. COMPUTED PROPERTIES (enhanced)
const containerStyle = computed(() => {
  const height = typeof props.height === 'number' ? `${props.height}px` : props.height
  return {
    height,
    position: 'relative' as const,
    overflow: 'hidden',
  }
})

const containerClasses = computed(() =>
  [
    'ht-grid-container',
    'bg-[var(--ht-bg)]',
    'rounded-[var(--radius-ht-md)]',
    // Renderer-specific classes
    `ht-renderer-${props.renderer}`,
    props.class,
  ]
    .filter(Boolean)
    .join(' ')
)

const isRendererReady = computed(
  () => !gridState.loading.value && !gridState.error.value && !rendererError.value
)

// 6. ENHANCED INTERACTION CONTRACT with renderer integration
const interactionContract: GridInteractionContract = {
  // [Core → Renderer] Update visual selection state
  updateSelection: (selectedIds: Set<string | number>, reason: 'programmatic' | 'user') => {
    // Get active renderer from registry
    const rendererInstance = api.getActiveRendererInstance?.()
    if (rendererInstance?.renderer?.interactionContract) {
      rendererInstance.renderer.interactionContract.updateSelection(selectedIds, reason)
    }
  },

  // [Core → Renderer] Set or clear the active focus item
  setActive: (activeId: string | number | null) => {
    const rendererInstance = api.getActiveRendererInstance?.()
    if (rendererInstance?.renderer?.interactionContract) {
      rendererInstance.renderer.interactionContract.setActive(activeId)
    }
  },

  // [Core → Renderer] Ensure specified item is visible in viewport
  ensureVisible: (id: string | number, behavior: 'smooth' | 'auto') => {
    const rendererInstance = api.getActiveRendererInstance?.()
    if (rendererInstance?.renderer?.interactionContract) {
      rendererInstance.renderer.interactionContract.ensureVisible(id, behavior)
    }
  },

  // [Core → Renderer] Toggle edit mode for specified cell/item
  toggleEditMode: async (id: string | number, mode: 'enter' | 'exit') => {
    const rendererInstance = api.getActiveRendererInstance?.()
    if (rendererInstance?.renderer?.interactionContract) {
      return await rendererInstance.renderer.interactionContract.toggleEditMode(id, mode)
    }
    return false
  },

  // [Renderer → Core] Register event emitter for renderer to send interaction events
  registerEventEmitter: <K extends keyof GridEvents>(emit: (eventName: K, payload: GridEvents[K]) => void) => {
    // Register emitter with the current renderer
    const rendererInstance = api.getActiveRendererInstance?.()
    if (rendererInstance?.renderer?.interactionContract) {
      rendererInstance.renderer.interactionContract.registerEventEmitter(emit)
    }
  },

  // [Renderer → Core] Update scroll position in core state
  updateScrollPosition: (position: { top: number; left: number }) => {
    const rendererInstance = api.getActiveRendererInstance?.()
    if (rendererInstance?.renderer?.interactionContract) {
      rendererInstance.renderer.interactionContract.updateScrollPosition?.(position)
    }
  },

  // [Core → Renderer] Force re-render of all visible content
  refresh: () => {
    const rendererInstance = api.getActiveRendererInstance?.()
    if (rendererInstance?.renderer?.interactionContract) {
      rendererInstance.renderer.interactionContract.refresh?.()
    }
  },

  // [Renderer → Core] Emit interaction events to core logic
  emit: <K extends keyof GridEvents>(eventName: K, payload: GridEvents[K]) => {
    // Only emit events that are defined in the component's emit interface
    const supportedEvents: (keyof GridEvents)[] = GRID_EVENT_NAMES as unknown as (keyof GridEvents)[]

    if (supportedEvents.includes(eventName)) {
      // Emit supported events through component emit
      // @ts-expect-error - Complex type mapping between GridEvents and component emit signature
      emit(eventName, payload)
    } else {
      // For other events, emit through the API event system
      api.emit(eventName, payload)
    }
  },

  // [Core → Renderer] Register core logic callbacks for interaction handling
  registerCoreCallbacks: (callbacks: Partial<CoreLogicCallbacks>) => {
    const rendererInstance = api.getActiveRendererInstance?.()
    if (rendererInstance?.renderer?.interactionContract) {
      rendererInstance.renderer.interactionContract.registerCoreCallbacks?.(callbacks)
    }
  },

  // [Core → Renderer] Cleanup and destroy the interaction contract
  destroy: () => {
    const rendererInstance = api.getActiveRendererInstance?.()
    if (rendererInstance?.renderer?.interactionContract) {
      rendererInstance.renderer.interactionContract.destroy?.()
    }
  }
}

// 7. PROVIDE/INJECT PATTERN (enhanced)
provide('gridState', gridState)
provide('interactionContract', interactionContract)
provide(GRID_API_KEY, api)

// 8. RENDERER SYSTEM INTEGRATION

/**
 * Initialize the renderer system
 */
const initializeRenderer = async () => {
  try {
    if (!rendererHost.value) {
      throw new Error('Renderer host element not found')
    }

    rendererError.value = null

    // Register table renderer if not already registered
    const availableRenderers = api.getAvailableRenderers?.() || []
    if (!availableRenderers.includes('table') && api.registerRenderer) {
      logger.debug('Registering table renderer')
      api.registerRenderer('table', (options?: RendererOptions) => createTableRenderer(options))
    }

    // Initialize renderer through the registry system
    const success = await api.initializeRenderer?.(rendererHost.value)

    if (!success) {
      throw new Error('Renderer initialization returned false')
    }

    rendererInitialized.value = true
    logger.debug(`Renderer system initialized with '${props.renderer}' renderer`)
    return true
  } catch (error) {
    const errorMessage = error instanceof Error ? error.message : 'Unknown error'
    rendererError.value = errorMessage
    logger.error('Renderer initialization failed:', { module: 'DataGrid' }, error)

    emit('renderer-error', {
      error: error instanceof Error ? error : new Error(errorMessage),
      context: 'initialization',
      renderer: props.renderer,
    })

    return false
  }
}

/**
 * Switch to a different renderer
 */
const switchRenderer = async (rendererName: string) => {
  if (!rendererInitialized.value) {
    logger.warn('Cannot switch renderer - system not initialized')
    return false
  }

  try {
    const previousRenderer = api.getRenderer?.() || 'unknown'
    const success = await api.setRenderer?.(rendererName)

    if (success) {
      emit('renderer-changed', {
        name: rendererName,
        previousRenderer,
      })
      logger.debug(`Switched from '${previousRenderer}' to '${rendererName}' renderer`)
    }

    return success
  } catch (error) {
    logger.error(`Failed to switch to renderer '${rendererName}':`, { module: 'DataGrid' }, error)
    emit('renderer-error', {
      error: error instanceof Error ? error : new Error('Renderer switch failed'),
      context: 'switch',
      renderer: rendererName,
    })
    return false
  }
}

// 9. KEYBOARD SHORTCUTS AND ACCESSIBILITY INTEGRATION
const handleShowShortcuts = () => {
  showShortcuts.value = true
}

const handleCloseShortcuts = () => {
  showShortcuts.value = false
}

// Determine which features are enabled based on loaded plugins
const keyboardFeaturesEnabled = computed(() => {
  const loadedPlugins = api.listPlugins().map(p => p.name)
  return {
    editing: loadedPlugins.includes('core-cell-editing'),
    selection: loadedPlugins.includes('core-selection'),
  }
})

// Set up grid event listeners for accessibility announcements
const setupAccessibilityIntegration = () => {
  if (!accessibilityAnnouncerRef.value) return

  const announcer = accessibilityAnnouncerRef.value

  // Listen for focus changes
  api.on?.('cell-focus-change', (payload: { rowId: string | number | null; columnId: string | null }) => {
    if (payload.rowId !== null && payload.columnId !== null) {
      // For now, we'll announce the cell change with available info
      // TODO: Convert rowId/columnId to indices when needed
      announcer.announceCellFocus(0, 0, undefined, payload.columnId)
    }
  })

  // Listen for selection changes
  api.on?.('row-select', (payload: { rowId: string | number; selected: boolean; selectedRows: (string | number)[]; row?: RowData }) => {
    if (payload.selectedRows) {
      const totalRows = gridState.data.value.visibleRows.length
      announcer.announceSelection(payload.selectedRows.length, totalRows)
    }
  })

  // Listen for edit mode changes
  api.on?.('cell-edit-start', (payload: { rowId: string | number; columnId: string; value: CellValue }) => {
    // Convert rowId and columnId to indices for accessibility announcements
    const rowIndex = gridState.data.value.visibleRows.findIndex(row => (row.id ?? row.index) === payload.rowId)
    const columnIndex = gridState.columns.value.findIndex(col => col.field === payload.columnId)
    const column = gridState.columns.value.find(col => col.field === payload.columnId)
    
    if (rowIndex >= 0 && columnIndex >= 0) {
      announcer.announceEditMode(true, {
        row: rowIndex,
        column: columnIndex,
        title: column?.title,
      })
    }
  })

  api.on?.('cell-edit-complete', () => {
    announcer.announceEditMode(false)
  })

  api.on?.('cell-edit-cancel', () => {
    announcer.announceEditMode(false)
  })

  // Listen for sorting changes (multi-sort only)
  api.on?.('multi-sort-change', (config: import('../types').MultiSortConfig) => {
    if (!config || !config.fields || config.fields.length === 0) {
      announcer.announceSorting('All columns', 'none')
      return
    }
    const [first] = config.fields
    if (!first) return
    const column = gridState.columns.value.find(c => c.field === first.field)
    if (column) {
      announcer.announceSorting(column.title, first.direction)
    }
  })

  // Listen for filtering changes
  api.on?.('filter-change', (payload: FilterConfig[]) => {
    const totalRows = props.data.length
    const visibleRows = gridState.data.value.visibleRows.length
    const activeFilters = payload.length || 0
    announcer.announceFiltering(activeFilters, totalRows, visibleRows)
  })

  // Listen for errors
  api.on?.('plugin-error', (payload: { error: Error }) => {
    announcer.announceError(`Plugin error: ${payload.error.message}`)
  })
}

// Bridge column resize lifecycle events to parent listeners
const columnResizeStartOff = api.on?.('column-resize-start', payload => {
  emit('column-resize-start', payload)
})
if (columnResizeStartOff) {
  teardownFns.push(columnResizeStartOff)
}

const columnResizeOff = api.on?.('column-resize', payload => {
  emit('column-resize', payload)
})
if (columnResizeOff) {
  teardownFns.push(columnResizeOff)
}

// Bridge core/public events from API to component emits (clean path)
const cellClickOff = api.on?.('cell-click', payload => {
  emit('cell-click', payload as any)
})
if (cellClickOff) teardownFns.push(cellClickOff)

const rowClickOff = api.on?.('row-click', payload => {
  emit('row-click', payload as any)
})
if (rowClickOff) teardownFns.push(rowClickOff)

const cellDblClickOff = api.on?.('cell-double-click', payload => {
  emit('cell-double-click', payload as any)
})
if (cellDblClickOff) teardownFns.push(cellDblClickOff)

const cellRightClickOff = api.on?.('cell-right-click', payload => {
  emit('cell-right-click', payload as any)
})
if (cellRightClickOff) teardownFns.push(cellRightClickOff)

const rowSelectOff = api.on?.('row-select', payload => {
  emit('row-select', payload as any)
})
if (rowSelectOff) teardownFns.push(rowSelectOff)

const toggleSelectionOff = api.on?.('toggle-selection', payload => {
  emit('toggle-selection', payload as any)
})
if (toggleSelectionOff) teardownFns.push(toggleSelectionOff)

const clearSelectionOff = api.on?.('clear-selection', payload => {
  emit('clear-selection', payload as any)
})
if (clearSelectionOff) teardownFns.push(clearSelectionOff)

const multiSortChangeOff = api.on?.('multi-sort-change', payload => {
  emit('multi-sort-change', payload as any)
})
if (multiSortChangeOff) teardownFns.push(multiSortChangeOff)

const filterChangeOff = api.on?.('filter-change', payload => {
  emit('filter-change', payload as any)
})
if (filterChangeOff) teardownFns.push(filterChangeOff)

const scrollOff = api.on?.('scroll', payload => {
  emit('scroll', payload as any)
})
if (scrollOff) teardownFns.push(scrollOff)

// Listen for multi-sort disabled warning
const multiSortDisabledOff = api.on?.('multi-sort-disabled-warning', (payload: { attempted: boolean; message: string }) => {
  showToast(payload.message, 'warn')
})
if (multiSortDisabledOff) teardownFns.push(multiSortDisabledOff)

// Listen for user notifications (general purpose)
const userNotificationOff = api.on?.('user-notification', (payload: { type: 'info' | 'warn' | 'error' | 'success'; message: string; details?: string }) => {
  showToast(payload.message, payload.type)
})
if (userNotificationOff) teardownFns.push(userNotificationOff)

// Helper function to show toast notifications
const showToast = (message: string, type: 'info' | 'warn' | 'error' | 'success' = 'info') => {
  toastMessage.value = message
  toastType.value = type
  toastVisible.value = true
}

const handleToastClose = () => {
  toastVisible.value = false
}

// Clean system: renderer → DataGrid emits are handled via interactionContract above.

// 10. LIFECYCLE HOOKS (enhanced)
onMounted(async () => {
  // Initialize grid with props data
  if (props.data.length > 0) {
    api.setData(props.data)
  }

  if (props.columns.length > 0) {
    api.setColumns(props.columns)
  }

  // Set loading state
  gridState.__internal.setLoading(props.loading)

  // Initialize renderer system
  await nextTick() // Ensure DOM is ready
  await initializeRenderer()

  // Set up accessibility integration
  await nextTick() // Ensure announcer component is ready
  setupAccessibilityIntegration()

  // Initialize current selection kind from processed columns
  currentSelectionKind.value = deriveSelectionKind(gridState.columns.value as any)
})

// 11. PROP WATCHERS (enhanced)
watch(
  () => props.data,
  newData => {
    api.setData(newData)
  }
)

watch(
  () => props.columns,
  newColumns => {
    api.setColumns(newColumns)
  }
)

// React to processed columns change to detect selection kind switches
watch(
  () => gridState.columns.value,
  (processedColumns) => {
    const nextKind = deriveSelectionKind(processedColumns as any)
    if (nextKind !== currentSelectionKind.value) {
      // Clear selection when switching between checkbox and radio
      ;(api as any).clearSelection?.()
      // Also reset renderer-side selection state immediately to avoid stale highlights
      interactionContract.updateSelection?.(new Set(), 'programmatic')
      // Align plugin selection mode if available
      if (nextKind === 'radio') {
        ;(api as any).setSelectionMode?.('single')
      } else if (nextKind === 'checkbox') {
        ;(api as any).setSelectionMode?.('multi')
      }
      currentSelectionKind.value = nextKind
    }
  }
)

watch(
  () => props.loading,
  newLoading => {
    gridState.__internal.setLoading(newLoading)
  }
)

watch(
  () => mergedConfig.value,
  newConfig => {
    api.updateConfig(newConfig)
  }
)

// Watch for renderer prop changes
watch(
  () => props.renderer,
  async (newRenderer, oldRenderer) => {
    if (newRenderer !== oldRenderer && rendererInitialized.value) {
      await switchRenderer(newRenderer)
    }
  }
)

// Watch for data/column changes and trigger re-render
watch(
  [() => gridState.data.value, () => gridState.columns.value],
  () => {
    // Vue's reactivity system will automatically handle re-rendering
    logger.debug('Data or columns changed, Vue will handle re-render')
  },
  { deep: false }
)

// 12. RESIZE HANDLING
let resizeObserver: ResizeObserver | null = null

onMounted(() => {
  if (rendererContainer.value) {
    resizeObserver = new ResizeObserver(entries => {
      for (const entry of entries) {
        const { width, height } = entry.contentRect
        api.resizeRenderer?.(width, height)
      }
    })

    resizeObserver.observe(rendererContainer.value)
  }
})

// 13. CLEANUP (enhanced)
onUnmounted(() => {
  if (resizeObserver) {
    resizeObserver.disconnect()
  }

  // Renderer cleanup is handled by useGridState composable

  teardownFns.forEach(off => {
    try {
      off()
    } catch (error) {
      logger.warn('Error cleaning up listener', error)
    }
  })
  teardownFns.length = 0
})

// Dynamically load/unload plugins when props.plugins changes (for demos/toggles)
watch(
  () => props.plugins,
  async (next) => {
    const nextNames = new Set((next || []).map(p => p.name))
    const current = installedPluginNames.value

    // Unload removed plugins
    for (const name of current) {
      if (!nextNames.has(name)) {
        try {
          await api.unloadPlugin?.(name)
        } catch (error) {
          logger.warn(`Failed to unload plugin ${name}:`, error)
        }
      }
    }

    // Load newly added plugins
    const nextByName = new Map((next || []).map(p => [p.name, p]))
    for (const name of nextNames) {
      if (!current.has(name)) {
        const plugin = nextByName.get(name)
        if (plugin) {
          try {
            await api.loadPlugin?.(plugin)
          } catch (error) {
            logger.warn(`Failed to load plugin ${name}:`, error)
          }
        }
      }
    }

    installedPluginNames.value = nextNames

    // Re-apply columns to ensure functional columns are normalized with current plugin set
    // This lets drag handle enable/disable state reflect the latest plugins
    if (props.columns && props.columns.length > 0) {
      try {
        api.setColumns(props.columns)
      } catch (error) {
        logger.warn('Failed to re-apply columns after plugin change:', error)
      }
    }
  },
  { deep: false }
)

// 14. EXPOSE METHODS for external control
defineExpose({
  switchRenderer,
  initializeRenderer,
  getAvailableRenderers: () => api.getAvailableRenderers?.() || [],
  getCurrentRenderer: () => api.getRenderer?.() || 'unknown',
  renderNow: () => api.renderWithActiveRenderer?.(),
  gridState,
  api,
})
</script>

<template>
  <div
    ref="gridContainer"
    :class="containerClasses"
    :style="containerStyle"
    role="grid"
    :aria-label="`Data grid with ${gridState.data.value.visibleRows.length} rows using ${props.renderer} renderer`"
    :aria-rowcount="gridState.data.value.visibleRows.length"
    :aria-colcount="gridState.columns.value.length"
    :data-renderer="props.renderer"
  >
    <!-- Loading State -->
    <div v-if="gridState.loading.value" class="ht-overlay ht-overlay-bg z-30">
      <div class="flex items-center space-x-2">
        <div class="animate-spin w-4 h-4 border-2 border-[var(--ht-primary)] border-t-transparent rounded-full"></div>
        <span class="text-[var(--ht-text-muted)]">Loading...</span>
      </div>
    </div>

    <!-- Grid Error State -->
    <div v-else-if="gridState.error.value" class="ht-overlay bg-[var(--ht-bg)] z-30">
      <div class="ht-overlay-panel">
        <div class="text-[var(--ht-danger)] mb-2">⚠️ Grid Error</div>
        <div class="text-[var(--ht-text)]">{{ gridState.error.value }}</div>
      </div>
    </div>

    <!-- Renderer Error State -->
    <div v-else-if="rendererError" class="ht-overlay bg-[var(--ht-bg)] z-30">
      <div class="ht-overlay-panel">
        <div class="text-[var(--ht-danger)] mb-2">⚠️ Renderer Error</div>
        <div class="text-[var(--ht-text)] text-sm mb-4">{{ rendererError }}</div>
        <HtButton size="sm" variant="primary" @click="initializeRenderer">Retry</HtButton>
      </div>
    </div>

    <!-- Filter Bar -->
    <FilterBar :api="api" />

    <!-- Renderer Container -->
    <div
      v-if="isRendererReady"
      ref="rendererContainer"
      class="h-full w-full ht-renderer-container"
      :class="`ht-renderer-${props.renderer}`"
    >
      <!-- Renderer Host (managed by RendererRegistry) -->
      <div ref="rendererHost" class="h-full w-full" />

      <!-- Empty State -->
      <div
        v-if="gridState.data.value.visibleRows.length === 0"
        class="ht-overlay ht-empty-overlay text-[var(--ht-text-muted)]"
      >
        <div class="ht-overlay-panel">
          <div class="text-4xl mb-2">📄</div>
          <div>No data available</div>
          <div class="text-xs mt-1 text-[var(--ht-text-muted)]">Renderer: {{ props.renderer }}</div>
        </div>
      </div>
    </div>

    <!-- Keyboard Navigation Components -->
    <KeyboardShortcuts
      ref="keyboardShortcutsRef"
      :show-shortcuts="showShortcuts"
      :show-help-hint="props.keyboardHelpHint"
      :enable-editing="keyboardFeaturesEnabled.editing"
      :enable-selection="keyboardFeaturesEnabled.selection"
      @show="handleShowShortcuts"
      @close="handleCloseShortcuts"
    />

    <!-- Accessibility Announcer -->
    <AccessibilityAnnouncer ref="accessibilityAnnouncerRef" />

    <!-- Toast Notification -->
    <ToastNotification
      :visible="toastVisible"
      :message="toastMessage"
      :type="toastType"
      :duration="4000"
      @close="handleToastClose"
    />
  </div>
</template>

