<script setup lang="ts">
import { computed, onMounted, onUnmounted, ref } from 'vue'
import type { GridAPI, FilterConfig } from '../../types'
import { createLogger } from '../../utils/logger'

const props = defineProps<{ api: GridAPI }>()
const logger = createLogger({ module: 'FilterBar' })

const filters = ref<FilterConfig[]>([])
const total = ref(0)
const count = ref(0)
const duration = ref(0)
const progress = ref<{ processed: number; total: number } | null>(null)
const cleanupFns = ref<Array<() => void>>([])

function refreshFromAPI() {
  try {
    filters.value = (props.api.getState()?.config?.filters || []) as FilterConfig[]
  } catch (err) {
    logger.warn('Failed to refresh filters from API', err)
  }
}

onMounted(() => {
  refreshFromAPI()
  if (props.api.on) {
    cleanupFns.value.push(props.api.on('filter-change', fs => { filters.value = fs || [] }))
    cleanupFns.value.push(
      props.api.on('data-pipeline-complete', payload => {
        total.value = payload?.originalCount ?? 0
        count.value = payload?.processedCount ?? 0
        duration.value = Math.round(payload?.processingTime ?? 0)
        progress.value = null
      })
    )
    cleanupFns.value.push(
      props.api.on('filter-progress', payload => {
        progress.value = payload ? { processed: payload.processed ?? 0, total: payload.total ?? 0 } : null
      })
    )
  }
})

onUnmounted(() => {
  cleanupFns.value.forEach(off => {
    try {
      off()
    } catch (err) {
      logger.warn('Failed to cleanup event listener', err)
    }
  })
  cleanupFns.value = []
})

function clearAll() {
  props.api.updateConfig({ filters: [] })
}

function cancelWorker() {
  try {
    props.api.emit('filter-cancel', null)
  } catch (err) {
    logger.warn('Failed to cancel worker', err)
  }
  progress.value = null
}

function removeFilter(f: FilterConfig) {
  const rest = filters.value.filter(x => !(x.field === f.field && x.operator === f.operator && x.value === f.value))
  props.api.updateConfig({ filters: rest })
}

function editFilter(f: FilterConfig) {
  try {
    props.api.emit('column-open-filter', { field: f.field })
  } catch (err) {
    logger.warn('Failed to emit column-open-filter event', err)
  }
}

function labelOf(f: FilterConfig): string {
  const opMap: Record<string,string> = {
    equals: '=',
    contains: 'Contains',
    startsWith: 'Starts with',
    endsWith: 'Ends with',
    gt: '>',
    gte: '≥',
    lt: '<',
    lte: '≤',
    between: 'Between',
    in: 'Includes',
    oneOf: 'One of',
    notIn: 'Excludes',
    notEquals: '≠'
  }
  const v = Array.isArray(f.value)
    ? (f.operator === 'between' ? `${f.value[0]}~${f.value[1]}` : `${(f.value as any[]).length} items`)
    : String(f.value)
  return `${f.field} ${opMap[f.operator] ?? f.operator} ${v}`
}

const hasFilters = computed(() => (filters.value?.length ?? 0) > 0)
</script>

<template>
  <div class="ht-filterbar mb-2" v-if="hasFilters || total">
    <div class="flex items-center justify-between gap-2">
      <div class="flex flex-wrap items-center gap-2">
        <template v-for="f in filters" :key="labelOf(f)">
          <span class="inline-flex items-center gap-2 rounded border border-[var(--ht-border)] bg-[var(--ht-bg-subtle)] px-2 py-1 text-xs text-[var(--ht-text)] cursor-pointer" @click="editFilter(f)">
            <span class="truncate max-w-[220px]">{{ labelOf(f) }}</span>
            <button class="text-[var(--ht-text-muted)] hover:text-[var(--ht-danger)]" @click.stop="removeFilter(f)">×</button>
          </span>
        </template>
        <button v-if="hasFilters" class="text-xs text-[var(--ht-primary)] hover:underline" @click="clearAll">Clear All</button>
      </div>
      <div class="text-xs text-[var(--ht-text-muted)]" aria-live="polite" aria-atomic="true">
        <template v-if="total">Showing {{ count || total }} / {{ total }}<span v-if="duration"> ({{ duration }} ms)</span></template>
        <template v-if="progress && progress.total">
          <div class="mt-1 flex items-center gap-2">
            <div class="relative h-2 w-24 rounded bg-[var(--ht-bg-subtle)]">
              <div class="h-2 rounded bg-[var(--ht-primary)]" :style="{ width: Math.min(100, Math.round((progress.processed / progress.total) * 100)) + '%' }"></div>
            </div>
            <span>{{ Math.min(100, Math.round((progress.processed / progress.total) * 100)) }}%</span>
            <button class="px-2 py-0.5 rounded border border-[var(--ht-border)] text-[var(--ht-text-muted)] hover:text-[var(--ht-danger)]" @click="cancelWorker">Cancel</button>
          </div>
        </template>
      </div>
    </div>
  </div>
  <div v-else></div>
  
</template>

