<template>
  <div
    class="ht-actions-cell"
    data-ht-functional="actions"
    @click.stop
    @dblclick.stop
    @contextmenu.stop
    @mousedown.stop
    @mouseup.stop
    @pointerdown.stop
    @touchstart.stop
    @touchend.stop
  >
    <slot
      name="actions"
      :row-data="rowData"
      :row-index="rowIndex"
    >
      <!-- Default buttons if slot not provided and options.buttons exist -->
      <template v-if="hasDefaultButtons">
        <HtButton
          v-for="(button, index) in visibleButtons"
          :key="index"
          variant="secondary"
          size="sm"
          class="gap-1"
          :disabled="isButtonDisabled(button)"
          @click="handleButtonClick(button)"
        >
          <span v-if="button.icon" class="ht-action-icon" v-html="button.icon" />
          <span class="ht-action-label">{{ button.label }}</span>
        </HtButton>
      </template>
    </slot>
  </div>
</template>

<script setup lang="ts">
import { computed } from 'vue'
import type { RowData, ActionsColumnOptions } from '../../types'
import HtButton from '../primitives/HtButton.vue'

interface Props {
  /** Row data */
  rowData: RowData
  /** Zero-based row index */
  rowIndex: number
  /** Column configuration options */
  options?: ActionsColumnOptions
}

const props = withDefaults(defineProps<Props>(), {
  options: () => ({}),
})

const hasDefaultButtons = computed(() => {
  return props.options?.buttons && props.options.buttons.length > 0
})

const visibleButtons = computed(() => {
  if (!props.options?.buttons) return []

  return props.options.buttons.filter(button => {
    // Check if button should be visible
    if (button.visible) {
      return button.visible(props.rowData)
    }
    return true
  })
})

function isButtonDisabled(button: NonNullable<ActionsColumnOptions['buttons']>[number]): boolean {
  if (button.disabled) {
    return button.disabled(props.rowData)
  }
  return false
}

function handleButtonClick(button: NonNullable<ActionsColumnOptions['buttons']>[number]) {
  if (!isButtonDisabled(button)) {
    button.onClick(props.rowData)
  }
}
</script>

<style scoped>
.ht-actions-cell {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.ht-action-icon {
  display: inline-flex;
  width: 1rem;
  height: 1rem;
}

.ht-action-label {
  white-space: nowrap;
}
</style>
