/**
 * Renderer Registry - Dynamic renderer management and switching
 *
 * Manages multiple renderer instances and provides safe switching
 * between TableRenderer and ExcelRenderer (or other custom renderers).
 */

import type {
  RendererFactory,
  RendererInstance,
  RendererOptions,
  VirtualRow,
} from './renderer-interface'
import type { GridAPI, GridState, ColumnDef, RowData } from '../../types'
import { createLogger } from '../../utils/logger'

/**
 * Registry for managing renderer instances
 */
export class RendererRegistry {
  private renderers = new Map<string, RendererFactory>()
  private instances = new Map<string, RendererInstance>()
  private currentRenderer: string | null = null
  private logger = createLogger('RendererRegistry')

  /**
   * Register a renderer factory
   */
  register(name: string, factory: RendererFactory): void {
    this.renderers.set(name, factory)
  }

  /**
   * Unregister a renderer
   */
  unregister(name: string): void {
    // Cleanup instance if exists
    const instance = this.instances.get(name)
    if (instance) {
      instance.renderer.unmount()
    }

    this.renderers.delete(name)
    this.instances.delete(name)

    // If this was the current renderer, clear it
    if (this.currentRenderer === name) {
      this.currentRenderer = null
    }
  }

  /**
   * Get available renderer names
   */
  getAvailable(): string[] {
    return Array.from(this.renderers.keys())
  }

  /**
   * Check if a renderer is registered
   */
  has(name: string): boolean {
    return this.renderers.has(name)
  }

  /**
   * Create or get a renderer instance
   */
  getInstance(
    name: string,
    _host: HTMLElement,
    _api: GridAPI,
    options?: RendererOptions
  ): RendererInstance | null {
    // Check if factory exists
    const factory = this.renderers.get(name)
    if (!factory) {
      this.logger.error(`Renderer '${name}' is not registered`)
      return null
    }

    // Check if instance already exists and can be reused
    let instance = this.instances.get(name)
    if (instance) {
      // Update with new options if provided
      if (options && instance.renderer.updateConfig) {
        instance.renderer.updateConfig(options)
      }
      return instance
    }

    // Create new instance
    try {
      const renderer = factory(options)
      instance = {
        renderer,
        factory,
        options: options || {},
        metadata: {
          createdAt: new Date(),
          lastUsed: new Date(),
          renderCount: 0,
        },
      }

      this.instances.set(name, instance)
      return instance
    } catch (error) {
      this.logger.error(`Failed to create renderer '${name}':`, error)
      return null
    }
  }

  /**
   * Switch to a different renderer
   */
  async switchTo(
    name: string,
    host: HTMLElement,
    api: GridAPI,
    options?: RendererOptions
  ): Promise<RendererInstance | null> {
    try {
      // Deactivate current renderer
      if (this.currentRenderer) {
        const current = this.instances.get(this.currentRenderer)
        if (current) {
          await current.renderer.unmount()
        }
      }

      // Get or create new renderer instance
      const newInstance = this.getInstance(name, host, api, options)
      if (!newInstance) {
        return null
      }

      // Activate new renderer
      await newInstance.renderer.mount(host, api)
      newInstance.metadata.lastUsed = new Date()

      // Force a render to ensure proper display
      const state = api.getState()
      if (state) {
        const virtualRows = this.buildVirtualRows(state, state.data || [])
        newInstance.renderer.render(state, state.columns || [], virtualRows)
        newInstance.metadata.renderCount++
      }

      this.currentRenderer = name
      return newInstance
    } catch (error) {
      this.logger.error(`Failed to switch to renderer '${name}':`, error)
      return null
    }
  }

  /**
   * Get the current active renderer
   */
  getCurrent(): RendererInstance | null {
    if (!this.currentRenderer) return null
    return this.instances.get(this.currentRenderer) || null
  }

  /**
   * Get the current renderer name
   */
  getCurrentName(): string | null {
    return this.currentRenderer
  }

  /**
   * Force render on current renderer
   */
  render(state: GridState, columns: ColumnDef[], rows: RowData[]): void {
    const current = this.getCurrent()
    if (current) {
      const virtualRows = this.buildVirtualRows(state, rows)
      current.renderer.render(state, columns, virtualRows)
      current.metadata.renderCount++
    }
  }

  /**
   * Update size on current renderer
   */
  updateSize(width: number, height: number): void {
    const current = this.getCurrent()
    if (current && current.renderer.resize) {
      current.renderer.resize(width, height)
    }
  }

  /**
   * Cleanup all renderer instances
   */
  cleanup(): void {
    for (const [name, instance] of this.instances) {
      try {
        instance.renderer.unmount()
      } catch (error) {
        this.logger.error(`Error cleaning up renderer '${name}':`, error)
      }
    }

    this.instances.clear()
    this.currentRenderer = null
  }

  /**
   * Get renderer statistics
   */
  getStats(): {
    registered: number
    instances: number
    current: string | null
    renderers: Array<{
      name: string
      hasInstance: boolean
      renderCount: number
      lastUsed?: Date
    }>
  } {
    const renderers = Array.from(this.renderers.keys()).map(name => {
      const instance = this.instances.get(name)
      return {
        name,
        hasInstance: !!instance,
        renderCount: instance?.metadata.renderCount || 0,
        lastUsed: instance?.metadata.lastUsed,
      }
    })

    return {
      registered: this.renderers.size,
      instances: this.instances.size,
      current: this.currentRenderer,
      renderers,
    }
  }

  private buildVirtualRows(state: GridState, rows: RowData[]): VirtualRow[] {
    const rowHeight = state?.config?.rowHeight ?? 32

    return rows.map((row, index) => {
      const rawId = row?.id
      const id = typeof rawId === 'string' || typeof rawId === 'number' ? rawId : index

      return {
        id,
        index,
        data: row,
        top: index * rowHeight,
        height: rowHeight,
        visible: true,
      }
    })
  }
}

/**
 * Global renderer registry instance
 */
export const rendererRegistry = new RendererRegistry()

/**
 * Helper function to register common renderers
 */
export function registerCommonRenderers(): void {
  // This would register TableRenderer, ExcelRenderer, etc.
  // Implementation depends on actual renderer implementations
  const logger = createLogger('RendererRegistry')
  logger.info('Common renderers registration would happen here')
}

/**
 * Helper to check renderer compatibility
 */
export function checkRendererCompatibility(
  name: string,
  state: GridState
): {
  compatible: boolean
  issues: string[]
  recommendations: string[]
} {
  const issues: string[] = []
  const recommendations: string[] = []

  // Basic compatibility checks
  if (!rendererRegistry.has(name)) {
    issues.push(`Renderer '${name}' is not registered`)
  }

  // Check data size for performance considerations
  const dataSize = state.data?.length || 0
  if (name === 'excel' && dataSize > 50000) {
    recommendations.push('Consider using pagination for large datasets in Excel mode')
  }

  if (name === 'table' && dataSize > 100000) {
    recommendations.push('Enable virtualization for large datasets in Table mode')
  }

  // Check feature compatibility
  const hasGrouping = !!(state as any).grouping
  const hasPivot = !!(state as any).pivot

  if (name === 'table' && (hasGrouping || hasPivot)) {
    recommendations.push('Excel renderer might provide better support for grouping/pivot features')
  }

  return {
    compatible: issues.length === 0,
    issues,
    recommendations,
  }
}

/**
 * Performance monitoring for renderer switching
 */
export class RendererPerformanceMonitor {
  private metrics = new Map<
    string,
    {
      switchCount: number
      avgSwitchTime: number
      lastSwitch: number
      errors: number
    }
  >()

  recordSwitch(rendererName: string, duration: number, success: boolean): void {
    const current = this.metrics.get(rendererName) || {
      switchCount: 0,
      avgSwitchTime: 0,
      lastSwitch: 0,
      errors: 0,
    }

    current.switchCount++
    current.lastSwitch = Date.now()

    if (success) {
      // Update average switch time
      current.avgSwitchTime =
        (current.avgSwitchTime * (current.switchCount - 1) + duration) / current.switchCount
    } else {
      current.errors++
    }

    this.metrics.set(rendererName, current)
  }

  getMetrics(): Map<string, any> {
    return new Map(this.metrics)
  }

  reset(): void {
    this.metrics.clear()
  }
}

/**
 * Default renderer selection logic
 */
export function selectOptimalRenderer(state: GridState): string {
  const dataSize = state.data?.length || 0
  const columnCount = state.columns?.length || 0
  const hasComplexFeatures = !!(state as any).grouping || !!(state as any).pivot

  // Simple heuristic for renderer selection
  if (hasComplexFeatures || dataSize > 10000) {
    return 'excel' // Better for complex operations
  }

  if (columnCount > 20) {
    return 'excel' // Better for wide tables
  }

  return 'table' // Default to table renderer
}

/**
 * Create a renderer registry instance
 */
export function createRendererRegistry(): RendererRegistry {
  return new RendererRegistry()
}

/**
 * Switch to a different renderer using the global registry
 */
export function switchRenderer(
  name: string,
  host: HTMLElement,
  api: GridAPI,
  options?: RendererOptions
): Promise<RendererInstance | null> {
  return rendererRegistry.switchTo(name, host, api, options)
}

/**
 * Get the current active renderer from global registry
 */
export function getCurrentRenderer(): RendererInstance | null {
  return rendererRegistry.getCurrent()
}

/**
 * Get all available renderers from global registry
 */
export function getAvailableRenderers(): string[] {
  return rendererRegistry.getAvailable()
}
