/**
 * DragAutoScroller - Automatic scrolling during drag operations
 *
 * Provides smooth auto-scrolling when dragging near container edges
 * Supports both vertical and horizontal scrolling with configurable zones
 */

export interface AutoScrollOptions {
  /** Distance from edge to trigger scrolling (px) */
  scrollZone?: number
  /** Scroll speed (px per frame) */
  scrollSpeed?: number
  /** Enable horizontal scrolling */
  horizontal?: boolean
  /** Enable vertical scrolling */
  vertical?: boolean
}

export class DragAutoScroller {
  private scrollContainer: HTMLElement | null = null
  private scrollInterval: number | null = null
  private readonly scrollZone: number
  private readonly scrollSpeed: number
  private readonly horizontal: boolean
  private readonly vertical: boolean
  // Keep latest desired scroll direction; interval loop reads these continuously
  private currentScrollX: number = 0
  private currentScrollY: number = 0

  constructor(container: HTMLElement, options: AutoScrollOptions = {}) {
    this.scrollContainer = container
    this.scrollZone = options.scrollZone ?? 50
    this.scrollSpeed = options.scrollSpeed ?? 10
    this.horizontal = options.horizontal ?? false
    this.vertical = options.vertical ?? true
  }

  start(event: DragEvent) {
    if (!this.scrollContainer) return

    const rect = this.scrollContainer.getBoundingClientRect()
    const { clientX, clientY } = event

    // Compute desired scroll vector for this frame
    let nextX = 0
    let nextY = 0

    // Calculate vertical scroll
    if (this.vertical) {
      const distanceFromTop = clientY - rect.top
      const distanceFromBottom = rect.bottom - clientY

      if (distanceFromTop < this.scrollZone && distanceFromTop >= 0) {
        nextY = -this.scrollSpeed
      } else if (distanceFromBottom < this.scrollZone && distanceFromBottom >= 0) {
        nextY = this.scrollSpeed
      }
    }

    // Calculate horizontal scroll
    if (this.horizontal) {
      const distanceFromLeft = clientX - rect.left
      const distanceFromRight = rect.right - clientX

      if (distanceFromLeft < this.scrollZone && distanceFromLeft >= 0) {
        nextX = -this.scrollSpeed
      } else if (distanceFromRight < this.scrollZone && distanceFromRight >= 0) {
        nextX = this.scrollSpeed
      }
    }

    // Update current desired vector
    this.currentScrollX = nextX
    this.currentScrollY = nextY

    // Start or stop scrolling based on calculated values
    if (this.currentScrollX !== 0 || this.currentScrollY !== 0) {
      this.ensureInterval()
    } else {
      this.stop()
    }
  }

  private ensureInterval() {
    if (this.scrollInterval) return

    this.scrollInterval = window.setInterval(() => {
      if (!this.scrollContainer) return

      if (this.currentScrollY !== 0) {
        this.scrollContainer.scrollTop += this.currentScrollY
      }
      if (this.currentScrollX !== 0) {
        this.scrollContainer.scrollLeft += this.currentScrollX
      }

      // If vector becomes zero (updated by subsequent start calls), stop interval
      if (this.currentScrollX === 0 && this.currentScrollY === 0) {
        this.stop()
      }
    }, 16) // ~60fps
  }

  stop() {
    if (this.scrollInterval) {
      clearInterval(this.scrollInterval)
      this.scrollInterval = null
    }
  }

  destroy() {
    this.stop()
    this.scrollContainer = null
  }
}
