<!--
  BodyCell.vue - Individual Cell Component

  Responsibilities:
  1. Render cell content with formatting
  2. Handle cell-level interactions
  3. Focus and edit state management
  4. Accessibility compliance

  Vue 3 Pattern: Minimal reactive overhead
-->

<script setup lang="ts">
import { computed } from 'vue'
import type { RowData, InternalColumnDef, CellValue } from '../../../types'
import { isFunctionalColumn } from '../../../utils/functional-columns'
import { SeqCell, ActionsCell, CheckboxCell, RadioCell, DragCell } from '../../functional'
import { TextCell, NumberCell, DateCell, BooleanCell } from '../cells'

// Props
const props = defineProps<{
  row: RowData
  column: InternalColumnDef
  value: CellValue
  focused: boolean
  rowSelected: boolean
  rowIndex: number
  columnIndex: number
  borderClasses?: string
}>()

// Emits
interface SelectionTogglePayload {
  rowData: RowData
  rowIndex: number
  column: InternalColumnDef
  selectionType: 'checkbox' | 'radio'
}

const emit = defineEmits<{
  'cell-click': [column: InternalColumnDef, value: CellValue, event: MouseEvent]
  'cell-double-click': [column: InternalColumnDef, value: CellValue, event: MouseEvent]
  'cell-right-click': [column: InternalColumnDef, value: CellValue, event: MouseEvent]
  'toggle-selection': [payload: SelectionTogglePayload]
  'row-drag-start': [payload: { row: RowData; rowIndex: number; event: DragEvent }]
}>()

// Computed
const cellStyle = computed(() => ({
  width: `${props.column.width || 150}px`,
  minWidth: `${props.column.minWidth || 50}px`,
  maxWidth: `${props.column.maxWidth || 400}px`,
}))

const cellClasses = computed(() =>
  [
    'ht-body-cell',
    'px-3 py-1',
    props.borderClasses || '', // Use dynamic border classes
    'flex items-center',
    'text-[var(--ht-text)]',
    'text-sm',
    'truncate',
    'relative',
    {
      'bg-[var(--ht-cell-focused)]': props.focused,
      'ring-2 ring-[var(--ht-primary)] ring-inset': props.focused,
      'cursor-text': props.column.dataType === 'text',
      'cursor-pointer': !props.column.dataType || props.column.dataType !== 'text',
      'ht-cell-interactive': props.column.dataType === 'custom' || props.column.formatter,
      // Add separator classes for pinned columns
      'is-last-pinned': props.column.isLastPinned,
      'is-first-pinned-right': props.column.isFirstRightPinned,
    },
  ].filter(Boolean)
)

// Content formatting (only for custom formatters and TextCell fallback)
const formattedValue = computed(() => {
  // Custom formatter takes precedence
  if (props.column.formatter) {
    return props.column.formatter(props.value, props.row)
  }

  // Fallback for TextCell (type-specific cells handle their own formatting)
  return props.value ?? ''
})

// Event handlers
const handleClick = (event: MouseEvent) => {
  emit('cell-click', props.column, props.value, event)
}

const handleDoubleClick = (event: MouseEvent) => {
  emit('cell-double-click', props.column, props.value, event)
}

const handleContextMenu = (event: MouseEvent) => {
  event.preventDefault()
  emit('cell-right-click', props.column, props.value, event)
}

const handleKeyDown = (event: KeyboardEvent) => {
  if (props.focused) {
    switch (event.key) {
      case 'Enter':
        event.preventDefault()
        // Enter edit mode or trigger action
        break
      case 'F2':
        event.preventDefault()
        // Enter edit mode
        break
      case 'Escape':
        event.preventDefault()
        // Exit edit mode
        break
    }
  }
}

// Accessibility
const cellRole = 'gridcell'

const ariaLabel = computed(() => {
  return `${props.column.title}: ${formattedValue.value}`
})

// Functional column detection
const isFunctional = computed(() => isFunctionalColumn(props.column))
const functionalType = computed(() => isFunctional.value ? (props.column as any).type : null)

// Event handlers for functional columns
const handleToggleSelection = () => {
  if (!functionalType.value || (functionalType.value !== 'checkbox' && functionalType.value !== 'radio')) {
    return
  }

  emit('toggle-selection', {
    rowData: props.row,
    rowIndex: props.rowIndex,
    column: props.column,
    selectionType: functionalType.value,
  })
}

const handleDragStart = (rowData: RowData, rowIndex: number, event: DragEvent) => {
  // Bubble up to row/grid body
  emit('row-drag-start', { row: rowData, rowIndex, event })
}

const handleDragEnd = (_rowData: RowData, _rowIndex: number, _event: DragEvent) => {
  // No-op here; final drop is handled by BodyRow @drop to provide destination index
}
</script>

<template>
  <div
    :class="cellClasses"
    :style="cellStyle"
    :role="cellRole"
    :aria-label="ariaLabel"
    :tabindex="focused ? 0 : -1"
    :data-cell-id="`${row.id || row.index}-${column.id}`"
    :data-row-id="row.id || row.index"
    :data-column-id="column.id"
    :data-row-index="rowIndex"
    :data-column-index="columnIndex"
    @click="handleClick"
    @dblclick="handleDoubleClick"
    @contextmenu="handleContextMenu"
    @keydown="handleKeyDown"
  >
    <!-- Functional Columns -->
    <SeqCell
      v-if="isFunctional && functionalType === 'seq'"
      :row-index="rowIndex"
      :options="(column as any).functionalOptions"
      :pagination-offset="0"
    />

    <CheckboxCell
      v-else-if="isFunctional && functionalType === 'checkbox'"
      :row-data="row"
      :row-index="rowIndex"
      :is-selected="rowSelected"
      :column="column"
      :options="(column as any).functionalOptions"
      @toggle-selection="handleToggleSelection"
    />

    <RadioCell
      v-else-if="isFunctional && functionalType === 'radio'"
      :row-data="row"
      :row-index="rowIndex"
      :is-selected="rowSelected"
      :column="column"
      :options="(column as any).functionalOptions"
      @toggle-selection="handleToggleSelection"
    />

    <DragCell
      v-else-if="isFunctional && functionalType === 'drag'"
      :row-data="row"
      :row-index="rowIndex"
      :options="(column as any).functionalOptions"
      @drag-start="handleDragStart"
      @drag-end="handleDragEnd"
    />

    <ActionsCell
      v-else-if="isFunctional && functionalType === 'actions'"
      :row-data="row"
      :row-index="rowIndex"
      :options="(column as any).functionalOptions"
    >
      <!-- Slot for custom actions -->
      <template v-if="column.cellSlot" #default="slotProps">
        <slot :name="column.cellSlot" v-bind="slotProps" />
      </template>
    </ActionsCell>

    <!-- Custom Cell Renderer -->
    <component
      :is="column.cellRenderer"
      v-else-if="column.cellRenderer"
      :value="value"
      :row="row"
      :column="column"
      class="w-full"
    />

    <!-- Type-specific Cell Renderers -->
    <BooleanCell
      v-else-if="column.dataType === 'boolean'"
      :value="value as boolean"
    />

    <NumberCell
      v-else-if="column.dataType === 'number'"
      :value="value as number"
    />

    <DateCell
      v-else-if="column.dataType === 'date'"
      :value="value as Date | string | number"
    />

    <!-- Default Text Cell -->
    <TextCell
      v-else
      :value="String(formattedValue)"
    />

    <!-- Focus Indicator -->
    <div
      v-if="focused"
      class="absolute inset-0 pointer-events-none ring-2 ring-[var(--ht-primary)] ring-inset"
      aria-hidden="true"
    />
  </div>
</template>

<style scoped>
.ht-body-cell {
  contain: layout style;
}

.ht-body-cell:focus {
  outline: none;
}

.ht-body-cell:focus-visible {
  outline: 2px solid var(--ht-primary);
  outline-offset: -2px;
}

/* Interactive cell styling */
.ht-cell-interactive:hover {
  background: var(--ht-cell-hover);
}

/* Text selection */
.ht-body-cell {
  user-select: text;
}
</style>
