<!--
  ValueFilterEditor.vue - Excel-style checkbox list filter

  Responsibilities:
  - Display value counts with search
  - Virtual scrolling for large datasets
  - Lazy pagination (100 items at a time)
  - Null value handling
-->

<script setup lang="ts">
import { computed, ref, watch, onMounted } from 'vue'
import { useVirtualizer } from '../../../utils/virtualization'

interface ValueFilterProps {
  field: string
  allData: Record<string, unknown>[]
  currentFilter?: { operator: 'in' | 'notIn'; value: unknown[] } | null
}

const props = defineProps<ValueFilterProps>()

// Search and selection state
const search = ref('')
// Tri-state selection model:
// - mode 'all': visually all checked, maintain excludedValues for per-item uncheck
// - mode 'subset': only items in selectedValues are checked
const mode = ref<'all' | 'subset'>('all')
const selectedValues = ref<Set<unknown>>(new Set())
const excludedValues = ref<Set<unknown>>(new Set())
const includeNull = ref(true)

// Expose state to parent component
defineExpose({
  // Return best operator+values per current tri-state selection.
  // - null => no filter (equivalent to all selected)
  getValueFilter: () => {
    const domain = nonNullAll.value.map(([k]) => k)
    const q = search.value.trim().toLowerCase()
    // Mode: all
    if (mode.value === 'all') {
      // If search is active, apply filter to match only visible domain
      if (q.length > 0) {
        const list: unknown[] = [...domain]
        // Include null only if user wants and it matches the search
        const nullMatches = '(null)'.toLowerCase().includes(q)
        if (includeNull.value && nullMatches) list.push(null)
        return { operator: 'in', value: list }
      }
      const excludes = Array.from(excludedValues.value)
      const nullExcluded = !includeNull.value
      if (excludes.length === 0 && !nullExcluded) return null
      const list: unknown[] = [...excludes]
      if (nullExcluded) list.push(null)
      return { operator: 'notIn', value: list }
    }
    // Mode: subset
    const includes = Array.from(selectedValues.value)
    const nullIncluded = includeNull.value
    if (includes.length === domain.length && nullIncluded) return null
    // Optimize: prefer notIn if more than half selected
    if (includes.length > domain.length / 2) {
      const excludeSet = new Set(domain)
      includes.forEach(v => excludeSet.delete(v))
      const list: unknown[] = Array.from(excludeSet)
      if (!nullIncluded) list.push(null)
      return { operator: 'notIn', value: list }
    }
    const list: unknown[] = [...includes]
    if (nullIncluded) list.push(null)
    return { operator: 'in', value: list }
  },
  remeasure: () => {
    if (scrollEl.value) {
      virtualizer.setContainerSize({ height: scrollEl.value.clientHeight, width: scrollEl.value.clientWidth })
    }
  }
})

// Cache value counts across opens keyed by data array identity and field
const countsCache: WeakMap<object, Map<string, Array<[unknown, number]>>> = new WeakMap()

const valueCounts = computed(() => {
  const dataRef = props.allData as unknown as object
  let fieldMap = countsCache.get(dataRef)
  if (!fieldMap) {
    fieldMap = new Map<string, Array<[unknown, number]>>()
    countsCache.set(dataRef, fieldMap)
  }
  const cached = fieldMap.get(props.field)
  if (cached) return cached

  const map = new Map<unknown, number>()
  for (const row of props.allData) {
    const v = row[props.field]
    const key = v === null || v === undefined ? '__NULL__' : v
    map.set(key, (map.get(key) || 0) + 1)
  }
  const arr = Array.from(map.entries())
  fieldMap.set(props.field, arr)
  return arr
})

const filteredCounts = computed(() => {
  const q = search.value.trim().toLowerCase()
  let arr = valueCounts.value
  if (q) {
    arr = arr.filter(([k]) => (k === '__NULL__' ? '(null)' : String(k)).toLowerCase().includes(q))
  }
  return arr.sort((a, b) => b[1] - a[1])
})

// Pagination
const pageSize = 100
const loadedCount = ref(pageSize)
const nonNullAll = computed(() => filteredCounts.value.filter(([k]) => k !== '__NULL__'))
const nonNullPaged = computed(() => nonNullAll.value.slice(0, loadedCount.value))

// Virtual scrolling
const scrollEl = ref<HTMLElement | null>(null)
const valueRowHeight = 32
const virtualizer = useVirtualizer({
  count: computed(() => nonNullPaged.value.length),
  getItemSize: () => valueRowHeight,
  overscan: 6,
})

onMounted(() => {
  virtualizer.scrollElement.value = scrollEl.value
  if (scrollEl.value) {
    virtualizer.setContainerSize({ height: scrollEl.value.clientHeight, width: scrollEl.value.clientWidth })
  }
})

// Auto-load when near end (debounced to avoid rapid repeated increments)
let loadMoreTimer: number | null = null
let rafDebounceCount = 0
const RAF_DEBOUNCE_FRAMES = 4 // ~60ms at 60fps

watch(
  () => virtualizer.endIndex.value,
  (end) => {
    if (end >= loadedCount.value - 5 && loadedCount.value < nonNullAll.value.length) {
      if (loadMoreTimer) return

      rafDebounceCount = 0
      const scheduleLoad = () => {
        loadMoreTimer = requestAnimationFrame(() => {
          rafDebounceCount++
          if (rafDebounceCount < RAF_DEBOUNCE_FRAMES) {
            scheduleLoad()
          } else {
            loadedCount.value = Math.min(nonNullAll.value.length, loadedCount.value + pageSize)
            loadMoreTimer = null
            rafDebounceCount = 0
          }
        })
      }
      scheduleLoad()
    }
  }
)

// Reset pagination on search
watch([valueCounts, search], () => {
  loadedCount.value = pageSize
  if (scrollEl.value) {
    scrollEl.value.scrollTop = 0
    virtualizer.updateScrollPosition(0)
  }
})

// Sync from current filter
watch(
  () => props.currentFilter,
  (filter) => {
    if (filter && Array.isArray(filter.value)) {
      if (filter.operator === 'notIn') {
        // notIn: switch to 'all' mode with excluded values
        mode.value = 'all'
        const excludedSet = new Set(filter.value.filter(v => v !== null && v !== undefined))
        excludedValues.value = excludedSet
        selectedValues.value = new Set()
        includeNull.value = !filter.value.some(v => v === null || v === undefined)
      } else {
        // in: switch to subset mode with explicit includes
        mode.value = 'subset'
        includeNull.value = filter.value.some(v => v === null || v === undefined)
        selectedValues.value = new Set(filter.value.filter(v => v !== null && v !== undefined))
        excludedValues.value = new Set()
      }
    } else {
      // Default: all selected visually
      mode.value = 'all'
      includeNull.value = true
      selectedValues.value = new Set()
      excludedValues.value = new Set()
    }
  },
  { immediate: true }
)

function toggleValue(val: unknown, checked: boolean) {
  if (mode.value === 'all') {
    const ex = new Set(excludedValues.value)
    if (checked) ex.delete(val)
    else ex.add(val)
    excludedValues.value = ex
    return
  }
  const next = new Set(selectedValues.value)
  if (checked) next.add(val)
  else next.delete(val)
  selectedValues.value = next
}

function selectAllVisible() {
  // Switch to 'all' visual mode (no explicit includes)
  mode.value = 'all'
  excludedValues.value = new Set()
  includeNull.value = true
}

function clearAll() {
  // Switch to subset with empty includes
  mode.value = 'subset'
  selectedValues.value = new Set()
  excludedValues.value = new Set()
  includeNull.value = false
}

function isChecked(val: unknown): boolean {
  if (mode.value === 'all') {
    return !excludedValues.value.has(val)
  }
  return selectedValues.value.has(val)
}
</script>

<template>
  <div class="space-y-3">
    <!-- Search and actions -->
    <div class="flex items-center gap-2">
      <input
        v-model="search"
        type="text"
        placeholder="Search values"
        class="h-8 flex-1 rounded-md border border-[var(--ht-border)] bg-[var(--ht-bg)] px-3 text-sm text-[var(--ht-text)] placeholder:text-[var(--ht-text-subtle)] transition-colors focus:border-[var(--ht-primary)] focus:outline-none focus:ring-2 focus:ring-[var(--ht-primary)]/20"
      />
      <button
        class="h-8 px-3 text-xs font-medium text-[var(--ht-text-muted)] hover:text-[var(--ht-primary)] hover:bg-[var(--ht-bg-subtle)] rounded-md transition-colors"
        @click="clearAll"
      >
        Clear
      </button>
      <button
        class="h-8 px-3 text-xs font-medium text-[var(--ht-primary)] hover:text-[var(--ht-primary-hover)] hover:bg-[var(--ht-primary-subtle)] rounded-md transition-colors"
        @click="selectAllVisible"
      >
        Select All
      </button>
    </div>

    <!-- Value list with virtual scrolling -->
    <div ref="scrollEl" class="relative overflow-auto rounded-md border border-[var(--ht-border)] bg-[var(--ht-bg-subtle)]" style="max-height: 16rem; height: 16rem;">
      <!-- Null checkbox (shown only when search empty or matches '(null)') -->
      <label
        v-if="!search || '(null)'.toLowerCase().includes(search.trim().toLowerCase())"
        class="flex items-center gap-3 px-3 py-2.5 text-sm bg-[var(--ht-bg)] border-b border-[var(--ht-border-subtle)] hover:bg-[var(--ht-bg-subtle)] cursor-pointer transition-colors group"
      >
        <input
          v-model="includeNull"
          type="checkbox"
          class="w-4 h-4 rounded border-[var(--ht-border-strong)] text-[var(--ht-primary)] focus:ring-2 focus:ring-[var(--ht-primary)]/30 cursor-pointer"
        />
        <span class="font-medium text-[var(--ht-text-muted)] group-hover:text-[var(--ht-text)]">(null)</span>
        <span class="ml-auto text-xs text-[var(--ht-text-subtle)] tabular-nums">{{ valueCounts.find(([k]) => k === '__NULL__')?.[1] ?? 0 }}</span>
      </label>

      <!-- Virtual list -->
      <div :style="{ height: virtualizer.totalSize.value + 'px', position: 'relative' }">
        <div
          v-for="item in virtualizer.virtualItems.value"
          :key="item.index"
          :style="{ position: 'absolute', top: item.start + 'px', height: item.size + 'px', left: 0, right: 0 }"
        >
          <label class="flex items-center gap-3 px-3 py-2 text-sm hover:bg-[var(--ht-cell-hover)] cursor-pointer transition-colors group">
            <input
              type="checkbox"
              class="w-4 h-4 rounded border-[var(--ht-border-strong)] text-[var(--ht-primary)] focus:ring-2 focus:ring-[var(--ht-primary)]/30 cursor-pointer"
              :checked="isChecked(nonNullPaged[item.index]?.[0])"
              @change="(e: Event) => toggleValue(nonNullPaged[item.index]?.[0], (e.target as HTMLInputElement)?.checked)"
            />
            <span class="truncate flex-1 text-[var(--ht-text)] group-hover:text-[var(--ht-text)]" :title="String(nonNullPaged[item.index]?.[0] ?? '')">{{ String(nonNullPaged[item.index]?.[0] ?? '') }}</span>
            <span class="text-xs text-[var(--ht-text-subtle)] tabular-nums">{{ nonNullPaged[item.index]?.[1] ?? 0 }}</span>
          </label>
        </div>
      </div>
    </div>

    <!-- Pagination info -->
    <div class="flex items-center justify-between text-xs text-[var(--ht-text-muted)] pt-1 border-t border-[var(--ht-border-subtle)]">
      <span class="tabular-nums">Showing {{ Math.min(loadedCount, nonNullAll.length) }} of {{ nonNullAll.length }}</span>
    </div>
  </div>
</template>
