/**
 * TableRenderer - Modern web-style table renderer implementation
 *
 * Implements the GridRenderer interface and wraps the existing TableRenderer.vue
 * component to provide a clean separation between logic and rendering.
 *
 * Features:
 * - Three-pane layout (left/center/right)
 * - Virtual scrolling support
 * - Complete GridInteractionContract implementation
 * - Event delegation for all interactions
 */

import { createApp, type App } from 'vue'
import type {
  GridRenderer,
  GridInteractionContract,
  VirtualRow,
  RendererOptions,
} from '../base/renderer-interface'
import type { GridAPI, GridState, ColumnDef, GridEvents } from '../../types'
import {
  createInteractionContract,
  type CoreLogicCallbacks,
  type InteractionPayload,
} from '../base/interaction-contract'
import { createLogger } from '../../utils/logger'

// Import the existing Vue component (will be created next)
import TableRendererComponent from '../../components/table-renderer/TableRendererComponent.vue'

/**
 * Interface for the Vue component instance methods
 */
// interface ComponentInstance {
//   updateSelection?: (selectedIds: Set<string | number>, reason: 'programmatic' | 'user') => void
//   setActive?: (activeId: string | number | null) => void
//   ensureVisible?: (id: string | number, behavior: 'smooth' | 'auto') => void
//   toggleEditMode?: (id: string | number, mode: 'enter' | 'exit') => Promise<boolean>
//   updateScrollPosition?: (position: { top: number; left: number }) => void
//   refresh?: () => void
//   updateState?: (state: GridState, columns: ColumnDef[], rows: VirtualRow[]) => void
//   resize?: (width: number, height: number) => void
//   updateConfig?: (options: RendererOptions) => void
//   handleCellClick?: (payload: any) => void
//   handleCellDoubleClick?: (payload: any) => void
//   handleCellRightClick?: (payload: any) => void
//   handleRowSelect?: (payload: any) => void
//   handleScroll?: (payload: any) => void
// }

/**
 * TableRenderer implementation using Vue component
 */
export class TableRenderer implements GridRenderer {
  readonly name = 'table' as const
  readonly interactionContract: GridInteractionContract
  private logger: ReturnType<typeof createLogger>

  private app: App | null = null
  private componentInstance: any | null = null
  private hostElement: HTMLElement | null = null
  private isDestroyed = false
  private readonly options: RendererOptions

  // Current state tracking
  private currentState: GridState | null = null
  private currentColumns: ColumnDef[] = []
  private currentRows: VirtualRow[] = []

  constructor(options: RendererOptions = {}) {
    this.options = options
    // Logger for table renderer
    this.logger = createLogger('TableRenderer')
    const contract = createInteractionContract('table-renderer')

    // Create a wrapper to match the GridInteractionContract interface
    this.interactionContract = {
      updateSelection: (selectedIds: Set<string | number>, reason: 'programmatic' | 'user') => {
        // Forward to component instance
        if (this.componentInstance?.updateSelection) {
          this.componentInstance.updateSelection(selectedIds, reason)
        }
      },
      setActive: (activeId: string | number | null) => {
        // Forward to component instance
        if (this.componentInstance?.setActive) {
          this.componentInstance.setActive(activeId)
        }
      },
      ensureVisible: (id: string | number, behavior: 'smooth' | 'auto') => {
        // Forward to component instance
        if (this.componentInstance?.ensureVisible) {
          this.componentInstance.ensureVisible(id, behavior)
        }
      },
      toggleEditMode: async (id: string | number, mode: 'enter' | 'exit') => {
        // Forward to component instance
        if (this.componentInstance?.toggleEditMode) {
          return await this.componentInstance.toggleEditMode(id, mode)
        }
        return false
      },
      registerEventEmitter: <K extends keyof GridEvents>(emit: (eventName: K, payload: GridEvents[K]) => void) => {
        contract.registerEventEmitter(emit)
      },
      updateScrollPosition: (position: { top: number; left: number }) => {
        // Forward to component instance
        if (this.componentInstance?.updateScrollPosition) {
          this.componentInstance.updateScrollPosition(position)
        }
      },
      refresh: () => {
        // Force component re-render
        if (this.componentInstance?.refresh) {
          this.componentInstance.refresh()
        }
      },
      emit: <K extends keyof GridEvents>(eventName: K, payload: GridEvents[K]) => {
        contract.emit?.(eventName, payload)
      },
      registerCoreCallbacks: (callbacks: CoreLogicCallbacks) => {
        contract.registerCoreCallbacks?.(callbacks)
      },
      destroy: () => {
        contract.destroy?.()
      },
    }

    // Setup core logic callbacks
    this.setupCoreCallbacks()
  }

  /**
   * Mount renderer to host element
   */
  async mount(host: HTMLElement, api: GridAPI): Promise<void> {
    if (this.isDestroyed) {
      throw new Error('Cannot mount destroyed TableRenderer')
    }

    if (this.app) {
      this.logger.warn('Already mounted, unmounting first')
      await this.unmount()
    }

    try {
      this.hostElement = host

      // Create Vue app with TableRenderer component
      this.app = createApp(TableRendererComponent, {
        // Pass renderer context to component
        renderer: this,
        interactionContract: this.interactionContract,
        gridAPI: api,
        options: this.options,
      })

      // Mount component
      this.componentInstance = this.app.mount(host)

      // Register event emitter with interaction contract
      // Clean system: emit public event names without legacy prefixes
      this.interactionContract.registerEventEmitter(<K extends keyof GridEvents>(eventName: K, payload: GridEvents[K]) => {
        api.emit(eventName as any, payload)
      })

      this.logger.debug('Mounted successfully')
    } catch (error) {
      this.logger.error('Mount failed:', { module: 'TableRenderer' }, error)
      await this.cleanup()
      throw error
    }
  }

  /**
   * Unmount renderer and cleanup
   */
  async unmount(): Promise<void> {
    await this.cleanup()
  }

  /**
   * Render current state
   */
  render(state: GridState, columns: ColumnDef[], rows: VirtualRow[]): void {
    if (this.isDestroyed || !this.componentInstance) {
      return
    }

    try {
      // Store current state
      this.currentState = state
      this.currentColumns = columns
      this.currentRows = rows

      // Update component state through reactive properties
      if (this.componentInstance.updateState) {
        this.componentInstance.updateState(state, columns, rows)
      }
    } catch (error) {
      this.logger.error('Render failed:', { module: 'TableRenderer' }, error)
    }
  }

  /**
   * Handle resize events
   */
  resize(width: number, height: number): void {
    if (this.isDestroyed || !this.componentInstance) {
      return
    }

    try {
      if (this.componentInstance.resize) {
        this.componentInstance.resize(width, height)
      }
    } catch (error) {
      this.logger.error('Resize failed:', { module: 'TableRenderer' }, error)
    }
  }

  /**
   * Get renderer configuration schema
   */
  getConfigSchema(): Record<string, any> {
    return {
      performance: {
        type: 'object',
        properties: {
          enableRAF: { type: 'boolean', default: true },
          batchUpdates: { type: 'boolean', default: true },
          maxConcurrentRenders: { type: 'number', default: 3 },
        },
      },
      accessibility: {
        type: 'object',
        properties: {
          enableAria: { type: 'boolean', default: true },
          announceChanges: { type: 'boolean', default: true },
          keyboardNavigation: { type: 'boolean', default: true },
        },
      },
      layout: {
        type: 'object',
        properties: {
          showHeader: { type: 'boolean', default: true },
          showFooter: { type: 'boolean', default: true },
          enablePanes: { type: 'boolean', default: true },
          stickyHeader: { type: 'boolean', default: true },
        },
      },
    }
  }

  /**
   * Update configuration
   */
  updateConfig(config: Record<string, any>): void {
    Object.assign(this.options, config)

    if (this.componentInstance?.updateConfig) {
      this.componentInstance.updateConfig(this.options)
    }
  }

  /**
   * Get current state (for debugging)
   */
  getCurrentState(): {
    state: GridState | null
    columns: ColumnDef[]
    rows: VirtualRow[]
  } {
    return {
      state: this.currentState,
      columns: this.currentColumns,
      rows: this.currentRows,
    }
  }

  /**
   * Setup core logic callbacks for interaction contract
   */
  private setupCoreCallbacks(): void {
    const callbacks: Partial<CoreLogicCallbacks> = {
      onCellClick: (payload: InteractionPayload) => {
        // Handle cell click events
        if (this.componentInstance?.handleCellClick) {
          this.componentInstance.handleCellClick(payload)
        }
      },

      onCellDoubleClick: (payload: InteractionPayload) => {
        // Handle cell double-click events
        if (this.componentInstance?.handleCellDoubleClick) {
          this.componentInstance.handleCellDoubleClick(payload)
        }
      },

      onCellRightClick: (payload: InteractionPayload) => {
        // Handle cell right-click events
        if (this.componentInstance?.handleCellRightClick) {
          this.componentInstance.handleCellRightClick(payload)
        }
      },

      onRowSelect: (payload: InteractionPayload) => {
        // Handle row selection
        if (this.componentInstance?.handleRowSelect) {
          this.componentInstance.handleRowSelect(payload)
        }
      },

      onScroll: (payload: InteractionPayload) => {
        // Handle scroll events
        if (this.componentInstance?.handleScroll) {
          this.componentInstance.handleScroll(payload)
        }
      },
    }

    // Register callbacks with interaction contract
    this.interactionContract.registerCoreCallbacks?.(callbacks)
  }

  /**
   * Cleanup resources
   */
  private async cleanup(): Promise<void> {
    try {
      // Unmount Vue app
      if (this.app && this.hostElement) {
        this.app.unmount()
        this.app = null
        this.componentInstance = null
      }

      // Clear host element
      if (this.hostElement) {
        this.hostElement.innerHTML = ''
        this.hostElement = null
      }

      // Cleanup interaction contract
      this.interactionContract.destroy?.()

      // Clear state references
      this.currentState = null
      this.currentColumns = []
      this.currentRows = []
    } catch (error) {
      this.logger.error('Cleanup failed:', { module: 'TableRenderer' }, error)
    }
  }

  /**
   * Mark renderer as destroyed
   */
  destroy(): void {
    this.isDestroyed = true
    this.cleanup()
  }
}

/**
 * Factory function to create TableRenderer instances
 */
export function createTableRenderer(options: RendererOptions = {}): TableRenderer {
  return new TableRenderer(options)
}

/**
 * Default export for convenience
 */
export default createTableRenderer
