// Plugin system constants
export const PLUGIN_API_VERSION = '1.1.0'
export const RENDERER_API_VERSION = '1.0.0'

/**
 * Check if a plugin is compatible with the current API version
 */
export function isPluginCompatible(pluginVersion?: string): boolean {
  if (!pluginVersion) return true // Assume compatible if no version specified

  // Simple version check (can be enhanced with semver)
  return pluginVersion === PLUGIN_API_VERSION
}

/**
 * Check if a renderer is compatible with the current API version
 */
export function isRendererCompatible(rendererVersion?: string): boolean {
  if (!rendererVersion) return true // Assume compatible if no version specified

  // Simple version check (can be enhanced with semver)
  return rendererVersion === RENDERER_API_VERSION
}

/**
 * Get information about the renderer system
 */
export function getRendererSystemInfo() {
  return {
    apiVersion: RENDERER_API_VERSION,
    supportedRenderers: ['table', 'excel'],
    defaultRenderer: 'table',
    features: {
      dynamicSwitching: true,
      pluginProvided: true,
      statePreservation: true,
      hotSwapping: true,
    },
  }
}

