/**
 * Quick start helper for common use cases
 */
export const quickStart = {
  /**
   * Create a basic table setup
   */
  table: (data: RowData[], columns: ColumnDef[]) => ({
    data,
    columns,
    renderer: 'table' as const,
    config: {
      virtualizationThreshold: 1000,
      enableVirtualization: true,
    },
  }),

  /**
   * Create an Excel-style setup (requires Excel plugin)
   */
  excel: (data: RowData[], columns: ColumnDef[]) => ({
    data,
    columns,
    renderer: 'excel' as const,
    config: {
      virtualizationThreshold: 1000,
      enableVirtualization: true,
    },
    // Note: Requires @happy-table/excel-plugin to be installed and registered
  }),

  /**
   * Create a high-performance setup with all optimizations
   */
  highPerformance: (data: RowData[], columns: ColumnDef[]) => ({
    data,
    columns,
    config: {
      virtualizationThreshold: 100,
      enableVirtualization: true,
      rowHeight: 32,
      headerHeight: 40,
    },
    // Automatically includes all performance utilities
    utilities: {
      memoryManagement: true,
      performanceMonitoring: true,
      dataPipeline: true,
      browserOptimizations: true,
    },
  }),
}
