<script setup lang="ts">
/**
 * TextCell - Default Text Cell Renderer
 *
 * Purpose: Renders plain text content in table cells (default renderer)
 * Design: Minimal styling, left-aligned, single-line with ellipsis overflow
 */

export interface TextCellProps {
  // Core
  value: string | number | null | undefined

  // Display
  align?: 'left' | 'center' | 'right'
  truncate?: boolean
}

const props = withDefaults(defineProps<TextCellProps>(), {
  align: 'left',
  truncate: true,
})

// Alignment classes
const alignClasses: Record<string, string> = {
  left: 'text-left',
  center: 'text-center',
  right: 'text-right',
}

// Handle null/undefined values
const displayValue = props.value ?? ''
</script>

<template>
  <span
    :class="[
      'inline-block w-full',
      alignClasses[align],
      truncate ? 'truncate' : '',
    ]"
    :title="truncate ? String(displayValue) : undefined"
  >
    {{ displayValue }}
  </span>
</template>
