<!--
  FilterPopover.vue - Filter-specific popover with standardized structure

  Responsibilities:
  - Provide two-layer structure: Title / Content
  - Wrap BasePopover for filter-specific scenarios
  - Standardize filter UI layout (without action buttons)

  Design:
  - Composes BasePopover (not modifies it)
  - Maintains BasePopover's flexibility for other use cases
  - Follows Single Responsibility Principle
  - Action buttons handled by child components (ValueFilterEditor, CustomFilterEditor)
-->

<script setup lang="ts">
import BasePopover, { type PopoverPosition } from './BasePopover.vue'

withDefaults(defineProps<{
  open: boolean
  anchor: HTMLElement | null
  title: string
  position?: PopoverPosition
}>(), {
  position: () => ({ placement: 'bottom', align: 'center', offset: 4, autoFlip: true }),
})

const emit = defineEmits<{
  'update:open': [value: boolean]
  'opened': []
  'closed': []
}>()
</script>

<template>
  <BasePopover
    :open="open"
    :anchor="anchor"
    :position="position"
    :aria-label="title"
    @update:open="emit('update:open', $event)"
    @opened="emit('opened')"
    @closed="emit('closed')"
  >
    <div class="min-w-[300px] max-w-[540px] rounded-lg border border-[var(--ht-border)] bg-[var(--ht-bg)] shadow-[var(--shadow-ht-xl)] backdrop-blur-sm">
      <!-- Title Layer -->
      <div class="border-b border-[var(--ht-border-subtle)] px-4 py-3">
        <div class="text-sm font-semibold text-[var(--ht-text)]">
          <slot name="title">{{ title }}</slot>
        </div>
      </div>

      <!-- Content Layer (child components handle their own actions) -->
      <div class="p-4">
        <slot />
      </div>
    </div>
  </BasePopover>
</template>

<style scoped>
/* No custom styles needed - all handled by Tailwind utilities */
</style>
