<!--
  GridBody.vue - Virtual Scrolling Body Component

  Responsibilities:
  1. Virtual scrolling container management
  2. Row rendering with position-based layout
  3. Three-pane synchronized scrolling
  4. Event delegation for cell interactions

  Performance: 60FPS scrolling with 100k+ rows
-->

<script setup lang="ts">
import { computed, ref, onMounted, onUnmounted, defineExpose, provide } from 'vue'
import BodyRow from './BodyRow.vue'
import { createLogger } from '../../../utils/logger'
import { useRAFScroll } from '../../../utils/scroll-optimization'
import { DragAutoScroller } from '../../../utils/drag-auto-scroll'

const logger = createLogger('grid-body')
import type {
  RowData,
  ColumnDef,
  VirtualizerReturn,
  GridInteractionContract,
  PaneConfig,
  VirtualData,
  CellValue,
} from '../../../types'

// Props
const props = defineProps<{
  data: VirtualData
  columns: ColumnDef[]
  paneConfig: PaneConfig
  virtualizer: VirtualizerReturn
  rowHeight: number
  isContentOverflowing: boolean
  focusedCell: { rowId: string | number; columnId: string } | null
  selectedRows: (string | number)[]
  interactionContract: GridInteractionContract
  bodyRowBorderClasses?: string
  bodyCellBorderClasses?: string
  stripeEnabled?: boolean
}>()

// Emits
const emit = defineEmits<{
  scroll: [event: Event]
}>()

// Refs
const bodyContainer = ref<HTMLElement>()
const bodyScrollArea = ref<HTMLElement>()
const centerScrollArea = ref<HTMLElement>()

// Computed
const bodyStyle = computed(() => ({
  height: '100%',
  overflow: 'hidden',
  position: 'relative' as const,
}))

const virtualContainerStyle = computed(() => ({
  height: `${props.data.totalHeight}px`,
  position: 'relative' as const,
  overflow: 'hidden',
}))

// 中心滚动区域的虚拟容器需要设置内容宽度以触发横向滚动
const centerVirtualContainerStyle = computed(() => ({
  ...virtualContainerStyle.value,
  width: `${props.paneConfig.center.width}px`,
}))

const centerScrollAreaStyle = computed(() => ({
  height: '100%',
  overflow: 'auto',
  // 移除固定宽度设置，让滚动容器适应外层容器尺寸
}))

// Scroll state management
const currentScrollLeft = ref(0)
const currentScrollTop = ref(0)
const hoveredRowKey = ref<string | number | null>(null)

// Drag state management
const draggingRowId = ref<string | number | null>(null)
const setDraggingRowId = (id: string | number | null) => {
  draggingRowId.value = id
}

// Drop target state management (shared across all panes)
const dropTargetRowId = ref<string | number | null>(null)
const dropTargetPosition = ref<'top' | 'bottom' | null>(null)

const setDropTarget = (rowId: string | number | null, position: 'top' | 'bottom' | null) => {
  dropTargetRowId.value = rowId
  dropTargetPosition.value = position
}

// Provide drag state to child components
provide('draggingRowId', draggingRowId)
provide('setDraggingRowId', setDraggingRowId)
provide('dropTargetRowId', dropTargetRowId)
provide('dropTargetPosition', dropTargetPosition)
provide('setDropTarget', setDropTarget)

// Auto-scroll support during drag
let autoScroller: DragAutoScroller | null = null

// Virtual items from virtualizer
const virtualItems = computed(() => props.virtualizer.virtualItems.value || [])

// Processed virtual items with row data
const processedVirtualItems = computed(() => {
  return virtualItems.value
    .map(item => {
      // Direct mapping using the virtualizer's index
      const row = props.data.visibleRows[item.index]

      if (!row) {
        logger.warn(`Missing row data for index ${item.index}, total rows: ${props.data.visibleRows.length}`)
        return null
      }

      return {
        ...item,
        row,
        rowArrayIndex: item.index,
      }
    })
    .filter(item => item !== null) // Filter out null items
})

const setHoveredRowKey = (rowKey: string | number) => {
  hoveredRowKey.value = rowKey
}

const clearHoveredRowKey = (rowKey: string | number) => {
  if (hoveredRowKey.value === rowKey) {
    hoveredRowKey.value = null
  }
}

// Event handlers with RAF optimization and velocity tracking
const handleBodyScrollCore = (event: Event) => {
  const target = event.target as HTMLElement

  // Update scroll top from body container
  currentScrollTop.value = target.scrollTop

  // Create a wrapper that looks like the original event but with correct scroll values
  const eventWrapper = {
    ...event,
    target: {
      ...target,
      scrollLeft: currentScrollLeft.value,
      scrollTop: currentScrollTop.value,
      dataset: { pane: 'body' }
    }
  }

  emit('scroll', eventWrapper as Event)
}

const handleCenterScrollCore = (event: Event) => {
  const target = event.target as HTMLElement

  // Update scroll left from center pane
  currentScrollLeft.value = target.scrollLeft

  // Create a wrapper that looks like the original event but with correct scroll values
  const eventWrapper = {
    ...event,
    target: {
      ...target,
      scrollLeft: currentScrollLeft.value,
      scrollTop: currentScrollTop.value,
      dataset: { pane: 'body' }
    }
  }

  emit('scroll', eventWrapper as Event)
}

// RAF-optimized scroll handlers with debouncing and velocity tracking
const bodyScrollHandler = useRAFScroll({
  onScroll: handleBodyScrollCore,
  debounceDelay: 150,
  trackVelocity: true,
})

const centerScrollHandler = useRAFScroll({
  onScroll: handleCenterScrollCore,
  debounceDelay: 150,
  trackVelocity: true,
})

// Public scroll handlers
const handleBodyScroll = bodyScrollHandler.handleScroll
const handleCenterScroll = centerScrollHandler.handleScroll

// Row click handler
const handleRowClick = (row: RowData, index: number) => {
  props.interactionContract.emit?.('row-click', { row, index })
}

// Cell interaction handlers
const handleCellClick = (row: RowData, column: ColumnDef, value: CellValue) => {
  props.interactionContract.emit?.('cell-click', { row, column, value })
}

const handleCellDoubleClick = (row: RowData, column: ColumnDef, value: CellValue) => {
  // Emit explicit double-click event for consumer handlers
  props.interactionContract.emit?.('cell-double-click' as any, { row, column, value } as any)
}

const handleCellRightClick = (row: RowData, column: ColumnDef, value: CellValue) => {
  props.interactionContract.emit?.('cell-right-click' as any, { row, column, value } as any)
}

// Selection handlers
const handleToggleSelection = (payload: {
  rowData: RowData
  rowIndex: number
  rowId: string | number
  column: ColumnDef
  columnIndex: number
  selectionType: 'checkbox' | 'radio'
}) => {
  props.interactionContract.emit?.('toggle-selection', {
    row: payload.rowData,
    rowId: payload.rowId,
    rowIndex: payload.rowIndex,
    selectionType: payload.selectionType,
  })
}

// Row drag handlers
const handleRowDragStart = (payload: { row: RowData; rowIndex: number; event?: DragEvent }) => {
  props.interactionContract.emit?.('row-drag-start' as any, payload as any)
}

const handleRowDragOver = (event: DragEvent) => {
  autoScroller?.start(event)
}

const handleRowDragEnd = (payload: { result: any; sourceId?: string | number; targetId?: string | number }) => {
  autoScroller?.stop()
  props.interactionContract.emit?.('row-drag-end' as any, payload as any)
}

// no-op removed: dragleave handler unused

// Edit mode handlers
const recalculate = () => {
  // Force virtualizer recalculation
  if (props.virtualizer.updateScrollPosition) {
    // Use current scroll top state
    props.virtualizer.updateScrollPosition(currentScrollTop.value)
  }
}

const enterEditMode = async (id: string | number): Promise<boolean> => {
  // Delegate to interaction contract if available
  if (props.interactionContract.toggleEditMode) {
    return await props.interactionContract.toggleEditMode(id, 'enter')
  }
  return false
}

const completeEdit = async (id: string | number, _value: CellValue): Promise<boolean> => {
  // Complete edit and exit edit mode
  if (props.interactionContract.toggleEditMode) {
    return await props.interactionContract.toggleEditMode(id, 'exit')
  }
  return false
}

const cancelEdit = (id: string | number) => {
  // Cancel edit without saving
  if (props.interactionContract.toggleEditMode) {
    props.interactionContract.toggleEditMode(id, 'exit')
  }
}

// Expose methods
defineExpose({
  scrollToIndex: props.virtualizer.scrollToIndex,
  recalculate,
  enterEditMode,
  completeEdit,
  cancelEdit,
  centerScrollArea, // Expose for parent component
})

// Lifecycle
onMounted(() => {
  // Initialize auto-scroller for body scroll area
  if (bodyScrollArea.value) {
    autoScroller = new DragAutoScroller(bodyScrollArea.value, {
      vertical: true,
      horizontal: false,
      scrollZone: 50,
      scrollSpeed: 10,
    })
  }
})

onUnmounted(() => {
  // Cleanup RAF scroll handlers
  bodyScrollHandler.cleanup()
  centerScrollHandler.cleanup()

  // Cleanup auto-scroller
  autoScroller?.destroy()
  autoScroller = null
})
</script>

<template>
  <div ref="bodyContainer" class="ht-body h-full" :style="bodyStyle" role="rowgroup">
    <!-- Main scroll container (vertical scrolling for entire body) -->
    <div
      ref="bodyScrollArea"
      class="ht-scroll-area h-full overflow-y-auto overflow-x-hidden ht-custom-scrollbar"
      @scroll="handleBodyScroll"
    >
      <!-- Virtual container wrapper with fixed height -->
      <div class="ht-virtual-wrapper" :style="virtualContainerStyle">
        <!-- Three-pane layout container -->
        <div class="ht-panes-container flex">
          <!-- Left Frozen Pane -->
          <div
            v-if="paneConfig.left.columns.length > 0"
            class="ht-pane-left flex-shrink-0 relative z-10 overflow-hidden"
            :style="{ width: `${paneConfig.left.width}px` }"
          >
            <div class="ht-virtual-container-left relative">
              <BodyRow
                v-for="item in processedVirtualItems"
                :key="`left-${item.row.id || item.index}`"
                :row="item.row"
                :columns="paneConfig.left.columns"
                :row-index="item.index"
                :virtual-item="item"
                :row-height="rowHeight"
                :focused-cell="focusedCell"
                :selected="item.row.id !== null && item.row.id !== undefined && selectedRows.includes(item.row.id)"
                :hovered-row-key="hoveredRowKey"
                :row-border-classes="bodyRowBorderClasses"
                :cell-border-classes="bodyCellBorderClasses"
                :stripe-enabled="stripeEnabled"
                @row-click="handleRowClick"
                @cell-click="handleCellClick"
                @cell-double-click="handleCellDoubleClick"
                @cell-right-click="handleCellRightClick"
                @row-hover="setHoveredRowKey"
                @row-leave="clearHoveredRowKey"
                @toggle-selection="handleToggleSelection"
                @row-drag-start="handleRowDragStart"
                @row-drag-over="handleRowDragOver"
                @row-drag-end="handleRowDragEnd"
              />
            </div>
          </div>

          <!-- Center Scrollable Pane -->
          <div
            class="ht-pane-center relative overflow-hidden"
            :class="{ 'flex-1': isContentOverflowing }"
          >
            <div
              ref="centerScrollArea"
              class="ht-scroll-area-horizontal h-full overflow-x-auto overflow-y-hidden"
              :style="centerScrollAreaStyle"
              @scroll="handleCenterScroll"
            >
              <div class="ht-virtual-container-center" :style="centerVirtualContainerStyle">
                <BodyRow
                  v-for="item in processedVirtualItems"
                  :key="`center-${item.row.id || item.index}`"
                  :row="item.row"
                  :columns="paneConfig.center.columns"
                  :row-index="item.index"
                  :virtual-item="item"
                  :row-height="rowHeight"
                  :focused-cell="focusedCell"
                  :selected="item.row.id !== null && item.row.id !== undefined && selectedRows.includes(item.row.id)"
                  :hovered-row-key="hoveredRowKey"
                  :row-border-classes="bodyRowBorderClasses"
                  :cell-border-classes="bodyCellBorderClasses"
                  :stripe-enabled="stripeEnabled"
                  @row-click="handleRowClick"
                  @cell-click="handleCellClick"
                  @cell-double-click="handleCellDoubleClick"
                  @cell-right-click="handleCellRightClick"
                  @row-hover="setHoveredRowKey"
                  @row-leave="clearHoveredRowKey"
                  @toggle-selection="handleToggleSelection"
                  @row-drag-start="handleRowDragStart"
                  @row-drag-over="handleRowDragOver"
                  @row-drag-end="handleRowDragEnd"
                />
              </div>
            </div>
          </div>

          <!-- Right Frozen Pane -->
          <div
            v-if="paneConfig.right.columns.length > 0"
            class="ht-pane-right flex-shrink-0 relative z-10 overflow-hidden"
            :style="{ width: `${paneConfig.right.width}px` }"
          >
            <div class="ht-virtual-container-right relative">
              <BodyRow
                v-for="item in processedVirtualItems"
                :key="`right-${item.row.id || item.index}`"
                :row="item.row"
                :columns="paneConfig.right.columns"
                :row-index="item.index"
                :virtual-item="item"
                :row-height="rowHeight"
                :focused-cell="focusedCell"
                :selected="item.row.id !== null && item.row.id !== undefined && selectedRows.includes(item.row.id)"
                :hovered-row-key="hoveredRowKey"
                :row-border-classes="bodyRowBorderClasses"
                :cell-border-classes="bodyCellBorderClasses"
                :stripe-enabled="stripeEnabled"
                @row-click="handleRowClick"
                @cell-click="handleCellClick"
                @cell-double-click="handleCellDoubleClick"
                @cell-right-click="handleCellRightClick"
                @row-hover="setHoveredRowKey"
                @row-leave="clearHoveredRowKey"
                @toggle-selection="handleToggleSelection"
                @row-drag-start="handleRowDragStart"
                @row-drag-over="handleRowDragOver"
                @row-drag-end="handleRowDragEnd"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped>
.ht-body {
  contain: layout style paint;
}

/* Shadow for pinned panes is now defined in pinned-columns.css */

/* Main vertical scroll container */
.ht-scroll-area {
  /* Performance optimizations */
  transform: translateZ(0);
  will-change: scroll-position;
}

/* Horizontal scroll container for center pane */
.ht-scroll-area-horizontal {
  /* Performance optimizations */
  transform: translateZ(0);
  will-change: scroll-position;
}

.ht-virtual-wrapper {
  position: relative;
}

.ht-panes-container {
  position: relative;
}

.ht-virtual-container-center {
  position: relative;
}

/* Virtual item positioning */
.ht-body :deep(.ht-virtual-item) {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  contain: layout style paint;
  will-change: transform;
}
</style>
