<script setup lang="ts">
/**
 * NumberCell - Number Cell Renderer
 *
 * Purpose: Renders numeric values with proper formatting and alignment
 * Design: Right-aligned by default, supports locale formatting, handles null/NaN
 */

export interface NumberCellProps {
  // Core
  value: number | string | null | undefined

  // Formatting
  decimals?: number
  locale?: string
  useGrouping?: boolean // Thousands separator

  // Display
  align?: 'left' | 'center' | 'right'
  prefix?: string // Currency symbol, etc.
  suffix?: string // Unit, percentage, etc.
}

const props = withDefaults(defineProps<NumberCellProps>(), {
  align: 'right',
  locale: 'en-US',
  useGrouping: true,
})

// Parse and format number
const formattedValue = (() => {
  // Handle null/undefined
  if (props.value === null || props.value === undefined) {
    return ''
  }

  // Parse to number
  const num = typeof props.value === 'string' ? parseFloat(props.value) : props.value

  // Handle NaN
  if (isNaN(num)) {
    return String(props.value)
  }

  // Format with Intl.NumberFormat
  const formatter = new Intl.NumberFormat(props.locale, {
    minimumFractionDigits: props.decimals,
    maximumFractionDigits: props.decimals,
    useGrouping: props.useGrouping,
  })

  const formatted = formatter.format(num)

  // Add prefix/suffix
  return `${props.prefix || ''}${formatted}${props.suffix || ''}`
})()

// Alignment classes
const alignClasses: Record<string, string> = {
  left: 'text-left',
  center: 'text-center',
  right: 'text-right',
}
</script>

<template>
  <span
    :class="[
      'inline-block w-full tabular-nums',
      alignClasses[align],
    ]"
    :title="String(value)"
  >
    {{ formattedValue }}
  </span>
</template>
