<!--
  CellNumberEditor.vue - Number Input Editor (using HtInput)

  Responsibilities:
  1. Number input with validation
  2. Formatting and parsing
  3. Min/max value constraints
  4. Decimal place handling
-->

<script setup lang="ts">
import { ref, nextTick, onMounted, computed, watch } from 'vue'
import type { GridAPI, ColumnDef, RowData, CellValue } from '../../../types'
import HtInput from '../../primitives/HtInput.vue'

// Props
const props = defineProps<{
  value: CellValue
  row: RowData
  column: ColumnDef
  cellRect: DOMRect
  gridAPI: GridAPI
}>()

// Emits
const emit = defineEmits<{
  'update:value': [value: CellValue]
  commit: []
  cancel: []
}>()

// Local state
const inputRef = ref<InstanceType<typeof HtInput>>()
const localValue = ref<number | string>(formatNumber(props.value))
const hasError = ref(false)
const errorMessage = ref('')

// Computed
const inputStyle = computed(() => ({
  position: 'absolute' as const,
  left: `${props.cellRect.left}px`,
  top: `${props.cellRect.top}px`,
  width: `${props.cellRect.width}px`,
  height: `${props.cellRect.height}px`,
  zIndex: 1000,
}))

const inputState = computed(() => {
  return hasError.value ? 'error' : 'default'
})

// Methods
function formatNumber(value: any): number | string {
  if (value === null || value === undefined || value === '') {
    return ''
  }

  const num = Number(value)
  if (isNaN(num)) {
    return String(value)
  }

  // Apply decimal places if specified
  if (props.column.decimals !== undefined) {
    return Number(num.toFixed(props.column.decimals))
  }

  return num
}

function parseNumber(value: string | number): number | null {
  if (value === '' || value === null || value === undefined) {
    return null
  }

  const num = Number(value)
  return isNaN(num) ? null : num
}

function validateNumber(value: string | number): { valid: boolean; message?: string } {
  if (value === '') {
    return { valid: true }
  }

  const num = Number(value)
  if (isNaN(num)) {
    return {
      valid: false,
      message: 'Must be a valid number',
    }
  }

  if (props.column.min !== undefined && num < props.column.min) {
    return {
      valid: false,
      message: `Must be at least ${props.column.min}`,
    }
  }

  if (props.column.max !== undefined && num > props.column.max) {
    return {
      valid: false,
      message: `Must be at most ${props.column.max}`,
    }
  }

  return { valid: true }
}

const handleInput = (value: string | number) => {
  localValue.value = value

  // Validate input
  const validation = validateNumber(value)
  hasError.value = !validation.valid
  errorMessage.value = validation.message || ''

  // Emit the parsed value
  const parsedValue = parseNumber(value)
  emit('update:value', parsedValue)

  // Update plugin state
  if (props.gridAPI?.updateCellEditValue) {
    props.gridAPI.updateCellEditValue(parsedValue)
  }
}

const handleKeydown = (event: KeyboardEvent) => {
  switch (event.key) {
    case 'Enter':
      if (!event.shiftKey) {
        event.preventDefault()
        event.stopPropagation()
        if (!hasError.value) {
          emit('commit')
        }
      }
      break
    case 'Escape':
      event.preventDefault()
      event.stopPropagation()
      emit('cancel')
      break
    case 'Tab':
      event.preventDefault()
      event.stopPropagation()
      if (!hasError.value) {
        emit('commit')
      }
      break
    case 'ArrowUp':
      event.preventDefault()
      incrementValue(1)
      break
    case 'ArrowDown':
      event.preventDefault()
      incrementValue(-1)
      break
  }
}

const handleBlur = () => {
  // Auto-commit on blur if no error
  if (!hasError.value) {
    emit('commit')
  }
}

const incrementValue = (delta: number) => {
  const current = parseNumber(localValue.value) || 0
  const step = props.column.step || 1
  const newValue = current + delta * step

  // Apply constraints
  let constrainedValue = newValue
  if (props.column.min !== undefined) {
    constrainedValue = Math.max(constrainedValue, props.column.min)
  }
  if (props.column.max !== undefined) {
    constrainedValue = Math.min(constrainedValue, props.column.max)
  }

  localValue.value = formatNumber(constrainedValue)
  handleInput(localValue.value)
}

const focus = () => {
  // Focus the native input inside HtInput
  const inputElement = inputRef.value?.$el?.querySelector('input')
  if (inputElement) {
    inputElement.focus()
    inputElement.select()
  }
}

// Watch for external value changes
watch(
  () => props.value,
  newValue => {
    localValue.value = formatNumber(newValue)
    hasError.value = false
    errorMessage.value = ''
  }
)

// Lifecycle
onMounted(async () => {
  await nextTick()
  focus()
})

// Expose methods for parent
defineExpose({
  focus,
})
</script>

<template>
  <div :style="inputStyle" class="ht-cell-number-editor">
    <HtInput
      ref="inputRef"
      :model-value="localValue"
      type="number"
      variant="default"
      size="md"
      :state="inputState"
      :placeholder="column.placeholder || `Enter ${column.title}`"
      autocomplete="off"
      class="ht-cell-editor-input"
      @update:model-value="handleInput"
      @keydown="handleKeydown"
      @blur="handleBlur"
    />

    <!-- Error message -->
    <div
      v-if="hasError && errorMessage"
      class="absolute bg-[var(--ht-danger)] text-[var(--ht-text-inverse)] text-xs px-2 py-1 rounded shadow-lg z-[1001]"
      :style="{
        left: `${cellRect.left}px`,
        top: `${cellRect.bottom + 2}px`,
        maxWidth: `${cellRect.width}px`,
      }"
    >
      {{ errorMessage }}
    </div>
  </div>
</template>

<style scoped>
.ht-cell-number-editor {
  /* Ensure proper absolute positioning */
  display: block;
}

.ht-cell-editor-input {
  /* Override default styles for cell editing */
  width: 100%;
  height: 100%;
}

/* Enhance focus ring for cell editing context */
.ht-cell-editor-input :deep(input) {
  border-width: 2px;
  border-color: var(--ht-primary);
  box-shadow: 0 0 0 1px var(--ht-primary);
  border-radius: 4px;
  height: 100%;
  text-align: right; /* Numbers align right */
}

.ht-cell-editor-input :deep(input:focus) {
  outline: none;
  box-shadow: 0 0 0 2px var(--ht-primary);
}

/* Remove number input spinners for cleaner look */
.ht-cell-editor-input :deep(input::-webkit-outer-spin-button),
.ht-cell-editor-input :deep(input::-webkit-inner-spin-button) {
  -webkit-appearance: none;
  margin: 0;
}

.ht-cell-editor-input :deep(input[type='number']) {
  -moz-appearance: textfield;
}
</style>
