<!--
  HeaderCell.vue - Individual Header Cell Component

  Responsibilities:
  1. Render column header with sorting indicators
  2. Handle column resizing interactions
  3. Accessibility compliance
  4. Visual feedback for user interactions

  Interaction Model:
  - Only the sort icon is clickable (prevents accidental sorting)
  - Header cell text selection does not trigger sorting
  - Provides precise control and better UX

  Vue 3 Pattern: Event delegation to child components
-->

<script setup lang="ts">
import { computed, toRef } from 'vue'
import type { InternalColumnDef, GridAPI, DragPosition, ColumnDragContext } from '../../../types'
import { isFunctionalColumn } from '../../../utils/functional-columns'
import { CheckboxHeader, RadioHeader } from '../../functional'
import { useColumnDrag } from './composables/useColumnDrag'
import { useColumnResize } from './composables/useColumnResize'
import SortIndicator from './SortIndicator.vue'
import FilterIndicator from './FilterIndicator.vue'

// Props
const props = defineProps<{
  column: InternalColumnDef
  width: number
  gridAPI: GridAPI
  dragContext?: ColumnDragContext | null
  borderClasses?: string
  selectedRows?: (string | number)[]
  totalRows?: number
}>()

// Emits
const emit = defineEmits<{
  'sort-indicator-focus': [field: string]
  'column-drag-start': [payload: ColumnDragContext]
  'column-drop': [payload: { columnId: string; position: DragPosition }]
  'column-drag-end': []
  'select-all': []
  'clear-selection': []
}>()

// Computed
const cellStyle = computed(() => ({
  width: `${props.width}px`,
  minWidth: `${props.column.minWidth || 50}px`,
  maxWidth: `${props.column.maxWidth || 400}px`,
}))

const columnRef = toRef(props, 'column')
const dragContextRef = toRef(props, 'dragContext')

const {
  pinnedSide,
  isColumnDraggable,
  isDragSource,
  dragOverPosition,
  isDropBlocked,
  handleColumnDragStart,
  handleColumnDragEnd,
  handleColumnDragOver,
  handleColumnDragLeave,
  handleColumnDrop,
} = useColumnDrag({
  columnRef,
  dragContextRef,
  emit,
})

const {
  isColumnResizable,
  isResizeActive,
  handleResize,
  handleResizeDoubleClick,
} = useColumnResize({
  columnRef,
  gridAPI: props.gridAPI,
  getCurrentWidth: () => props.width,
})

const isRightPinned = computed(() => pinnedSide.value === 'right')

const cellClasses = computed(() =>
  [
    'ht-header-cell',
    props.borderClasses || '', // Use dynamic border classes
    'px-3 py-2',
    'flex items-center justify-between',
    'bg-[var(--ht-header-bg)]',
    'text-[var(--ht-header-text)]',
    'font-medium text-sm',
    'select-none',
    isColumnResizable.value ? 'is-resizable' : 'is-fixed-width',
    isResizeActive.value ? 'is-resizing' : null,
    {
      'is-dragging': isDragSource.value,
      'drag-over-before': !isDragSource.value && dragOverPosition.value === 'before',
      'drag-over-after': !isDragSource.value && dragOverPosition.value === 'after',
      'is-draggable': isColumnDraggable.value,
      'drop-forbidden': !isDragSource.value && isDropBlocked.value,
      // Add separator classes for pinned columns
      'is-last-pinned': props.column.isLastPinned,
      'is-first-pinned-right': props.column.isFirstRightPinned,
    },
  ].filter(Boolean)
)

// Remove old sort icon logic - now handled by SortIndicator component

// Computed: Check if column is sortable
const isSortable = computed(() => {
  return props.column.sortable !== undefined && props.column.sortable !== false
})

// Event handlers (sorting handled by SortIndicator component only)

// Sort indicator event handlers
const handleSortPriorityChange = (field: string, newPriority: number) => {
  const changeSortPriority = props.gridAPI?.changeSortPriority
  if (changeSortPriority) {
    changeSortPriority(field, newPriority)
  }
}

const handleSortFieldRemove = (field: string) => {
  const removeSortField = props.gridAPI?.removeSortField
  if (removeSortField) {
    removeSortField(field)
  }
}

const handleSortIndicatorFocus = (field: string) => {
  emit('sort-indicator-focus', field)
}

// Functional column detection
const isFunctional = computed(() => isFunctionalColumn(props.column))
const functionalType = computed(() => isFunctional.value ? (props.column as any).type : null)

// Event handlers for functional columns
const handleSelectAll = () => {
  emit('select-all')
}

const handleClearSelection = () => {
  emit('clear-selection')
}
</script>

<template>
  <div
    :class="cellClasses"
    :style="cellStyle"
    role="columnheader"
    :data-resizable="isColumnResizable ? 'true' : 'false'"
    :data-pinned="pinnedSide"
    :aria-sort="isSortable ? 'none' : undefined"
    :aria-label="`Column ${column.title}${isSortable ? ', sortable' : ''}`"
    :draggable="isColumnDraggable"
    @dragstart="handleColumnDragStart"
    @dragend="handleColumnDragEnd"
    @dragover="handleColumnDragOver"
    @dragleave="handleColumnDragLeave"
    @drop="handleColumnDrop"
  >
    <!-- Functional Column Headers -->
    <CheckboxHeader
      v-if="isFunctional && functionalType === 'checkbox'"
      :options="(column as any).functionalOptions"
      :selected-count="selectedRows?.length || 0"
      :total-selectable-rows="totalRows || 0"
      @select-all="handleSelectAll"
      @clear-selection="handleClearSelection"
    />

    <RadioHeader
      v-else-if="isFunctional && functionalType === 'radio'"
      :options="(column as any).functionalOptions"
      :selected-count="selectedRows?.length || 0"
      :total-selectable-rows="totalRows || 0"
    />

    <!-- Column Title with responsive alignment -->
    <div
      v-else
      class="flex items-center min-w-0 flex-1 group"
      :class="{
        'justify-start': !column.dataType || column.dataType === 'text',
        'justify-end': column.dataType === 'number',
        'justify-center': column.dataType === 'boolean'
      }"
    >
      <!-- For right-aligned content (numbers), sort indicator comes first -->
      <SortIndicator
        v-if="column.dataType === 'number'"
        :column="column"
        :grid-a-p-i="gridAPI"
        class="order-1"
        @sort-priority-change="handleSortPriorityChange"
        @sort-field-remove="handleSortFieldRemove"
        @sort-indicator-focus="handleSortIndicatorFocus"
        @dragstart.stop
      />

      <span
        class="truncate"
        :class="{
          'order-1': !column.dataType || column.dataType === 'text' || column.dataType === 'boolean' || column.dataType === 'date',
          'order-2': column.dataType === 'number'
        }"
      >
        {{ column.title }}
      </span>

      <!-- For left-aligned and center-aligned content, sort indicator comes after -->
      <SortIndicator
        v-if="!column.dataType || column.dataType === 'text' || column.dataType === 'boolean' || column.dataType === 'date'"
        :column="column"
        :grid-a-p-i="gridAPI"
        class="order-2"
        @sort-priority-change="handleSortPriorityChange"
        @sort-field-remove="handleSortFieldRemove"
        @sort-indicator-focus="handleSortIndicatorFocus"
        @dragstart.stop
      />

      <!-- Filter indicator (if filterable) -->
      <FilterIndicator
        v-if="column.filterable !== false"
        :column="column"
        :grid-a-p-i="gridAPI"
        class="order-3"
        @filter-open="() => {}"
      />
    </div>

    <!-- Resize Handle -->
    <div
      v-if="isColumnResizable"
      :class="[
        'ht-resize-handle w-1 h-full cursor-col-resize bg-transparent hover:bg-[var(--ht-primary)] opacity-0 hover:opacity-100 transition-all',
        isRightPinned ? 'left-handle mr-2' : 'right-handle ml-2'
      ]"
      @mousedown="handleResize"
      @dblclick="handleResizeDoubleClick"
      @dragstart.prevent
      @click.stop
    />
  </div>
</template>

<style scoped>
.ht-header-cell {
  contain: layout style;
  position: relative;
}

.ht-header-cell:focus-visible {
  outline: 2px solid var(--ht-primary);
  outline-offset: -2px;
}

.ht-resize-handle {
  position: absolute;
  top: 0;
  bottom: 0;
  z-index: 1;
}

.ht-resize-handle.right-handle {
  right: 0;
  left: auto;
}

.ht-resize-handle.left-handle {
  left: 0;
  right: auto;
}

.ht-resize-handle:hover {
  background: var(--ht-primary);
}

.ht-header-cell.is-resizing {
  cursor: col-resize;
  user-select: none;
}

.ht-header-cell.is-resizing .ht-resize-handle {
  opacity: 1;
}

.ht-header-cell.is-draggable {
  cursor: grab;
}

.ht-header-cell.is-draggable:active {
  cursor: grabbing;
}

.ht-header-cell.is-dragging {
  opacity: 0.85;
  cursor: grabbing;
}

.ht-header-cell.drop-forbidden {
  cursor: not-allowed;
}

.ht-header-cell.drop-forbidden::before {
  content: '';
  position: absolute;
  inset: 2px;
  border: 2px dashed var(--ht-danger);
  border-radius: 6px;
  background: var(--ht-danger-bg);
  opacity: 0.65;
  pointer-events: none;
  z-index: 6;
}

.ht-header-cell.drag-over-before::before,
.ht-header-cell.drag-over-after::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  width: 3px;
  background: var(--ht-primary);
  z-index: 5;
  border-radius: 999px;
}

.ht-header-cell.drag-over-before::before {
  left: 0;
}

.ht-header-cell.drag-over-after::before {
  right: 0;
}

:global(body.ht-grid-resizing) {
  cursor: col-resize !important;
  user-select: none !important;
}

.ht-header-cell::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 1px;
  background: var(--ht-border);
  opacity: 0.35;
  transition: opacity 0.15s ease, background-color 0.15s ease;
  pointer-events: none;
}

.ht-header-cell[data-resizable='true']::after {
  opacity: 0.55;
}

.ht-header-cell[data-resizable='true']:hover::after,
.ht-header-cell.is-resizing::after {
  background: var(--ht-primary);
  opacity: 0.9;
}

.ht-header-cell[data-resizable='false']::after {
  opacity: 0.18;
}

.ht-header-cell[data-pinned='right']::after {
  left: 0;
  right: auto;
}

.ht-header-cell[data-resizable='false'] .ht-resize-handle {
  display: none;
}

.ht-header-cell[data-resizable='false'] .group {
  opacity: 0.75;
}
</style>
