<script setup lang="ts">
/**
 * HtSelectItem - 单个选项
 *
 * 职责:
 * - 渲染选项内容
 * - 处理选择逻辑
 * - 显示选中状态
 * - ARIA 属性
 * - 注册选项文本到 context
 * - 检测是否在分组内，应用缩进样式
 */
import { computed, watch, ref, onMounted, onUnmounted, inject } from 'vue'
import { injectSelectContext, injectSelectProps, SELECT_GROUP_KEY } from './composables/useSelectContext'

// 注入分组状态 (如果在 HtSelectGroup 内部则为 true)
const isInGroup = inject(SELECT_GROUP_KEY, false)

const props = defineProps<{
  value: any
  disabled?: boolean
  textValue?: string
  itemIndex?: number
}>()

const context = injectSelectContext()
const selectProps = injectSelectProps()
const itemRef = ref<HTMLElement | null>(null)

// Get text content from slot or textValue prop
const getTextValue = (): string => {
  if (props.textValue) return props.textValue
  if (itemRef.value) {
    return itemRef.value.textContent?.trim() || String(props.value)
  }
  return String(props.value)
}

// Register option on mount and update
onMounted(() => {
  if (!itemRef.value) return
  // Register option text
  context.registerOption(props.value, getTextValue())
})

// Unregister option on unmount
onUnmounted(() => {
  context.unregisterOption(props.value)
})

// Calculate item index (reactive, based on enabled items only)
const itemIndex = computed(() => {
  if (typeof props.itemIndex === 'number') {
    return props.itemIndex
  }

  if (!itemRef.value) return -1

  const content = itemRef.value.closest('.ht-select-content')
  if (!content) return -1

  // Only count enabled items (same logic as keyboard navigation)
  const items = Array.from(
    content.querySelectorAll('[data-ht-select-item="true"]:not([aria-disabled="true"])')
  )
  return items.indexOf(itemRef.value)
})

const isSelected = computed(() => context.isSelected(props.value))

const isHighlighted = computed(() => {
  return itemIndex.value >= 0 && context.highlightedIndex.value === itemIndex.value
})

const handleClick = () => {
  if (!props.disabled) {
    context.select(props.value)
  }
}

// Auto-scroll when highlighted
watch(isHighlighted, (highlighted) => {
  if (highlighted && itemRef.value) {
    itemRef.value.scrollIntoView({ block: 'nearest', behavior: 'smooth' })
  }
})
</script>

<template>
  <div
    ref="itemRef"
    data-ht-select-item="true"
    :id="itemIndex >= 0 ? `${context.ids.contentId}-opt-${itemIndex}` : undefined"
    role="option"
    :aria-selected="isSelected ? 'true' : 'false'"
    :aria-disabled="disabled ? 'true' : 'false'"
    :data-highlighted="isHighlighted ? 'true' : 'false'"
    class="ht-select-item"
    :class="{
      'ht-select-item--selected': isSelected,
      'ht-select-item--disabled': disabled,
      'ht-select-item--highlighted': isHighlighted,
      'ht-select-item--multiline': selectProps.showMultiLines && selectProps.showMultiLines > 1,
      'ht-select-item--grouped': isInGroup,
    }"
    @click="handleClick"
  >
    <!-- Text content wrapper for ellipsis -->
    <span
      class="ht-select-item__text"
      :style="{
        WebkitLineClamp: selectProps.showMultiLines && selectProps.showMultiLines > 1 ? selectProps.showMultiLines : undefined,
      }"
    >
      <slot />
    </span>

    <!-- Check icon for selected item -->
    <svg
      v-if="isSelected"
      class="w-4 h-4 ml-auto shrink-0"
      fill="currentColor"
      viewBox="0 0 20 20"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        fill-rule="evenodd"
        d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
        clip-rule="evenodd"
      />
    </svg>
  </div>
</template>

<style scoped>
.ht-select-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.375rem 0.5rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
  border-radius: var(--radius-ht-sm);
  cursor: default;
  user-select: none;
  transition: var(--ht-transition-fast);
  min-height: var(--size-ht-9);
  outline: none;
}

/* Grouped item indentation */
.ht-select-item--grouped {
  padding-left: 1.5rem;
}

/* Multi-line mode: allow items to grow in height */
.ht-select-item--multiline {
  align-items: flex-start; /* 顶部对齐，适应多行文本 */
  min-height: auto; /* 移除固定最小高度 */
}

/* Text content wrapper */
.ht-select-item__text {
  flex: 1;
  min-width: 0; /* Critical for text-overflow to work in flex */

  /* 单行模式 (默认) */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Multi-line text mode */
.ht-select-item--multiline .ht-select-item__text {
  /* 启用多行显示 */
  white-space: normal;
  overflow: hidden;
  word-wrap: break-word;
  word-break: break-word;

  /* 使用 -webkit-line-clamp 限制行数 */
  display: -webkit-box;
  -webkit-box-orient: vertical;
  /* -webkit-line-clamp 由内联样式动态设置 */
}

.ht-select-item:hover:not(.ht-select-item--disabled) {
  background-color: var(--ht-cell-hover);
}

.ht-select-item--highlighted {
  background-color: var(--ht-cell-hover);
  /* Make highlighted state more visible */
  outline: 2px solid var(--ht-primary);
  outline-offset: -2px;
}

.ht-select-item--selected {
  background-color: var(--ht-primary-subtle);
  color: var(--ht-primary);
  font-weight: 500;
}

.ht-select-item--disabled {
  opacity: 0.5;
  pointer-events: none;
  cursor: not-allowed;
}
</style>
