<script setup lang="ts">
import { computed, useAttrs, useId } from 'vue'
import type { InputStyleProps } from '../../types/primitives'

/**
 * HtInput - Atomic Input Component
 *
 * Design: shadcn-vue pattern (80%) + Headless UI interactions (10%)
 * Features: Variant/Size/State system, clearable, prefix/suffix slots, full accessibility
 */

export interface HtInputProps extends InputStyleProps {
  // Core
  modelValue?: string | number
  type?: 'text' | 'number' | 'email' | 'password' | 'search' | 'url' | 'tel'

  // Functionality
  placeholder?: string
  disabled?: boolean
  readonly?: boolean
  clearable?: boolean

  // Native attributes
  autocomplete?: string
  maxlength?: number
  minlength?: number
  pattern?: string

  // Accessibility
  required?: boolean
}

export interface HtInputEmits {
  'update:modelValue': [value: string | number]
  'clear': []
  'focus': [event: FocusEvent]
  'blur': [event: FocusEvent]
  'keydown': [event: KeyboardEvent]
}

const props = withDefaults(defineProps<HtInputProps>(), {
  type: 'text',
  size: 'md',
  variant: 'default',
  state: 'default',
  clearable: false,
})

const emit = defineEmits<HtInputEmits>()

// Generate stable ID for accessibility (SSR compatible)
const inputId = useId()
const attrs = useAttrs()

// Base styles aligned with HtButton pattern
const baseClasses = [
  'rounded-md transition-colors outline-none',
  'focus-visible:ring-1 focus-visible:ring-offset-1',
  'disabled:opacity-50 disabled:pointer-events-none disabled:cursor-not-allowed',
  'placeholder:text-[var(--ht-text-subtle)]',
].join(' ')

// Variant system (matches CoreVariant from shared types)
// Uses stronger border for better visibility, aligned with checkbox/radio pattern
const variantClasses: Record<string, string> = {
  default: 'bg-[var(--ht-bg)] border border-[var(--ht-border-strong)] text-[var(--ht-text)] hover:border-[var(--ht-text)]/60',
  filled: 'bg-[var(--ht-bg-subtle)] border border-transparent text-[var(--ht-text)]',
  outline: 'bg-transparent border border-[var(--ht-border-strong)] text-[var(--ht-text)] hover:border-[var(--ht-text)]/60',
  ghost: 'bg-transparent border border-transparent text-[var(--ht-text)] hover:bg-[var(--ht-bg-subtle)]',
}

// Size system (uses @theme tokens from primitives.css)
const sizeClasses: Record<string, string> = {
  sm: 'h-[var(--size-ht-input-sm)] px-2.5 text-sm',  // 32px
  md: 'h-[var(--size-ht-input-md)] px-3 text-sm',    // 36px
  lg: 'h-[var(--size-ht-input-lg)] px-4 text-base',  // 44px
}

// State system (visual feedback for validation)
const stateClasses: Record<string, string> = {
  default: 'focus-visible:ring-[var(--ht-primary)]',
  error: 'border-[var(--ht-danger)] focus-visible:ring-[var(--ht-danger)]',
  success: 'border-[var(--ht-success)] focus-visible:ring-[var(--ht-success)]',
  warning: 'border-[var(--ht-warning)] focus-visible:ring-[var(--ht-warning)]',
}

const inputClasses = computed(() => [
  baseClasses,
  variantClasses[props.variant],
  sizeClasses[props.size],
  stateClasses[props.state],
].join(' '))

// Check if input has value (handles string, number, 0)
const hasValue = computed(() => {
  if (props.modelValue === undefined || props.modelValue === null) {
    return false
  }

  if (typeof props.modelValue === 'string') {
    return props.modelValue.length > 0
  }

  // Number 0 is a valid value
  if (typeof props.modelValue === 'number') {
    return !isNaN(props.modelValue)
  }

  return true
})

const handleInput = (event: Event) => {
  const target = event.target as HTMLInputElement
  const value = props.type === 'number' ? (target.valueAsNumber || 0) : target.value
  emit('update:modelValue', value)
}

const handleClear = () => {
  emit('update:modelValue', props.type === 'number' ? 0 : '')
  emit('clear')
}

const handleKeydown = (event: KeyboardEvent) => {
  emit('keydown', event)

  // Escape key clears input if clearable
  if (event.key === 'Escape' && props.clearable && hasValue.value) {
    handleClear()
    event.preventDefault()
  }
}
</script>

<template>
  <div class="relative inline-flex items-center w-full">
    <!-- Prefix slot -->
    <div
      v-if="$slots.prefix"
      class="absolute left-3 flex items-center pointer-events-none text-[var(--ht-text-muted)]"
    >
      <slot name="prefix" />
    </div>

    <!-- Main input element -->
    <input
      :id="inputId"
      :type="type"
      :value="modelValue"
      :class="[
        inputClasses,
        'w-full',
        $slots.prefix ? 'pl-10' : '',
        clearable || $slots.suffix ? 'pr-10' : '',
      ]"
      :placeholder="placeholder"
      :disabled="disabled"
      :readonly="readonly"
      :required="required"
      :autocomplete="autocomplete"
      :maxlength="maxlength"
      :minlength="minlength"
      :pattern="pattern"
      :aria-invalid="state === 'error'"
      :aria-describedby="attrs['aria-describedby'] as string"
      :aria-errormessage="attrs['aria-errormessage'] as string"
      :aria-required="required"
      v-bind="$attrs"
      @input="handleInput"
      @focus="emit('focus', $event as FocusEvent)"
      @blur="emit('blur', $event as FocusEvent)"
      @keydown="handleKeydown"
    />

    <!-- Clear button / Suffix slot -->
    <div
      v-if="clearable || $slots.suffix"
      class="absolute right-3 flex items-center"
    >
      <button
        v-if="clearable && hasValue && !disabled && !readonly"
        type="button"
        class="text-[var(--ht-text-muted)] hover:text-[var(--ht-text)] transition-colors"
        :aria-label="'Clear input'"
        tabindex="-1"
        @click="handleClear"
      >
        <svg class="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
          <path
            fill-rule="evenodd"
            d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z"
            clip-rule="evenodd"
          />
        </svg>
      </button>
      <div v-else-if="$slots.suffix" class="text-[var(--ht-text-muted)]">
        <slot name="suffix" />
      </div>
    </div>
  </div>
</template>
