<script setup lang="ts">
/**
 * HtSelectLabel - 分组标签
 *
 * 职责:
 * - 显示分组名称
 * - 提供分组语义标识
 * - 与 HtSelectGroup 的 aria-labelledby 配合使用
 */
import { computed } from 'vue'

const props = withDefaults(defineProps<{
  /**
   * 标签 ID (用于 aria-labelledby 关联)
   * 如果不提供，将自动生成
   */
  id?: string

  /**
   * 控制是否在滚动时保持吸附在顶部
   */
  sticky?: boolean
}>(), {
  sticky: false,
})

// 生成唯一 ID
const labelId = computed(() => {
  return props.id || `ht-select-label-${Math.random().toString(36).substr(2, 9)}`
})
</script>

<template>
  <div
    :id="labelId"
    :class="['ht-select-label', { 'ht-select-label--sticky': props.sticky }]"
    role="presentation"
  >
    <slot />
  </div>
</template>

<style scoped>
.ht-select-label {
  padding: 0.5rem 0.75rem 0.25rem;
  font-size: 0.75rem;
  line-height: 1rem;
  font-weight: 600;
  color: var(--ht-text-muted);
  text-transform: uppercase;
  letter-spacing: 0.05em;
  user-select: none;
}

.ht-select-label--sticky {
  position: sticky;
  top: 0;
  background-color: var(--ht-bg);
  z-index: 1;
  box-shadow: 0 1px 0 0 transparent;
  transition: box-shadow 150ms ease;
}

.ht-select-label--sticky::after {
  content: '';
  position: absolute;
  bottom: -1px;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.1),
    transparent
  );
  opacity: 0;
  transition: opacity 150ms ease;
}

.ht-select-label:not(:first-child) {
  margin-top: 0.5rem;
}
</style>
