import { computed, ref, watch, type Ref } from 'vue'
import type { ColumnDef, DragPosition, ColumnDragContext } from '../../../../types'
import { isColumnDropBlocked, getColumnPinnedLocation } from '../../../../utils/column-reorder'
import { isFunctionalColumn } from '../../../../utils/functional-columns'

interface ColumnDragEmit {
  (event: 'column-drag-start', payload: ColumnDragContext): void
  (event: 'column-drop', payload: { columnId: string; position: DragPosition }): void
  (event: 'column-drag-end'): void
}

interface UseColumnDragOptions {
  columnRef: Ref<ColumnDef>
  dragContextRef: Ref<ColumnDragContext | null | undefined>
  emit: ColumnDragEmit
}

export function useColumnDrag({ columnRef, dragContextRef, emit }: UseColumnDragOptions) {
  const pinnedSide = computed(() => getColumnPinnedLocation(columnRef.value))

  const isDragSource = ref(false)
  const dragOverPosition = ref<DragPosition | null>(null)
  const isDropBlocked = ref(false)

  const isColumnDraggable = computed(() => {
    const column = columnRef.value as ColumnDef & {
      reorderable?: boolean
      draggable?: boolean
    }

    // Functional columns (checkbox, radio, seq, drag, actions) are not draggable
    if (isFunctionalColumn(column)) {
      return false
    }

    if (column.reorderable === false || column.draggable === false) {
      return false
    }

    return true
  })

  watch(dragContextRef, context => {
    if (!context) {
      isDropBlocked.value = false
      dragOverPosition.value = null
    }
  })

  const resetVisualState = () => {
    dragOverPosition.value = null
    isDropBlocked.value = false
  }

  const handleColumnDragStart = (event: DragEvent) => {
    if (!isColumnDraggable.value) {
      event.preventDefault()
      return
    }

    isDragSource.value = true
    dragOverPosition.value = null

    if (event.dataTransfer) {
      event.dataTransfer.effectAllowed = 'move'
      event.dataTransfer.setData('text/plain', columnRef.value.id)
      event.dataTransfer.setData('application/happy-table-column', columnRef.value.id)
    }

    emit('column-drag-start', { columnId: columnRef.value.id, pinned: pinnedSide.value })
  }

  const handleColumnDragEnd = () => {
    isDragSource.value = false
    resetVisualState()
    emit('column-drag-end')
  }

  const evaluateDragPosition = (event: DragEvent): DragPosition => {
    const target = event.currentTarget as HTMLElement
    const rect = target.getBoundingClientRect()
    const offsetX = event.clientX - rect.left
    return offsetX <= rect.width / 2 ? 'before' : 'after'
  }

  const handleColumnDragOver = (event: DragEvent) => {
    if (!isColumnDraggable.value || isDragSource.value) return

    const dropBlocked = isColumnDropBlocked(dragContextRef.value, columnRef.value)
    if (dropBlocked) {
      isDropBlocked.value = true
      dragOverPosition.value = null
      if (event.dataTransfer) {
        event.dataTransfer.dropEffect = 'none'
      }
      return
    }

    isDropBlocked.value = false
    event.preventDefault()

    if (event.dataTransfer) {
      event.dataTransfer.dropEffect = 'move'
    }

    const position = evaluateDragPosition(event)
    if (dragOverPosition.value !== position) {
      dragOverPosition.value = position
    }
  }

  const handleColumnDragLeave = () => {
    if (dragOverPosition.value !== null) {
      dragOverPosition.value = null
    }

    if (isDropBlocked.value) {
      isDropBlocked.value = false
    }
  }

  const handleColumnDrop = (event: DragEvent) => {
    if (!isColumnDraggable.value) return

    const dropBlocked = isColumnDropBlocked(dragContextRef.value, columnRef.value)
    if (dropBlocked) {
      isDropBlocked.value = true
      dragOverPosition.value = null
      if (event.dataTransfer) {
        event.dataTransfer.dropEffect = 'none'
      }
      return
    }

    event.preventDefault()

    if (event.dataTransfer) {
      event.dataTransfer.dropEffect = 'move'
    }

    const position = isDragSource.value ? 'after' : evaluateDragPosition(event)
    resetVisualState()
    emit('column-drop', { columnId: columnRef.value.id, position })
  }

  return {
    pinnedSide,
    isColumnDraggable,
    isDragSource,
    dragOverPosition,
    isDropBlocked,
    handleColumnDragStart,
    handleColumnDragEnd,
    handleColumnDragOver,
    handleColumnDragLeave,
    handleColumnDrop,
  }
}
