import type { ColumnDef, RowData } from '../types'

export type InferredDataType = NonNullable<ColumnDef['dataType']> | 'text'

interface InferOptions {
  sampleSize?: number
  enumMaxUnique?: number
  enumMaxUniqueRatio?: number
  locale?: string // e.g. 'en-US', 'en-GB', 'zh-CN'
}

const DEFAULTS: Required<InferOptions> = {
  sampleSize: 500,
  enumMaxUnique: 50,
  enumMaxUniqueRatio: 0.2,
  locale: 'en-US',
}

const CURRENCY_SYMBOLS = ['$', '€', '£', '¥', '￥', '₩', '₹']

function isNullish(v: unknown) {
  return v === null || v === undefined
}

function isBooleanLike(v: unknown): boolean {
  if (typeof v === 'boolean') return true
  if (typeof v === 'number') return v === 0 || v === 1
  if (typeof v === 'string') {
    const s = v.trim().toLowerCase()
    return s === 'true' || s === 'false'
  }
  return false
}

function isPercentageLike(v: unknown): boolean {
  if (typeof v !== 'string') return false
  const s = v.trim()
  if (!s.endsWith('%')) return false
  const num = s.slice(0, -1).replace(/[,\s]/g, '')
  return num.length > 0 && !isNaN(Number(num))
}

function isCurrencyLike(v: unknown): boolean {
  if (typeof v !== 'string') return false
  const s = v.trim()
  if (!s) return false
  const hasSymbol = CURRENCY_SYMBOLS.some(sym => s.includes(sym))
  if (!hasSymbol) return false
  const num = s.replace(/[\p{Sc},\s]/gu, '')
  return num.length > 0 && !isNaN(Number(num))
}

function isNumberLike(v: unknown): boolean {
  if (typeof v === 'number') return Number.isFinite(v)
  if (typeof v === 'string') {
    const s = v.trim()
    if (!s) return false
    // Allow thousands separators and decimals
    const normalized = s.replace(/,/g, '')
    return normalized.length > 0 && !isNaN(Number(normalized))
  }
  return false
}

function isISODateString(s: string): { isDate: boolean; isDateTime: boolean } {
  // Basic checks for YYYY-MM-DD and optional time
  const d = s.trim()
  // yyyy-mm-dd
  const dateOnly = /^\d{4}-\d{2}-\d{2}$/
  if (dateOnly.test(d)) return { isDate: true, isDateTime: false }
  // yyyy-mm-ddThh:mm(:ss)? or with space
  const dateTime = /^\d{4}-\d{2}-\d{2}[T\s]\d{2}:\d{2}(:\d{2})?/
  if (dateTime.test(d)) return { isDate: false, isDateTime: true }
  return { isDate: false, isDateTime: false }
}

function isDateLike(v: unknown): 'none' | 'date' | 'datetime' {
  if (v instanceof Date && !isNaN(v.getTime())) {
    // Treat Date objects as datetime (most precise)
    return 'datetime'
  }
  if (typeof v === 'string') {
    const { isDate, isDateTime } = isISODateString(v)
    if (isDate || isDateTime) return isDate ? 'date' : 'datetime'
    // Locale-aware quick checks
    const localeCheck = detectLocaleDate(v)
    if (localeCheck !== 'none') return localeCheck
    // Fallback to Date parse
    const t = new Date(v)
    if (!isNaN(t.getTime())) {
      const lowered = v.toLowerCase()
      const hasTimeHint = lowered.includes('t') || /\d{2}:\d{2}/.test(lowered)
      return hasTimeHint ? 'datetime' : 'date'
    }
  }
  return 'none'
}

// Detect common non-ISO date formats across locales
function detectLocaleDate(s: string, localeHint?: string): 'none' | 'date' | 'datetime' {
  const str = s.trim()
  if (!str) return 'none'

  // Chinese formats: 2024年10月14日, 2024年10月14日 12:30:00
  const zh = /^(\d{4})年(\d{1,2})月(\d{1,2})日(?:\s+\d{1,2}:\d{2}(?::\d{2})?)?$/
  if (zh.test(str)) {
    return str.includes(':') ? 'datetime' : 'date'
  }

  // yyyy/MM/dd or yyyy.MM.dd (with optional time)
  const ymd = /^(\d{4})[/.](\d{1,2})[/.](\d{1,2})(?:[ T]\d{1,2}:\d{2}(?::\d{2})?)?$/
  if (ymd.test(str)) {
    return /\d{1,2}:\d{2}/.test(str) ? 'datetime' : 'date'
  }

  // dd/MM/yyyy vs MM/dd/yyyy (slashes or dashes or dots), with optional time
  const dmyOrMdy = /^(\d{1,2})[-/.](\d{1,2})[-/.](\d{2,4})(?:[ T]\d{1,2}:\d{2}(?::\d{2})?)?$/
  const m = str.match(dmyOrMdy)
  if (m) {
    const a = parseInt(m[1]!, 10)
    const b = parseInt(m[2]!, 10)
    const year = parseInt(m[3]!, 10)
    const hasTime = /\d{1,2}:\d{2}/.test(str)
    // Disambiguate: if one part > 12, that must be the day
    if (a > 12 && b <= 12) return hasTime ? 'datetime' : 'date' // dd/MM/yyyy
    if (b > 12 && a <= 12) return hasTime ? 'datetime' : 'date' // MM/dd/yyyy
    // If ambiguous, use localeHint
    if (localeHint) {
      const l = localeHint.toLowerCase()
      if (l.includes('en-us')) return hasTime ? 'datetime' : 'date' // favor MM/dd for US
      if (l.includes('en-gb') || l.includes('zh-cn') || l.includes('de') || l.includes('fr') || l.includes('es')) {
        return hasTime ? 'datetime' : 'date' // favor dd/MM for EU/GB/zh-CN perception
      }
    }
    // As a safe fallback, ensure both a and b in 1..12 and year is sensible
    if (year >= 1900 && year <= 2100 && a >= 1 && a <= 31 && b >= 1 && b <= 31) {
      return hasTime ? 'datetime' : 'date'
    }
  }

  return 'none'
}

export function inferDataType(values: unknown[], opts: InferOptions = {}): InferredDataType {
  const { sampleSize, enumMaxUnique, enumMaxUniqueRatio } = { ...DEFAULTS, ...opts }
  const sample: unknown[] = []

  for (let i = 0; i < values.length && sample.length < sampleSize; i++) {
    sample.push(values[i])
  }

  let nonNull = 0
  let boolCount = 0
  let numberLike = 0
  let percentLike = 0
  let currencyLike = 0
  let dateOnly = 0
  let dateTime = 0
  let timeOnly = 0
  let intervalLike = 0
  let jsonLike = 0
  let stringCount = 0
  let multiLineCount = 0
  const unique = new Set<string>()

  for (const v of sample) {
    if (isNullish(v)) continue
    nonNull++

    // collect unique key (stringified) for enum detection
    unique.add(String(v))

    if (isBooleanLike(v)) boolCount++
    if (isPercentageLike(v)) percentLike++
    if (isCurrencyLike(v)) currencyLike++
    if (typeof v === 'object' && v !== null && !(v instanceof Date)) jsonLike++
    if (typeof v === 'string') {
      stringCount++
      const s = v
      if (s.includes('\n') || s.includes('\r')) multiLineCount++
      // JSON string detection (conservative): starts with { or [ and parse ok
      const t = s.trim()
      if ((t.startsWith('{') && t.endsWith('}')) || (t.startsWith('[') && t.endsWith(']'))) {
        let parsedOk = false
        try {
          JSON.parse(t)
          parsedOk = true
        } catch (_err) {
          parsedOk = false
        }
        if (parsedOk) jsonLike++
      }
      if (isNumberLike(v)) numberLike++
    } else {
      if (isNumberLike(v)) numberLike++
    }
    // Time-only vs duration/interval detection for strings (when not recognized as date/datetime)
    if (typeof v === 'string') {
      const s = v.trim()
      // ISO 8601 duration: P[nY][nM][nW][nD][T[nH][nM][nS]]
      if (/^P(?=\d|T)(\d+Y)?(\d+M)?(\d+W)?(\d+D)?(T(\d+H)?(\d+M)?(\d+S)?)?$/i.test(s)) {
        intervalLike++
      } else if (/(\d+\s+(years?|months?|weeks?|days?|hours?|hrs?|minutes?|mins?|seconds?|secs?))/i.test(s)) {
        intervalLike++
      } else {
        const m = s.match(/^(\d{1,3}):(\d{2})(?::(\d{2})(?:\.(\d{1,3}))?)?(?:\s*(AM|PM))?$/i)
        if (m) {
          const hh = parseInt(m[1]!, 10)
          const mm = parseInt(m[2]!, 10)
          const ss = m[3] ? parseInt(m[3]!, 10) : 0
          if (hh >= 24 || mm >= 60 || ss >= 60) {
            intervalLike++
          } else {
            timeOnly++
          }
        }
      }
    }

    const d = typeof v === 'string' ? (detectLocaleDate(v, opts.locale) || isDateLike(v)) : isDateLike(v)
    if (d === 'date') dateOnly++
    if (d === 'datetime') dateTime++
  }

  if (nonNull === 0) return 'text'

  // Ratios
  const boolR = boolCount / nonNull
  const numR = numberLike / nonNull
  const pctR = percentLike / nonNull
  const curR = currencyLike / nonNull
  const dateR = (dateOnly + dateTime) / nonNull
  const timeR = timeOnly / nonNull
  const intervalR = intervalLike / nonNull
  const jsonR = jsonLike / nonNull
  const multiR = stringCount > 0 ? multiLineCount / stringCount : 0

  // 1) Strong signals first
  if (boolR >= 0.95) return 'boolean'

  if (pctR >= 0.9) return 'percentage'
  if (curR >= 0.9) return 'currency'

  // JSON-like data (objects or JSON strings) dominates
  if (jsonR >= 0.9) return 'json'

  if (dateR >= 0.9) {
    // If time occurs notably, call datetime; else date
    const timeR = dateTime / (dateOnly + dateTime)
    return timeR >= 0.2 ? 'datetime' : 'date'
  }

  if (timeR >= 0.9) return 'time'
  if (intervalR >= 0.9) return 'interval'

  if (numR >= 0.95) return 'number'

  // 2) Enum detection (limited cardinality vs sample size)
  const uniqueCount = unique.size
  const uniqueRatio = uniqueCount / nonNull
  if (uniqueCount > 0 && uniqueCount <= enumMaxUnique && uniqueRatio <= enumMaxUniqueRatio && nonNull >= 10) {
    return 'enum'
  }

  // 3) Multiline text
  if (multiR >= 0.3) return 'multiline'

  // 4) Fallback
  return 'text'
}

export function inferDataTypeForColumn(
  data: RowData[],
  column: ColumnDef,
  opts: InferOptions = {}
): InferredDataType {
  // If user configured options for select-like columns, treat as enum
  const hasOptions = (column as any).options && Array.isArray((column as any).options)
  if (hasOptions) return 'enum'

  const values: unknown[] = []
  const field = column.field
  const { sampleSize = DEFAULTS.sampleSize } = opts
  for (let i = 0; i < data.length && values.length < sampleSize; i++) {
    values.push((data[i] as any)?.[field])
  }
  const locale = opts.locale || column.locale
  return inferDataType(values, { ...opts, locale })
}
