<!--
  CellDateEditor.vue - Date Input Editor

  Responsibilities:
  1. Date input with calendar picker
  2. Date formatting and parsing
  3. Min/max date constraints
  4. Locale-aware date handling
-->

<script setup lang="ts">
import { ref, nextTick, onMounted, computed, watch } from 'vue'
import type { GridAPI, ColumnDef, RowData, CellValue } from '../../../types'
import HtButton from '../../primitives/HtButton.vue'

// Props
const props = defineProps<{
  value: CellValue
  row: RowData
  column: ColumnDef
  cellRect: DOMRect
  gridAPI: GridAPI
}>()

// Emits
const emit = defineEmits<{
  'update:value': [value: CellValue]
  commit: []
  cancel: []
}>()

// Helper function to safely convert CellValue to date input format
function safeFormatDateForInput(value: CellValue): string {
  // Only pass valid date types to formatDateForInput
  if (value instanceof Date || typeof value === 'string' || value === null || value === undefined) {
    return formatDateForInput(value)
  }
  // For other types (number, boolean), return empty string
  return ''
}

// Local state
const inputRef = ref<HTMLInputElement>()
const localValue = ref<string>(safeFormatDateForInput(props.value))
const hasError = ref(false)
const errorMessage = ref('')

// Computed
const inputStyle = computed(() => ({
  position: 'absolute' as const,
  left: `${props.cellRect.left}px`,
  top: `${props.cellRect.top}px`,
  width: `${props.cellRect.width}px`,
  height: `${props.cellRect.height}px`,
  zIndex: 1000,
}))

const inputClasses = computed(() => [
  'ht-cell-editor',
  'ht-date-editor',
  'px-3 py-1',
  'border-2',
  hasError.value ? 'border-[var(--ht-danger)]' : 'border-[var(--ht-primary)]',
  'bg-[var(--ht-bg)]',
  'text-[var(--ht-text)]',
  'text-sm',
  'outline-none',
  'rounded',
  'shadow-md',
])

const inputProps = computed(() => {
  const baseProps = {
    type: 'date',
    autocomplete: 'off',
  }

  if (props.column.minDate) {
    ;(baseProps as Record<string, unknown>).min = formatDateForInput(props.column.minDate)
  }

  if (props.column.maxDate) {
    ;(baseProps as Record<string, unknown>).max = formatDateForInput(props.column.maxDate)
  }

  return baseProps
})

// Methods
function formatDateForInput(value: Date | string | null | undefined): string {
  if (!value) return ''

  try {
    const date = value instanceof Date ? value : new Date(value)
    if (isNaN(date.getTime())) return ''

    // Format as YYYY-MM-DD for input[type="date"]
    return date.toISOString().split('T')[0] || ''
  } catch {
    return ''
  }
}

function parseDate(value: string): Date | null {
  if (!value) return null

  try {
    const date = new Date(`${value}T00:00:00`)
    return isNaN(date.getTime()) ? null : date
  } catch {
    return null
  }
}

function formatDateForDisplay(date: Date | null): string {
  if (!date) return ''

  try {
    const locale = props.column.locale || 'en-US'
    const options = props.column.dateOptions || {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
    }

    return date.toLocaleDateString(locale, options)
  } catch {
    return date.toISOString().split('T')[0] || ''
  }
}

function validateDate(value: string): { valid: boolean; message?: string } {
  if (!value) {
    return { valid: true }
  }

  const date = parseDate(value)
  if (!date) {
    return {
      valid: false,
      message: 'Invalid date format',
    }
  }

  // Check min date constraint
  if (props.column.minDate) {
    const minDate = new Date(props.column.minDate)
    if (date < minDate) {
      return {
        valid: false,
        message: `Date must be after ${formatDateForDisplay(minDate)}`,
      }
    }
  }

  // Check max date constraint
  if (props.column.maxDate) {
    const maxDate = new Date(props.column.maxDate)
    if (date > maxDate) {
      return {
        valid: false,
        message: `Date must be before ${formatDateForDisplay(maxDate)}`,
      }
    }
  }

  return { valid: true }
}

const handleInput = (event: Event) => {
  const target = event.target as HTMLInputElement
  localValue.value = target.value

  // Validate input
  const validation = validateDate(target.value)
  hasError.value = !validation.valid
  errorMessage.value = validation.message || ''

  // Emit the parsed value
  const parsedValue = parseDate(target.value)
  emit('update:value', parsedValue)

  // Update plugin state
  if (props.gridAPI?.updateCellEditValue) {
    props.gridAPI.updateCellEditValue(parsedValue)
  }
}

const handleKeydown = (event: KeyboardEvent) => {
  switch (event.key) {
    case 'Enter':
      if (!event.shiftKey) {
        event.preventDefault()
        event.stopPropagation()
        if (!hasError.value) {
          emit('commit')
        }
      }
      break
    case 'Escape':
      event.preventDefault()
      event.stopPropagation()
      emit('cancel')
      break
    case 'Tab':
      event.preventDefault()
      event.stopPropagation()
      if (!hasError.value) {
        emit('commit')
      }
      break
    case 'ArrowDown':
    case 'ArrowUp':
      // Let browser handle date navigation
      break
  }
}

const handleBlur = () => {
  // Auto-commit on blur if no error
  if (!hasError.value) {
    emit('commit')
  }
}

const setToday = () => {
  const today = new Date()
  localValue.value = formatDateForInput(today)

  // Trigger input handling
  if (inputRef.value) {
    inputRef.value.value = localValue.value
    const event = new Event('input', { bubbles: true })
    inputRef.value.dispatchEvent(event)
  }
}

const clearDate = () => {
  localValue.value = ''

  // Trigger input handling
  if (inputRef.value) {
    inputRef.value.value = ''
    const event = new Event('input', { bubbles: true })
    inputRef.value.dispatchEvent(event)
  }
}

const focus = () => {
  if (inputRef.value) {
    inputRef.value.focus()
    // Show calendar picker on focus
    inputRef.value.showPicker?.()
  }
}

// Watch for external value changes
watch(
  () => props.value,
  newValue => {
    localValue.value = safeFormatDateForInput(newValue)
    hasError.value = false
    errorMessage.value = ''
  }
)

// Lifecycle
onMounted(async () => {
  await nextTick()
  focus()
})

// Expose methods for parent
defineExpose({
  focus,
})
</script>

<template>
  <div class="ht-date-container relative">
    <!-- Date input -->
    <input
      ref="inputRef"
      :value="localValue"
      :class="inputClasses"
      :style="inputStyle"
      :placeholder="column.placeholder || 'Select date'"
      v-bind="inputProps"
      @input="handleInput"
      @keydown="handleKeydown"
      @blur="handleBlur"
    />

    <!-- Quick action buttons (optional) -->
    <div
      v-if="column.showQuickActions !== false"
      class="absolute flex gap-1 z-[1001]"
      :style="{
        left: `${cellRect.right - 80}px`,
        top: `${cellRect.bottom + 2}px`,
      }"
    >
      <HtButton
        type="button"
        size="xs"
        variant="primary"
        title="Set to today"
        @click="setToday"
      >
        Today
      </HtButton>
      <HtButton
        type="button"
        size="xs"
        variant="secondary"
        title="Clear date"
        @click="clearDate"
      >
        Clear
      </HtButton>
    </div>

    <!-- Error message -->
    <div
      v-if="hasError && errorMessage"
      class="absolute bg-[var(--ht-danger)] text-[var(--ht-text-inverse)] text-xs px-2 py-1 rounded shadow-lg z-[1001]"
      :style="{
        left: `${cellRect.left}px`,
        top: `${cellRect.bottom + 2}px`,
        maxWidth: `${cellRect.width}px`,
      }"
    >
      {{ errorMessage }}
    </div>
  </div>
</template>

<style scoped>
.ht-date-editor:focus {
  box-shadow: 0 0 0 1px var(--ht-primary);
}

.ht-date-editor[aria-invalid='true'] {
  border-color: var(--ht-danger);
}

/* Style the calendar icon */
.ht-date-editor::-webkit-calendar-picker-indicator {
  cursor: pointer;
  opacity: 0.6;
  transition: opacity 0.2s;
}

.ht-date-editor:hover::-webkit-calendar-picker-indicator {
  opacity: 1;
}

/* positioning handled by ht-cell-editor utility */

/* Dark mode support for date input */
@media (prefers-color-scheme: dark) {
  .ht-date-editor::-webkit-calendar-picker-indicator {
    filter: invert(1);
  }
}
</style>
