<!--
  GridHeader.vue - Table Header Component

  Responsibilities:
  1. Render column headers with sorting indicators
  2. Handle column resize interactions
  3. Synchronize horizontal scroll with body
  4. Support three-pane layout (frozen columns)

  Performance: Sticky positioning, minimal re-renders
-->

<script setup lang="ts">
import { computed, ref, defineExpose, watch, nextTick } from 'vue'
import HeaderCell from './HeaderCell.vue'
import type { ColumnDef, PaneConfig, GridAPI, DragPosition, ColumnDragContext } from '../../../types'
import { isColumnDropBlocked, getColumnPinnedLocation } from '../../../utils/column-reorder'
import { createLogger } from '../../../utils/logger'

const logger = createLogger({ module: 'GridHeader' })

// Props
const props = defineProps<{
  columns: ColumnDef[]
  paneConfig: PaneConfig
  scrollLeft: number
  height: number
  isContentOverflowing: boolean
  gridAPI: GridAPI
  headerCellBorderClasses?: string
  selectedRows: (string | number)[]
  totalRows: number
}>()

// Emits
const emit = defineEmits<{
  'header-scroll': [scrollLeft: number]
  'sort-indicator-focus': [field: string]
  'select-all': []
  'clear-selection': []
}>()

// Refs
const centerPane = ref<HTMLElement>()
const centerScrollArea = ref<HTMLElement>()

// 用于防止循环同步的标志
let isSyncing = false

const draggedColumn = ref<ColumnDragContext | null>(null)

const columnOrderApi = computed(() => {
  return props.gridAPI as GridAPI & {
    moveColumn?: (columnId: string, targetIndex: number) => boolean
    setColumnOrder?: (order: string[]) => boolean
  }
})

// Computed
const headerStyle = computed(() => ({
  height: `${props.height}px`,
  position: 'sticky' as const,
  top: 0,
  // Raise z-index above body panes (which use z-10)
  zIndex: 40,
  background: 'var(--ht-header-bg)',
}))

const centerPaneStyle = computed(() => ({
  width: `${props.paneConfig.center.width}px`,
}))

// Watch prop changes to sync scroll position (from Body to Header)
watch(
  () => props.scrollLeft,
  (newScrollLeft) => {
    if (isSyncing) return
    if (!centerScrollArea.value) return
    if (centerScrollArea.value.scrollLeft === newScrollLeft) return

    isSyncing = true
    centerScrollArea.value.scrollLeft = newScrollLeft
    nextTick(() => {
      isSyncing = false
    })
  }
)

// Handle header scroll events (from Header to Body)
const handleHeaderScroll = (event: Event) => {
  if (isSyncing) return

  const target = event.target as HTMLElement
  const scrollLeft = target.scrollLeft

  isSyncing = true
  emit('header-scroll', scrollLeft)
  nextTick(() => {
    isSyncing = false
  })
}

// Handle sort indicator focus events
const handleSortIndicatorFocus = (field: string) => {
  emit('sort-indicator-focus', field)
}

const resetDragState = () => {
  draggedColumn.value = null
}

const handleColumnDragStart = (payload: ColumnDragContext) => {
  draggedColumn.value = payload
}

/**
 * Validate if the column drop operation is allowed
 * @returns Validation result with columns and order if valid, null otherwise
 */
const validateColumnDrop = (
  sourceId: string,
  targetColumnId: string
): { columns: ColumnDef[]; targetColumn: ColumnDef; order: string[] } | null => {
  const columns = props.gridAPI.getState().columns || []
  const targetColumn = columns.find(column => column.id === targetColumnId)

  if (!targetColumn) {
    return null
  }

  if (isColumnDropBlocked(draggedColumn.value, targetColumn)) {
    return null
  }

  const order = columns.map(column => column.id)
  if (!order.includes(sourceId)) {
    return null
  }

  return { columns, targetColumn, order }
}

/**
 * Calculate the insertion index for a dropped column within the same pinned section
 * @returns The index where the column should be inserted
 */
const calculateInsertionIndex = (
  filteredOrder: string[],
  targetColumnId: string,
  targetColumn: ColumnDef,
  position: DragPosition,
  columns: ColumnDef[]
): number => {
  if (!filteredOrder.includes(targetColumnId)) {
    return filteredOrder.length
  }

  const baseIndex = filteredOrder.findIndex(id => id === targetColumnId)
  const targetPinned = getColumnPinnedLocation(targetColumn)
  
  // Get all columns in the same pinned section
  const samePinnedColumns = filteredOrder
    .map(id => columns.find(column => column.id === id))
    .filter((column): column is ColumnDef => Boolean(column))
    .filter(column => getColumnPinnedLocation(column) === targetPinned)
    .map(column => column.id)

  const relativeIndex = samePinnedColumns.findIndex(id => id === targetColumnId)
  
  if (relativeIndex !== -1) {
    const mappedIndex = filteredOrder.findIndex(id => id === samePinnedColumns[relativeIndex])
    return position === 'before' ? mappedIndex : mappedIndex + 1
  }
  
  return position === 'before' ? baseIndex : baseIndex + 1
}

/**
 * Execute the column move operation using available API methods
 */
const executeColumnMove = (sourceId: string, insertionIndex: number, filteredOrder: string[]) => {
  const moveColumn = columnOrderApi.value.moveColumn

  if (typeof moveColumn === 'function') {
    moveColumn(sourceId, insertionIndex)
  } else if (typeof columnOrderApi.value.setColumnOrder === 'function') {
    const newOrder = [...filteredOrder]
    newOrder.splice(insertionIndex, 0, sourceId)
    columnOrderApi.value.setColumnOrder(newOrder)
  }
}

/**
 * Handle column drop event - orchestrates the entire drop operation
 */
const handleColumnDrop = (payload: { columnId: string; position: DragPosition }) => {
  const sourceId = draggedColumn.value?.columnId
  
  // Early exit if no valid source
  if (!sourceId || payload.columnId === sourceId) {
    resetDragState()
    return
  }

  // Validate the drop operation
  const validationResult = validateColumnDrop(sourceId, payload.columnId)
  if (!validationResult) {
    resetDragState()
    return
  }

  const { columns, targetColumn, order } = validationResult

  // Calculate where to insert the column
  const filteredOrder = order.filter(id => id !== sourceId)
  const insertionIndex = calculateInsertionIndex(
    filteredOrder,
    payload.columnId,
    targetColumn,
    payload.position,
    columns
  )

  // Execute the move
  executeColumnMove(sourceId, insertionIndex, filteredOrder)

  resetDragState()
}

const handleColumnDragEnd = () => {
  resetDragState()
}

// Programmatic open: scroll the center pane to make the column visible, then re-emit open event
if (typeof props.gridAPI.on === 'function') {
  props.gridAPI.on('column-open-filter', payload => {
    try {
      const field = payload?.field
      if (!field) return
      if (payload?.__skipHeader) return
      const columns = props.gridAPI.getState().columns || []
      const col = columns.find((c: any) => c.field === field || c.id === field)
      if (!col) return
      const pinned = getColumnPinnedLocation(col)
      if (pinned === 'center') {
        const targetIndex = (props.paneConfig.center.columns || []).findIndex(
          (c: any) => c.field === field || c.id === field
        )
        if (targetIndex >= 0 && centerScrollArea.value) {
          // Compute target left by summing widths of previous center columns
          const centerCols = props.paneConfig.center.columns as any[]
          let targetLeft = 0
          for (let i = 0; i < targetIndex; i++) {
            const w = Number(centerCols[i]?.width) || 150
            targetLeft += w
          }
          const cellWidth = Number(centerCols[targetIndex]?.width) || 150
          const el = centerScrollArea.value
          const viewLeft = el.scrollLeft
          const viewRight = viewLeft + el.clientWidth
          let newLeft = viewLeft
          if (targetLeft < viewLeft) newLeft = targetLeft
          else if (targetLeft + cellWidth > viewRight) newLeft = Math.max(0, targetLeft - (el.clientWidth - cellWidth))
          if (newLeft !== viewLeft) {
            try {
              el.scrollTo({ left: newLeft, behavior: 'smooth' })
            } catch {
              el.scrollLeft = newLeft
            }
          }
          // Re-emit after scroll to ensure popover opens in-view
          // Wait ~11 frames (~180ms at 60fps) for smooth scroll animation
          let frameCount = 0
          const waitForScroll = () => {
            frameCount++
            if (frameCount < 11) {
              requestAnimationFrame(waitForScroll)
            } else {
              props.gridAPI.emit('column-open-filter', { field, __skipHeader: true })
            }
          }
          requestAnimationFrame(waitForScroll)
          return
        }
      }
      // For pinned columns or if not found in center, directly re-emit to open
      requestAnimationFrame(() => props.gridAPI.emit('column-open-filter', { field, __skipHeader: true }))
    } catch (err) {
      logger.warn('Failed to handle column-open-filter event', err)
    }
  })
}

const handleSelectAll = () => {
  emit('select-all')
}

const handleClearSelection = () => {
  emit('clear-selection')
}

// Expose methods to parent - 不再需要焦点相关的方法
defineExpose({
  // 保留为空对象以防止破坏现有的 ref 调用
})
</script>

<template>
  <div class="ht-header flex" :style="headerStyle" role="rowgroup">
    <!-- Left Frozen Pane -->
    <div
      v-if="paneConfig.left.columns.length > 0"
      class="ht-pane-left flex-shrink-0 flex bg-[var(--ht-header-bg)] relative z-10"
      :style="{ width: `${paneConfig.left.width}px` }"
    >
      <HeaderCell
        v-for="column in paneConfig.left.columns"
        :key="column.id"
        :column="column"
        :width="column.width || 150"
        :grid-a-p-i="gridAPI"
        :drag-context="draggedColumn"
        :border-classes="headerCellBorderClasses"
        :selected-rows="selectedRows"
        :total-rows="totalRows"
        @column-drag-start="handleColumnDragStart"
        @column-drop="handleColumnDrop"
        @column-drag-end="handleColumnDragEnd"
        @select-all="handleSelectAll"
        @clear-selection="handleClearSelection"
      />
    </div>

    <!-- Center Scrollable Pane -->
    <div
      ref="centerScrollArea"
      class="ht-pane-center overflow-x-auto overflow-y-visible relative ht-hide-scrollbar"
      :class="{ 'flex-1': isContentOverflowing }"
      @scroll="handleHeaderScroll"
    >
      <div ref="centerPane" class="flex" :style="centerPaneStyle">
        <HeaderCell
          v-for="column in paneConfig.center.columns"
          :key="column.id"
          :column="column"
          :width="column.width || 150"
          :grid-a-p-i="gridAPI"
          :drag-context="draggedColumn"
          :border-classes="headerCellBorderClasses"
          :selected-rows="selectedRows"
          :total-rows="totalRows"
          @sort-indicator-focus="handleSortIndicatorFocus"
          @column-drag-start="handleColumnDragStart"
          @column-drop="handleColumnDrop"
          @column-drag-end="handleColumnDragEnd"
          @select-all="handleSelectAll"
          @clear-selection="handleClearSelection"
        />
      </div>
    </div>

    <!-- Right Frozen Pane -->
    <div
      v-if="paneConfig.right.columns.length > 0"
      class="ht-pane-right flex-shrink-0 flex bg-[var(--ht-header-bg)] relative z-10"
      :style="{ width: `${paneConfig.right.width}px` }"
    >
      <HeaderCell
        v-for="column in paneConfig.right.columns"
        :key="column.id"
        :column="column"
        :width="column.width || 150"
        :grid-a-p-i="gridAPI"
        :drag-context="draggedColumn"
        :border-classes="headerCellBorderClasses"
        :selected-rows="selectedRows"
        :total-rows="totalRows"
        @column-drag-start="handleColumnDragStart"
        @column-drop="handleColumnDrop"
        @column-drag-end="handleColumnDragEnd"
        @select-all="handleSelectAll"
        @clear-selection="handleClearSelection"
      />
    </div>
  </div>
</template>

<style scoped>
.ht-header {
  /* Remove paint containment to allow header popovers to overflow below header */
  contain: layout style;
  position: relative;
}

/* Single bottom 2px gradient highlight to separate header from body */
.ht-header::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: var(--ht-pane-edge-width);
  background: linear-gradient(to bottom, var(--ht-pane-edge-highlight), transparent);
  z-index: var(--z-ht-header);
  pointer-events: none;
}

/* Shadow for pinned panes is now defined in pinned-columns.css */

/* 隐藏标题行的滚动条，但保持滚动功能 */
.ht-hide-scrollbar {
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE 10+ */
}

.ht-hide-scrollbar::-webkit-scrollbar {
  display: none; /* Chrome, Safari, Edge */
}
</style>
