<template>
  <div
    class="ht-radio-header"
    data-ht-functional="radio-header"
  >
    <span class="ht-radio-header-label" :title="headerTitle">
      {{ headerLabel }}
    </span>
  </div>
</template>

<script setup lang="ts">
import { computed } from 'vue'
import type { RadioColumnOptions } from '../../types'

interface Props {
  /** Total number of selectable rows */
  totalSelectableRows: number
  /** Number of currently selected rows (should be 0 or 1) */
  selectedCount: number
  /** Column configuration options */
  options?: RadioColumnOptions
}

const props = withDefaults(defineProps<Props>(), {
  options: () => ({}),
})

const headerLabel = computed(() => {
  return props.options?.headerLabel || ''
})

const headerTitle = computed(() => {
  if (props.selectedCount === 1) {
    return '1 row selected'
  }
  return `${props.totalSelectableRows} rows available`
})
</script>

<script lang="ts">
export default {
  name: 'HtRadioHeader',
}
</script>

