<script setup lang="ts">
/**
 * HtSelectContent - 下拉内容容器
 *
 * 职责:
 * - 包装 BasePopover 实现定位
 * - 内容滚动区域
 * - 自动匹配 trigger 宽度
 */
import { ref, computed, watch } from 'vue'
import BasePopover from '../../shared/BasePopover.vue'
import { injectSelectContext, injectSelectProps } from './composables/useSelectContext'

const props = withDefaults(defineProps<{
  placement?: 'bottom' | 'top' | 'left' | 'right'
  align?: 'start' | 'center' | 'end'
  maxHeight?: string
}>(), {
  placement: 'bottom',
  align: 'start',
  maxHeight: '300px',
})

const context = injectSelectContext()
const selectProps = injectSelectProps()
const contentRef = ref<HTMLElement | null>(null)

// Auto-detect trigger width
const triggerWidth = ref<string>('')

// Watch for trigger element changes and measure its width
watch(
  () => context.triggerRef.value,
  (triggerEl) => {
    if (triggerEl) {
      // Get computed width of trigger element
      const computedWidth = triggerEl.getBoundingClientRect().width
      triggerWidth.value = `${computedWidth}px`
    }
  },
  { immediate: true }
)

// Also update width when dropdown opens (in case trigger size changed)
watch(
  () => context.open.value,
  (isOpen) => {
    if (isOpen && context.triggerRef.value) {
      const computedWidth = context.triggerRef.value.getBoundingClientRect().width
      triggerWidth.value = `${computedWidth}px`
    }
  }
)

// Compute content style with auto-detected width
const contentStyle = computed(() => {
  const baseStyle: Record<string, string> = {
    maxHeight: props.maxHeight,
  }

  // Priority: explicit absolute width > auto-detected width > fallback
  // Note: Percentage widths (like "100%") don't work with fixed positioning,
  // so we treat them as "auto" and use trigger width instead
  const hasAbsoluteWidth = selectProps.width && !selectProps.width.includes('%')

  if (hasAbsoluteWidth) {
    // Explicit absolute width set by user (e.g., "280px", "20rem")
    baseStyle.width = selectProps.width!
  } else if (triggerWidth.value) {
    // Auto-detected trigger width: use as fixed width (not minWidth)
    // This keeps dropdown width consistent with trigger
    // Also used when width is percentage (e.g., "100%")
    baseStyle.width = triggerWidth.value
  } else {
    // Fallback: minimum width
    baseStyle.width = '8rem'
  }

  return baseStyle
})
</script>

<template>
  <BasePopover
    :open="context.open.value"
    :anchor="context.triggerRef.value"
    :position="{ placement, align, offset: 4, autoFlip: true }"
    role="dialog"
    :close-on-click-outside="true"
    :close-on-escape="true"
    :trap-focus="false"
    :keep-alive="true"
    @update:open="context.open.value = $event"
  >
    <div
      ref="contentRef"
      :id="context.ids.contentId"
      role="listbox"
      :aria-labelledby="context.ids.triggerId"
      tabindex="-1"
      class="ht-select-content"
      :style="contentStyle"
    >
      <slot />
    </div>
  </BasePopover>
</template>

<style scoped>
.ht-select-content {
  overflow-y: auto;
  overflow-x: hidden;
  border-radius: var(--radius-ht-md);
  border: 1px solid var(--ht-border);
  background-color: var(--ht-bg);
