  box-shadow: var(--shadow-ht-md);
  padding: 0.25rem;
  outline: none;

  /* Smooth scrolling for better UX */
  scroll-behavior: smooth;

  /* Scroll shadows for better depth perception */
  background:
    /* Top shadow */
    linear-gradient(var(--ht-bg) 30%, rgba(255, 255, 255, 0)),
    linear-gradient(rgba(255, 255, 255, 0), var(--ht-bg) 70%) 0 100%,
    /* Top shadow cover */
    radial-gradient(farthest-side at 50% 0, rgba(0, 0, 0, 0.15), transparent),
    /* Bottom shadow cover */
    radial-gradient(farthest-side at 50% 100%, rgba(0, 0, 0, 0.15), transparent) 0 100%;
  background-repeat: no-repeat;
  background-color: var(--ht-bg);
  background-size: 100% 40px, 100% 40px, 100% 14px, 100% 14px;
  background-attachment: local, local, scroll, scroll;
}

/* Custom scrollbar */
.ht-select-content::-webkit-scrollbar {
  width: var(--ht-scrollbar-width);
}

.ht-select-content::-webkit-scrollbar-track {
  background: var(--ht-scrollbar-track);
  border-radius: var(--radius-ht-md);
}

.ht-select-content::-webkit-scrollbar-thumb {
  background: var(--ht-scrollbar-thumb);
  border-radius: var(--radius-ht-md);
  transition: background 150ms ease;
}

.ht-select-content::-webkit-scrollbar-thumb:hover {
  background: var(--ht-scrollbar-thumb-hover);
}
</style>
