/**
 * Unified Plugin Registry - Modern Architecture
 *
 * Eliminates dual registry mental burden through composition-based design.
 * Follows SOLID principles with clear separation of concerns.
 * Provides zero-configuration defaults with progressive enhancement.
 *
 * Architecture: Core Registry + Composable Extensions + Smart Defaults
 */

import type { GridAPI } from '../types'
import type {
  GridPlugin,
  PluginError,
  GridCapabilities,
  CapabilityGetter,
  TaskStatus,
} from './index'
import { createLogger } from '../utils/logger'

// Core plugin operation result
export interface PluginOperationResult {
  success: boolean
  error?: PluginError
  metadata?: Record<string, any>
}

// Extension interface for modular functionality
export interface UnifiedRegistryExtension {
  name: string
  install(registry: CorePluginRegistry): void
  uninstall?(): void
}

// Health status for monitoring
export interface PluginHealth {
  pluginName: string
  healthy: boolean
  errorCount: number
  lastError?: PluginError
  healthScore: number
  recoveryAttempts: number
}

export interface SystemHealth {
  overallHealth: number
  totalPlugins: number
  healthyPlugins: number
  criticalIssues: number
  recommendations: string[]
}

// Configuration interface
export interface UnifiedPluginRegistryOptions {
  // Environment configuration
  mode?: 'development' | 'production' | 'auto'

  // Feature flags
  enableErrorHandling?: boolean
  enableMonitoring?: boolean
  enableDebugging?: boolean
  enableCapabilities?: boolean

  // Performance settings
  errorRetryLimit?: number
  healthCheckInterval?: number
  asyncTimeout?: number

  // Callbacks
  onError?: (error: PluginError, context: string) => void
  onRecovery?: (pluginName: string, success: boolean) => void
  onHealthChange?: (health: SystemHealth) => void
}

/**
 * Core Plugin Registry - Focused on essential plugin operations
 * Follows single responsibility principle
 */
export class CorePluginRegistry {
  private logger = createLogger('CorePluginRegistry')
  private plugins = new Map<string, GridPlugin>()
  private loadOrder: string[] = []
  private extensions = new Map<string, UnifiedRegistryExtension>()
  private api: GridAPI

  constructor(api: GridAPI) {
    this.api = api
  }

  // Core plugin operations - simple and focused
  async register(plugin: GridPlugin): Promise<PluginOperationResult> {
    try {
      // Basic validation
      if (!plugin.name || typeof plugin.install !== 'function') {
        throw new Error(`Invalid plugin: missing name or install function`)
      }

      if (this.plugins.has(plugin.name)) {
        throw new Error(`Plugin '${plugin.name}' is already registered`)
      }

      // Store plugin
      this.plugins.set(plugin.name, plugin)

      // Install with basic error handling
      await this.installPlugin(plugin)

      // Track load order
      this.loadOrder.push(plugin.name)

      return { success: true }
    } catch (error) {
      const pluginError: PluginError = {
        type: 'installation',
        message: error instanceof Error ? error.message : 'Unknown error',
        name: error instanceof Error ? error.name : 'PluginInstallationError',
        timestamp: Date.now(),
        stack: error instanceof Error ? error.stack : undefined,
      }

      // Notify extensions
      this.notifyExtensions('plugin-error', { plugin: plugin.name, error: pluginError })

      return { success: false, error: pluginError }
    }
  }

  async unregister(pluginName: string): Promise<PluginOperationResult> {
    const plugin = this.plugins.get(pluginName)
    if (!plugin) {
      return {
        success: false,
        error: {
          type: 'installation',
          message: `Plugin '${pluginName}' not found`,
          name: 'PluginNotFoundError',
          timestamp: Date.now(),
        },
      }
    }

    try {
      // Cleanup plugin
      if (plugin.cleanup) {
        await plugin.cleanup()
      }

      // Remove from registry
      this.plugins.delete(pluginName)
      this.loadOrder = this.loadOrder.filter(name => name !== pluginName)

      // Notify extensions
      this.notifyExtensions('plugin-unregistered', { plugin: pluginName })

      return { success: true }
    } catch (error) {
      const pluginError: PluginError = {
        type: 'installation',
        message: error instanceof Error ? error.message : 'Cleanup failed',
        name: error instanceof Error ? error.name : 'PluginCleanupError',
        timestamp: Date.now(),
        stack: error instanceof Error ? error.stack : undefined,
      }

      return { success: false, error: pluginError }
    }
  }

  get(pluginName: string): GridPlugin | undefined {
    return this.plugins.get(pluginName)
  }

  list(): GridPlugin[] {
    return Array.from(this.plugins.values())
  }

  getLoadOrder(): string[] {
    return [...this.loadOrder]
  }

  // Extension system for progressive enhancement
  use(extension: UnifiedRegistryExtension): this {
    if (this.extensions.has(extension.name)) {
      this.logger.warn(`Extension '${extension.name}' is already installed`)
      return this
    }

    this.extensions.set(extension.name, extension)
    extension.install(this)

    return this
  }

  unuse(extensionName: string): this {
    const extension = this.extensions.get(extensionName)
    if (extension) {
      extension.uninstall?.()
      this.extensions.delete(extensionName)
    }
    return this
  }

  // Extension access for composition
  getExtension<T extends UnifiedRegistryExtension>(name: string): T | undefined {
    return this.extensions.get(name) as T | undefined
  }

  // Internal methods
  private async installPlugin(plugin: GridPlugin): Promise<void> {
    // Get capability getter from capability extension
    const capabilityExt = this.getExtension<CapabilityExtension>('capabilities')
    const capabilityGetter: CapabilityGetter = capabilityExt
      ? capabilityExt.createGetter()
      : () => undefined

    // Install plugin with API and capability access
    await plugin.install(this.api, capabilityGetter)
  }

  private notifyExtensions(event: string, data: any): void {
    this.extensions.forEach(extension => {
      try {
        if ('onEvent' in extension && typeof extension.onEvent === 'function') {
          ;(extension.onEvent as (event: string, data: any) => void)(event, data)
        }
      } catch (error) {
        this.logger.warn(`Extension '${extension.name}' error in event '${event}':`, error)
      }
    })
  }

  // Cleanup
  async cleanup(): Promise<void> {
    // Unregister all plugins in reverse order
    const pluginNames = [...this.loadOrder].reverse()
    for (const name of pluginNames) {
      await this.unregister(name)
    }

    // Cleanup extensions
    for (const extension of this.extensions.values()) {
      extension.uninstall?.()
    }
    this.extensions.clear()
  }
}

/**
 * Capability Extension - Manages capability system
 */
export class CapabilityExtension implements UnifiedRegistryExtension {
  name = 'capabilities'
  private capabilities = new Map<keyof GridCapabilities, any>()
  private logger = createLogger('CapabilityExtension')

  install(_registry: CorePluginRegistry): void {
    // Registry reference not needed for this implementation
    this.initializeDefaultCapabilities()
  }

  uninstall(): void {
    this.capabilities.clear()
  }

  register<T extends keyof GridCapabilities>(name: T, capability: GridCapabilities[T]): void {
    this.capabilities.set(name, capability)
  }

  get<T extends keyof GridCapabilities>(name: T): GridCapabilities[T] | undefined {
    return this.capabilities.get(name) as GridCapabilities[T] | undefined
  }

  list(): (keyof GridCapabilities)[] {
    return Array.from(this.capabilities.keys())
  }

  createGetter(): CapabilityGetter {
    return <T extends keyof GridCapabilities>(name: T) => this.get(name)
  }

  private initializeDefaultCapabilities(): void {
    // Initialize built-in capabilities
    this.initializeDataPipelineCapability()
    this.initializeAsyncTasksCapability()
    this.initializeWebSocketCapability()
    this.initializeAICapability()
  }

  private initializeDataPipelineCapability(): void {
    const interceptors = new Map<string, any[]>()

    this.register('data-pipeline', {
      registerDataInterceptor: (type: string, interceptor: any) => {
        if (!interceptors.has(type)) {
          interceptors.set(type, [])
        }
        const typeInterceptors = interceptors.get(type)!
        const existing = typeInterceptors.findIndex(i => i.name === interceptor.name)

        if (existing !== -1) {
          typeInterceptors[existing] = interceptor
        } else {
          typeInterceptors.push(interceptor)
          typeInterceptors.sort((a, b) => b.priority - a.priority)
        }
      },

      removeDataInterceptor: (type: string, name: string) => {
        const typeInterceptors = interceptors.get(type)
        if (typeInterceptors) {
          const index = typeInterceptors.findIndex(i => i.name === name)
          if (index !== -1) {
            typeInterceptors.splice(index, 1)
          }
        }
      },

      getDataPipeline: () => {
        const allInterceptors: any[] = []
        for (const [_, typeInterceptors] of interceptors) {
          allInterceptors.push(...typeInterceptors)
        }
        return allInterceptors
      },

      processData: (data: any[], type: string) => {
        const typeInterceptors = interceptors.get(type)
        if (!typeInterceptors || typeInterceptors.length === 0) {
          return data
        }

        let processedData = data
        for (const interceptor of typeInterceptors) {
          if (interceptor.enabled !== false) {
            try {
              processedData = interceptor.transform(processedData)
            } catch (error) {
              this.logger.error(`Error in interceptor ${interceptor.name}:`, error)
            }
          }
        }
        return processedData
      },

      clearPipeline: (type?: string) => {
        if (type) {
          interceptors.delete(type)
        } else {
          interceptors.clear()
        }
      },
    })
  }

  private initializeAsyncTasksCapability(): void {
    const tasks = new Map<string, TaskStatus>()
    let taskIdCounter = 0

    this.register('async-tasks', {
      scheduleAsyncTask: async (task: any) => {
        const taskId = `task-${++taskIdCounter}-${Date.now()}`
        const taskStatus: TaskStatus = {
          id: taskId,
          name: task.name,
          status: 'pending',
          startTime: Date.now(),
        }

        tasks.set(taskId, taskStatus)

        // Execute task asynchronously
        setTimeout(
          async () => {
            try {
              taskStatus.status = 'running'
              const result = await task.execute()
              taskStatus.status = 'completed'
              taskStatus.result = result
              taskStatus.endTime = Date.now()
            } catch (error) {
              taskStatus.status = 'failed'
              taskStatus.error = error as Error
              taskStatus.endTime = Date.now()
            }
          },
          task.priority === 'high' ? 0 : 10
        )

        return taskId
      },

      cancelTask: (taskId: string) => {
        const task = tasks.get(taskId)
        if (task && (task.status === 'pending' || task.status === 'running')) {
          task.status = 'cancelled'
          task.endTime = Date.now()
          return true
        }
        return false
      },

      getTaskStatus: (taskId: string) => {
        return (
          tasks.get(taskId) ||
          ({
            id: taskId,
            name: 'unknown',
            status: 'failed',
            error: new Error('Task not found'),
          } as TaskStatus)
        )
      },

      getAllTasks: () => new Map(tasks),

      onTaskComplete: () => {}, // Stub implementation
      onTaskError: () => {}, // Stub implementation
      clearCompletedTasks: () => {
        for (const [taskId, task] of tasks) {
          if (['completed', 'failed', 'cancelled'].includes(task.status)) {
            tasks.delete(taskId)
          }
        }
      },
    })
  }

  private initializeWebSocketCapability(): void {
    this.register('websocket', {
      connect: async () => {
        // Stub implementation
        return {
          id: `ws-${Date.now()}`,
          url: '',
          readyState: 1,
          send: () => {},
          close: () => {},
          addEventListener: () => {},
          removeEventListener: () => {},
        }
      },
      disconnect: () => {},
      subscribe: () => () => {},
      unsubscribe: () => {},
      broadcast: () => {},
      getConnection: () => null,
      getConnectionState: () => 'disconnected' as const,
    })
  }

  private initializeAICapability(): void {
    this.register('ai', {
      analyzeData: async () => ({
        summary: 'Stub analysis',
        patterns: [],
        recommendations: [],
        statistics: { totalRecords: 0, fields: {} },
        confidence: 0.8,
      }),
      detectAnomalies: async () => [],
      suggestOptimizations: async () => [],
      generateSummary: async () => ({
        recordCount: 0,
        fieldSummaries: [],
        insights: [],
        quality: { completeness: 1, consistency: 1, accuracy: 1 },
      }),
      predictTrends: async (_data: any[], field: string) => ({
        field,
        predictions: [],
        trend: 'stable' as const,
        accuracy: 0.75,
      }),
      classifyData: async (_data: any[], field: string) => ({
        field,
        categories: [],
        confidence: 0.8,
      }),
    })
  }
}

/**
 * Error Handling Extension - Progressive error management
 */
export class ErrorHandlingExtension implements UnifiedRegistryExtension {
  name = 'error-handling'
  private errors = new Map<string, PluginError>()
  private mode: 'development' | 'production' = 'production'
  private onErrorCallback?: (error: PluginError, context: string) => void
  private logger = createLogger('ErrorHandlingExtension')

  constructor(
    options: {
      mode?: 'development' | 'production'
      onError?: (error: PluginError, context: string) => void
    } = {}
  ) {
    this.mode = options.mode || 'production'
    this.onErrorCallback = options.onError
  }

  install(_registry: CorePluginRegistry): void {
    // Extension is self-contained
  }

  onEvent(event: string, data: any): void {
    if (event === 'plugin-error') {
      this.handleError(data.error, data.plugin)
    }
  }

  private handleError(error: PluginError, pluginName: string): void {
    this.errors.set(pluginName, error)

    if (this.mode === 'development') {
      // Fail fast in development
      this.logger.error(`Plugin error in ${pluginName}:`, error)
      throw new Error(`Plugin '${pluginName}' failed: ${error.message}`)
    } else {
      // Graceful handling in production
      this.logger.warn(`Plugin error in ${pluginName}:`, error.message)
      this.onErrorCallback?.(error, pluginName)
    }
  }

  getErrors(): Map<string, PluginError> {
    return new Map(this.errors)
  }

  clearErrors(): void {
    this.errors.clear()
  }

  async recover(pluginName: string): Promise<boolean> {
    // Basic recovery implementation
    this.errors.delete(pluginName)
    return true
  }
}

/**
 * Monitoring Extension - Performance and health tracking
 */
export class MonitoringExtension implements UnifiedRegistryExtension {
  name = 'monitoring'
  private healthData = new Map<string, PluginHealth>()
  private metrics = {
    totalOperations: 0,
    successfulOperations: 0,
    failedOperations: 0,
    averageOperationTime: 0,
  }

  install(_registry: CorePluginRegistry): void {
    // Start basic monitoring
  }

  onEvent(event: string, data: any): void {
    if (event === 'plugin-error') {
      this.recordError(data.plugin)
    } else if (event === 'plugin-unregistered') {
      this.recordSuccess()
    }
  }

  private recordError(pluginName: string): void {
    const health = this.healthData.get(pluginName) || {
      pluginName,
      healthy: true,
      errorCount: 0,
      healthScore: 100,
      recoveryAttempts: 0,
    }

    health.errorCount++
    health.healthy = health.errorCount < 3
    health.healthScore = Math.max(0, 100 - health.errorCount * 20)

    this.healthData.set(pluginName, health)
    this.metrics.failedOperations++
  }

  private recordSuccess(): void {
    this.metrics.successfulOperations++
  }

  getHealth(pluginName?: string): PluginHealth | SystemHealth {
    if (pluginName) {
      return (
        this.healthData.get(pluginName) || {
          pluginName,
          healthy: true,
          errorCount: 0,
          healthScore: 100,
          recoveryAttempts: 0,
        }
      )
    }

    // System health
    const allHealth = Array.from(this.healthData.values())
    const healthyPlugins = allHealth.filter(h => h.healthy).length
    const totalPlugins = allHealth.length

    return {
      overallHealth: totalPlugins > 0 ? (healthyPlugins / totalPlugins) * 100 : 100,
      totalPlugins,
      healthyPlugins,
      criticalIssues: allHealth.filter(h => h.healthScore < 30).length,
      recommendations: this.generateRecommendations(allHealth),
    }
  }

  private generateRecommendations(healthData: PluginHealth[]): string[] {
    const recommendations: string[] = []

    const criticalPlugins = healthData.filter(h => h.healthScore < 30)
    if (criticalPlugins.length > 0) {
      recommendations.push(`${criticalPlugins.length} plugins need immediate attention`)
    }

    return recommendations
  }
}

/**
 * Smart Factory Function - Zero Configuration with Progressive Enhancement
 */
export function createUnifiedPluginRegistry(
  api: GridAPI,
  options: UnifiedPluginRegistryOptions = {}
): CorePluginRegistry {
  const registry = new CorePluginRegistry(api)

  // Determine mode
  const mode =
    options.mode === 'auto'
      ? process.env.NODE_ENV === 'development'
        ? 'development'
        : 'production'
      : options.mode || 'production'

  // Always enable capabilities (core functionality)
  if (options.enableCapabilities !== false) {
    registry.use(new CapabilityExtension())
  }

  // Enable error handling based on mode
  if (options.enableErrorHandling !== false) {
    registry.use(
      new ErrorHandlingExtension({
        mode,
        onError: options.onError,
      })
    )
  }

  // Enable monitoring for production or large datasets
  if (options.enableMonitoring !== false) {
    registry.use(new MonitoringExtension())
  }

  return registry
}

// Convenience methods for progressive enhancement
export function withErrorHandling(
  registry: CorePluginRegistry,
  options: { mode?: 'development' | 'production' } = {}
): CorePluginRegistry {
  return registry.use(new ErrorHandlingExtension(options))
}

export function withMonitoring(registry: CorePluginRegistry): CorePluginRegistry {
  return registry.use(new MonitoringExtension())
}

export function withCapabilities(registry: CorePluginRegistry): CorePluginRegistry {
  return registry.use(new CapabilityExtension())
}

// Export type aliases for compatibility
export type UnifiedPluginRegistry = CorePluginRegistry
export interface RegistryFeatures {
  errorHandling: boolean
  monitoring: boolean
  debugging: boolean
  capabilities: boolean
}

// Alias for backward compatibility with main plugins index export
export const createPluginRegistry = createUnifiedPluginRegistry
