<script setup lang="ts">
/**
 * HtSelectGroup - 分组容器
 *
 * 职责:
 * - 包裹分组标签和分组选项
 * - 提供分组语义化结构
 * - 支持分组样式定制
 * - 提供分组状态给子选项 (用于缩进)
 */
import { provide } from 'vue'
import { SELECT_GROUP_KEY } from './composables/useSelectContext'

defineProps<{
  /**
   * 分组标识符 (用于 aria-labelledby)
   */
  id?: string
}>()

// 提供分组状态给子选项，用于应用缩进样式
provide(SELECT_GROUP_KEY, true)
</script>

<template>
  <div
    role="group"
    :aria-labelledby="id"
    class="ht-select-group"
  >
    <slot />
  </div>
</template>

<style scoped>
.ht-select-group {
  /* Group container with better spacing */
  padding: 0;
}

/* Space between groups */
.ht-select-group + .ht-select-group {
  margin-top: 0.5rem;
}

/* Add bottom padding to last item in group for visual separation */
.ht-select-group > :last-child {
  margin-bottom: 0.25rem;
}
</style>
