/**
 * Memory Management and Leak Prevention
 *
 * Features:
 * - Automatic cleanup on component unmount
 * - WeakMap-based caching to prevent memory leaks
 * - Resource pooling and recycling
 * - Memory usage monitoring
 * - Automatic garbage collection triggers
 */

import { onUnmounted, ref, watch } from 'vue'
import { createLogger } from './logger'

const logger = createLogger('MemoryManagement')

// WeakMap-based cache that doesn't prevent garbage collection
export class WeakCache<K extends object, V> {
  private cache = new WeakMap<K, V>()

  get(key: K): V | undefined {
    return this.cache.get(key)
  }

  set(key: K, value: V): void {
    this.cache.set(key, value)
  }

  has(key: K): boolean {
    return this.cache.has(key)
  }

  delete(key: K): void {
    this.cache.delete(key)
  }
}

// Resource cleanup manager
export class ResourceManager {
  private resources = new Set<() => void>()
  private timers = new Set<number>()
  private intervals = new Set<number>()
  private listeners = new Map<
    EventTarget,
    Array<{ event: string; handler: EventListener; options?: AddEventListenerOptions }>
  >()

  // Register cleanup function
  register(cleanup: () => void): void {
    this.resources.add(cleanup)
  }

  // Register timer for automatic cleanup
  setTimeout(callback: () => void, delay: number): number {
    const id = window.setTimeout(callback, delay)
    this.timers.add(id)
    return id
  }

  // Register interval for automatic cleanup
  setInterval(callback: () => void, interval: number): number {
    const id = window.setInterval(callback, interval)
    this.intervals.add(id)
    return id
  }

  // Register event listener for automatic cleanup
  addEventListener(
    target: EventTarget,
    event: string,
    handler: EventListener,
    options?: AddEventListenerOptions
  ): void {
    target.addEventListener(event, handler, options)

    if (!this.listeners.has(target)) {
      this.listeners.set(target, [])
    }

    this.listeners.get(target)!.push({ event, handler, options })
  }

  // Clear specific timer
  clearTimeout(id: number): void {
    clearTimeout(id)
    this.timers.delete(id)
  }

  // Clear specific interval
  clearInterval(id: number): void {
    clearInterval(id)
    this.intervals.delete(id)
  }

  // Cleanup all resources
  cleanup(): void {
    // Execute cleanup functions
    this.resources.forEach(cleanup => {
      try {
        cleanup()
      } catch (error) {
        logger.warn('Error during cleanup:', { module: 'MemoryManagement' }, error)
      }
    })

    // Clear timers
    this.timers.forEach(id => clearTimeout(id))

    // Clear intervals
    this.intervals.forEach(id => clearInterval(id))

    // Remove event listeners
    this.listeners.forEach((listeners, target) => {
      listeners.forEach(({ event, handler, options }) => {
        target.removeEventListener(event, handler, options)
      })
    })

    // Clear collections
    this.resources.clear()
    this.timers.clear()
    this.intervals.clear()
    this.listeners.clear()
  }
}

// Memory usage monitor
export class MemoryMonitor {
  private measurements: Array<{ timestamp: number; used: number; total: number }> = []
  private interval: number
  private onThreshold?: (usage: number) => void
  private threshold: number

  constructor(intervalMs: number = 5000, thresholdMB: number = 200) {
    this.threshold = thresholdMB * 1024 * 1024 // Convert to bytes
    this.interval = setInterval(() => {
      this.measure()
    }, intervalMs)
  }

  private measure(): void {
    if ('memory' in performance) {
      const memory = (performance as any).memory
      const measurement = {
        timestamp: Date.now(),
        used: memory.usedJSHeapSize,
        total: memory.totalJSHeapSize,
      }

      this.measurements.push(measurement)

      // Keep only last 50 measurements
      if (this.measurements.length > 50) {
        this.measurements.shift()
      }

      // Check threshold
      if (this.onThreshold && measurement.used > this.threshold) {
        this.onThreshold(measurement.used / (1024 * 1024))
      }
    }
  }

  onMemoryThreshold(callback: (usageMB: number) => void): void {
    this.onThreshold = callback
  }

  getCurrentUsage(): { used: number; total: number } | null {
    if ('memory' in performance) {
      const memory = (performance as any).memory
      return {
        used: memory.usedJSHeapSize / (1024 * 1024), // MB
        total: memory.totalJSHeapSize / (1024 * 1024), // MB
      }
    }
    return null
  }

  getHistory(): Array<{ timestamp: number; used: number; total: number }> {
    return [...this.measurements]
  }

  destroy(): void {
    clearInterval(this.interval)
    this.measurements = []
    this.onThreshold = undefined
  }
}

// Object pool for memory efficiency
export class ObjectPool<T> {
  private pool: T[] = []
  private createFn: () => T
  private resetFn: (obj: T) => void
  private maxSize: number

  constructor(createFn: () => T, resetFn: (obj: T) => void = () => {}, maxSize: number = 100) {
    this.createFn = createFn
    this.resetFn = resetFn
    this.maxSize = maxSize
  }

  acquire(): T {
    let obj = this.pool.pop()

    if (!obj) {
      obj = this.createFn()
    }

    return obj
  }

  release(obj: T): void {
    if (this.pool.length < this.maxSize) {
      this.resetFn(obj)
      this.pool.push(obj)
    }
  }

  clear(): void {
    this.pool = []
  }

  get size(): number {
    return this.pool.length
  }
}

// Automatic garbage collection helper
export class GCScheduler {
  private interval: number
  private threshold: number
  private lastGC: number = Date.now()

  constructor(intervalMs: number = 30000, memoryThresholdMB: number = 150) {
    this.threshold = memoryThresholdMB * 1024 * 1024
    this.interval = setInterval(() => {
      this.checkAndGC()
    }, intervalMs)
  }

  private checkAndGC(): void {
    if ('memory' in performance && 'gc' in window) {
      const memory = (performance as any).memory
      const now = Date.now()

      // Force GC if memory usage is high or it's been a while
      if (memory.usedJSHeapSize > this.threshold || now - this.lastGC > 60000) {
        try {
          ;(window as any).gc()
          this.lastGC = now
        } catch (_error) {
          // GC not available in this environment
        }
      }
    }
  }

  forceGC(): void {
    if ('gc' in window) {
      try {
        ;(window as any).gc()
        this.lastGC = Date.now()
      } catch (error) {
        logger.warn('Manual GC failed:', { module: 'MemoryManagement' }, error)
      }
    }
  }

  destroy(): void {
    clearInterval(this.interval)
  }
}

// Data structure for memory-efficient row storage
export class RowDataManager {
  private rowCache = new WeakCache<object, Map<string, any>>()
  private columnCache = new Map<string, any>()
  private pool = new ObjectPool<Map<string, any>>(
    () => new Map(),
    map => map.clear()
  )

  // Get row data with caching
  getRowData(row: Record<string, unknown>, columnId: string): unknown {
    let rowMap = this.rowCache.get(row)

    if (!rowMap) {
      rowMap = this.pool.acquire()
      this.rowCache.set(row, rowMap)
    }

    if (!rowMap.has(columnId)) {
      // Compute and cache the value
      const value = (row as any)[columnId]
      rowMap.set(columnId, value)
    }

    return rowMap.get(columnId)
  }

  // Release row data when no longer needed
  releaseRowData(row: object): void {
    const rowMap = this.rowCache.get(row)
    if (rowMap) {
      this.pool.release(rowMap)
    }
  }

  // Clear all caches
  clear(): void {
    this.columnCache.clear()
    this.pool.clear()
  }

  getStats(): { poolSize: number; cacheSize: number } {
    return {
      poolSize: this.pool.size,
      cacheSize: this.columnCache.size,
    }
  }
}

// Vue composable for memory management
export function useMemoryManagement() {

  const resourceManager = new ResourceManager()
  const memoryMonitor = new MemoryMonitor()
  const memoryUsage = ref<{ used: number; total: number } | null>(null)
  const memoryWarning = ref(false)

  // Monitor memory usage
  memoryMonitor.onMemoryThreshold(usage => {
    memoryWarning.value = true
    logger.warn(`High memory usage detected: ${usage.toFixed(2)}MB`)
  })

  // Update memory usage periodically
  const updateMemoryUsage = () => {
    memoryUsage.value = memoryMonitor.getCurrentUsage()
  }

  resourceManager.setInterval(updateMemoryUsage, 5000)

  // Watch for memory warnings and trigger cleanup
  const stopWatcher = watch(memoryWarning, warning => {
    if (warning) {
      // Force garbage collection if available
      if ('gc' in window) {
        try {
          ;(window as any).gc()
        } catch (_error) {
          logger.warn('GC not available')
        }
      }

      // Reset warning after 10 seconds
      resourceManager.setTimeout(() => {
        memoryWarning.value = false
      }, 10000)
    }
  })

  // Cleanup on unmount
  onUnmounted(() => {
    stopWatcher()
    resourceManager.cleanup()
    memoryMonitor.destroy()
  })

  return {
    resourceManager,
    memoryUsage,
    memoryWarning,
    getCurrentMemoryUsage: () => memoryMonitor.getCurrentUsage(),
    getMemoryHistory: () => memoryMonitor.getHistory(),
    forceCleanup: () => {
      resourceManager.cleanup()
      if ('gc' in window) {
        try {
          ;(window as any).gc()
        } catch (_error) {
          logger.warn('Manual GC failed')
        }
      }
    },
  }
}

// Debounced array operations for large datasets
export class LazyArray<T> {
  private data: T[] = []
  private pendingOperations: Array<() => void> = []
  private rafId: number | null = null

  constructor(initialData: T[] = []) {
    this.data = [...initialData]
  }

  push(item: T): void {
    this.scheduleOperation(() => {
      this.data.push(item)
    })
  }

  pop(): T | undefined {
    let result: T | undefined
    this.scheduleOperation(() => {
      result = this.data.pop()
    })
    return result
  }

  splice(start: number, deleteCount?: number, ...items: T[]): T[] {
    let result: T[] = []
    this.scheduleOperation(() => {
      result = this.data.splice(start, deleteCount || 0, ...items)
    })
    return result
  }

  get(index: number): T | undefined {
    return this.data[index]
  }

  get length(): number {
    return this.data.length
  }

  toArray(): T[] {
    this.flush()
    return [...this.data]
  }

  private scheduleOperation(operation: () => void): void {
    this.pendingOperations.push(operation)

    if (this.rafId) return

    this.rafId = requestAnimationFrame(() => {
      const operations = this.pendingOperations.splice(0)
      operations.forEach(op => op())
      this.rafId = null
    })
  }

  flush(): void {
    if (this.rafId) {
      cancelAnimationFrame(this.rafId)
      this.rafId = null
    }

    const operations = this.pendingOperations.splice(0)
    operations.forEach(op => op())
  }

  clear(): void {
    this.flush()
    this.data = []
  }
}
