/**
 * Utility functions for functional columns
 */

import type {
  ColumnDef,
  InternalColumnDef,
  FunctionalColumnDef,
  FunctionalColumnType,
} from '../types'
import {
  isFunctionalColumn,
  FUNCTIONAL_COLUMN_DEFAULTS,
  DEFAULT_LEFT_FUNCTIONAL_ORDER,
} from '../types/functional-columns'

// Re-export for convenience
export { isFunctionalColumn }

/**
 * Generate a unique ID for a functional column
 */
export function generateFunctionalColumnId(type: FunctionalColumnType, index: number = 0): string {
  return `__functional_${type}_${index}`
}

/**
 * Normalize a functional column definition with defaults
 */
export function normalizeFunctionalColumn(
  col: FunctionalColumnDef,
  index: number = 0
): InternalColumnDef {
  const defaults = FUNCTIONAL_COLUMN_DEFAULTS[col.type]

  return {
    ...col,
    id: col.id || generateFunctionalColumnId(col.type, index),
    field: col.field || `__functional_${col.type}`,
    title: col.title || '',
    pinned: col.pinned || defaults.pinned,
    width: col.width ?? defaults.width,
    minWidth: col.minWidth ?? defaults.minWidth,
    resizable: col.resizable ?? false,
    isDraggable: col.isDraggable ?? false,
    sortable: false,
    filterable: false,
    visible: col.visible ?? true,
  } as InternalColumnDef
}

/**
 * Create a default functional column definition
 */
export function createDefaultFunctionalColumn(type: FunctionalColumnType, index: number = 0): FunctionalColumnDef {
  const defaults = FUNCTIONAL_COLUMN_DEFAULTS[type]

  const base = {
    type,
    id: generateFunctionalColumnId(type, index),
    field: `__functional_${type}`,
    title: '',
    pinned: defaults.pinned,
    width: defaults.width,
    minWidth: defaults.minWidth,
  }

  // Return with proper discriminated union type
  switch (type) {
    case 'checkbox':
      return { ...base, type: 'checkbox' } as FunctionalColumnDef
    case 'radio':
      return { ...base, type: 'radio' } as FunctionalColumnDef
    case 'seq':
      return { ...base, type: 'seq' } as FunctionalColumnDef
    case 'drag':
      return { ...base, type: 'drag' } as FunctionalColumnDef
    case 'actions':
      return { ...base, type: 'actions' } as FunctionalColumnDef
    default:
      return { ...base, type: 'seq' } as FunctionalColumnDef
  }
}

/**
 * Merge functional columns from props and columnDefs
 * Deduplicates by type and maintains order
 */
export function mergeFunctionalColumns(
  propsColumns: FunctionalColumnType[] | boolean | undefined,
  columnDefs: ColumnDef[]
): ColumnDef[] {
  // Extract existing functional columns from columnDefs
  const existingFunctional = columnDefs.filter(isFunctionalColumn)
  const existingTypes = new Set(existingFunctional.map(c => (c as any).type as FunctionalColumnType))

  // Determine which types to add from props
  const propsTypes: FunctionalColumnType[] = Array.isArray(propsColumns)
    ? propsColumns
    : propsColumns === true
      ? [] // true means use only what's in columnDefs
      : []

  // Create missing functional columns from props
  const additionalColumns = propsTypes
    .filter(type => !existingTypes.has(type))
    .map((type, index) => createDefaultFunctionalColumn(type, existingFunctional.length + index))

  // Merge and return - cast to ColumnDef[] since FunctionalColumnDef extends ColumnDef
  return [...additionalColumns, ...columnDefs] as ColumnDef[]
}

/**
 * Sort functional columns by default order
 * Left-pinned functional columns: drag → checkbox → radio → seq
 * Right-pinned: actions
 * Non-functional: preserved order
 */
export function sortFunctionalColumns(columns: InternalColumnDef[]): InternalColumnDef[] {
  const functional = columns.filter(isFunctionalColumn)
  const nonFunctional = columns.filter(c => !isFunctionalColumn(c))

  const leftFunctional = functional
    .filter(c => c.pinned === 'left')
    .sort((a, b) => {
      const aType = (a as any).type as FunctionalColumnType
      const bType = (b as any).type as FunctionalColumnType
      const aIndex = DEFAULT_LEFT_FUNCTIONAL_ORDER.indexOf(aType)
      const bIndex = DEFAULT_LEFT_FUNCTIONAL_ORDER.indexOf(bType)
      return (aIndex === -1 ? 999 : aIndex) - (bIndex === -1 ? 999 : bIndex)
    })

  const rightFunctional = functional.filter(c => c.pinned === 'right')

  return [
    ...leftFunctional,
    ...nonFunctional,
    ...rightFunctional,
  ]
}

/**
 * Enforce mutual exclusivity for selection columns (checkbox/radio).
 * If both exist or duplicates exist, keep the first encountered among
 * checkbox/radio in the original input order and remove the rest.
 * Also deduplicate multiple instances of the same selection type.
 */
export function enforceSelectionColumnExclusivity(columns: InternalColumnDef[]): InternalColumnDef[] {
  let seenSelection = false
  return columns.filter(col => {
    if (!isFunctionalColumn(col)) return true
    const t = (col as any).type as FunctionalColumnType
    if (t === 'checkbox' || t === 'radio') {
      if (seenSelection) return false
      seenSelection = true
      return true
    }
    return true
  })
}

/**
 * Validate functional column dependencies
 * Returns validation result with error message if invalid
 */
export interface FunctionalColumnValidation {
  valid: boolean
  error?: string
  columnId?: string
}

export function validateFunctionalColumn(
  col: FunctionalColumnDef,
  hasPlugin: (name: string) => boolean
): FunctionalColumnValidation {
  // Validate drag column requires drag-drop plugin
  if (col.type === 'drag') {
    if (!hasPlugin('drag-drop')) {
      return {
        valid: false,
        error: 'Drag column requires "drag-drop" plugin. Please install it or remove the drag column.',
        columnId: col.id,
      }
    }
  }

  // Add more validations as needed
  return { valid: true }
}

/**
 * Check if selection mode is compatible with functional columns
 */
export function validateSelectionMode(
  columns: ColumnDef[],
  selectionMode: string | undefined
): { valid: boolean; warning?: string; suggestedMode?: string } {
  const hasCheckbox = columns.some(c => isFunctionalColumn(c) && (c as FunctionalColumnDef).type === 'checkbox')
  const hasRadio = columns.some(c => isFunctionalColumn(c) && (c as FunctionalColumnDef).type === 'radio')

  if (hasCheckbox && selectionMode !== 'multiple') {
    return {
      valid: false,
      warning: 'Checkbox column detected but selectionMode is not "multiple". Auto-adjusting to "multiple".',
      suggestedMode: 'multiple',
    }
  }

  if (hasRadio && selectionMode !== 'single') {
    return {
      valid: false,
      warning: 'Radio column detected but selectionMode is not "single". Auto-adjusting to "single".',
      suggestedMode: 'single',
    }
  }

  if (hasCheckbox && hasRadio) {
    return {
      valid: false,
      warning: 'Both checkbox and radio columns detected. Please use only one selection column type.',
    }
  }

  return { valid: true }
}
