<script setup lang="ts">
import { computed, useId } from 'vue'
import type { CheckboxStyleProps } from '../../types/primitives'

/**
 * HtCheckbox - Atomic Checkbox Component
 *
 * Design: shadcn-vue pattern (80%) + Headless UI interactions (10%)
 * Features: Variant/Size/State system, indeterminate support, full accessibility
 */

export interface HtCheckboxProps extends CheckboxStyleProps {
  // Core
  modelValue?: boolean | 'indeterminate'
  value?: string | number // Value for checkbox groups

  // Functionality
  disabled?: boolean
  required?: boolean
  indeterminate?: boolean

  // Label
  label?: string
  description?: string

  // Accessibility
  name?: string
}

export interface HtCheckboxEmits {
  'update:modelValue': [value: boolean | 'indeterminate']
  'change': [checked: boolean, event: Event]
}

const props = withDefaults(defineProps<HtCheckboxProps>(), {
  size: 'md',
  variant: 'default',
  state: 'default',
  modelValue: false,
  indeterminate: false,
})

const emit = defineEmits<HtCheckboxEmits>()

// Generate stable ID for accessibility (SSR compatible)
const checkboxId = useId()

// Computed
const isChecked = computed(() => {
  if (props.indeterminate || props.modelValue === 'indeterminate') {
    return false
  }
  return props.modelValue === true
})

const isIndeterminate = computed(() => {
  return props.indeterminate || props.modelValue === 'indeterminate'
})

// Base styles aligned with HtButton/HtInput pattern + 2025 UI standards
const baseClasses = [
  'inline-flex items-center justify-center',
  'rounded-sm border-[1px]', // Use 1.5px border for better visibility
  'cursor-pointer',
  'transition-all duration-200', // Smooth 0.2s transitions per 2025 standards
  'will-change-auto',
].join(' ')

// Variant system (matches CoreVariant from shared types)
// Uses text color for maximum visibility in dark mode, matching main text brightness
const variantClasses: Record<string, string> = {
  default: 'border-[var(--ht-text)] bg-[var(--ht-bg)]',
  filled: 'border-[var(--ht-text)] bg-[var(--ht-bg-subtle)]',
  outline: 'border-[var(--ht-text)] bg-transparent',
  ghost: 'border-[var(--ht-text)] bg-transparent',
}

// Checked state styles - uses high-opacity primary bg for clear visibility (2025 UI standards)
const checkedVariantClasses: Record<string, string> = {
  default: 'border-[var(--ht-primary)] bg-[var(--ht-primary)]/90 text-[var(--ht-text-inverse)]',
  filled: 'border-[var(--ht-primary)] bg-[var(--ht-primary)]/90 text-[var(--ht-text-inverse)]',
  outline: 'border-[var(--ht-primary)] bg-[var(--ht-primary)]/90 text-[var(--ht-text-inverse)]',
  ghost: 'border-[var(--ht-primary)] bg-[var(--ht-primary)]/90 text-[var(--ht-text-inverse)]',
}

// Disabled unchecked state - gray border, no background (for label element)
const disabledVariantClasses: Record<string, string> = {
  default: 'border-[var(--ht-text-subtle)] bg-[var(--ht-bg)]',
  filled: 'border-[var(--ht-text-subtle)] bg-[var(--ht-bg-subtle)]',
  outline: 'border-[var(--ht-text-subtle)] bg-transparent',
  ghost: 'border-[var(--ht-text-subtle)] bg-transparent',
}

// Disabled checked state - gray border and background (for label element)
const disabledCheckedVariantClasses: Record<string, string> = {
  default: 'border-[var(--ht-text-subtle)] bg-[var(--ht-text-subtle)]/10 text-[var(--ht-text-subtle)]',
  filled: 'border-[var(--ht-text-subtle)] bg-[var(--ht-text-subtle)]/10 text-[var(--ht-text-subtle)]',
  outline: 'border-[var(--ht-text-subtle)] bg-[var(--ht-text-subtle)]/10 text-[var(--ht-text-subtle)]',
  ghost: 'border-[var(--ht-text-subtle)] bg-[var(--ht-text-subtle)]/10 text-[var(--ht-text-subtle)]',
}

// Common disabled behavior - 40% opacity per 2025 UI standards
const disabledClasses = [
  'opacity-40',
  'cursor-not-allowed',
  'pointer-events-none',
].join(' ')

// Size system (uses @theme tokens from primitives.css)
const sizeClasses: Record<string, string> = {
  sm: 'w-4 h-4',   // 16px
  md: 'w-5 h-5',   // 20px
  lg: 'w-6 h-6',   // 24px
}

// State system (visual feedback for validation)
const stateClasses: Record<string, string> = {
  default: 'focus-visible:ring-[var(--ht-primary)]',
  error: 'border-[var(--ht-danger)] focus-visible:ring-[var(--ht-danger)]',
  success: 'border-[var(--ht-success)] focus-visible:ring-[var(--ht-success)]',
  warning: 'border-[var(--ht-warning)] focus-visible:ring-[var(--ht-warning)]',
}

const checkboxClasses = computed(() => {
  const isActive = isChecked.value || isIndeterminate.value

  // Choose classes based on disabled state and checked state
  let stateClass: string
  if (props.disabled) {
    // Disabled state - use gray colors
    stateClass = isActive
      ? (disabledCheckedVariantClasses[props.variant] ?? '')
      : (disabledVariantClasses[props.variant] ?? '')
  } else {
    // Enabled state - use primary colors when checked
    stateClass = isActive
      ? (checkedVariantClasses[props.variant] ?? '')
      : (variantClasses[props.variant] ?? '')
  }

  return [
    baseClasses,
    props.disabled ? disabledClasses : '',
    stateClass,
    sizeClasses[props.size],
    stateClasses[props.state],
  ].join(' ')
})

// Icon size based on checkbox size
const iconSize = computed(() => {
  const sizes = {
    sm: 'w-3 h-3',
    md: 'w-4 h-4',
    lg: 'w-5 h-5',
  }
  return sizes[props.size]
})

// Top margin offset based on checkbox size for better text alignment
// All sizes use top alignment (items-start) for consistency with multi-line text
const topOffset = computed(() => {
  const offsets = {
    sm: 'mt-[-4px]',  // 16px checkbox + 12px text: negative offset to lift checkbox closer to text top
    md: 'mt-[-1px]',     // 20px checkbox + 14px text: 2px positive offset
    lg: 'mt-[2px]',   // 24px checkbox + 16px text: 3px positive offset for better balance
  }
  return offsets[props.size]
})

// Text size based on checkbox size for proportional appearance
const textSize = computed(() => {
  const sizes = {
    sm: 'text-xs',    // 12px font for 16px checkbox
    md: 'text-sm',    // 14px font for 20px checkbox
    lg: 'text-base',  // 16px font for 24px checkbox
  }
  return sizes[props.size]
})

// Description text size (always one step smaller than label)
const descriptionSize = computed(() => {
  const sizes = {
    sm: 'text-[11px]',  // Extra small for sm checkbox
    md: 'text-xs',      // 12px for md checkbox
    lg: 'text-sm',      // 14px for lg checkbox
  }
  return sizes[props.size]
})

// Methods
const handleChange = (event: Event) => {
  if (props.disabled) return

  const target = event.target as HTMLInputElement
  const checked = target.checked

  emit('update:modelValue', checked)
  emit('change', checked, event)
}

const handleLabelClick = (event: MouseEvent) => {
  // Prevent default label behavior that might cause unwanted scrolling
  // The click will still toggle the checkbox via the for attribute
  event.preventDefault()

  if (props.disabled) return

  // Manually toggle the checkbox
  const newValue = !isChecked.value
  emit('update:modelValue', newValue)
  emit('change', newValue, event)
}

const handleKeydown = (event: KeyboardEvent) => {
  // Space key toggles checkbox
  if (event.key === ' ') {
    event.preventDefault()
    const checkbox = event.target as HTMLInputElement
    checkbox.click()
  }
}
</script>

<template>
  <div class="ht-checkbox-wrapper inline-flex items-start gap-2">
    <div class="ht-checkbox-container relative flex-shrink-0" :class="topOffset">
      <!-- Hidden native checkbox for form integration -->
      <input
        :id="checkboxId"
        type="checkbox"
        :name="name"
        :value="value"
        :checked="isChecked"
        :disabled="disabled"
        :required="required"
        :aria-checked="isIndeterminate ? 'mixed' : isChecked"
        :aria-invalid="state === 'error'"
        :aria-required="required"
        class="sr-only peer"
        @change="handleChange"
        @keydown="handleKeydown"
      />

      <!-- Visual checkbox -->
      <label
        :for="checkboxId"
        :class="checkboxClasses"
        @click="handleLabelClick"
      >
        <!-- Always render SVG container to prevent layout shift -->
        <!-- Check icon -->
        <svg
          v-show="isChecked && !isIndeterminate"
          :class="iconSize"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
          stroke-width="3"
          aria-hidden="true"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            d="M5 13l4 4L19 7"
          />
        </svg>

        <!-- Indeterminate icon -->
        <svg
          v-show="isIndeterminate"
          :class="iconSize"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
          stroke-width="3"
          aria-hidden="true"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            d="M6 12h12"
          />
        </svg>

        <!-- Empty placeholder for unchecked state -->
        <span
          v-show="!isChecked && !isIndeterminate"
          :class="iconSize"
          aria-hidden="true"
        ></span>
      </label>
    </div>

    <!-- Label and description -->
    <div v-if="label || description || $slots.default" class="ht-checkbox-label-container flex-1">
      <label
        v-if="label || $slots.default"
        :for="checkboxId"
        :class="[
          textSize,
          'font-medium text-[var(--ht-text)] cursor-pointer select-none block leading-snug',
          { 'opacity-50': disabled }
        ]"
      >
        <slot>{{ label }}</slot>
      </label>
      <p
        v-if="description || $slots.description"
        :class="[
          descriptionSize,
          'text-[var(--ht-text-muted)] mt-1 leading-snug',
          { 'opacity-50': disabled }
        ]"
      >
        <slot name="description">{{ description }}</slot>
      </p>
    </div>
  </div>
</template>

<style scoped>
/* Screen reader only - hidden but accessible */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border-width: 0;
}

/* Prevent layout shift on state change */
.ht-checkbox-container {
  /* Use contain to isolate layout changes */
  contain: layout;
}

.ht-checkbox-container label {
  /* Fixed dimensions prevent layout shift */
  display: inline-flex;
  /* Prevent any size changes during transitions */
  flex-shrink: 0;
}

/* Focus state from peer (native checkbox) - use outline with offset for universal visibility */
.peer:focus-visible + label {
  outline: 1px solid var(--ht-primary);
  outline-offset: 1px;
}

/* Disabled state */
.ht-checkbox-container input:disabled + label {
  cursor: not-allowed;
}

/* Ensure icons don't trigger layout */
.ht-checkbox-container label > * {
  display: block;
  flex-shrink: 0;
}
</style>
