/**
 * Table Interactions - Event handling and user interaction management for TableRenderer
 *
 * Provides a clean abstraction layer between DOM events and the GridInteractionContract,
 * handling keyboard navigation, mouse interactions, and accessibility features.
 */

import type { GridInteractionContract } from '../base/renderer-interface'
import type { ColumnDef, RowData, GridAPI } from '../../types'

/**
 * Keyboard navigation directions
 */
type NavigationDirection = 'up' | 'down' | 'left' | 'right' | 'home' | 'end' | 'pageUp' | 'pageDown'

/**
 * Mouse interaction types
 */
type MouseInteractionType = 'click' | 'doubleClick' | 'contextMenu' | 'dragStart' | 'dragEnd'

/**
 * Cell position interface
 */
interface CellPosition {
  rowIndex: number
  columnIndex: number
  rowId: string | number
  columnId: string
}

/**
 * TableInteractionHandler - Manages all user interactions for TableRenderer
 */
export class TableInteractionHandler {
  private keyboardNavEnabled = true
  private mouseNavEnabled = true
  private editMode = false
  private currentFocus: CellPosition | null = null
  private isDestroyed = false
  private readonly interactionContract: GridInteractionContract
  private readonly gridAPI: GridAPI

  constructor(interactionContract: GridInteractionContract, gridAPI: GridAPI) {
    this.interactionContract = interactionContract
    this.gridAPI = gridAPI
    this.setupEventListeners()
  }

  /**
   * Initialize interaction handling for a container element
   */
  initialize(container: HTMLElement): void {
    if (this.isDestroyed) return

    // Set up basic interaction attributes
    container.setAttribute('role', 'grid')
    container.setAttribute('tabindex', '0')

    // Initialize keyboard navigation plugin if available
    if ((this.gridAPI as any).initializeKeyboardHandling) {
      ;(this.gridAPI as any).initializeKeyboardHandling(container)
    } else {
      // Fallback to basic keyboard navigation
      this.enableKeyboardNavigation(container)
    }

    // Enable mouse interactions
    this.enableMouseInteractions(container)
  }

  /**
   * Enable keyboard navigation
   */
  enableKeyboardNavigation(container: HTMLElement): void {
    if (!this.keyboardNavEnabled) return

    const handleKeyDown = (event: KeyboardEvent) => {
      this.handleKeyboardEvent(event)
    }

    container.addEventListener('keydown', handleKeyDown)

    // Store cleanup function
    const cleanup = () => {
      container.removeEventListener('keydown', handleKeyDown)
    }

    // Store cleanup for later
    ;(container as any).__cleanup = cleanup
  }

  /**
   * Enable mouse interactions
   */
  enableMouseInteractions(container: HTMLElement): void {
    if (!this.mouseNavEnabled) return

    const handleClick = (event: MouseEvent) => {
      this.handleMouseEvent(event, 'click')
    }

    const handleDoubleClick = (event: MouseEvent) => {
      this.handleMouseEvent(event, 'doubleClick')
    }

    const handleContextMenu = (event: MouseEvent) => {
      this.handleMouseEvent(event, 'contextMenu')
    }

    container.addEventListener('click', handleClick)
    container.addEventListener('dblclick', handleDoubleClick)
    container.addEventListener('contextmenu', handleContextMenu)

    // Store cleanup function
    const cleanup = () => {
      container.removeEventListener('click', handleClick)
      container.removeEventListener('dblclick', handleDoubleClick)
      container.removeEventListener('contextmenu', handleContextMenu)
    }

    // Store cleanup for later
    ;(container as any).__cleanup = cleanup
  }

  /**
   * Handle keyboard events
   */
  private handleKeyboardEvent(event: KeyboardEvent): void {
    if (!this.keyboardNavEnabled || this.isDestroyed) return

    // Delegate to keyboard navigation plugin if available
    if ((this.gridAPI as any).handleKeyboardEvent) {
      const handled = (this.gridAPI as any).handleKeyboardEvent(event)
      if (handled) {
        return
      }
    }

    // Fallback to basic navigation
    const direction = this.getNavigationDirection(event)
    if (direction) {
      event.preventDefault()
      this.navigateCell(direction)
    }

    // Handle other keyboard shortcuts
    switch (event.key) {
      case 'Enter':
        if (this.currentFocus) {
          event.preventDefault()
          this.activateCell(this.currentFocus)
        }
        break
      case 'Escape':
        if (this.editMode) {
          event.preventDefault()
          this.exitEditMode()
        }
        break
      case ' ':
        if (this.currentFocus && !this.editMode) {
          event.preventDefault()
          this.toggleCellSelection(this.currentFocus)
        }
        break
    }
  }

  /**
   * Handle mouse events
   */
  private handleMouseEvent(event: MouseEvent, type: MouseInteractionType): void {
    if (!this.mouseNavEnabled || this.isDestroyed) return

    const cellInfo = this.getCellFromEvent(event)
    if (!cellInfo) return

    switch (type) {
      case 'click':
        this.handleCellClick(event, cellInfo.position, cellInfo.rowData, cellInfo.columnDef)
        break
      case 'doubleClick':
        this.handleCellDoubleClick(event, cellInfo.position, cellInfo.rowData, cellInfo.columnDef)
        break
      case 'contextMenu':
        this.handleCellRightClick(event, cellInfo.position, cellInfo.rowData, cellInfo.columnDef)
        break
    }
  }

  /**
   * Get navigation direction from keyboard event
   */
  private getNavigationDirection(event: KeyboardEvent): NavigationDirection | null {
    switch (event.key) {
      case 'ArrowUp':
        return 'up'
      case 'ArrowDown':
        return 'down'
      case 'ArrowLeft':
        return 'left'
      case 'ArrowRight':
        return 'right'
      case 'Home':
        return 'home'
      case 'End':
        return 'end'
      case 'PageUp':
        return 'pageUp'
      case 'PageDown':
        return 'pageDown'
      default:
        return null
    }
  }

  /**
   * Navigate to a cell based on direction
   */
  private navigateCell(direction: NavigationDirection): void {
    if (!this.currentFocus) {
      // Set initial focus to first cell
      this.setFocus({ rowIndex: 0, columnIndex: 0, rowId: '0', columnId: 'col-0' })
      return
    }

    let newPosition: CellPosition | null = null

    switch (direction) {
      case 'up':
        if (this.currentFocus.rowIndex > 0) {
          newPosition = {
            ...this.currentFocus,
            rowIndex: this.currentFocus.rowIndex - 1,
            rowId: String(this.currentFocus.rowIndex - 1),
          }
        }
        break
      case 'down':
        newPosition = {
          ...this.currentFocus,
          rowIndex: this.currentFocus.rowIndex + 1,
          rowId: String(this.currentFocus.rowIndex + 1),
        }
        break
      case 'left':
        if (this.currentFocus.columnIndex > 0) {
          newPosition = {
            ...this.currentFocus,
            columnIndex: this.currentFocus.columnIndex - 1,
            columnId: `col-${this.currentFocus.columnIndex - 1}`,
          }
        }
        break
      case 'right':
        newPosition = {
          ...this.currentFocus,
          columnIndex: this.currentFocus.columnIndex + 1,
          columnId: `col-${this.currentFocus.columnIndex + 1}`,
        }
        break
      case 'home':
        newPosition = {
          ...this.currentFocus,
          columnIndex: 0,
          columnId: 'col-0',
        }
        break
      case 'end':
        // Move to last column (would need column count)
        break
    }

    if (newPosition) {
      this.setFocus(newPosition)
    }
  }

  /**
   * Set focus to a specific cell
   */
  private setFocus(position: CellPosition): void {
    this.currentFocus = position

    // Notify interaction contract
    this.interactionContract.setActive(position.rowId)

    // Ensure cell is visible
    this.interactionContract.ensureVisible(position.rowId, 'smooth')
  }

  /**
   * Activate a cell (enter edit mode or trigger action)
   */
  private activateCell(position: CellPosition): void {
    // Enter edit mode for the cell
    this.interactionContract.toggleEditMode(position.rowId, 'enter')
    this.editMode = true
  }

  /**
   * Exit edit mode
   */
  private exitEditMode(): void {
    if (this.currentFocus) {
      this.interactionContract.toggleEditMode(this.currentFocus.rowId, 'exit')
    }
    this.editMode = false
  }

  /**
   * Toggle cell selection
   */
  private toggleCellSelection(position: CellPosition): void {
    // Create a selection set with the current cell
    const selection = new Set<string | number>([position.rowId])
    this.interactionContract.updateSelection(selection, 'user')
  }

  /**
   * Handle cell click
   */
  private handleCellClick(
    _event: MouseEvent,
    position: CellPosition,
    rowData: RowData,
    columnDef: ColumnDef
  ): void {
    // Set focus to clicked cell
    this.setFocus(position)

    // Emit cell click event
    this.interactionContract.emit?.('cell-click', {
      row: rowData,
      column: columnDef,
      value: rowData[columnDef.field],
    })
  }

  /**
   * Handle cell double-click
   */
  private handleCellDoubleClick(
    _event: MouseEvent,
    position: CellPosition,
    rowData: RowData,
    columnDef: ColumnDef
  ): void {
    // Enter edit mode on double-click
    this.activateCell(position)

    // Emit cell double-click event
    this.interactionContract.emit?.('cell-double-click', {
      row: rowData,
      column: columnDef,
      value: rowData[columnDef.field],
    })
  }

  /**
   * Handle cell right-click
   */
  private handleCellRightClick(
    event: MouseEvent,
    _position: CellPosition,
    rowData: RowData,
    columnDef: ColumnDef
  ): void {
    event.preventDefault()

    // Emit cell right-click event
    this.interactionContract.emit?.('cell-right-click', {
      row: rowData,
      column: columnDef,
      value: rowData[columnDef.field],
    })
  }

  /**
   * Get cell information from DOM event
   */
  private getCellFromEvent(event: MouseEvent): {
    position: CellPosition
    rowData: RowData
    columnDef: ColumnDef
  } | null {
    // Basic implementation - would need proper DOM traversal
    const target = event.target as HTMLElement
    const cellElement = target.closest('[data-cell-id]')

    if (!cellElement) return null

    // Extract position from data attributes
    const cellId = cellElement.getAttribute('data-cell-id')
    const rowId = cellElement.getAttribute('data-row-id')
    const columnId = cellElement.getAttribute('data-column-id')

    if (!cellId || !rowId || !columnId) return null

    // Parse indices from IDs (basic implementation)
    const rowIndex = parseInt(rowId, 10)
    const columnIndex = parseInt(columnId, 10)

    return {
      position: {
        rowIndex,
        columnIndex,
        rowId,
        columnId,
      },
      rowData: {}, // Would need to get actual row data
      columnDef: {} as ColumnDef, // Would need to get actual column definition
    }
  }

  /**
   * Setup event listeners for interaction contract
   */
  private setupEventListeners(): void {
    // Register event emitter with interaction contract
    this.interactionContract.registerEventEmitter((eventName, payload) => {
      // Forward events to grid API
      this.gridAPI.emit(eventName, payload)
    })
  }

  /**
   * Enable/disable keyboard navigation
   */
  setKeyboardNavigationEnabled(enabled: boolean): void {
    this.keyboardNavEnabled = enabled
  }

  /**
   * Enable/disable mouse interactions
   */
  setMouseInteractionsEnabled(enabled: boolean): void {
    this.mouseNavEnabled = enabled
  }

  /**
   * Get current focus position
   */
  getCurrentFocus(): CellPosition | null {
    return this.currentFocus
  }

  /**
   * Check if in edit mode
   */
  isInEditMode(): boolean {
    return this.editMode
  }

  /**
   * Cleanup and destroy handler
   */
  destroy(): void {
    this.isDestroyed = true
    this.currentFocus = null
    this.editMode = false
  }
}

/**
 * Factory function to create table interaction handler
 */
export function createTableInteractionHandler(
  interactionContract: GridInteractionContract,
  gridAPI: GridAPI
): TableInteractionHandler {
  return new TableInteractionHandler(interactionContract, gridAPI)
}

export default createTableInteractionHandler
