<template>
  <HtRadio
    :model-value="isSelected ? props.rowData : undefined"
    :value="props.rowData"
    :disabled="isDisabled"
    :name="radioGroupName"
    :title="cellTitle"
    size="sm"
    variant="default"
    data-ht-functional="radio"
    @update:model-value="handleToggle"
    @click.stop
  />
</template>

<script setup lang="ts">
import { computed, inject } from 'vue'
import type { RowData, RadioColumnOptions, ColumnDef } from '../../types'
import { GRID_INSTANCE_ID_KEY } from '../../utils/injection-keys'
import { HtRadio } from '../primitives'

interface Props {
  /** Row data object */
  rowData: RowData
  /** Row index in the data array */
  rowIndex: number
  /** Whether this row is selected */
  isSelected: boolean
  /** Column definition */
  column: ColumnDef
  /** Column configuration options */
  options?: RadioColumnOptions
  /** Whether the row is filtered */
  isFiltered?: boolean
  /** Whether the row is grouped */
  isGrouped?: boolean
}

const props = withDefaults(defineProps<Props>(), {
  options: () => ({}),
  isFiltered: false,
  isGrouped: false,
})

const emit = defineEmits<{
  'toggle-selection': [rowData: RowData, rowIndex: number]
}>()

// Grid instance id ensures radio groups don't collide across grids
const gridInstanceId = inject<string>(GRID_INSTANCE_ID_KEY, 'ht-grid')

// Radio group name for grouping radio buttons
const radioGroupName = computed(() => {
  if (props.options?.radioGroupName) {
    return props.options.radioGroupName
  }

  const columnIdentifier = props.column?.id || props.column?.field || 'radio'
  return `${gridInstanceId}--${columnIdentifier}`
})

const selectabilityResult = computed(() => {
  // Use condition from column definition (top-level)
  if (props.column.condition) {
    const isSelectable = props.column.condition(props.rowData)
    return {
      disabled: !isSelectable,
      reason: isSelectable ? undefined : 'Row does not meet selection criteria',
    }
  }

  return { disabled: false }
})

const isDisabled = computed(() => selectabilityResult.value.disabled)
const disabledReason = computed(() => selectabilityResult.value.reason)

const cellTitle = computed(() => {
  if (isDisabled.value) {
    return disabledReason.value || 'Cannot select this row'
  }
  return props.isSelected ? 'Selected row' : 'Select this row'
})

function handleToggle(_value: any) {
  if (!isDisabled.value) {
    emit('toggle-selection', props.rowData, props.rowIndex)
  }
}
</script>

<script lang="ts">
export default {
  name: 'HtRadioCell',
}
</script>
